<template>
        <PickerFrame
          :show="show"
          :header="headerFrame"
          :position="options.position"
          @close="show = false"
          @reload="reloadPicker"
          @loadFreeImg="loadFreeImg"
          @update:search="updateSearch"
          @update:tabs="$emit('update:tabs', $event)"
        >
          <template #content>
                  <div
                    v-for="i in sizeContent"
                    :key="i"
                  >
                      <PickerContent
                        v-if="options?.tabs?.cur == options?.tabs?.list?.[i - 1].value"
                        ref="picker-content"
                        :show="show"
                        :search="search"
                        :value="value"
                        :multiple="options.multiple"
                        :type="options.type"
                        :layout="options.layout"
                        :responsive="options.responsive"
                        :tab="options?.tabs?.cur"
                        @clickSelect="eSelect"
                      />
                  </div>
          </template>
          <template #footer>
            <PickerFooter
              :data="value"
              :is-change="isChange"
              :multiple="options.multiple"
              :type="options.type"
              :text-select="options.textSelect"
              @clickCancel="show = false"
              @clickSelect="eSelect"
            />
          </template>
        </PickerFrame>
</template>

<script>
import { mapGetters } from 'vuex';
import PickerFrame from './PickerFrame.vue';
import PickerContent from './PickerContent.vue';
import PickerFooter from './PickerFooter.vue';
export default {
  name: 'PickerPopup',
  components: {
    PickerFrame,
    PickerContent,
    PickerFooter,
  },
  props: {
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    options: {
        type: Object,
        default(){
            return {};
        }
    },
    isChange: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['isSelected', 'update:tabs'],

  data() {
    return {
      show: false,
      search: '',
      isSelect: false
    };
  },
  computed: {
    ...mapGetters('picker',{'store_search': 'search'}),
    headerFrame(){
        return {
            title: this.options?.title,
            type: this.options?.type,
            subtype: this.options?.tabs?.cur,
            search: {
                show: this.options.useSearch,
                value: this.search
            },
            tabs: this.options?.tabs ?? {},
            reload: true
        };
    },
    sizeContent(){
        return this.options.tabs?.list?.length ?? 1;
    },
    type_with_tab(){
      return {type: this.options.type ,subtype: (this.options.tabs.cur ?? '')};
    }
  },
  watch: {
    show: function (val) {
      if (!val) {
        this.$emit('isSelected', this.isSelect);
        this.isSelect = false;
        this.$store.commit('picker/freeImgCat', null);
      }
    }
  },
  mounted () {
    this.search = this.store_search(this.type_with_tab);
  },
  methods: {

    updateSearch(val) {
      this.search = val;
    },
    eSelect() {
      this.isSelect = true;
      this.show = false;
      this.$store.commit('picker/freeImgCat', null);
    },
    reloadPicker(){
      this.$store.dispatch('picker/reload', this.type_with_tab);
      if(this.$refs['picker-content'])
      {
        if(this.$refs['picker-content'].reload)
        {
            this.$refs['picker-content'].reload();
        }
        else if(this.$refs['picker-content'].length && this.$refs['picker-content'][0].reload)
        {
            this.$refs['picker-content'][0].reload();
        }
        else
        {
            console.log('$refs  found but not work' ,this.$refs);
        }

      }
      else
      {
          console.log('$refs not found' ,this.$refs);
      }

    },
    loadFreeImg() {
      if(this.$refs['picker-content'])
      {
        if(this.$refs['picker-content'].loadFreeCat)
        {
            this.$refs['picker-content'].loadFreeCat();
        }
        else if(this.$refs['picker-content'].length && this.$refs['picker-content'][0].loadFreeCat)
        {
            this.$refs['picker-content'][0].loadFreeCat();
        }
        else
        {
            console.log('$refs  found but not work' ,this.$refs);
        }

      }
      else
      {
          console.log('$refs not found' ,this.$refs);
      }

    }
  }
};
</script>
