import { vendors } from '@/constants';
export default {
    inject: ['exporting'],
    data() {
        return {
            vendorReady: false,
            executedJS: false
        }
    },
    computed: {

        edata() {
            return this.data;
        },

        prepareJavascript() {
            let js = this.layout && this.layout.javascript ? this.layout.javascript : this.javascript;
            if (typeof js == 'function') {
                js = js.toString();

                js =  js.substring(js.indexOf('{') + 1, js.lastIndexOf('}')).trim();
                if(this.exporting === false)
                {
                    js = 'window.__ectimmers = window.__ectimmers ||{};window.__ectimmers["'+ this.data.id+'"]=  window.__ectimmers["'+ this.data.id+'"] || {};'+"\n" + js;
                    js = js.replace(/(\=)?([\s]+)?(setInterval|window\.setInterval)/g,(...m)=>{
                        let randomid = Math.random().toString(36).substr(2, 9);
                        if(m[1])
                        {
                            return '= window.__ectimmers["'+this.data.id+'"]["'+randomid+'"] = '+m[3];
                        }
                        else
                        {
                            return 'window.__ectimmers["'+this.data.id+'"]["'+randomid+'"] = '+m[3];
                        }
                    });
                }
                return "'use strict';\n"+js;
            } else {
                return '';
            }
        },

        reactiveJavascript() {
            let typefill = (val) => {
                    if (val?.constructor === Array) {
                        return val.length;
                    } else if (val?.constructor === Object) {
                        return Object.keys(val).join(',');
                    } else {
                        return val;
                    }
                };
            /*this.prepareJavascript
                .replace(/([^\w\d]+|^)this\??\.settings\??(\.|\[('|"))([\w\d_]*)(\3\])?/g, (...m) => {
                    if (m[4]) {
                        react[m[4]] = typefill(this.data.settings ? this.data.settings[m[4]]: null);
                    }
                });*/
            let react = {};
            if (typeof this.jsreactives == 'object') {
                this.jsreactives.forEach((k) => {
                    if (
                        !react[k] && this.data?.settings &&
                        k in this.data.settings
                    ) {
                        react[k] = typefill(this.data.settings[k]);
                    }
                });
            }
            return react;
        },

        vendorsLoaded() {

            if (!this.$options.vendors && !this?.layout?.$options?.vendors) {
                return true;
            }

            let loaded = true,
                allVendors = [];

            if (this.$options.vendors) {
                allVendors = allVendors.concat(this.$options.vendors);
            }

            if (this?.layout?.$options?.vendors) {
                allVendors = allVendors.concat(this.layout.$options.vendors);
            }

            allVendors.forEach((name) => {
                if (
                    vendors &&
                    vendors[name] &&
                    vendors[name].match(/.js([?#]|$)/) &&
                    this.elementVendors.load[name] !== true
                ) {
                    loaded = false;
                }
            });

            return loaded;

        },

        elementVendors() {
            return this.$store.getters['builder/elementVendors'];
        },

        _id() {
            return this.data.id;
        }

    },
    methods: {
        runJavascript() {

            if (this.vendorReady !== true || this.exporting !== false) {
                return;
            }

            this.executedJS = true;

            if (this.prepareJavascript !== '') {
                if (
                    this.exporting === false &&
                    this.layout !== null
                ) {
                    try {
                        // console.log( this.prepareJavascript);
                        window.EComposerBuilder.contentWindow.Function(
                            this.prepareJavascript
                        ).call({
                            $el: this.$el,
                            settings: this.data.settings,
                            id: this.data.id
                        });
                    } catch (ex) {
                        this.$store.commit(
                            'global/flash',
                            {
                                open: true,
                                message: 'An error occurred while executing the javascript code of element <strong>'+this.$options.name+'</strong>',
                                type: 'error',
                            }
                        );

                        console.error('[ECOMPOSER ERROR] process javascript of element "' + this.$options.name + '": ', ex);
                        if(window.ECOM_DEBUG)
                        {
                            console.log(this.prepareJavascript);
                        }
                    }
                }
                this.$store.commit('page/storeJavascript', {
                    id: this.data.id,
                    data: this.prepareJavascript,
                    settings: this.data.settings
                });
            }
        }
    },
    watch: {
        reactiveJavascript: {
            deep: true,
            handler(val) {
                // Change the key of block to re-mount component to re-call js
                if (Object.keys(val).length > 0) {
                    this.edata.refresh = this.$helpers.randid();
                }
            }
        },
        vendorsLoaded: {
            immediate: true,
            handler(val) {
                if (val !== this.vendorReady) {
                    this.vendorReady = val;
                }
            }
        },
        vendorReady () {
            setTimeout(this.runJavascript, 1);
        }
    },
    mounted() {
        setTimeout(this.runJavascript, 1);
    },
    unmounted() {
        if (this.exporting === false) {
            // this.$el.dispatchEvent(new CustomEvent('ohyeah', {}));
            this.$store.commit('page/revokeJavascript', this.data.id);
            try {
                window.EComposerBuilder.contentWindow.Function(
                    'if(window.__ectimmers && window.__ectimmers["'+this.data.id+'"]) { for(let i in window.__ectimmers["'+this.data.id+'"]) { clearTimeout(window.__ectimmers["'+this.data.id+'"][i]); } window.__ectimmers["'+this.data.id+'"] = {}; }'
                ).call({

                });
            } catch (ex) {

                console.warn('[ECOMPOSER ERROR] error when revoke js: "' + this.$options.name + '": ', ex);
            }
            this.$el = null;
        }
    }
};
