<template>
    <Liquid :data="liquidBefore" />
    <Structure v-if="columns.length === 0" />
    <form
        class="ecom-click"
        v-else-if="(exporting === false || exporting === 'preview')  && ['product', 'cart'].indexOf(shopify_type) > -1"
        method="post"
        :action="shopify_type == 'product' ? '/cart/add' : '/cart/update'"
        :class="shopify_type == 'product' ? 'ecom-product-form ecom-product-form--single' : 'ecom-cart-form'"
        enctype="multipart/form-data"
        @submit.prevent="false"
    >
        <Row
            ref="row"
            :deep="1"
            :data="data"
            :index="index"
            :section_id="data.id"
        />
    </form>
    <Row
        v-else
        ref="row"
        :deep="1"
        :data="data"
        :index="index"
        :section_id="data.id"
    />
    <Liquid :data="liquidAfter" />
</template>

<script lang="ts">
    import { Core, ProcessLiquid, Element } from '@/mixins';
    import Structure from './Structure.vue';
    export default {
        name: 'Section',
        components: { Structure },
        mixins: [Core, Element, ProcessLiquid],
        inject: ['exporting'],
        props: {
            data: {
                type: Object,
                default () {
                    return {
                        columns: [],
                        settings: {
                        }
                    };
                }
            },
            'index': {
                type: Number,
                default: 0
            }
        },
        data() {
            return {
                timmer: 0,
                isProductBox: false
            };
        },
        computed: {
            deep () {
                return 1;
            },
            sections () {
                return this.$parent.sections;
            },
            visibleSections () {
                return this.$parent.visibleSections;
            },
            stack () {
                return this.sections;
            },
            section () {
                return this.data;
            },
            columns () {
                return this.section.columns ? this.section.columns : [];
            },
            isFirst () {
                return this.index == 0;
            },
            isLast () {
                return this.sections && this.index == this.sections.length - 1;
            },


            editingElement(){
                return this.$store.getters['builder/editingElement'];
            },
            sectionCss(){
                return this.$store.getters['page/sectionCss']
            },
            params(){
                return this.$store.getters['page/params']
            },
            current(){
                return this.$store.getters['page/section'](this.data?.id)
            }
        },

        watch: {
            current: {
                handler: function (val) {
                    if(this.exporting )
                    {
                        return false;
                    }

                    let activeElement =  window.document.activeElement;

                    if(activeElement?.type === 'INPUT' &&  activeElement?.type !== 'number')
                    {
                        return false;
                    }
                    else if( activeElement?.nodeName === 'INPUT'
                            && activeElement?.type !== 'range'
                            &&  activeElement?.type !== 'number'
                            && activeElement?.type !== 'checkbox'
                            && activeElement?.name != 'inputColor'
                    )
                    {
                        return false;
                    }
                    else if(activeElement?.type === 'IFRAME' && activeElement.id !== 'EComposerBuilder')
                    {
                        return false;
                    }

                    // console.log('Start watch current section',val);
                    clearTimeout(this.timmer);
                    this.timmer = setTimeout(() => {
                        this.renderSectionCss();
                    }, 100);
                },
                deep: true

            },



        },
        beforeUnmount () {
            if (this.exporting !== false) {
                return;
            }
            let linkcss = window.EComposerBuilder.contentWindow['section-' + this.data.id];
            if (
                linkcss &&
                this.sections.filter((s) => {
                    return s.id == this.data.id;
                }).length === 0

            ) {
                try{
                    window.EComposerBuilder.contentDocument.head.removeChild(linkcss);
                }catch(e){
                    console.warn(e.message);
                }
            }
            this.$store.commit('page/revokeSectionCss', this.data.id);
        },

        methods: {
            dropOut () {
                let realIndex= this.stack.findIndex((s) => s.id == this.data.id);
                if (realIndex > -1) {
                    return this.stack.splice(realIndex, 1)[0];
                }
                else
                {
                    return this.stack.splice(this.index, 1)[0];
                }
            },
            dropIn (current, pos) {
                if (this.is == 'section') {
                    this.stack.splice(this.index + pos, 0, current.dropOut());
                }
            },

            paste (clipboard) {
                if (this.$refs.row) {
                    this.$refs.row.paste(clipboard);
                } else {
                    this.section.columns = [{
                        width: 100,
                        blocks: []
                    }];
                    setTimeout(() => {
                        this.$refs.row.paste(clipboard);
                    }, 100);
                }
            },

            settings () {
                return this.$refs?.row?.settings ?? [];
            },

            style () {
                return this.$refs?.row?.style ?? [];
            },

            refreshCols () {

                if (this.columns.length === 0) {
                    this.sections.splice(this.index, 1);
                    // this.$sections.splice(this.index, 1);
                }
                let w = 0;
                this.columns.forEach((c) => {
                    w += c.width;
                });
                w = (w - 100) / this.columns.length;
                this.columns.forEach((c) => {
                    c.width -= w;
                    /*if (this.stacks[c.id]?.column) {
                        this.stacks[c.id].column.refresh = this.$helpers.randid();
                    }*/
                });

                //this.section.refresh = this.$helpers.randid();

            },

            renderSectionCss() {
                if (this.data.id) {
                    let id = 'section-' + this.data.id;
                    let css  =  this.sectionCss?.[this.data?.id];
                    this.$helpers.renderCss(id, css);
                    let globalUsed = this.$helpers.getGlobalInCss( css);
                    this.$store.dispatch('page/setGlobalUsed', {id, payload: globalUsed});
                }
            }

        },
    };
</script>
