import { ROOT_STORE } from "@/store/constants";

import type { GetterTree } from "vuex";
import type { ControlsType, RootStateType, IRootGettersTypes } from "@/store/interfaces";

export const getters: GetterTree<RootStateType, RootStateType> & IRootGettersTypes = {
    [ROOT_STORE.GETTERS.CONTROLS]: (state: RootStateType): ControlsType => {
        return state.controls;
    },
    [ROOT_STORE.GETTERS.REFRESH]: (state: RootStateType): number => {
        return state.refresh;
    },
    [ROOT_STORE.GETTERS.UPDATE_VERSION]: (state: RootStateType): string => {
        return state.version;
    }
};
