                        icon: `<svg class="h-4 w-4"  fill="currentColor"  xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path d="M480 416C497.7 416 512 430.3 512 448C512 465.7 497.7 480 480 480H150.6C133.7 480 117.4 473.3 105.4 461.3L25.37 381.3C.3786 356.3 .3786 315.7 25.37 290.7L258.7 57.37C283.7 32.38 324.3 32.38 349.3 57.37L486.6 194.7C511.6 219.7 511.6 260.3 486.6 285.3L355.9 416H480zM265.4 416L332.7 348.7L195.3 211.3L70.63 336L150.6 416L265.4 416z"/></svg>`,
                        action: 'removeFormat'
                    },
                    {
                        name: 'Code',
                        icon: `<svg width="18" height="14" viewBox="0 0 18 14" fill="currentColor" xmlns="http://www.w3.org/2000/svg"><path d="M10.7377 0.0342853C10.2729 -0.0996708 9.78904 0.170975 9.65509 0.635721L6.15583 12.8831C6.02187 13.3479 6.29252 13.8318 6.75726 13.9657C7.22201 14.0997 7.70589 13.829 7.83985 13.3643L11.3391 1.11687C11.4731 0.652123 11.2024 0.168241 10.7377 0.0342853ZM12.9411 3.31758C12.5994 3.6593 12.5994 4.21426 12.9411 4.55599L15.3824 7L12.9384 9.44401C12.5967 9.78574 12.5967 10.3407 12.9384 10.6824C13.2801 11.0241 13.8351 11.0241 14.1768 10.6824L17.2386 7.62057C17.5804 7.27885 17.5804 6.72389 17.2386 6.38216L14.1768 3.32031C13.8351 2.97858 13.2801 2.97858 12.9384 3.32031L12.9411 3.31758ZM4.55656 3.31758C4.21483 2.97585 3.65987 2.97585 3.31815 3.31758L0.256293 6.37943C-0.0854312 6.72115 -0.0854312 7.27611 0.256293 7.61784L3.31815 10.6797C3.65987 11.0214 4.21483 11.0214 4.55656 10.6797C4.89828 10.338 4.89828 9.783 4.55656 9.44128L2.11254 7L4.55656 4.55599C4.89828 4.21426 4.89828 3.6593 4.55656 3.31758Z" fill="currentColor"/></svg>`,
                        action: 'codeFormat'
                    },
                ];
                if(this.field?.options?.dynamic) {
                    toolbar.push(
                        {
                            name: 'Insert Dynamic Sources',
                            icon: `<svg width="16" height="14" viewBox="0 0 16 14" fill="currentColor" xmlns="http://www.w3.org/2000/svg"><path d="M7.53846 5.38462C11.7018 5.38462 15.0769 4.17846 15.0769 2.69231C15.0769 1.20615 11.7018 0 7.53846 0C3.37508 0 0 1.20615 0 2.69231C0 4.17846 3.37508 5.38462 7.53846 5.38462ZM7.53846 7.53846C10.6292 7.53846 13.286 6.874 14.4502 5.92308C14.8529 6.25262 15.0769 6.61662 15.0769 7C15.0769 8.48615 11.7018 9.69231 7.53846 9.69231C3.37508 9.69231 0 8.48615 0 7C0 6.61662 0.224 6.25262 0.626769 5.92308C1.78985 6.874 4.44769 7.53846 7.53846 7.53846ZM7.53846 11.8462C10.6292 11.8462 13.286 11.1817 14.4502 10.2308C14.8529 10.5603 15.0769 10.9243 15.0769 11.3077C15.0769 12.7938 11.7018 14 7.53846 14C3.37508 14 0 12.7938 0 11.3077C0 10.9243 0.224 10.5603 0.626769 10.2308C1.78985 11.1817 4.44769 11.8462 7.53846 11.8462Z" fill="currentColor"/></svg>`,
                            action: 'dynamicSources'
                        }
                    )
                }
                if(this.field?.options?.generator) {
                    toolbar.push(
                        {
                            name: 'AI Content Generator',
                            icon: `<svg width="15" height="14" viewBox="0 0 15 14" fill="currentColor" xmlns="http://www.w3.org/2000/svg"><path d="M13.9575 6.72442C13.8161 6.3828 13.513 6.30089 13.3079 6.28129V4.19935C13.3079 3.4272 12.6799 2.79926 11.9078 2.79926H7.70751V1.8262C7.81405 1.73168 7.90008 1.61632 7.96028 1.48724C8.02048 1.35817 8.05357 1.21811 8.05751 1.07574C8.06145 0.933373 8.03615 0.791707 7.98317 0.659501C7.93019 0.527294 7.85067 0.407356 7.74951 0.307097C7.60266 0.160429 7.41564 0.0605676 7.21207 0.0201268C7.00849 -0.020314 6.7975 0.000479296 6.60574 0.0798804C6.41398 0.159282 6.25005 0.293729 6.13466 0.466242C6.01926 0.638756 5.95758 0.841596 5.95739 1.04915C5.95739 1.35857 6.0939 1.63438 6.30742 1.8262V2.79926H2.10714C1.33499 2.79926 0.707047 3.4272 0.707047 4.19935V6.29739L0.649643 6.30159C0.473212 6.31433 0.308144 6.39338 0.187625 6.52286C0.0671055 6.65234 7.15763e-05 6.82265 0 6.99954V8.39963C0 8.58529 0.0737546 8.76335 0.205039 8.89464C0.336323 9.02592 0.514382 9.09968 0.700046 9.09968H0.707047V12.5999C0.707047 13.3721 1.33499 14 2.10714 14H11.9078C12.6799 14 13.3079 13.3721 13.3079 12.5999V9.09968C13.4935 9.09968 13.6716 9.02592 13.8029 8.89464C13.9342 8.76335 14.0079 8.58529 14.0079 8.39963V7.04294C14.016 6.9343 13.9987 6.82527 13.9575 6.72442ZM2.10714 12.5999V4.19935H11.9078L11.9085 6.99674L11.9078 6.99954V8.39963L11.9085 8.40313L11.9092 12.5999H2.10714Z" fill="currentColor"/><path d="M4.20728 9.79972H9.80765V11.1998H4.20728V9.79972ZM9.45762 8.39963C10.0373 8.39963 10.5077 7.77309 10.5077 6.99954C10.5077 6.22599 10.0373 5.59945 9.45762 5.59945C8.87799 5.59945 8.40755 6.22599 8.40755 6.99954C8.40755 7.77309 8.87799 8.39963 9.45762 8.39963ZM4.5573 8.39963C5.13694 8.39963 5.60737 7.77309 5.60737 6.99954C5.60737 6.22599 5.13694 5.59945 4.5573 5.59945C3.97766 5.59945 3.50723 6.22599 3.50723 6.99954C3.50723 7.77309 3.97766 8.39963 4.5573 8.39963Z" fill="currentColor"/></svg>`,
                            action: 'contentGenerator',

                        }
                    )
                }
                return toolbar;
            },
            toolbarFilterd(){
                let toolbarTemp = this.toolbars;
                if (this.type == 0) {
                    return [];
                } else if (this.type == 1){
                    toolbarTemp = toolbarTemp.filter(
                        x => x && ['strikeThrough', 'insertImage', 'insertVideo'].indexOf(x?.action) === -1
                    );
                }
                return toolbarTemp;
            },
            CMDs(){
                return this.toolbarFilterd.map(x => x.action);
            },
        },
        methods: {
            styleButton(cmd){
                let val = this.state[cmd];
                switch(cmd){
                    case 'foreColor':
                    case 'codeFormat':
                        return {'--ta-fill' : val};
                    case 'backColor':{
                        let c = '';
                        if(!val) {
                            c = 'rgb(0, 0, 0)';
                        }
                        else{
                            c = (val == 'rgb(255, 255, 255)') ?
                                'rgb(0, 0, 0)' : val;
                        }
                        return {'--ta-fill' : c};
                    }
                    default:
                        return {};
                }
            },
            classToolbar(cmd){
                let val = this.state[cmd];
                switch(cmd){
                    case 'foreColor':
                    case 'backColor':
                        return 'hover:bg-gray-200 rounded dark:hover:bg-dark dark:hover:text-primary-700';
                    default:
                        return val ?
                                'text-primary-600 bg-gray-100' :
                                'text-gray-500 dark:text-dark-color dark:hover:bg-dark dark:hover:text-primary-700 hover:bg-gray-200';
                }
            },
            clickToolbar($event,action){
                if(action === 'backColor' || action === 'foreColor') {
                    let $toolbar = this.$refs['toolbar'];
                    if($toolbar) {
                        let $warpper =  $toolbar.closest('div.px-2.w-full');
                        if($warpper) {
                            $warpper.style.zIndex = action === this.showing ? '1' : '99';
                        }
                    }
                }

                this.showing = action === this.showing ? null: action;
                this.$emit('select',action)
            },
            changeColor(val, action){
                this.colors[action] = val;
                let itv = null;
                clearTimeout(itv);

                itv = setTimeout(() => {
                    this.$emit('updateColor',{val, action})
                }, 100)
            },
        }
    };
</script>
<style>
    .flex-break{
        flex-basis: 100%;
        max-height: 0;
        overflow: hidden;
    }
</style>