export default {
    namespaced: true,
    state: {
        open: false,
        ready: false,
        currentScreen: 'library',
        template: {}
    },
    getters: {
        open(state) {
            return state.open;
        },
        currentScreen(state) {
          return state.currentScreen;
        },
        template(state) {
          return state.template;
        },
    },
    mutations: {
        open(state, payload) {
            state.open = payload;
        },
        currentScreen(state, payload) {
          state.currentScreen = payload;
        },
        template(state, payload) {
          state.template = payload;
        },
    },
    actions: {

    }
};