import { createStore } from "vuex"; 

import type { RootStateType } from "@/store/interfaces";

import root from "@/store/modules/root";
import type { RootStoreModuleTypes } from "@/store/modules/root/types";

export const store = createStore<RootStateType>(root);

type StoreModules = {
    root: RootStoreModuleTypes;
};

export type Store = RootStoreModuleTypes<Pick<StoreModules, "root">>;
