<template>
    <div class="flex flex-wrap" :class="isFull ? 'w-full' : !$parent.isElement ? 'p-0' : 'pl-2 pr-4'">
        <div
            v-for="(group, index) in fieldsFiltered"
            :key="isGroup(group) ? index : ((group.group_name + group.selector) || index)"
            v-show="isShow(group)"
            :class="[
                !isGroup(group) && !freeform ? 'px-2' : !$parent.isElement ? '' : '',
                isSettings ? 'border-b last:border-b-0 pl-4 dark:border-gray-700' : '',
            //freeform ? 'bg-white' : '',
            group.options?.half ? 'w-1/2 field-half' : 'w-full'
            ,{
                'border-gray-200 rounded': deep === 0 && !freeform && !isSettings,
                'rounded shadow-sm yy dark:border-transparent': deep === 0 && freeform && 'group_title' in group,
                'flex items-center justify-between' : group.options?.oneline,
                'mb-3': index < fields.length - 1 && isGroup(group) && deep === 0 && !isSettings,
                'justify-center': isFull
            }]"
        >
            <GroupsWrap
                v-if="isGroup(group)"
                :data="group"
                :field-values="values"
                :fields="fields"
                :index="index"
                @update:data="$emit('update:data', $event)"
                @update:delete="$emit('update:delete', $event)"
            />
            <Field
                v-else
                :data="group"
                :field-values="values"
                :freeform="freeform"
                :ispop="ispop"
                class="mb-4"
                @update:data="$emit('update:data', $event)"
                @update:delete="$emit('update:delete', $event)"
                @update:label="$emit('update:label', $event)"
            />
        </div>
    </div>
</template>
<script lang="ts">
    import GroupsWrap from '../components/atoms/Groups.vue';
    import Field from '../components/atoms/Field.vue';

    import { fieldMixin, relationMixin } from '../mixins';


    export default {
        components: {
            GroupsWrap,
            Field
        },
        mixins: [fieldMixin, relationMixin],
        props: {
            fields: {
                type: Array,
                default () {
                    return [];
                }
            },
            isFull: {
                type: Boolean,
                default: false
            },
            // eslint-disable-next-line vue/require-default-prop
            data: Object,
            deep: {
                type: Number,
                default: 0
            },
            freeform: {
                type: Boolean,
                default: false
            },
            ispop: {
                type: Boolean,
                default: false
            },
            openDefault: {
                type: Boolean,
                default: true
            },
            isSettings: {
                type: Boolean,
                default: false
            }
        },
        emits: ['update:label', 'update:delete', 'update:clicked', 'update:data'],
        data() {
            const editing = 'content';
            return {
                valueDeleted: {},
                activeGroup: this.openDefault ? (editing == 'content' || this?.fields.length == 1  ? 0 : -1) : -1,
                values: {},
            };
        },
        computed: {
            fieldsFiltered() {
                let fields = this.$helpers.formatFields(this.fields);
                return fields.filter(f => {
                    if(this.isGroup(f)) {
                        return this.fieldVisible(f, {});
                    }
                    return this.fieldVisible({}, f);
                });
            }
        },
        watch: {
            data: {
                deep: true,
                immediate: true,
                handler(val) {
                    if(val && typeof val === 'object') {
                        this.values = val;
                    }
                    else {
                        this.values = this.$helpers.defaultValues(this.fields);
                    }
                }
            },
            values: {
                deep: true,
                handler: function(val) {
                    this.$emit('update:data', val);
               }
            },
        },
        methods: {
            fieldVisible(g, f){
                return this.fieldsHidden.indexOf(this.pathField(g, f)) === -1;
            },
             isShow(gr){
                return !('visible' in gr) || gr.visible === true;
            }
        }
    };
</script>

<style>
div.field-half+.field-half {
    padding-left: 10px !important;
}
div:not(.field-half)+div.field-half,
div>.field-half:first-child {
    padding-right: 10px !important;
}
</style>
