<template>
    <component
        :is="data?.settings?.tag ?? 'div'"
        v-bind="colAttrs"
        @mouseenter="overCol"
        @mouseleave="outCol"
        @contextmenu="rightClickMenu"
        @click="activeCol"
    >
        <template v-if="isActive">
            <div
                class="core__column--settings"
                :style="styleattr.settings"
                title="Edit column"
                @mousedown="startDrag"
                @click="editCol"
            >
                <span class="editicon">
                    <text>{{ scwidth }}</text>

                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" fill="currentColor"><path d="M373.2 16.97C395.1-4.901 430.5-4.901 452.4 16.97L495 59.6C516.9 81.47 516.9 116.9 495 138.8L182.3 451.6C170.9 462.9 156.9 471.2 141.5 475.8L20.52 511.3C14.9 512.1 8.827 511.5 4.687 507.3C.5466 503.2-1.002 497.1 .6506 491.5L36.23 370.5C40.76 355.1 49.09 341.1 60.44 329.7L373.2 16.97zM429.8 39.6C420.4 30.22 405.2 30.22 395.8 39.6L341 94.4L417.6 170.1L472.4 116.2C481.8 106.8 481.8 91.6 472.4 82.23L429.8 39.6zM109.6 402.4L173.4 415.2L394.1 193.6L318.4 117L96.84 338.6L109.6 402.4zM70.51 370.2C69.08 373.2 67.88 376.3 66.93 379.5L39.63 472.4L132.4 445.1C135.7 444.1 138.8 442.9 141.8 441.5L92.86 431.7C86.53 430.4 81.58 425.5 80.31 419.1L70.51 370.2z"/></svg>
                </span>
            </div>
            <template v-if="canDrop">
                <div
                    class="placeholder__left"
                    :class="{
                        'drop__placeholder--column': dropPos == 'left' && dropToPlaceholder
                    }"
                    @mouseenter="placehover"
                />
                <div
                    class="placeholder__right"
                    :class="{
                        'drop__placeholder--column': dropPos == 'right' && dropToPlaceholder
                    }"
                    @mouseenter="placehover"
                />
            </template>
            <template v-else />
        </template>
        <template v-else />
        <div class="core__column--wrapper">
            <Blocks
                :data="colBlocks()"
                :deep="deep"
                :index="index"
                :column="column"
                :section_id="section_id"
            />
        </div>

        <div v-if="is == 'column'" class="ecom-column__overlay">
            <video
                v-if="show_video_background && ['upload','external'].includes(data?.settings?.source)"
                class="ecom-video-background"
                @loadeddata="handleLoadVideo"
                v-bind="attrVideo"
                :data-play-pause-inview="data.settings?.inview_playpause"
                :src="video_url"
                ref="video"
            />
            <div
                class="ecom-video-background-youtube"
                v-if="show_video_background && ['youtube','vimeo'].includes(data?.settings?.source)"
            >
                <iframe

                    class="ecom-video-background"
                    frameborder="0"
                    allowfullscreen="1"
                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    style="border: 0; width: 100%; height: 100% !important"
                    :src="data?.settings?.source == 'vimeo' ? linkVimeo : linkYoutube"
                />
            </div>
            <div class="ecom-overlay" />
        </div>

        <div
            v-if="!lastCol && screen == 'desktop' && $parent.over"
            class="core__column--resize"
            @mousedown="startResize"
        />
        <template v-else />
    </component>
</template>



<script lang="ts">
import Blocks from './Blocks.vue';
import { Core, Element } from '@/mixins';
import { mapGetters } from 'vuex';
export default {
    name: 'Column',
    components: { Blocks },
    mixins: [ Core, Element ],
    props: {
        'data': {
            type: Object,
            default() {
                return {
                    columns: []
                };
            }
        },
        'deep': {
            type: Number,
            default: 0
        },
        'section_id': {
            type: String,
            default: ''
        },
        'index': {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            is: 'column',
            name: 'column',
            over: false,
            dragging: false,
            validElements: ['heading', 'text', 'image', 'divi']
        };
    },
    computed: {
        attrVideo() {
            return {
                controls: false,
                autoplay: this.data?.settings?.autoplay,
                loop: this.data?.settings?.loop,
                playsinline:this.data?.settings?.autoplay,
                preload: this.data?.settings?.autoplay ? true: false,
                'data-ecom-muted': this.exporting && this.data?.settings?.muted
            };
        },
        stack() {
            return this.$parent.columns;
        },

        blocks() {
            return this.column.blocks;
        },

        column() {
            return this.data;
        },

        width() {
            return this.column.width;
        },

        auwidth() {
            return this.column?.settings?.width ? this.column.settings.width : 0;
        },

        scwidth() {
            return parseInt(this.column?.settings?.['width'+(this.screen == 'desktop' ? '' : '__'+this.screen)] ?? this.width)+'%';
        },

        firstCol() {
            return this.index == 0;
        },

        lastCol() {
            return this.index == this.$parent.row.columns.length - 1;
        },

        styleattr() {
            const attrs = {
                wrapper: {
                    width: (
                            this.exporting === false &&
                            this.screen == 'desktop'
                        ) ? this.column.width + '%' : '',
                    border: this.isActive && (
                                !this.over ||
                                this.stack.length > 1 ||
                                this.width !== 100
                            ) && !this.isDragging ? '1px dashed rgba(var(--text-color), .75)' : '1px dashed rgba(var(--text-color), .0)'
                },
                settings: `display: ${
                    this.over && !this.isDragging ? 'inline-block' : 'none'
                }`
            };

            if(this.scrollingAnimation) {
                attrs['ecom-scrolling-animation'] = this.scrollingAnimation;
            }

            return attrs;
        },

        isActive() {
            return (
                (
                    !this.isFullScreen &&
                    this.exporting === false
                ) &&
                (
                    this.over ||
                    this.activeElement?.data?.id === this.data.id ||
                    this.editingElement?.data?.id === this.data.id
                ) &&
                (
                    !this.dragCurrent ||
                    this.dragCurrent.is != 'section'
                )
            );
        },

        dragColMe() {
            return this.dragCurrent === this;
        },

        dropToPlaceholder() {
            return this.dropTo && this.canDrop;
        },

        canDrop() {
            return this.$helpers.canDrop(this.dragCurrent, this);
        },

        show_video_background(){
            return this.is === 'column' && ['upload','external', 'youtube', 'vimeo'].includes(this.data.settings?.source);
        },

        video_url(){
            let source = this.data?.settings?.source;
            if(!['upload','external'].includes(source))
            {
                return '';
            }
            if(source === 'upload')
            {
                return this.data?.settings.video?.value??'';
            }
            return this.data?.settings?.url??'';
        },

        linkVimeo() {
            if (this.data?.settings?.srcVimeo) {
                const regExpVm = /(?:http?s?:\/\/)?(?:www\.)?(?:vimeo\.com)\/?(.+)/;
                let vals = this.data?.settings?.srcVimeo.match(regExpVm);
                let videoIdVm = vals ? vals[1] : null;
                return `${
                    location.protocol
                }//player.vimeo.com/video/${videoIdVm}?rel=0?version=3&controls=0&mute=${this.data?.settings?.muted ? 1 : 0}${
                    this.data?.settings?.start_time ? "&byline=1#t=00h00m" + this.data?.settings?.start_time + "ss" : ""
                }&showinfo=0&autoplay=${this.data?.settings?.autoplay ? 1 : 0}&loop=${this.data?.settings?.loop ? 1 : 0}`;
            } else {
                return "";
            }
        },

        linkYoutube() {
            if (this.data?.settings?.srcYoutube) {
                const regExpYt = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
                let val = this.data?.settings?.srcYoutube.match(regExpYt);
                let videoIdYt = val && val[2].length === 11 ? val[2] : null;
                return `${location.protocol}//www.youtube.com/embed/${videoIdYt}?version=3?${
                    this.data?.settings?.start_time ? "&start=" + this.data?.settings?.start_time : ""
                }${this.data?.settings?.end_time ? "&end=" + this.data?.settings?.end_time : ""}&controls=0&mute=${
                    this.data?.settings?.muted ? 1 : 0
                }&showinfo=0&iv_load_policy=3&playlist=${videoIdYt}&autoplay=${this.data?.settings?.autoplay ? 1 : 0}&loop=${this.data?.settings?.loop ? 1 : 0}`;
            } else {
                return "";
            }
        },

        colAttrs() {
            return {
                style: this.styleattr.wrapper,
                ref: 'wrapper',
                key: this.column.refresh,
                class: {
                    'ecom-column ecom-core': true,
                    'core__column--first': this.firstCol,
                    'core__column--last': this.lastCol,
                    'ecom-animated': !this.exporting,
                    'ecom-animation': this.data?.advanced?.animation_type,
                    ...this.classes
                },
                ...this.attributes
            };
        },
        settings() {
            let settings = [{
                group_title: "General",
                params:  [{
                    type: 'number',
                    label: 'Column width (%)',
                    name: 'width',
                    css: {
                        selector: 'root'
                    },
                    value: this.column.width+'%',
                    options: {
                        responsive: true,
                        slider: true,
                        reset: true,
                        focus: true,
                        units: {
                            '%': {
                                min: 10,
                                max: 100,
                                step: 1
                            }
                        }
                    }
                },
                {
                    type: 'number',
                    label: 'Elements space <span class="lowercase">(px)</span>',
                    name: 'margin-top',
                    css: {
                        selector: '>div.core__column--wrapper>div.core__blocks>div.core__blocks--body>div.ecom-block.elmspace:not(:first-child)'
                    },
                    options: {
                        responsive: true,
                        slider: true,
                        units: {
                            px: {
                                min: 0,
                                max: 100,
                                step: 1
                            }
                        }
                    }
                },
                {
                    type: 'line'
                },
                {
                    type: 'paragraph',
                    content: '** Align elements inside **'
                },
                {
                    type: 'popup',
                    label: 'Align items',
                    name: 'align-items',
                    css: {
                        selector:  '>div.core__column--wrapper>div.core__blocks>div.core__blocks--body',

                    },
                    options: {
                        responsive: true,
                        values: {
                            'flex-start': 'Top',
                            center: 'Middle',
                            'flex-end': 'Bottom',
                            'baseline': 'Base line',
                            stretch: 'Stretch'
                        },
                        preview: 'title',
                        type: 'dropdown'
                    }
                },
                {
                    type: 'popup',
                    label: 'Align content',
                    name: 'align-content',
                    css: {
                        selector: '>div.core__column--wrapper>div.core__blocks>div.core__blocks--body',

                    },
                    options: {
                        responsive: true,
                        values: {
                            'flex-start': 'Top',
                            'center': 'Middle',
                            'flex-end': 'Bottom',
                            'space-between': 'Space Between',
                            'space-around': 'Space Around',
                            'space-evenly': 'Space Evenly'
                        },
                        preview: 'title',
                        type: 'dropdown'
                    }
                },
                {
                    type: 'popup',
                    label: 'Horizontal align',
                    name: 'justify-content',
                    css: {
                        selector: '>div.core__column--wrapper>div.core__blocks>.core__blocks--body'
                    },
                    options: {
                        responsive: true,
                        values: {
                            'left': 'Left',
                            'center': 'Center',
                            'right': 'Right'
                        },
                        type: 'dropdown'
                    }
                },
                {
                    type: 'line'
                },
                {
                    type: 'popup',
                    label: '<span class="uppercase">HTML</span> Tag',
                    name: 'tag',
                    options: {
                        values: {
                            'div': 'div',
                            'header': 'header',
                            'footer': 'footer',
                            'main': 'main',
                            'article': 'article',
                            'section': 'section',
                            'aside': 'aside',
                            'nav': 'nav'
                        },
                        type: 'dropdown'
                    }
                }]
            }];

            if(this.is === 'column')
            {
                settings.push({
                    group_title: 'Video background',
                    params: [
                        {
                            type:'popup',
                            name:'source',
                            label:'Video source',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    none: 'None',
                                    upload: 'Upload video',
                                    external: 'Custom video url',
                                    youtube: "Youtube",
                                    vimeo: "Vimeo",
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Choose video <i>(maximum 10Mb)</i>',
                            name: 'video',
                            options: {
                                type: 'video',
                                visible:function(data){ return data['source'] === 'upload'}
                            }
                        },
                        {
                            type: 'text',
                            label: 'Enter your custom video url',
                            name: 'url',
                            placeholder: 'https://the-domain.com/*/video.mp4',
                            options: {
                                visible:function(data){ return data['source'] === 'external'}
                            }
                        },
                        {
                            name: "srcYoutube",
                            label: "Youtube link",
                            type: "text",
                            options: {
                                visible:function(data){ return data['source'] === 'youtube'}
                            }
                        },
                        {
                            name: "srcVimeo",
                            label: "Vimeo link",
                            type: "text",
                            options: {
                                visible:function(data){ return data['source'] === 'vimeo'}
                            }
                        },
                        {
                            name: "start_time",
                            label: "Start time (s)",
                            type: "number",
                            options: {
                                half: true,
                                slider: false,
                                min: 0,
                                max: 1000,
                                visible:function(data){ return data['source'] === 'vimeo' || data['source'] === 'youtube'}
                            }
                        },
                        {
                            name: "end_time",
                            label: "End time",
                            type: "number",
                            options: {
                                slider: false,
                                min: 0,
                                max: 1000,
                                half: true,
                                visible:function(data){ return data['source'] === 'youtube'}
                            }
                        },
                        {
                            type:'toggle',
                            name: 'loop',
                            label:'Loop',
                            options:{
                                values:{
                                    on:{
                                        label:'On',
                                        value: true
                                    },
                                    off: {
                                        label: 'Off',
                                        value: false
                                    }
                                },
                                //visible:function(data){ return data['source'] === 'external' || data['source'] === 'upload'}
                            }
                        },
                        {
                            type:'toggle',
                            name: 'autoplay',
                            label:'Autoplay',
                            options:{
                                values:{
                                    on:{
                                        label:'On',
                                        value: true
                                    },
                                    off: {
                                        label: 'Off',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'paragraph',
                            content: 'Note: Chromium browsers do not allow autoplay in most cases. However, muted autoplay is always allowed.'
                        },
                        {
                            type:'toggle',
                            name: 'muted',
                            label:'Muted',
                            options:{
                                values:{
                                    on:{
                                        label:'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                visible:function(data){ return data['source'] != 'vimeo'}
                            }
                        },
                        {
                            type:'toggle',
                            name: 'inview_playpause',
                            label:'Auto play/pause when in view',
                            options:{
                                values:{
                                    on:{
                                        label:'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'paragraph',
                            content: 'Note: This featured not working on Safari.'
                        },
                    ]
                })
                if(['collection','product','article'].includes(this.params.page))
                {
                    settings.push({
                        group_title: 'Dynamic background',
                        params: [
                            {
                                type: 'paragraph',
                                content: `Show current ${this.params.page} featured image as background.\n
                                You can config background properties on tab "Design" in "Background" group\n
                                **The feature image only work when publish on live site**`
                            },
                            {
                                type: 'toggle',
                                label: 'Show featured image as background',
                                name: 'dynamic_background',
                                /*description: 'Stretch the section to the full width of the page using JS',*/
                                options: {
                                    values: {
                                        on: {
                                            label: 'YES',
                                            value: true
                                        },
                                        off: {
                                            label: 'NO',
                                            value: false
                                        }
                                    },
                                    oneline: true
                                }
                            }
                        ]
                    })
                }
            }



            return settings;
        },
        style() {
            return [{
                group_alias: 'background',
                options:{
                    selector: '>.core__column--wrapper',
                    group_title: 'Background',
                }
            },
            (this.is == 'column'
                ? {
                    group_alias: 'background:overlay',
                    options:{
                        group_title: 'Background Overlay',
                        group_name: 'backgroud_overlay',
                        selector: '>div.ecom-column__overlay'
                    },
                    modify: {
                            remove: {
                                index: 5,
                                length: 1
                            },
                            params: [
                                {
                                    position:6,
                                    fields:[
                                        {
                                            type: "background",
                                            label: "Background",
                                            name: "columnBackgroundHover",
                                            options: {
                                                oneline: true,
                                                responsive: true,
                                                types: ["classic", "gradient"]
                                            },
                                            css: {
                                                selector: "root:hover>.ecom-column__overlay>.ecom-overlay",
                                                properties: {
                                                    background: ""
                                                }
                                            }
                                        },
                                    ]
                                }
                            ]
                        }
                    }
                : null
            ),
            {
                group_alias: 'border',
                options:{
                    group_title: 'Border',
                    selector: '>.core__column--wrapper',
                }
            },{
                group_alias: 'typography',
                options:{
                    selector: '>.core__column--wrapper',
                    group_title: 'Typography',
                }
            }];
        },

        css() {
            return `
                .ecom-column {
                    position: relative;
                    /* display: flex; responsive generate display block */
                }
                .ecom-column>.core__column--wrapper {
                    align-items: flex-start; /* fix tam de mn lam layout */
                    display: flex;
                }
                .ecom-column:hover {
                    z-index: 1;
                }

                .ecom-column__overlay .ecom-overlay {
                    pointer-events: none;
                }

                .ecom-column__overlay > .ecom-overlay, .ecom-column__overlay .ecom-video-background-youtube {
                    position: absolute;
                    inset: 0;
                }

                @media only screen and (min-width: 768px) {
                    .ecom-column>.core__column--wrapper {
                        height: 100%;
                    }
                }`;
        },

        ...mapGetters('builder', ['stacks', 'screen'])

    },
    watch: {
        width(val) {
            if (!this.column.settings.width) {1
                this.column.settings.width = '100%';
            }

            if (val < 10) {
                return this.column.settings.width = '10%';
            }

            this.column.settings.width = val+'%';

            // auto change width of other columns in same row
            if (val && this.stack.length > 1) {
                let total = 0;
                this.stack.forEach((col) => {
                    total += col.width;
                });
                let sib = this.stack[this.index+(this.stack[this.index+1] ? 1 : -1)];
                if (sib.width + (100 - total) > 10) {
                    sib.width += (100 - total);
                } else {
                    // sib.width = 10;
                }
            }
        },
        auwidth(val) {
            if (val != this.width+'%') {
                this.column.width = parseFloat(parseFloat(val).toFixed(2).replace('.00', ''));
            }
        }
    },
    mounted() {
        if (this.exporting === false) {
            this.stacks[this.data.id] = this;
        }

        if (!this.column?.settings?.width) {
            this.column.settings.width = (this.data?.width ?? 100)+'%';
        }
        if (!this.column?.settings?.width__mobile) {
            this.column.settings.width__mobile = '100%';
        }
    },
    updated() {
        if (this.column.blocks === undefined) {
            this.column.blocks = [];
        }
    },
    methods: {
        handleLoadVideo() {
            if(this.$refs.video) {
                if(this.data?.settings?.muted == true) {
                    this.$refs.video.muted = true;
                    this.$refs.video.setAttribute('muted', '');
                } else {
                    this.$refs.video.muted = false;
                    this.$refs.video.removeAttribute('muted');
                }
            }
        },
        colBlocks() {
            if (!this.column.blocks || typeof this.column.blocks != 'object') {
                this.column.blocks = [];
            }
            return this.column.blocks;
        },

        overCol() {
            if (!this.isFullScreen && this.exporting === false /*&& !this.dragCurrent*/) {
                this.over = true;
                if (this.$parent.over === false) {
                    this.$parent.over = true;
                }
                clearTimeout(this.timmer);
            }
        },

        outCol() {
            if (!this.isFullScreen) {
                clearTimeout(this.timmer);
                this.timmer = setTimeout(() => {
                    this.over = false;
                }, 100);
            }
        },

        editCol() {
            this.$store.commit('builder/setActiveElement', this);
            this.$store.commit('builder/setEditingElement', this);
        },
        edit(){
            this.editCol();
        },

        activeCol(e) {
            if (
                !this.isFullScreen &&
                this.exporting === false &&
                (
                    !e ||
                    (
                        this.editingElement !== this &&
                        e.target.closest('.ecom-column.ecom-core') === this.$el &&
                        (e.target.closest('.ecom-block')?.contains(this.$el) ?? true)
                    )
                )
            ) {
                this.$store.commit('builder/setDragging', { timmer: new Date().getTime() });
                this.$store.commit('builder/setActiveElement', this);
                //this.$store.commit('builder/setEditingElement', this._row ? this._row : this._section);
                this.$store.dispatch('builder/setEditingElement', null);
                this.$store.commit('SET_TAB', 'elements');
            }
        },

        insertCol() {
            if (this.stack.length >= 10) {
                this.notice({
                    message: 'Can not insert more than 10 columns in a row',
                    type: 'error'
                });
            } else {
                this.stack.splice(this.index+1, 0, {
                    width: 50,
                    settings: {
                        width: '50%'
                    },
                    blocks: []
                });
                this.$parent.refreshCols();
            }
        },

        duplicate() {
            if (this.stack.length >= 10) {
                this.notice({
                    message: 'Can not insert more than 10 columns in a row',
                    type: 'error'
                });
            } else {
                //let clone = this.cloneMe();
                const clone = this.cloneData();

                // clone.blocks = clone.blocks.map(block=>{
                //     if(!block?.row?.columns)
                //     {
                //         return block;
                //     }
                //     block.row.columns = block.row.columns.map(column=>{
                //         if(!column?.blocks)
                //         {
                //             return column;
                //         }
                //          column.blocks = column.blocks.map(c=>{

                //                 if(c.group)
                //                 {
                //                     c.group.id =  this.$helpers.randid();
                //                 }
                //                 else
                //                 {
                //                     c.id = this.$helpers.randid();
                //                 }
                //                 return c;
                //          });
                //          return column;
                //     })
                //     return block;
                // });
                this.stack.splice(this.index+1, 0, clone);
                setTimeout(() => {
                    this.$parent.refreshCols();
                }, 10);
            }
        },

        paste(clipboard) {
            if(!clipboard) {
                return;
            }
            let data = this.$helpers.copy(clipboard.data),
                sect;
            switch (clipboard.is) {
            case 'section':
                sect = this.parent('section');
                sect.stack.splice(sect.index+1, 0, data);
                this.scroll(sect.$refs.wrapper.offsetHeight);
                break;
            case 'column':
                if (this.stack.length >= 10) {
                    this.notice({
                    message: 'Can not insert more than 10 columns in a '+this.$parent.is,
                    type: 'error'
                });
                } else {
                    this.stack.splice(this.index+1, 0, data);
                    this.$parent.refreshCols();
                }
                break;
            case 'block':
                this.blocks.push(data);
                this.scroll(this.$refs.wrapper.offsetHeight);
                break;
            }
        },

        startResize(e) {

            let col = e.target.parentNode,
                current = {
                    x: e.pageX,
                    w: this.column.width,
                    pw: col.offsetWidth,
                    ref: !this.lastCol ? this.$parent.row.columns[this.index + 1] : null
                };

            current.rw = current.ref.width;

            this.idoc().body.style.userSelect = 'none';

            this.idoc().onmousemove = (_e) => {

                let w = ((current.x - _e.pageX) * (current.w / current.pw)),
                    cw = current.w - w,
                    rw = current.rw + w;

                if (cw < 10 || rw < 10) {
                    return;
                }

                if (cw % 5 < 1 || cw % 5 > 4) {
                    cw = Math.round(cw);
                    rw = Math.round(rw);
                }

                this.column.width = Number(cw.toFixed(1));
                current.ref.width = Number(rw.toFixed(1));

                let totalWidth = 0;
                this.$parent.columns.forEach((col, i) => {
                    totalWidth += col.width;
                    if (this.stacks[col.id]) {
                        this.stacks[col.id].over = true;
                    }
                });

                // Make sure total width of all columns is 100%
                cw -= ((totalWidth-99.9)/2);
                rw -= ((totalWidth-99.9)/2);

                this.$refs.wrapper.style.width = Number(cw.toFixed(1))+'%';
                this.$refs.wrapper.nextElementSibling.style.width = Number(rw.toFixed(1))+'%';

            };

            this.idoc().onmouseup = () => {
                this.idoc().onmousemove = null;
                this.idoc().onmouseup = null;
                this.idoc().body.style.userSelect = '';
                this.stack.forEach((col) => {
                    if (this.stacks[col.id]) {
                        this.stacks[col.id].over = false;
                    }
                    col.width = parseFloat(parseFloat(col.width).toFixed(2).replace('.00', ''));
                });
                this.dispatchResize();
            };
        },

        placehover(e) {

            if (!this.canDrop) {
                return;
            }

            this.over = true;
            clearTimeout(this.timmer);

            let pos = e.target.classList.contains('placeholder__left') ? 'left' : 'right';
            if (
                this.dropTo !== this ||
                this.dropPos != pos
            ) {
                if (
                    this.dragCurrent.$parent !== this.$parent ||
                    (
                        this.index - this.dragCurrent.index !== 1 &&
                        pos == 'left'
                    )
                    ||
                    (
                        this.dragCurrent.index - this.index !== 1 &&
                        pos == 'right'
                    )
                ) {
                    this.$store.commit('builder/setDropTo', this);
                    this.$store.commit('builder/setDropPos', pos);
                } else {
                    this.$store.commit('builder/setDropTo', this.dragCurrent);
                    this.$store.commit('builder/setDropPos', null);
                }
            }
        },

        dropOut() {
            setTimeout((cur) => {
                if (cur) {
                    this.$store.dispatch('builder/setEditingElement', null);
                }
                this.dispatchResize();
            }, 50, this.data.id == this.editingElement?.data?.id);
            let currentID = this.data.id;
            let realIndex = this.stack.findIndex((item)=>{
                return item.id  === currentID;
            });
            if(isNaN(realIndex))
            {
                realIndex = this.index;
            }
            return this.stack.splice(realIndex, 1)[0];
        },

        dropIn(target, pos) {
            if(this.section_id && this.sections) {
                const action = this.section_id == target.section_id ? 'add' : 'move',
                    section = this.sections.find((s:any) => s.id == this.section_id);
                let payload = {
                    type: 'block',
                    data: section,
                    section_id: this.section_id,
                    action: action
                }
                if(action == 'move') {
                    payload.section_from = target.section_id;
                    payload.section_from_data = this.sections.find((s:any) => s.id == target.section_id);
                }
                this.$store.dispatch('history/addStack', payload);
            }

            if (target.is == 'column') {
                let row = target.$parent;
                this.stack.splice(this.index+pos, 0, target.dropOut());
                if (row.data.id !== this.$parent.data.id) {
                    this.$parent.refreshCols();
                    row.refreshCols();
                }

            } else {
                this.stack.push(target.dropOut());
            }
        }

    }
};
</script>
