import aliases from './index';
import helpers from '@/utils/helpers';
const fieldAlias = (type: string): FieldType | FieldType[] | null => {

    const name2Label = (name: string): string => {
        let label: string = name;
        if(name) {
            label = name.replace(/^\w|-\w/g, function(v) {
                        return v.replace('-', ' ').toUpperCase();
                    });
        }
        return label;
    }
    const label: string = name2Label(type);

    switch(type) {
        case 'border-top-width':
        case 'border-left-width':
        case 'border-right-width':
        case 'border-bottom-width':
        case 'border-top-left-radius':
        case 'border-top-right-radius':
        case 'border-bottom-right-radius':
        case 'border-bottom-left-radius':
        case 'border-image-width':
            return {
                name: type,
                label,
                type: 'number',
                css: {
                    properties: {
                        [type]: ''
                    }
                }
            }
        case 'box-shadow': {
            return {
                name: type,
                label,
                type: 'popup',
                options: {
                    oneline: true,
                    type: 'box-shadow',
                },
                css: {
                    properties: {
                        [type]: ''
                    }
                }
            }
        }
        case 'border-width':
            return {
                name: type,
                label,
                type: 'dimension',
                options: {
                    responsive: true,
                    simple: true,
                    units: {
                        px: {
                            min: 0,
                            max: 30
                        }
                    },
                },
                css: {
                    properties: {
                        [type]: ''
                    }
                }
            }
        case 'border-radius':
            return {
                name: type,
                label,
                type: 'dimension',
                options: {
                    responsive: true,
                    type: 'radius',
                    units: {
                        px: {
                            min: 0,
                            max: 1000
                        },
                        '%': {
                            min: 0,
                            max: 100
                        }
                    }
                },
                css: {
                    properties: {
                        [type]: ''
                    }
                }
            }
        case 'color':
        case 'border-color':
        case 'border-left-color':
        case 'border-right-color':
        case 'border-top-color':
        case 'border-bottom-color':
        case 'background-color':
        case 'caret-color':
        case 'column-rule-color':
        case 'outline-color':
        case 'text-decoration-color':
            return {
                name: type, label, type: 'color',
                options: {
                    global: { type: 'colors' }
                }
            };
        case 'background':
            return {
                name: type, label, type: 'background',
                options: {
                    oneline: true,
                    responsive: true,
                    types: ['classic', 'gradient']
                }
            }
        case 'border':
            return {
                name: type, label, type: 'popup',
                options: {
                    type: "border",
                    size: "small"
                }
            }
        case 'outline':
                return {
                    type: 'popup',label,name: type,
                    options: {
                        responsive: true,
                        type:'outline'
                    },
                    css:{
                        properties:{
                            [type]:''
                        }
                    }
                };

        case 'background-position':
        case 'background-size':
        case 'background-repeat':
        case 'background-attachment':
        case 'text-decoration':
        case 'font-style':
        case 'text-transform':
        case 'font-weight':
        case 'vertical-align':
        case 'animation':
        case 'border-style':
        case 'transitions':
            return { name: type, label, type: 'popup',
                    options: {
                        type: 'transitions',
                        default: false,
                        oneline:true,
                        visible: {
                            keep_data: true,
                            condition: (data: any) => {
                                return data["tab"] == "hover" || data["itemtab"] == "hover" || data["tab"] == "focus" || data["itemtab"] == "focus";
                            }
                        }
                    }
                    };
        case 'mix-blend-mode':
            return { name: type, label, type: 'popup',
                    options: {type, default: false, oneline:true}
                    };
        case 'spacing':
            return {
                type: 'dimension',label,name: type,
                options: {responsive: true,units: 'default'},
                css:{
                    properties:{
                        spacing:''
                    }
                }
            };
        case 'padding':
        case 'margin':
            return {
                type: 'dimension',label,name: type,
                options: {responsive: true,units: 'default', simple: true},
                css:{
                    properties:{
                        [type]:''
                    }
                }
            };
        case 'justify-content':
            return {
                type: 'choose',
                name: type,
                label,
                options: {
                    responsive: true,
                    type: 'text-align',
                    values: ['flex-start', 'center', 'flex-end']
                },
                css: {
                    properties: {
                        [type]: ''
                    }
                }
            }
        case 'align-items':
            return {
                type: 'choose',
                name: type,
                label,
                options: {
                    responsive: true,
                    type: 'text-align',
                    values: ['flex-start', 'center', 'flex-end']
                },
                css: {
                    properties: {
                        [type]: ''
                    }
                }
            }
        case 'align-self':
            return {
                type: 'choose',
                name: type,
                label,
                options: {
                    responsive: true,
                    type: 'text-align',
                    values: ['flex-start', 'center', 'flex-end']
                },
                css: {
                    properties: {
                        [type]: ''
                    }
                }
            }
        case 'text-align':
            return {
                type: 'choose',
                name: type,
                label,
                options: {
                    responsive: true,
                    type: 'text-align',
                    values: ['left', 'center', 'right']
                },
                css: {
                    properties: {
                        [type]: ''
                    }
                }
            }
        case 'width':
            return {
                type: "number",
                name: type,
                label,
                options: {
                    responsive: true,
                    reset: true,
                    units: {
                        "%": {
                            min: 0,
                            max: 100
                        },
                        px: {
                            min: 0,
                            max: 1000
                        },
                        vw: {
                            min: 0,
                            max: 100
                        }
                    }
                },
                css: {
                    properties: {
                        width: ""
                    }
                }
            };
        case 'height':
            return {
                type: "number",
                name: type,
                label,
                options: {
                    responsive: true,
                    reset: true,
                    units: {
                        "%": {
                            min: 0,
                            max: 100
                        },
                        px: {
                            min: 0,
                            max: 1000
                        },
                        vw: {
                            min: 0,
                            max: 100
                        }
                    }
                },
                css: {
                    properties: {
                        height: ""
                    }
                }
            };

        default:
            return null;
    }
}

function optionsAlias(field: FieldType | FieldType[] | AliasFieldType |  GroupFieldType | null, modify: FieldType | GroupFieldType) {
    let result;
    if(field &&
    (field?.constructor === Object ||
    field?.constructor === Array)) {
        if((<AliasFieldType>field)?.alias) {
            field = helpers.copy(fieldAlias((<AliasFieldType>field).alias as string));
            result = field;
        }
        else {
            field = helpers.copy(field);
        }
        modify = helpers.copy(modify);
        if(modify?.constructor === Object) {
            let isArr = field?.constructor === Array;
            let fields =  isArr?
                            <FieldType[]>field :
                            <FieldType[]>[field];
            type keyFieldType = keyof FieldType | keyof GroupFieldType;

            let kField: keyFieldType;

            for(kField in modify) {
                if(kField === 'css' || kField === 'options') {
                    let options: any;
                    if(kField === 'css') {
                        if(typeof modify[kField] !== 'object') {
                            fields.forEach(field => field[kField] = modify[kField]);
                        }
                        else {
                            let modifyCss = <CssFieldType>modify['css']
                            options = modifyCss;
                        }
                    }
                    else {
                        options = modify[kField];
                    }

                    if(options) {
                        for(let k in options) {
                            fields.forEach(field => {
                                field[kField] = typeof field[kField] === 'object' ? field[kField] : {};
                                field[kField][k] = options[k];
                            });
                        }
                    }
                }
                else {
                    fields.forEach(field => field[kField] = modify[kField]);
                }
            }
            result = isArr ? fields : fields[0];
        }
    }
    return result || field;
}
function modifyGroupField(group: GroupFieldType, modify: ModifyType) : GroupFieldType {

    if(modify && group?.params) {
        let { params: paramsModify, remove }  = modify;
        group = helpers.copy(group);
        let params: ParamsModifyType[] | undefined;
        if(paramsModify?.constructor === Array && paramsModify.length) {
            paramsModify = Object.assign([], paramsModify);
            if('fields' in paramsModify[0]) {
                params = paramsModify as ParamsModifyType[];
            }
            else {
                params = (<FieldType[]>paramsModify).map(f => {
                    return {fields: [f]}
                });
            }
        }
        else if(paramsModify?.constructor === Object) {
            paramsModify = Object.assign({}, paramsModify);
            if('fields' in paramsModify) {
                params = [paramsModify];
            }
            else {
                params = [{fields: [paramsModify as FieldType]}];
            }
        }
        if(params) {
            let i: number = 0;
            params.forEach(param => {
                let { position, fields } = param;
                if(fields) {
                    fields = <FieldType[] | AliasFieldType[]>(fields.constructor === Object ? [fields] : fields);
                    let pos: number;
                    if(position !== undefined) {
                        pos = position;
                    }
                    else {
                        pos = i++;
                    }
                    group.params.splice(pos, 0, ...fields);
                    group.params = group.params.map(field => {
                        if((<AliasFieldType>field).alias) {
                            return optionsAlias((<AliasFieldType>field), field.options)
                        }
                        return field;
                    });

                }
            })
        }
        if(remove) {
            let removes: RemoveModifyType[] = [];
            if(remove.constructor === Object) {
                removes = [<RemoveModifyType>remove];
            }
            else if(remove.constructor === Array) {
                removes = <RemoveModifyType[]>remove;
            }
            if(removes) {

                removes.forEach(r => {
                    let { index, length = 0, label, name } = r;
                    if(label) {
                        if(typeof label === 'string') {
                            label = [label];
                        }
                        label.forEach(l => {
                            group.params = group.params.filter(p => p.label !== l);
                        });
                    }
                    else if(name) {
                        if(typeof name === 'string') {
                            name = [name];
                        }

                        name.forEach(n => {
                            group.params = group.params.filter(p => p.name !== n);
                        });
                    }
                    else {
                        if(typeof index === 'number') {
                            index = [index];
                        }
                        if(typeof index === 'object') {
                            index.forEach(i => group.params.splice(i, length));
                        }
                    }
                });
            }
        }
        delete group['modify'];
    }

    return group;
}

function getAlias (type: string | AliasType[] | string[] | undefined, isGroup: boolean = true): GroupFieldType | FieldType | FieldType[] | null  {

    let group: GroupFieldType | FieldType[] | FieldType | null = null;
    if(!type) {
        return null;
    }
    let typeContructor = type?.constructor;
    if(typeContructor === String) {
        let typeString = (type as string);
        if(isGroup) {
            let gAlias = aliases?.[typeString];
            if(gAlias) {
                group = helpers.copy(typeof gAlias === 'function' ? gAlias() : gAlias);
            }
        }
        else if(!isGroup) {
            group = helpers.copy(fieldAlias(typeString));
        }
    }
    else if(typeContructor === Array) {
        let result: GroupFieldType | FieldType[] | FieldType | null = null;
        let typeArray = type as AliasType[];
        typeArray.forEach((item: AliasType | string, index: number) => {
            let g;
            if(typeof item === 'string') {
                g = getAlias(item);
            }
            else if(item?.constructor === Object) {
                let { type: subType, title, selector } = item as AliasType;
                let subField = getAlias(subType),
                    subFieldGroup = subField as GroupFieldType;
                if(subFieldGroup?.params) {
                    if(title) {
                        subFieldGroup?.params.splice(0,0 ,{type: 'paragraph', content: '**' + title + '**'});
                        if(index > 0) {
                            subFieldGroup?.params.splice(0, 0, {type: 'line'});
                        }
                    }
                    if(selector) {
                        subFieldGroup.params.forEach((param: FieldType | GroupFieldType) => {
                            param.css = param?.css?.constructor !== Object ? {} : param.css;
                            let paramCss = param.css as CssGroupType;
                            paramCss.selector = selector;
                            paramCss.isCss = true;
                        });
                    }
                }
                g = subField;
            }
            if(g) {
                if(!result || !(<GroupFieldType>result)?.params) {
                    result = g;
                }
                else if((<GroupFieldType>result)?.params && (g as GroupFieldType)?.params?.constructor === Array) {
                    (<GroupFieldType>result).params.push(...(g as GroupFieldType).params);
                }
            }
        });
        group = result;

    }
    return helpers.copy(group);
}

export default function (field: AliasFieldType): GroupFieldType | FieldType[] | FieldType | null {

    let { group_alias, alias, options } = field;

    let type = group_alias || alias;

    let objAlias = getAlias(type, !!group_alias);

    if(field.modify) {
        objAlias = modifyGroupField(<GroupFieldType>objAlias, field.modify as ModifyType);
    }
    if(objAlias) {
        if(options) {
            objAlias = optionsAlias(objAlias, options);
        }
    }
    if(group_alias) {
        delete (<GroupFieldType>objAlias)?.['modify'];
    }
    if('visible' in field)
    {
        objAlias.visible = field.visible;
    }
    return objAlias;
}