import helpers from '@/utils/helpers';
import {store} from '@/store';
import {animations, entrance_animations} from './animation';

let globalCss = {}, screens;

export const getFontFace = (value) => {
    if(value !== null && typeof value === 'object') {
        if(value.value && value.value.indexOf('fonts.googleapis.com') > -1) {
            return `@import url("${value.value}&display=swap");`;
        }
        else {
            let formats = {
                'eot': 'embedded-opentype',
                'woff2': 'woff2',
                'woff': 'woff',
                'ttf': 'truetype',
                'svg': 'svg',
                otf: 'opentype'
            };
            let format = formats[value.ext] ? formats[value.ext] : 'ttf';

            return `@font-face {font-family: '${value.name}'; src: url('${value.value}&display=swap') format('${format}');font-display: swap;}`;
        }
    }
};
export const globalValue = (type, payload) => {
    if (type === 'typography') {
        let value = globalCss?.[type]?.[payload]?.value;
        if (value) {
            let css = [],
            fonts = [];
            let key = '--ecom-global-' + type + '-' + payload;
            for (let k in value) {
                if(k === 'font-family') {
                    fonts.push(getFontFace(value[k]));
                }
                css.push(valueType(k, 'var(' + (key + '-' + k) + ')'));

            }
            return { css, fonts };
        }
    } else if (type === 'color') {
        if (typeof payload === 'object' && payload['global-colors']) {
            return 'var(--ecom-global-colors-' + payload['global-colors'] + ')';
        } else if (payload && typeof payload === 'string') {
            return payload;
        }
        return null;
    }
};

export const convertGradient = (payload = {}) => {
    if(payload?.constructor !== Object) {
        return;
    }
    let { positions = [], angle = 180, position = 'center center', type = 'linear' } = payload;

    positions = JSON.parse(JSON.stringify(positions
                    .filter(pos => pos.color)
                ));
    positions.forEach(pos => {
                pos.color = globalValue('color', pos.color);
            });
    let css ;
    if(positions.length === 0) {
        return '';
    }
    if(positions.length === 1 && positions[0].color) {
        return 'background: ' + positions[0].color;
    }
    let colors = positions.map(pos => `${pos.color} ${pos.index}%`).join(', ');
    css = `${type}-gradient(${type === 'radial' ? ('at ' + position) : (angle + 'deg')}, ${colors})`;

    return 'background: ' + css;
};

export const convertBg = (value) => {
    if (typeof value === 'object') {
        let type,
            k,
            css = [];
        for (type in value) {
            let vt = value[type];
            if(vt === undefined) {
                continue;
            }
            if (type === 'classic') {
                if(vt['background-image']) {
                    let {
                        'background-color': bgColor,
                        'image-size': imgSize,
                        'background-image': bgImage,
                        'background-attachment': bgAttr = 'scroll',
                        'background-position': bgPos = 'center center',
                        'background-repeat': bgRepeat = 'repeat' ,
                        'background-size': bgSize = 'cover' ,
                        'background-position-x': bgPosX = 0 ,
                        'background-position-y': bgPosY = 0,
                    } = vt;
                    let strCss = `url('${helpers.resizeImage(bgImage, imgSize)}') ${bgRepeat}`;
                    if(bgAttr !== 'inherit') {
                        strCss += ` ${bgAttr}`;
                    }
                    if(bgPos !== 'custom') {
                        strCss += ` ${bgPos}`;
                    }
                    else {
                        strCss += ` ${bgPosX} ${bgPosY}`;
                    }
                    if(bgSize !== 'inherit') {
                        strCss += `/${bgSize}`;
                    }
                    if(bgColor) {
                        strCss += ` ${globalValue('color', bgColor)}`;
                    }
                    css.unshift('background: ' + strCss);
                }
                else {
                    for (k in vt) {
                        if(vt[k] !== 'custom') {
                            css.unshift(valueType(k, vt[k]));
                        }
                    }
                }
            } else if (type === 'gradient') {
                css.push(convertGradient(vt));
            }
        }
        return css;
    }
    return [];
};

export const valueType = (type, value, format = '%value%', important) => {
    if (!type || (value !== null && typeof value === 'object' && !Object.keys(value).length)) {
        return '';
    }
    let v = '';
    let arrCss = [];
    let imp = important ? ' !important' : '';

    switch (type) {
        case 'text-shadow':
        {
            let { horizontal = 0, vertical = 0, blur = 0, color = '' } = value;
            arrCss.push(...[horizontal, vertical, blur]);
            if (color) {
                let valColor = globalValue('color', color);
                if (valColor) {
                    arrCss.push(valColor);
                }
            }
            v = arrCss.join(' ');
            break;
        }
        case 'box-shadow':
        {
            let { horizontal = 0, vertical = 0, blur = 0, spread = 0, color = '', position = '' } = value;
            arrCss.push(...[horizontal, vertical, blur, spread]);
            if (color) {
                let valColor = globalValue('color', color);
                if (valColor) {
                    arrCss.push(valColor);
                }
            }
            if (position === 'inset') {
                arrCss.push(position);
            }
            v = arrCss.join(' ');
            break;
        }
        case 'color':
        case 'background-color':
        case 'border-color':
            {
                let valColor = globalValue('color', value);
                v = valColor || '';
                break;
            }
        case 'background-image': {
            if(value.indexOf('url(') > -1) {
                v =  value;
            }
            else {
                v = `url("${value}")`;
            }
            break;
        }
        case 'border-radius':
        case 'padding':
        case 'margin':
        case 'border-width': {
            if (typeof value === 'string') {
                v = value;
            } else if (typeof value === 'object') {
                let { top, right, bottom, left } = value;
                if (
                    top !== undefined &&
                    right !== undefined &&
                    bottom !== undefined &&
                    left !== undefined
                ) {
                    if (top === right && right === bottom && bottom === left) {
                        v = top;
                    } else {
                        v = `${top} ${right} ${bottom} ${left}`;
                    }
                    break;
                }
                let css = [];
                ['top', 'right', 'bottom', 'left'].forEach((k) => {
                    if (value[k] !== undefined) {
                        switch (type) {
                            case 'padding':
                            case 'margin':
                                css.push(type + '-' + k + ': ' + value[k] + imp);
                                break;
                            case 'border-width':
                                css.push('border-' + k + '-width: ' + value[k] + imp);
                                break;
                            case 'border-radius':
                                {
                                    let kRadius = {
                                        top: 'border-top-left-radius',
                                        right: 'border-top-right-radius',
                                        bottom: 'border-bottom-right-radius',
                                        left: 'border-bottom-left-radius'
                                    };
                                    css.push(kRadius[k] + ': ' + value[k] + imp);
                                    break;
                                }
                            default:
                                break;
                        }
                    }
                });
                return { css };
            }
            break;
        }
        case 'transitions': {
            let { duration, delay, timing } = value;
            return {
                css: [
                    duration ? 'transition-duration: ' + duration : '',
                    timing ? 'transition-timing-function:' + timing : '',
                    delay ? 'transition-delay:' + delay : '',
                ]
            };
        }

        case 'outline':
            {
                return {
                    css: Object.keys(value).map(k=>`${k}:${value[k]}`)
                };
            }

        case 'filter': {
            let { brightness, contrast, saturate, blur, hue } = value;

            let attrs = [
                { name: 'brightness', value: brightness },
                { name: 'contrast', value: contrast },
                { name: 'saturate', value: saturate },
                { name: 'blur', value: blur },
                { name: 'hue', value: hue }
            ].filter((x) => x.value !== undefined);
            v = attrs
                .map((attr) => {
                    return `${attr.name.replace('hue', 'hue-rotate')}( ${attr.value} )`;
                })
                .join(' ');
            break;
        }
        case 'transform-advanced': {
            let { rotate, translateX, translateY, translateZ, perspective, scale, scaleX, scaleY, skewX, skewY, keepPropertions } = value;

            scaleX = scaleX ? scaleX : 1;
            scaleY = scaleY ? scaleY : 1;

            let attrs = [
                { name: 'rotate', value: rotate },
                { name: 'translateX', value: translateX },
                { name: 'translateY', value: translateY },
                { name: 'translateZ', value: translateZ },
                { name: 'perspective', value: perspective },
                { name: 'skewX', value: skewX },
                { name: 'skewY', value: skewY },
                { name: 'scale', value: keepPropertions ? scale : `${scaleX},${scaleY}` },
            ].filter((x) => x.value !== undefined);
            v = attrs
                .map((attr) => {
                    return `${attr.name}( ${attr.value} )`;
                })
                .join(' ');

                v += `;transition: transform .3s ease;`
            break;
        }
        case 'transform-origin': {
            let { offsetX, offsetY, offsetZ } = value;

            v = `${offsetX ? offsetX : ''} ${offsetY ? offsetY : 0} ${offsetZ ? offsetZ : 0}`;
            break;
        }
        case 'animation': {
            return animations[value] ?? '';
        }
        case 'entrance-animation': {
            let c = entrance_animations[value] ?? {};
            let { css, keyframe } = c;
            if (css) {
                css = [...css, 'animation-duration: .75s;'];
            }
            return { css, keyframe };
        }
        case 'width-max': {
            if (value !== 'custom') {
                return {
                    css: ['width: ' + value + imp, 'max-width:' + value + imp]
                };
            }
            break;
        }
        case 'custom-width':
            return {
                css: ['width: ' + value + imp, 'max-width:' + value + imp]
            };
        case 'custom-css':
            return;
        case 'global-typography':
            return globalValue('typography', value);
        case 'background': {
            let css = convertBg(value);
            return { css };
        }
        case '--ecom-color-custom':
        case '--bg-thumb-custom':     {
            let valColor = globalValue('color', value);
            v = valColor || '';
            break;
        }
        case 'transition-duration':
            return {
                css: ['transition-duration: ' + value + imp,
                    'transition-property: all' + imp,
                    'transition-timing-function: ease' + imp]
            };
        case 'inline':
            return value === 'no' ? 'width: 100% !important' : '';
        case 'font-size-icon':
            return {
                css: ['width: ' + value + imp, 'height: ' + value + imp]
            };
        case 'font-family': {
            if(typeof value === 'string') {
                v = value;
            }
            else if(typeof value === 'object') {
                let fonts = [];
                if(value.value.indexOf('fonts.googleapis') > -1) {
                    fonts = ['@import url("'+value.value+'&display=swap");'];
                }
                else {
                    fonts =  [getFontFace(value)];
                }
                return {
                    css: ['font-family: \'' + value.name + imp + '\', sans-serif'],
                    fonts
                };
            }
            break;
        }
        case 'spacing': {
            if(value && typeof value === 'object') {
                let css = [];
                for(let k in value) {
                    if(typeof value[k] === 'object' && Object.keys(value[k]).length > 0) {
                        let v = valueType(k, value[k], format, important);
                        if(typeof v === 'object') {
                            css.push(...v.css);
                        }
                        else {
                            css.push(v);
                        }
                    }
                }
                return { css };
            }
            return undefined;
        }
        default:
            v = value;
            break;
    }
    format = format ?? '';

    if(typeof format === 'string' && format?.indexOf('%value%') > -1) {
        v = format.replace(/%value%/g, v);
    }
    else {
        try{
            let f = new Function('value', format);
            v = f(value);
        }
        catch(e) {
            v = format;
        }
    }
    if(type == 'transform-advanced') {
        type = 'transform';
    }
    return (v !== '' && v !== null && v!== undefined) ? (type + ': ' + v + imp) : '';
};

export const obj2Value = (key, obj) => {
    let css = [],
        keyframes = [],
        fonts = [];
    const add = (obj) => {
        let { css:vCss = [], keyframe:vKeyframes = [], fonts:vFonts = [] } = obj;
        css.push(...vCss);
        keyframes.push(...[vKeyframes]);
        fonts.push(...vFonts);
    };

    let keyValueAccept = ['value', 'selector', 'properties', 'important', 'isCss', 'wrapper'];
    const inArray = (keys1) => {
        return keys1.reduce((acc, cur) => {
            return acc && (keyValueAccept.indexOf(cur) > -1);
        }, true);
    };

    if(obj && obj.constructor === Object && inArray(Object.keys(obj))) {
        let { value, properties = {}, important = false } = obj;
        if(value !== undefined && value !== null) {
            let keys;
            if(Object.keys(properties).length) {
                keys = Object.keys(properties);
            }
            else {
                keys = [key];
            }
            for(let k in keys) {
                let formatValue = properties[keys[k]] || '%value%';
                let vt = valueType(keys[k], value, formatValue, important);

                if(vt && typeof vt === 'string') {
                    css.push(vt);
                }
                else if(vt?.constructor === Object) {
                    add(vt);
                }
            }
        }
    }
    else {
        let vt = valueType(key, obj, '%value%');
        if(vt && typeof vt === 'string') {
            css.push(vt);
        }
        else if (vt?.constructor === Object) {
            add(vt);
        }
    }
    return { css, keyframes, fonts };
};
const isCssField = (group, field) => {

    if(field?.css === false || field?.css?.isCss === false) {
        return false;
    }
    const is = (field) => {
        return field ? ((field?.css && field?.css?.isCss !== false)) : false;
    };
    let isCssGroup = group ?
                    (is(group) ||
                    group.selector) :
                    false;
    return isCssGroup || is(field);

};
const wrapCustomCss = (prefix, css) => {

    if (
        css === undefined ||
        css === null ||
        css === '{}' ||
        (typeof css == 'string' && css.trim() === '')
    ) {
        return '';
    }

    if (!prefix) {
        prefix = ' .ecom-core ';
    }

    // process css for vendors
    // Only remove reset css such as html, body, *, tag standalone
    css = '} ' + css;
    // Remove comment code
    css = css
        .replace(/\/\*[\s\S]+?\*\//g, '')
        // Wrap all selector into section id
        // eslint-disable-next-line
        .replace(/([\{\}]\s*)([^\{\}\@\%]+?)\s*\{/g, (...m) => {
            m[2] = m[2].split(',');
            m[2].forEach((s, i) => {
                s = s.trim();
                if (['from', 'to'].indexOf(s.toLowerCase()) > -1) {
                    m[2][i] = s;
                    return;
                }
                if (s == 'html' || s == 'body' || s == '*') {
                    m[2][i] = prefix;
                } else if (s.indexOf(':') === 0) {
                    m[2][i] = ' ' + prefix + s;
                } else if (s.indexOf('&') === 0) {
                    m[2][i] = ' ' + s.replace('&', prefix);
                } else if (
                    // selector only tag or process into section id
                    s === '' ||
                    s.match(/^([a-z]*)$|^([a-z][1-6])$/) !== null ||
                    prefix !== null
                ) {
                    m[2][i] = prefix + ' ' + s;
                }
            });

            // Make sure non doublicate selector
            return m[1] + m[2].join(',').trim() + '{';
        });
    return css.substr(1);
};
export const objSelector  = (rootSelector, fields, values) => {
    if(!values || fields?.constructor !== Array || values?.constructor !== Object) {
        return {};
    }
    let rs = {}, fonts = [], keyframes = [], customCss = '';
    let regScreen =/__([a-zA-Z0-9]+)$/g;
    const add = (v) => {
        let { keyframes:vKeyframes = [], fonts:vFonts = [] } = v;
        fonts.push(...vFonts);
        keyframes.push(...vKeyframes);
    };
    const getValues = (group_name, name) => {
        if(!name) {
            return [];
        }
        let rs = [], v;
        v = group_name ? (values?.[group_name] ?? {}) : values;
        if(regScreen.exec(name)) {
            v[name] && rs.push({ key: name, value: v[name] });
        }
        else {
            for(let k in v) {
                let n = k.replace(regScreen, '');
                if(n === name && v[k] !== '' && v[k] !== null && v[k] !== undefined) {
                    rs.push({
                        key: k,
                        value:v[k]
                    });
                }
            }
        }
        return rs;
    };
    let check = [];
    const run = (group = {}, field) => {
        let { name } = field,
            { group_name } = group,
            isCss = isCssField(group, field),
            valueFields = getValues(group_name, name);
        if(isCss && valueFields.length) {
            let scr;
            valueFields.forEach( ({key, value}) => {
                regScreen = /__([a-zA-Z0-9]+)$/g ;
                scr = regScreen.exec(key)?.[1] || 'desktop';
                let nKey = key.replace(regScreen, '');
                let wraperCssGroup = (group?.css ?? {}).hasOwnProperty('wrapper'),
                    wraperCssField = (field?.css ?? {}).hasOwnProperty('wrapper');
                let s = helpers.mixS(
                            wraperCssGroup ? (group?.css?.wrapper ?? '') : rootSelector,
                            ((wraperCssGroup ? group?.css?.selector : group?.selector) ?? group?.css?.selector ?? ''),
                            rootSelector
                        );
                s = helpers.mixS(
                            wraperCssField ? (field?.css?.wrapper ?? '') : s,
                            field?.css?.selector,
                            rootSelector
                        );
                if(check.indexOf(key + '||' + s) === -1) {
                    check.push(key + '||' + s);
                    rs[scr] = rs[scr] ?? {};
                    rs[scr][s] = rs[scr][s] ?? '';
                    let objValues = [];
                    if(field?.type === 'popup' && value?.constructor === Object) {
                        let v = null;
                        if(field?.options?.type === 'custom') {
                            v = value?.custom ?? {};
                        }
                        else {
                            v = value;
                        }
                        for(let k in v) {
                            objValues.push(obj2Value(k, v[k]));
                        }
                    }
                    else {
                        if(nKey === 'custom-css' && value) {
                            customCss = wrapCustomCss(rootSelector, value);
                        }
                        else {
                            let { css = {}} = field;
                            let o  = {value: value, ...css};
                            objValues.push(obj2Value(nKey, o));
                        }
                    }
                    objValues.forEach(objValue => {
                        add(objValue);
                        if(objValue.css?.constructor === Array && objValue.css.length) {
                            rs[scr][s] += objValue.css.join(';') + ';';
                        }
                    });
                }

            });
        }
    };
    fields.forEach(field => {
        if(field?.type !== 'group' && field?.params?.constructor === Array) {
            field.params.forEach(p => {
                run(field, p);
            });
        }
        else if(field?.name && field?.type) {
            run(undefined, field);
        }
    });
    return {screens: rs, fonts, keyframes, custom: customCss};
};
export const convertCss = (rootSelector, fields, values) => {

    globalCss = store.getters['settings/globalDatas'];
    screens = store.getters['builder/screens'];
    if(!globalCss || !screens || !values || !fields) {
        return {};
    }
    let oSelector = objSelector(rootSelector, fields, values);
    let style = '';
    let { screens:oScreens = [], keyframes = [], fonts = [], custom  } = oSelector;

    for(let scr in screens) {
        if(oScreens[scr]) {
            if(scr === 'desktop' && screens[scr]) {
                for(let s in oScreens[scr]) {
                    if(oScreens[scr][s].includes('display: none')) {
                        style += '@media screen and (min-width: 1025px) {' ;
                        style += s + '{' + oScreens[scr][s] + '}';
                        style += '}' ;
                    } else {
                        style += s + '{' + oScreens[scr][s] + '}';
                    }
                }
            } else {
                style += '@media screen and (max-width: ' + screens[scr].width + 'px) and (min-width: ' + (screens[scr].min + 1) + 'px){' ;
                for(let s in oScreens[scr]) {
                    style += s + '{' + oScreens[scr][s] + '}';
                }
                style += '}' ;
            }
            // if(scr !== 'desktop' && screens[scr]) {
            //     style += '@media screen and (max-width: ' + screens[scr].width + 'px) {' ;
            // }
            // for(let s in oScreens[scr]) {
            //     style += s + '{' + oScreens[scr][s] + '}';
            // }
            // if(scr !== 'desktop' && screens[scr]) {
            //     style += '}' ;
            // }
        }
    }
    style += custom;
    return { style, keyframes, fonts };
};

