<template>
    <teleport v-if="isReady" :to="teleport_id">
        <transition name="slide-up">
            <PickerFrameChild
                v-if="show"
                :header="header"
                :position="position"
                @close="$emit('close')"
                @back="$emit('back')"
                @reload="$emit('reload')"
                @loadFreeImg="$emit('loadFreeImg')"
                @update:tabs="$emit('update:tabs', $event)"
                @update:search="$emit('update:search', $event)"
            >
                <template v-for="(_, slot) of $slots" #[slot]="props">
                    <slot :name="slot" v-bind="props" />
                </template>
            </PickerFrameChild>
        </transition>
    </teleport>
</template>
<script>
import PickerFrameChild from './PickerFrameChild.vue';
export default {
    name: 'PickerFrame',
    components: {
        PickerFrameChild
    },

    inject: ['teleport_id'],

    props: {
        show: {
            type: Boolean,
            default: false
        },
        header: {
            type: Object,
            default() {
                return {};
            }
        },
        position: {
            type: String,
            default: 'absolute'
        }
    },

    emits: ['close', 'back', 'update:tabs', 'update:search', 'reload', 'loadFreeImg'],

    data() {
        return {
            isReady: false
        };
    },

    mounted() {
        this.isReady = true;
    }
};
</script>

<style>
.slide-up-enter-from,
.slide-up-leave-to {
    transform: translateY(25%);
    opacity: 0;
}
</style>
