import { mapGetters } from 'vuex';

export default {
    inject: ['exporting'],
    beforeUnmount() {
        delete this.stacks?.[this?.data?.id];
    },
    computed: {
        isDragging() {
            return this.dragCurrent;
        },
        isEditing() {
            return !this.isFullScreen && this.editingElement?.data?.id == this.data.id;
        },
        isActive() {
            let isDragPadding = this.$store.getters['builder/isDragPadding'] || '';
            return !this.isFullScreen
                    && (
                        isDragPadding == this.data.id || isDragPadding == ''
                    )
                    && (
                        this.over || this.dropTo === this
                    );
        },
        isActiveBorder() {
            let isDragPadding = this.$store.getters['builder/isDragPadding'] || '';
            return !this.isFullScreen
                    && (
                        isDragPadding == this.data.id || isDragPadding == ''
                    )
                    && (
                        this.over ||
                        (
                            this.activeElement?.data?.id == this.data.id ||
                            this.editingElement?.data?.id == this.data.id
                        ) ||
                        this.dropTo === this
                    );
        },
        isDragMe() {
            return this.dragCurrent === this;
        },
        isDropTo() {
            return this.dropTo === this;
        },
        hideOnScreen() {
            return (!this.isFullScreen &&
                this?.data?.advanced?.['display' + (this.screen == 'desktop' ? '' : '__' + this.screen)] == 'none'
            );
        },
        isFullScreen() {
            return this.exporting !== false || this.$store.getters['builder/isFullScreen'];
        },
        classes() {
            return {
                'drag__placeholder': this.isDragMe,
                [this.data.id]: true,
                'hide-on-screen': this.hideOnScreen,
                'hide-on-desktop': this?.data?.advanced?.['display'] == 'none',
                'hide-on-tablet': this?.data?.advanced?.['display__tablet'] == 'none',
                'hide-on-mobile': this?.data?.advanced?.['display__mobile'] == 'none',
                [this.data?.advanced?.['classes'] ?? '']: true
            };
        },
        attributes() {

            let attr = {};

            if (this.data?.advanced?.['id']) {
                attr['id'] = this.data.advanced['id'];
            }
            if (this.data?.advanced?.['attributes']) {
                let attrs = this.data.advanced['attributes'].split(`\n`);
                attrs.forEach((att) => {
                    att = att.split(':');

                    if (att[0] && att[1] && ['id', 'class', 'data-placeholder'].indexOf(att[0]) === -1) {
                        attr[att[0].replace(/[^a-zA-Z0-9\-\_\s]/g, '').replaceAll(" ", "")] = att[1].replace(/[^a-zA-Z0-9\-\_\s]/g, '');
                    }
                });

            }
            return attr;
        },

        scrollingAnimation() {
            const advanced = this?.data?.group ? this.data.group?.advanced : this.data.advanced;
            if(!advanced) return;
            const animation = [];

            advanced.hasOwnProperty('scolling_horizontal') && animation.push({css: 'translateX', value: advanced.scolling_horizontal['scrolling-animation-px']});
            advanced.hasOwnProperty('scolling_vertical') && animation.push({css: 'translateY', value: advanced.scolling_vertical['scrolling-animation-px']});
            advanced.hasOwnProperty('scrolling_rotate') && animation.push({css: 'rotate', value: advanced.scrolling_rotate['scrolling-animation-deg']});
            advanced.hasOwnProperty('scrolling_opacity') && animation.push({css: 'opacity', value: advanced.scrolling_opacity['scrolling-animation-opacity']});
            advanced.hasOwnProperty('scrolling_scale') && animation.push({css: 'scale', value: advanced.scrolling_scale['scrolling-animation-scale']});
            advanced.hasOwnProperty('scrolling_blur') && animation.push({css: 'blur', value: advanced.scrolling_blur['scrolling-animation-blur']});

            return animation.length ? JSON.stringify(animation) : '';
        },

        /*Access to parent node*/
        _blocks() {
            return this.parent('blocks');
        },
        _block() {
            return this.parent('block');
        },
        _column() {
            return this.parent('column');
        },
        _row() {
            return this.parent('row');
        },
        _section() {
            return this.parent('section');
        },
        _group() {
            return this.parent('group');
        },
        // ...mapGetters('page', ['allNodes']),
        ...mapGetters('builder', ['stacks', 'screen', 'dragCurrent', 'dropPos', 'dropTo', 'activeElement', 'editingElement', 'contextMenu'])
    },
    methods: {

        idoc() {
            return window?.EComposerBuilder?.contentDocument ?? null;
        },

        iwin() {
            return window?.EComposerBuilder?.contentWindow ?? null;
        },

        parent(is) {
            let get = (o) => {
                return o ? (o.is == is ? o : get(o.$parent)) : null;
            };
            return get(this);
        },

        parentsOver(inst) {
            if (!inst) {
                inst = this;
            }
            if (inst.$parent) {
                if (inst.$parent.over === false) {
                    inst.$parent.over = true;
                }
                this.parentsOver(inst.$parent);
            }
        },

        parentsOut(inst) {
            if (!inst) {
                inst = this;
            }
            if (inst.$parent) {
                if (inst.$parent.over === true) {
                    inst.$parent.over = false;
                }
                this.parentsOut(inst.$parent);
            }
        },

        notice(params) {
            this.$store.commit('global/flash', {
                open: true,
                message: params.message,
                title: params.title,
                type: params.type,
            });
        },

        startDrag(e) {

            if (
                (e && e.target.closest('[data-stopdrag]') !== null) ||
                (!this.idoc || !this.iwin)
            ) {
                return;
            }

            this.placeholder = 'top';
            // For unbind select when click outside builder
            this.$store.commit('builder/setDragging', { timmer: new Date().getTime() });
            document.body.style.userSelect = 'none';
            document.body.style.webkitUserSelect = 'none';
            document.body.style.cursor = 'move';
            this.idoc().body.style.userSelect = 'none';
            this.idoc().body.style.webkitUserSelect = 'none';
            this.idoc().body.style.cursor = 'move';

            let html = this.idoc().querySelector('html'),
                body = this.idoc().body,
                rect = window.EComposerBuilder.getBoundingClientRect(),
                _x = null,
                _y = null;

            this.$store.commit('builder/resetDragging');

            let onDragMove = (_e) => {

                    if (_x === null) {
                        _x = _e.clientX;
                        _y = _e.clientY;
                    }

                    if (
                        (
                            Math.abs(_e.clientX - _x) > 5 ||
                            Math.abs(_e.clientY - _y) > 5
                        ) &&
                        !this.dragCurrent
                    ) {
                        this.$store.commit('builder/setDragging', {
                            current: this.is == 'group' ? this.$parent.$parent : this,
                            dropTo: null,
                            dropPos: 'top'
                        });
                        this.dragging = true;
                    } else if (!this.dragging) {
                        return;
                    }
                    // Scroll the view while dragging to the edge of top or bottom
                    if (_e.clientY < 100) {
                        html.scrollTo({ top: html.scrollTop - 50 });
                        body.scrollTo({ top: body.scrollTop - 50 });
                    } else if (_e.clientY > window.EComposerBuilder.offsetHeight - 100) {
                        html.scrollTo({ top: html.scrollTop + 50 });
                        body.scrollTo({ top: body.scrollTop + 50 });
                    }

                    if(rect.right - rect.left < _e.clientX || _e.clientX < 0) {
                        this.$store.commit('builder/setDropTo', null);
                    }
                    // Update position of cursor
                    this.$store.commit('builder/setDragging', {
                        x: _e.clientX + (_e.view !== window ? rect.left : 0),
                        y: _e.clientY + (_e.view !== window ? rect.top : 0)
                    });

                },

                onDrangEnd = () => {

                    document.onmousemove = null;
                    document.onmouseup = null;
                    this.idoc().onmousemove = null;
                    this.idoc().onmouseup = null;

                    document.body.style.userSelect = '';
                    document.body.style.webkitUserSelect = '';
                    document.body.style.cursor = '';
                    this.idoc().body.style.userSelect = '';
                    this.idoc().body.style.webkitUserSelect = '';
                    this.idoc().body.style.cursor = '';

                    if (
                        this.dropTo?.control !== false &&
                        this.dragCurrent &&
                        this.dropTo &&
                        this.dragCurrent !== this.dropTo
                    ) {
                        let pos = (this.dropPos == 'bottom' || this.dropPos == 'right') ? 1 : 0;
                        if (
                            (
                                this.dragCurrent.$parent === this.dropTo.$parent ||
                                this.dragCurrent.$parent.$parent.$parent === this.dropTo.$parent
                            ) &&
                            this.dragCurrent.index < this.dropTo.index
                        ) {
                            pos -= 1;
                        }
                        this.dragCurrent.over = false;
                        this.dropTo.over = false;
                        this.dropTo.dropIn(this.dragCurrent, pos);
                    }

                    this.dragging = false;
                    this.$store.commit('builder/resetDragging');
                    this.$store.commit('builder/setActiveElement', null);
                    this.$store.commit('builder/sectionDrag', null);

                };

            this.idoc().onmousemove = onDragMove;
            document.onmousemove = onDragMove;

            this.idoc().onmouseup = onDrangEnd;
            document.onmouseup = onDrangEnd;

        },

        rightClickMenu(e) {
            if (
                this.is == 'block' &&
                !this.control
            ) {
                return true;
            }

            if (
                !this.isFullScreen &&
                this.contextMenu &&
                !this.contextMenu.current
            ) {
                let rect = window.EComposerBuilder.getBoundingClientRect(), y = e.clientY + rect.top, top;
                if(y + 320 > rect.height) {
                    top  = y - (e.clientY + rect.top + 320 - rect.height)
                } else {
                    top = y;
                }

                this.$store.commit('builder/setContextMenu', {
                    current: this,
                    x: e.clientX + rect.left,
                    y: top,
                    time: new Date().getTime(),
                    stick: e.pageX < rect.width - 200 ? 'left' : 'right',
                    bottom: y + 320 > rect.height
                });
            }
            e.preventDefault();
        },

        cloneMe() {
            return this.$helpers.copy(this.data);
        },

        cloneData() {
            const _this = this;
            const regex = /"id"\s?:\s?"ecom-[^\"]+\"/g;
            let string = JSON.stringify(this.data);

            let clone = string.replace(regex, function() {
                return `"id":"${_this.$helpers.randid()}"`;
            });
            return JSON.parse(clone);
        },

        dispatchResize() {
            if (typeof(Event) === 'function') {
                this.iwin().dispatchEvent(new Event('resize'));
            }
        },

        scroll(to, bh) {

            let html = this.idoc().querySelector('html'),
                body = this.idoc().body;
            if(html)
                html.scrollTo({ top: html.scrollTop + to, behavior: bh !== false ? 'smooth' : '' });
            if(body)
                body.scrollTo({ top: body.scrollTop + to, behavior: bh !== false ? 'smooth' : '' });
        },

        scrollTo(to, bh) {

            let html = this.idoc().querySelector('html'),
                body = this.idoc().body;

            html.scrollTo({ top: to, behavior: bh !== false ? 'smooth' : '' });
            body.scrollTo({ top: to, behavior: bh !== false ? 'smooth' : '' });
        },


    }
};
