<template>
    <template v-if="showStructure">
        <div
            ref="wrapper"
            :class="{
                'ecom-core core__section--starting': true,
                'drop_placeholder': over,
                'isonly': isonly
            }"
            @mouseenter="overStart"
            @mouseleave="over=false"
        >
            <div class="core__starting--structure">
                <p class="font-editor text-structure">Select structure or drag element here</p>
                <div
                    v-for="(st, i) in structures"
                    :key="i"
                    class="core__structure--item"
                    @click="setStructure(st)"
                >
                    <ul>
                        <li
                            v-for="(s, j) in st"
                            :key="j"
                            :style="
                            typeof s == 'number' ?
                            `width: ${s}%` :
                            'background:transparent;width:100%;'
                        "
                            v-html="typeof s == 'string' ? s : ''"
                        />
                    </ul>
                </div>


            </div>

            <a
                v-if="!isonly"
                href="#"
                title="Close"
                class="core__starting--close"
                @click="close"
            >
                <!-- <ecom-icon :icon="icons.faXmark" /> -->
                <Icon :source="icons.CancelMajor" class="icon-builder"></Icon>
            </a>
            <template v-else />
        </div>
        <div  v-if="!isonly" :style="{display:'flex',justifyContent:'center', flexWrap: 'wrap',width: '100%'}">
            <button
                v-for="(source, i) in sources"
                :key="i"
                class="ecom-core-btn"
                :class="source.id != 'ecom' ? 'secondary' : ''"
                @click="openLibrary(source.id)"
            >
                <template v-if="source.id == 'ecom'">
                    <Icon :source="icons.TemplateMajor" class="icon-builder"></Icon>
                    <span>Explore Library</span>
                </template>
                <img v-else :src="source.logo" :style="{height:'100%', padding:'5px'}" />
            </button>
            <button  @click="pasteClipBoard" class="ecom-core-btn secondary">
                <!-- <ecom-icon :icon="icons.faPaste" class="ecom-icon" /> -->
                <Icon :source="icons.DuplicateMinor" class="icon-builder"></Icon>
                <span>Paste from Clipboard</span>
            </button>
            <button @click="openPageSetting" class="ecom-core-btn secondary">
                <!-- <ecom-icon :icon="icons.faFileImport" class="ecom-icon" /> -->
                <Icon :source="icons.ImportMinor" class="icon-builder"></Icon>
                <span>Import *.ecom file</span>
            </button>

    </div>
        <StartLibrary v-if="isonly" />
    </template>
</template>

<script>
    import { Core } from '@/mixins';
    // import {faXmark, faFolderOpen, faFileImport, faPaste} from '@fortawesome/pro-light-svg-icons'
    import StartLibrary from './StartLibrary.vue';
    import { CancelMajor, FolderMajor, ImportMinor, DuplicateMinor, TemplateMajor } from "@/assets/icons";
    export default {
        name: 'Structure',
        components: { StartLibrary },
        mixins: [Core],
        data () {
            return {
                over: false,
            };
        },
        computed: {
            icons(){
                return {
                    // faXmark, faFolderOpen, faFileImport,faPaste,
                    CancelMajor, FolderMajor, ImportMinor, DuplicateMinor, TemplateMajor
                };
            },
            isonly () {
                return this.$parent.$parent.visibleSections.length === 1;
            },

            structures () {
                return [
                    [
                        `<li style="width:100%;border: none;margin-bottom:2px;height:16px;">
                        <ul>
                            <li style="width:50%"></li>
                            <li style="width:50%"></li>
                        </ul>
                    </li>
                    <li style="width:100%;height:16px;border:none;"></li>`, [
                            {
                                width: 100,
                                blocks: [{
                                    row: {
                                        columns: [{
                                            width: 50,
                                            blocks: []
                                        }, {
                                            width: 50,
                                            blocks: []
                                        }]
                                    }
                                }, {
                                    row: {
                                        columns: [{
                                            width: 100,
                                            blocks: []
                                        }]
                                    }
                                }]
                            }
                        ]
                    ],
                    [
                        `<li style="width:100%;height:16px;margin-bottom:2px;border:none;"></li>
                    <li style="width:100%;border: none;height:16px;">
                        <ul>
                            <li style="width:50%"></li>
                            <li style="width:50%"></li>
                        </ul>
                    </li>`, [
                            {
                                width: 100,
                                blocks: [{
                                    row: {
                                        columns: [{
                                            width: 100,
                                            blocks: []
                                        }]
                                    }
                                }, {
                                    row: {
                                        columns: [{
                                            width: 50,
                                            blocks: []
                                        }, {
                                            width: 50,
                                            blocks: []
                                        }]
                                    }
                                }]
                            }
                        ]
                    ],
                    [
                        `<li style="width:50%;height:34px;"></li>
                    <li style="width:50%;height:34px;background:transparent">
                        <ul style="margin-bottom:2px">
                            <li style="width:50%;height:16px"></li>
                            <li style="width:50%;height:16px"></li>
                        </ul>
                        <ul>
                            <li style="width:50%;height:16px"></li>
                            <li style="width:50%;height:16px"></li>
                        </ul>
                    </li>`,
                        [
                            {
                                width: 50,
                                blocks: []
                            },
                            {
                                width: 50,
                                blocks: [{
                                    row: {
                                        columns: [{
                                            width: 50,
                                            blocks: []
                                        }, {
                                            width: 50,
                                            blocks: []
                                        }]
                                    }
                                }, {
                                    row: {
                                        columns: [{
                                            width: 50,
                                            blocks: []
                                        }, {
                                            width: 50,
                                            blocks: []
                                        }]
                                    }
                                }]
                            }
                        ]
                    ],
                    [
                        `<li style="width:50%;height:34px;background:transparent">
                        <ul style="margin-bottom:2px">
                            <li style="width:50%;height:16px"></li>
                            <li style="width:50%;height:16px"></li>
                        </ul>
                        <ul>
                            <li style="width:50%;height:16px"></li>
                            <li style="width:50%;height:16px"></li>
                        </ul>
                    </li>
                    <li style="width:50%;height:34px;"></li>`,
                        [
                            {
                                width: 50,
                                blocks: [{
                                    row: {
                                        columns: [{
                                            width: 50,
                                            blocks: []
                                        }, {
                                            width: 50,
                                            blocks: []
                                        }]
                                    }
                                }, {
                                    row: {
                                        columns: [{
                                            width: 50,
                                            blocks: []
                                        }, {
                                            width: 50,
                                            blocks: []
                                        }]
                                    }
                                }]
                            },
                            {
                                width: 50,
                                blocks: []
                            }
                        ]
                    ],
                    [
                        `<li style="width:100%;height:34px;background:transparent">
                        <ul style="margin-bottom:2px">
                            <li style="width:33%;height:16px"></li>
                            <li style="width:33%;height:16px"></li>
                            <li style="width:33%;height:16px"></li>
                        </ul>
                        <ul>
                            <li style="width:33%;height:16px"></li>
                            <li style="width:33%;height:16px"></li>
                            <li style="width:33%;height:16px"></li>
                        </ul>
                    </li>`,
                        [
                            {
                                width: 100,
                                blocks: [{
                                    row: {
                                        columns: [{
                                            width: 33.33,
                                            blocks: []
                                        }, {
                                            width: 33.33,
                                            blocks: []
                                        }, {
                                            width: 33.33,
                                            blocks: []
                                        }]
                                    }
                                }, {
                                    row: {
                                        columns: [{
                                            width: 33.33,
                                            blocks: []
                                        }, {
                                            width: 33.33,
                                            blocks: []
                                        }, {
                                            width: 33.33,
                                            blocks: []
                                        }]
                                    }
                                }]
                            }
                        ]
                    ],
                    [
                        `<li style="width:100%;height:34px;background:transparent">
                        <ul style="margin-bottom:2px">
                            <li style="width:25%;height:16px"></li>
                            <li style="width:25%;height:16px"></li>
                            <li style="width:25%;height:16px"></li>
                            <li style="width:25%;height:16px"></li>
                        </ul>
                        <ul>
                            <li style="width:25%;height:16px"></li>
                            <li style="width:25%;height:16px"></li>
                            <li style="width:25%;height:16px"></li>
                            <li style="width:25%;height:16px"></li>
                        </ul>
                    </li>`,
                        [
                            {
                                width: 100,
                                blocks: [{
                                    row: {
                                        columns: [{
                                            width: 25,
                                            blocks: []
                                        }, {
                                            width: 25,
                                            blocks: []
                                        }, {
                                            width: 25,
                                            blocks: []
                                        }, {
                                            width: 25,
                                            blocks: []
                                        }]
                                    }
                                }, {
                                    row: {
                                        columns: [{
                                            width: 25,
                                            blocks: []
                                        }, {
                                            width: 25,
                                            blocks: []
                                        }, {
                                            width: 25,
                                            blocks: []
                                        }, {
                                            width: 25,
                                            blocks: []
                                        }]
                                    }
                                }]
                            }
                        ]
                    ],
                    [100],
                    [50, 50],
                    [30, 70],
                    [70, 30],
                    [33.33, 33.33, 33.33],
                    [25, 25, 50],
                    [50, 25, 25],
                    [25, 50, 25],
                    [20, 60, 20],
                    [25, 25, 25, 25],
                    [20, 20, 20, 20, 20],
                    [16.66, 16.66, 16.66, 16.66, 16.66, 16.66]
                ];
            },

            posData () {
                return {
                    index: 0,
                    parent: this.$parent
                };
            },
            sources(){
                return this.$store.getters['library/sources']
            },
            params(){
                return this.$store.getters['page/params'];
            },
            showStructure(){
                return !this.isFullScreen && this.params.page !== 'menu';
            }
        },

        mounted () {
            let wrp = this.$refs.wrapper;
            if (!wrp) {
                return;
            }
            let h = this.$refs.wrapper.querySelector('.core__starting--structure').offsetHeight + 60,
                setStyle = `height: ${h}px;border-width: 2px;padding: 30px 20px;margin: 20px 40px 0px;opacity: 1`;
            //if (this.$parent.$parent.indexofnew != this.$parent.index) {
                //return this.$refs.wrapper.setAttribute('style', setStyle);
            //}
            this.$refs.wrapper.setAttribute('style', `height: 0;opacity: 0;border-width: 0px;`);
            setTimeout(() => {
                if (this.$refs.wrapper) {
                    this.$refs.wrapper.setAttribute('style', setStyle);
                }
                if (this.$parent.isLast) {
                    this.scroll(h);
                }
            }, 1);
            setTimeout(() => {
                if (this.$refs.wrapper) {
                    this.$refs.wrapper.style.height = 'auto';
                }
            }, 350);

            this.$store.commit('builder/setEditingElement', null);

        },
        methods: {

            overStart () {
                if (
                    this.isDragging &&
                    this.dragCurrent.is != 'section'
                ) {
                    this.over = true;
                    this.$store.commit('builder/setDropTo', this);
                }
            },
            setStructure (st) {
                if (
                    typeof st[0] == 'string' &&
                    typeof st[1] == 'object'
                ) {
                    if(!this.$parent.section.columns)
                    {
                        this.$parent.section.columns = [];
                    }
                    st[1].forEach((col) => {

                        this.$parent.columns.push(col);
                    });
                    return;
                }

                if(!!this.$parent.section.columns)
                {
                    this.$parent.section.columns = [];
                }
                st.forEach((s) => {

                    this.$parent.columns.push({
                        width: s,
                        blocks: []
                    });
                });
            },
            openLibrary (source) {
                this.$store.dispatch('library/open', {
                    section: this.$parent,
                    source: source,
                    tab: this.isonly ? 'pages' : 'sections'
                });
            },
            close () {

                this.$refs.wrapper.style.height = this.$refs.wrapper.offsetHeight + 'px';
                this.$refs.wrapper.classList.remove('core__helper--show');
                this.$refs.wrapper.classList.add('core__helper--close');

                setTimeout(() => { this.$parent.dropOut(); }, 450);

                let html = window.EComposerBuilder.contentDocument.querySelector('html'),
                    body = window.EComposerBuilder.contentDocument.body;

                html.scrollTo({ top: html.scrollTop - 250, behavior: 'smooth' });
                body.scrollTo({ top: body.scrollTop - 250, behavior: 'smooth' });

            },
            dropIn (current) {
                if (current.is == 'section') {
                    return;
                }
                if (current.is == 'column') {
                    let crow = current.$parent;
                    if(!this.$parent.section.columns)
                    {
                        this.$parent.section.columns = [];
                    }
                    this.$parent.columns.push(current.dropOut());
                    crow.refreshCols();
                } else {
                    if(!this.$parent.section.columns)
                    {
                        this.$parent.section.columns = [];
                    }
                    this.$parent.columns.push({
                        width: 50,
                        blocks: [current.dropOut()]
                    });
                }
                this.$parent.refreshCols();
            },
            openPageSetting(){
                this.$store.commit('page/setImportPopup', true);
            },
            pasteClipBoard(){
                if(!window.navigator.clipboard)
                {
                    return this.$store.dispatch('global/flash',{
                        type: 'error',
                        message: 'Your browser not support clipboard',
                        open: true

                    });
                }
                window.navigator.clipboard.readText().then((text)=>{
                    if(!text)
                    {
                        this.$store.dispatch('global/flash',{
                            type: 'error',
                            message: 'Your clipboard empty!',
                            open: true

                        });
                    }
                    else
                    {
                        try{
                            let data = JSON.parse(text);
                            this.$store.commit('page/insertSections',  {sections: [data]}, { root: true });
                        }
                        catch(e)
                        {
                            this.$store.dispatch('global/flash',{
                                type: 'error',
                                message: 'Your data from clipboard invalid',
                                open: true

                            });
                        }
                    }
                })
                .catch(e=>{

                    this.$store.dispatch('global/flash',{
                        type: 'error',
                        message: e.message,
                        open: true

                    });
                })
        }

        }
    };
</script>
