<template>
    <div class="relative ecom-start-library">
        <div :style="{display:'flex',flexWrap:'wrap',justifyContent:'center',marginBottom:'15px'}">
             <button
                v-for="(source, i) in sources"
                :key="i"
                class="ecom-core-btn"
                :class="source.id != 'ecom' ? 'secondary' : ''"
                @click="$parent.openLibrary(source.id)"
            >
                <template v-if="source.id == 'ecom'">
                    <Icon :source="icons.TemplateMajor" class="icon-builder"></Icon>
                    <span>Explore Library</span>
                </template>
                <img v-else :src="source.logo" :style="{height:'100%', padding:'10px'}"  />
            </button>
             <button @click="openPageSetting" class="ecom-core-btn secondary">
                <Icon :source="icons.ExitMajor" class="icon-builder icon-color"></Icon>
                <span>Import</span>
            </button>
            <button @click="pasteClipBoard"  class="ecom-core-btn secondary" >
                <Icon :source="icons.ecClipboard" class="icon-builder icon-color"></Icon>
                <span>Paste from Clipboard</span>
            </button>
        </div>
        <div v-if="loading == true" class="flex items-center justify-center -mt-10">
            <span>Loading....</span>
        </div>
        <template v-else-if="layoutTrending && layoutTrending.length > 0">
            <div class="ecom-trending-items" :style="{display:'flex',width:'100%', flexDirection:'column',flexWrap:'wrap'}">
                <h3>Trending templates</h3>
                <p class="ecom-trending-items--desc">Select demo and get started in no time!</p>
                <div 
                    class="ecom-start-library--items"
                    :class="[
                        screen == 'tablet' ? 'ecom-screen_tablet' : '',
                        screen == 'mobile' ? 'ecom-screen_mobile' : '',
                    ]"
                >
                    <div
                        v-for="(item, i) in layoutTrending"
                        :key="i"
                        class="ecom-start-library--item"
                    >
                        <div class="ecom-start-library--thumbnail" @mouseenter="thumbnSlide" @mouseleave="thumbnSlide">
                            <img :src="item.thumbnail_url ?? defaultImg" :alt="item.title" loading="lazy">
                        </div>
                        <div class="ecom-core-group-bot">
                            <span class="ecom-post-title">{{ item.title }}</span>
                            <div class="ecom-core-group-actions">
                                <button class="ecom-core-btn secondary" type="button" @click="insertPost(item)">
                                    Start Editing
                                </button>
                                <button class="ecom-core-btn secondary ecom-core-btn-preview" @click="previewTemplate(item)" type="button">
                                    Preview
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>
        <p v-else>
            No trending templates
        </p>

    </div>
</template>

<script>
    // import { faFolders, faFileImport, faPaste } from '@fortawesome/pro-light-svg-icons';
    import { fields} from '@/constants/confirm';
    import { TemplateMajor, ExitMajor, ecClipboard} from "@/assets/icons";
    export default {
        data () {
            return {
                defaultImg: '/images/placeholder.png',
                items: [],
                ready: false,
                // faFolders, faFileImport, faPaste,
                icons: {
                    TemplateMajor, ExitMajor, ecClipboard
                }
            };
        },
        computed: {
            layoutTrending(){
                return this.$store.getters['page/layoutTrending']?.slice(0,8);
            },

            loading(){
                return this.$store.getters['page/loading'];
            },
            sources(){
                return this.$store.getters['library/sources'];
            },
            screen(){
                return this.$store.getters['builder/screen'];
            },

        },
        methods: {
            previewTemplate (item) {
                this.$parent.openLibrary('ecom')
                this.$store.dispatch('library/setPreview', { open: true, ...item });
            },
            insertPost(item) {
                let key = 'EComCallback' + new Date().getTime();
                    window[key] = (type,params) => {
                        if(type === 'reject') return true;
                        this.$store.dispatch('library/insertPost', { id: item.id, type,params });
                    };
                    this.$store.commit('builder/setPopup',
                        {
                            show: true,
                            type: 'warning',
                            loading: false,
                            closeable: true,
                            title: 'Choose how you want to insert this template',
                            callback: key,
                            action: {
                                title: 'Insert'
                            },
                            cancel: 'Cancel',
                            fields:fields,
                            message: ``,
                            values:{
                                overwrite: false,
                                remove_typo: false
                            }
                    });
            },
            thumbnSlide(e) {
                let img = e.target.querySelector("img");
                if (e.type == "mouseenter") {
                    let top = img.offsetHeight - e.target.offsetHeight;
                    let slideTime = this.calculateSlideTime(img.offsetHeight);
                    img.style.transition = `top ${slideTime}s ease`;
                    img.style.top = -(top > 0 ? top : 0) + "px";
                } else {
                    img.style.transition = "top 0.8s ease";
                    img.style.top = "0px";
                }
            },

            calculateSlideTime(height) {
                let slideTime = height / 100 * 0.4;
                return slideTime;
            },

            openPageSetting(){
                this.$store.commit('page/setImportPopup', true);
            },
            pasteClipBoard(){
            if(!window.navigator.clipboard)
            {
                return this.$store.dispatch('global/flash',{
                    type: 'error',
                    message: 'Your browser not support clipboard',
                    open: true

                });
            }
            window.navigator.clipboard.readText().then((text)=>{
                if(!text)
                {
                    this.$store.dispatch('global/flash',{
                        type: 'error',
                        message: 'Your clipboard empty!',
                        open: true

                    });
                }
                else
                {
                    try{
                        let data = JSON.parse(text);
                        this.$store.commit('page/insertSections',  {sections: [data]}, { root: true });
                    }
                    catch(e)
                    {
                        this.$store.dispatch('global/flash',{
                            type: 'error',
                            message: 'Your data from clipboard invalid',
                            open: true

                        });
                    }
                }
            })
            .catch(e=>{
                this.$store.dispatch('global/flash',{
                    type: 'error',
                    message: e.message,
                    open: true

                });
            })
        }

        },


    };
</script>
<style>
    .loading {
        animation: loading 1.5s 0s infinite;
    }

    @keyframes loading {
        0% {
            opacity: 1;
            filter: contrast(1);
        }
        25% {
            opacity: 0.25;
        }
        50% {
            opacity: 0.75;
            filter: contrast(0);
        }
        75% {
            opacity: 0.25;
        }
        100% {
            opacity: 1;
            filter: contrast(1);
        }
    }
</style>
