export default {
    computed: {
        fieldsHidden() {
            let fieldsHidden = [];
            const checkVisible = (f, g) => {
                let p = this.pathField(g, f),
                    i = fieldsHidden.indexOf(p);
                let isVisible = this.visible(f, g);
                if(isVisible === 'ignore') {
                    return;
                }
                if(i === -1) {
                    if(!isVisible) {
                        fieldsHidden.push(p);
                    }
                }
                else if(isVisible) {
                    fieldsHidden.splice(i , 1);
                }
            };
            const run = (fields, group = {}) => {
                if(fields && typeof fields === 'object') {
                    for(let k in fields) {
                        let field = fields[k];
                        if(this.isGroup(field)) {
                            checkVisible({}, field);
                            if(field.params.constructor === Array) {
                                run(field.params, field);
                            }
                        }
                        else {
                            checkVisible(field, group);
                        }
                    }
                }
            };
            run(this.$helpers.copy(this.fields));
            return fieldsHidden;
        },
        screens() {
            return this.$store.getters['builder/screens'];
        }
    },
    methods: {
        delItemArr(index, arr){
            if(index > -1){
                arr.splice(index, 1);
            }
        },
        pathField(group = {}, field = {}, useGroupTitle = true) {
            let { group_name, group_title } = group;
            let name = this.isGroup(field) ? 'group_name' : 'name';
            let rs = useGroupTitle ?
                        [group_name, group_title , field?.[name]] :
                        [group_name , field?.[name]];
            return rs.filter(x => x).join('||');
        },
        isGroup(f) {
            return !!(f.type != 'group' && f.params);
        },
        mergeName(selector, name, type = ''){
            return [selector, name ? (name + type) : '']
                    .filter(x => x)
                    .join('||');
        },
        visible(field, group) {

            let check = Object.keys(field).length ? 'field' : 'group';
            if((check === 'field' && !field?.name) || (check === 'group' && !group?.group_name && !group?.group_title)) {
                return 'ignore';
            }
            let values = {};
            let screens = Object.keys(this.screens);
            let pathField = this.pathField(group, field, false);
            if(group?.group_name && check === 'field') {
                values = this.values?.[group.group_name] ?? {};
            }
            else {
                values = this.values;
            }
            let visible = check === 'field' ? field.options?.visible : group.options?.visible,
                condition = null,
                keepData = true;
            if(typeof visible === 'function') {
                condition = visible;
            }
            else if(typeof visible === 'object') {
                ({condition , 'keep_data': keepData = true} = visible);
            }
            if(condition) {
                let visible = condition(values, this.values);
                if(visible) {
                    screens.forEach(screen => {
                        let kScr = (screen === 'desktop' ? '' : ('__' + screen));

                        this.restoreValue(pathField + kScr);
                    });
                    
                    return true;
                }
                else {
                    if(!keepData && ((check === 'group' && group.group_name) || check === 'field')){
                        let k;
                        if(check === 'field') {
                            k = field.name;
                        }
                        else {
                            k = group.group_name;
                        }
                        
                        screens.forEach(screen => {
                            let kScr = (screen === 'desktop' ? '' : ('__' + screen));
                            if(values[k + kScr] !== undefined) {
                                this.valueDeleted[pathField + kScr] = values[k + kScr];
                                delete values[k + kScr];
                            }
                        });
                        
                    }
                    return false;
                }
            }
            else if (typeof visible === 'object' && visible.name && 'value' in visible)
            {
                return values[visible.name] === visible.value;
            }
            return true;

        },
        restoreValue(path){
            let value = this.valueDeleted[path];
            if(value !== undefined){
                this.$helpers.setValByKey(this.values, path, value, '||', false);
            }
        }
    }
};
