import { ROOT_STORE } from "@/store/constants";

import type { MutationTree } from "vuex";
import type { RootMutationsTypes, RootStateType } from "@/store/interfaces";

export const mutations: MutationTree<RootStateType> & RootMutationsTypes = {
    [ROOT_STORE.MUTATIONS.SET_TAB](state: RootStateType, payload: string) {
        state.controls.current = payload;
    },
    [ROOT_STORE.MUTATIONS.UPDATE_REFRESH](state: RootStateType) {
        state.refresh = Math.random();
    },
    [ROOT_STORE.MUTATIONS.UPDATE_SEARCHING](state: RootStateType, payload: string) {
        state.controls.search = payload;
    },
    [ROOT_STORE.MUTATIONS.UPDATE_VERSION](state: RootStateType, payload: string) {
        state.version = payload;
    }
};
