import { confirmExit } from '@/utils/helpers';
export default {
    namespaced: true,
    state: {
        open: false,
        preview: {
            open: false,
            markup: ''
        },
        ready: false,
        loadmore: false,
        currentName: 'pages',
        showSaveForm: false,
        detailTask: false,
        detailPost: [],
        errors: null,
        success: null,
        sections: {
            items: null,
            categories: null,
            metadata: {},
            loading: false
        },
        pages: {
            items: null,
            categories: [],
            metadata: {},
            loading: false
        },
        my_templates: {
            items: null,
            metadata: {
                paged: 1,
                limit: 30,
                search: '',
                type: 'all',
                loadmore: false
            },
            loading: false
        },

        my_tasks: {
            items: null,
            loading: false,
            metadata: {
                paged: 1,
                limit: 30
            },
        },

        favorites: [],

        categories : [],

        sources: [
            {
                title: 'Template',
                id: 'ecom',
                logo: '/images/logo.png',
                logo_white: '/images/logo.png',
                icon: '/images/logo.png',
                icon_white: '/images/logo.png'
            }
        ],

        source: 'ecom',
        type: 'library'
    },
    getters: {
        open(state) {
            return state.open;
        },
        loadmore(state) {
            return state.loadmore;
        },
        type(state) {
            return state.type;
        },
        favorites(state) {
            return state.favorites;
        },
        categories(state) {
            return state.categories;
        },
        currentName(state) {
            return state.currentName;
        },
        current(state) {
            return state[state.currentName];
        },
        items(state, getters) {
            return getters.current?.items;
        },
        metadata(state, getters) {
            return getters.current.metadata;
        },
        loading(state, getters) {
            return getters.current.loading;
        },
        showSaveForm(state) {
            return state.showSaveForm;
        },
        detailTask(state){
            return state.detailTask;
        },
        detailPost(state) {
            return state.detailPost;
        },
        preview(state){
            return state.preview;
        },
        sources(state) {
            return state.sources;
        },
        source(state) {
            return state.sources.filter((s) => {
                return s.id == state.source;
            })?.[0] ?? {
                name: 'EComposer',
                id: 'ecom',
                logo: 'images/logo.png'
            };
        },
        pages(state)
        {
            return state.pages;
        },
        sections(state)
        {
            return state.sections;
        }



    },
    mutations: {
        open(state, payload) {
            state.open = payload;
        },
        loadmore(state, payload) {
            state.loadmore = payload;
        },
        type(state, payload) {
            state.type = payload;
        },
        source(state, payload) {
            state.source = payload;
        },
        favorites(state, payload){
            state.favorites = payload;
        },
        categories(state, payload){
            state.categories = payload;
        },
        currentName(state, payload) {
            state.currentName = payload;
        },
        metadata(state, payload) {
            if(state[state.currentName])
                Object.assign(state[state.currentName].metadata, payload);
        },
        resetMeta(state) {
            state.sections.metadata = {
                paged: 1,
                limit: 30,
                type: 'newest',
                category: '',
                search: ''
            };
            state.pages.metadata = {
                paged: 1,
                limit: 30,
                type: 'newest',
                category: '',
                search: ''
            };
        },
        loaded(state, payload) {
            if (!payload?.items) {
                payload.items = {data: []};
            }
            if(state?.loadmore) {
                const items = [...state[state.currentName].items?.data, ...payload.items.data];
                payload.items.data = items;
                Object.assign(state[state.currentName], payload);
                state.loadmore = false;
            } else {
                Object.assign(state[state.currentName], payload);
            }
            state[state.currentName].loading = false;
        },
        showSaveForm(state, payload) {
            state.showSaveForm = payload;
        },
        detailTask(state, payload) {
            state.detailTask = payload;
        },
        setDetailPost(state, payload) {
            state.detailPost = payload;
        },
        setError(state,payload){
            state.errors = payload;
        },
        addSource(state,payload){
            if(!state.sources.find(s=>s.id === payload.id))
            {
                state.sources.push(payload);
            }
        },
        setSourceCategories(state, payload){
            state.pages.categories = payload;
            state.sections.categories = payload;
        }

    },
    actions: {
        open: async ({commit,state,dispatch, rootGetters}, payload)=>{
            let category_slugs = ['product','collection','blog','article','home'];
            if (!state.ready) {
                commit('resetMeta');
            }
            if(payload.tab)
            {
                state.currentName = payload.tab;
            }
            let page = rootGetters['page/params']?.page;
            if(category_slugs.includes(page))
            {
                state[state.currentName].metadata.category_slug=page;
            }

            if(page === 'section' && state.type === 'quickstart') {
                state[state.currentName].metadata.category='section';
            }

            if (state.source != payload?.source) {
                if(state.type !== 'quickstart') {
                    commit('open', payload?.section ?? false);
                }
                dispatch('changeLibrary', payload?.source);
            } else {
                commit('open', payload.section);
                commit('source', payload?.source ?? 'ecom');
                if (!state.ready) {
                    dispatch('initAsync');
                }
            }
        },
        changeLibrary({commit, state, dispatch}, payload) {
            if (state.source != payload) {
                state.sections.items = null;
                state.pages.items = null;

                commit('source', payload);

                if(state.type !== 'quickstart') {
                    commit('resetMeta');
                }

                if(payload == 'ecom') {
                    commit('currentName', 'sections');
                } else if(payload == 'theme' && state.currentName != 'pages')  {
                    commit('currentName', 'pages');
                }
                dispatch('initAsync');
                dispatch('load');
            }
        },

        setPreview({ state},payload){
            state.preview = payload;
        },

        initAsync ({commit, state}){
            return new Promise((accept,reject)=>{
                this.$worker.fetch(
                    window.route('builder.library.init', {library: encodeURIComponent(state.source)}), null
                ).then(response=>{
                    if(response && response.data.status == 'success')
                    {
                        state.ready = true;
                        commit('categories',response.data.data.categories);
                        commit('favorites',response.data.data.favorites);
                        accept(true);
                    }
                    else
                    {
                        throw new Error(response.message ? response.message : 'Unknow error');
                    }
                })
                .catch(e=>{
                    reject(e.message);
                });
            });
        },
        setLoading({state},payload){
            let current = state.currentName;
            state[current].loading = payload;
        },

        load({ state, getters, commit,dispatch }, payload) {
            let current = getters['current'];

            if (!current.loading) {

                dispatch('setLoading',true)

                let routes = {
                    pages: 'builder.pages',
                    sections: 'builder.sections',
                    my_templates: 'builder.my_templates',
                    my_tasks: 'builder.my_tasks'
                };

                if (!routes[state.currentName]) {
                    dispatch('global/flash',{
                        type: 'error',
                        open: true,
                        message: 'Uknow error'
                    },{root: true});
                    return false;
                }

                this.$worker.fetch(
                    window.route( routes[state.currentName], { page :current.metadata.paged }),
                    {
                        metadata: current.metadata,
                        library: state.source
                    },'POST'
                )
                .then((res) => {
                    commit('loaded', res.data);
                    current.loading = false;
                    if (res.data?.status === 'success') {

                    }
                    else
                    {
                        throw new Error(res.data.message);
                    }
                    dispatch('setLoading',false)
                })
                .catch((e) => {
                        dispatch('setLoading',false)
                        dispatch('global/flash',{
                        type: 'error',
                        open: true,
                        message: e.message
                    },{root: true});
                    return false;
                });
            }
        },

        handleFavorite({ commit, getters, dispatch }, payload){
            let current = getters['current'];
            if (!current.loading) {
                // current.loading = true;
                commit(
                    'global/setProgress', { show: true, percent: 100 }, { root: true }
                );
                this.$worker.fetch(window.route('builder.favorite'), {
                    id: payload.value,
                    status: payload.status
                },'POST')
                .then((res) => {
                    // current.loading = false;
                    if (res.data.status === 'success') {
                        commit(
                            'global/setProgress', { show: true, percent: 100 }, { root: true }
                        );
                        // dispatch('initAsync')
                        // this.dispatch('page/init');
                        commit('favorites',res.data.favorites);
                        dispatch('global/flash',{
                            type: 'success',
                            message: payload.status === 'active' ? 'Added to favorites' : 'Removed from favorites',
                        },{root: true});
                        // dispatch('load');
                    }
                })
                .catch((e) => {
                        dispatch('global/flash',{
                        type: 'error',
                        message: e.message
                    },{root: true});
                    return false;
                });
            }
        },

        handleRemoveTemplate({ state, getters, dispatch }, payload){
            let current = getters['current'];
            if (!current.loading) {
                current.loading = true;
                let items = typeof payload.values === 'object' ? payload.values: [payload.values];
                const itv = setInterval(()=>{
                    if (items.length === 0) {
                        current.loading = false;
                        clearInterval(itv);
                        if (typeof payload.callback == 'function') {
                            payload.callback();
                        }
                        return false;
                    }
                    let item = items.pop();
                    this.$worker.fetch(window.route('builder.template.delete',item), null,'DELETE');
                }, 1000);

            }
        },

        createTemplate({ commit, getters, state, dispatch }, payload){
            let current = getters['current'];
            if (!current.loading) {
                current.loading = true;
                commit(
                    'global/setProgress', { show: true, percent: 10 }, { root: true }
                );
                this.$worker.fetch(window.route('builder.template.create'),payload.formData, 'POST')
                    .then((response) => {
                        if (response.data.status === 'success') {
                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                            current.loading = false;
                            state.success = response.data.message;
                            state.errors = null;

                            state.my_templates.items = null;
                            state.my_templates.metadata.paged = 1;

                            dispatch('global/flash',{
                                type: 'success',
                                message: 'Your template has been created!',
                                open: true
                            },{ root: true});
                            if (typeof payload.callback == 'function') {
                                payload.callback();
                            }
                        }else{
                            throw new Error(response.data.message ? response.data.message : 'Unknow error');
                        }
                    })
                    .catch((e) => {
                        current.loading = false;
                        state.errors = e.message;
                        state.success = null;
                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                        dispatch('global/flash',{
                            type: 'error',
                            message: e.message,
                            open: true
                        },{ root: true});

                    });
                }
        },

        closeLibrary({ commit }, e) {
            if (e) {
                // commit('resetMeta');
                commit('open', false);
            }
        },

        setCurrent({ commit }, payload) {
            commit('currentName', payload);
            commit('showSaveForm', false);
            commit('detailTask', false);
            if ( ['my_templates','theme_partner'].includes(payload)) {
                commit('showSaveForm', false);

            }
        },
        setMeta({ commit, dispatch }, payload) {
            if(!payload.paged) {
                payload.paged = 1
            }
            commit('metadata', payload);
            dispatch('load');
        },
        saveAsTemplate({ commit, dispatch }, payload) {
            commit('open', true);
            commit('currentName', 'my_templates');
            commit('showSaveForm', true);
            commit('detailTask', false);
            commit(
                'global/setProgress', { show: true, percent: 10 }, { root: true }
            );
            this.$worker.fetch(window.route('builder.template',payload))
                    .then((response) => {
                        if (response.data.status === 'success') {
                            commit(
                                'global/setProgress', { show: false, percent: 100 }, { root: true }
                            );
                            confirmExit(false);
                        }else{
                            throw new Error(response.data.message ? response.data.message : 'Unknow error');
                        }
                    })
                    .catch((e) => {
                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                        dispatch('global/flash',{
                            type: 'error',
                            message: e.message,
                        },{root: true});
                        return false;
                });

        },
        saveAsLibrary({commit, state}){
            commit('open', true);
            commit('currentName', 'my_templates');
            commit('showSaveForm', true);
            commit('detailTask', false);
            state.success = null;
            state.error = null;
        },
        saveAsTask({commit,state, dispatch}, payload){
            commit('open', true);
            commit('currentName', 'my_tasks');
            commit('showSaveForm', true);
            commit('detailTask', false);
            state.success = null;
            state.error = null;
            commit(
                'global/setProgress', { show: false, percent: 10 }, { root: true }
            );
             this.$worker.fetch(window.route('builder.tasks',payload))
                    .then((response) => {
                        if (response.data.status === 'success') {
                            commit('setDetailPost', response.data.task);
                            commit(
                                'global/setProgress', { show: false, percent: 100 }, { root: true }
                            );
                            confirmExit(false);
                        }else{
                            throw new Error(response.data.message ? response.data.message : 'Unknow error');
                        }
                    })
                    .catch((e) => {
                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                        dispatch('global/flash',{
                            type: 'error',
                            message: e.message,
                        },{root: true});
                        return false;
                });

        },
        showDetailTask({commit, getters, dispatch}, payload){
            commit('open', true);
            commit('detailTask', true);
            commit('showSaveForm', false);
            commit(
                'global/setProgress', { show: false, percent: 10 }, { root: true }
            );
            let current = getters['current'];
            if (!current.loading) {
            current.loading = true;
                this.$worker.fetch(window.route('builder.tasks',payload))
                    .then((response) => {
                        if (response.data.status === 'success') {
                            commit(
                                'global/setProgress', { show: false, percent: 100 }, { root: true }
                            );
                            commit('setDetailPost', response.data.task);
                            current.loading = false;
                        }else{
                            throw new Error(response.data.message ? response.data.message : 'Unknow error');
                        }
                    })
                    .catch((e) => {
                            commit(
                                'global/setProgress', { show: false, percent: 100 }, { root: true }
                            );
                        dispatch('global/flash',{
                            type: 'error',
                            message: e.message,
                            open: true,
                        },{root: true});
                        return false;
                });
            }

        },
        updateTeamplate({commit, state, getters, dispatch}, payload){
            let current = getters['current'];
            commit(
                'global/setProgress', { show: false, percent: 10 }, { root: true }
            );
            this.$worker.fetch(window.route('builder.template',payload.id),payload.formData, 'POST')
            .then((response) => {
                    if (response.data.code === 200) {
                        state.success = response.data.message;
                        dispatch('load');
                        confirmExit(false);

                    } else {
                        throw new Error(response.data.message ? response.data.message : 'Unknow error');
                    }
                })
                .catch((e) => {
                    current.loading = false;
                    commit(
                        'global/setProgress', { show: false, percent: 100 }, { root: true }
                    );
                    dispatch('global/flash',{
                        type: 'error',
                        message: e.message,
                        open: true
                    },{ root: true});
                });
        },
        updateTask({ commit, state, getters, dispatch }, payload) {
            let current = getters['current'];
            if (!current.loading) {
                current.loading = true;
                commit(
                    'global/setProgress', { show: false, percent: 10 }, { root: true }
                );
                this.$worker
                    .fetch(window.route('builder.tasks.update',payload.id)
                    ,payload.formData, 'POST')
                    .then((response) => {
                        if (response.data.code === 200) {
                            commit(
                                'global/setProgress', { show: false, percent: 100 }, { root: true }
                            );
                            commit('setDetailPost', response.data.task);
                            current.loading = false;
                            state.success = response.data.status;
                            if (typeof payload.callback == 'function') {
                                payload.callback();
                            }
                            confirmExit(false);
                        }else{
                            throw new Error(response.data.message ? response.data.message : 'Unknow error');
                        }
                    })
                    .catch((e) => {
                        current.loading = false;
                            commit(
                                'global/setProgress', { show: false, percent: 100 }, { root: true }
                            );
                        state.errors = e.response;
                        dispatch('global/flash',{
                            type: 'error',
                            message: e.message,
                            open: true
                        },{ root: true});
                    });
            }
        },
        updateStatusTask({ commit, getters, dispatch }, payload) {
            let current = getters['current'];
            if (!current.loading) {
                current.loading = true;
                commit(
                    'global/setProgress', { show: true, percent: 10 }, { root: true }
                );
                this.$worker
                    .fetch(window.route('builder.tasks.update',payload.id),{
                        status: payload.status,
                        data: payload.data
                    },'PUT')
                    .then((response) => {
                        if (response.data.code === 200) {
                            commit('setDetailPost', response.data.task);
                            current.loading = false;
                            commit(
                                'global/setProgress', { show: true, percent: 100 }, { root: true }
                            );
                        }else{
                            throw new Error(response.data.message ? response.data.message : 'Unknow error');
                        }
                    })
                    .catch((e) => {
                        current.loading = true;
                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                        dispatch('global/flash',{
                            type: 'error',
                            message: e.message,
                            open: true
                        },{ root: true});
                    });
            }
        },
        closeSaveForm({ commit }) {
            commit('showSaveForm', false);
        },
        closeDetailPost({commit}){
            commit('detailTask', false);
        },
        insertPost({state, commit,dispatch,getters, rootGetters}, payload) {
            commit(

                'global/setProgress', { show: true, percent: 100 }, { root: true }
            );

            commit('builder/setPopup', {
                show: true,
                loading: true,
                message: 'Loading template..',
                closeable: true
            },{ root: true });

            let target = payload?.target ?? getters['open'],
                endpoint = state.source === 'theme' ?
                    'builder.theme_post' :
                    'builder.post';

            commit('open', false);

            this.$worker.fetch(
                window.route(endpoint,payload.id)
            ).then( (response) => {
                if (response.data.status === 'success') {
                    if (response.data.post.options) {
                        //const regex = /\{([^\{]+)?\"font-family\"\:\{([^\}]+)\}([^\}]+)\}/gm; delete font-size...
                        //const regex = /\{([^\{]+)?\"font-family\"\:\{([^\}]+)\},/gm; // Delete only font-family
                        const regex = /(\,?)\s*?"\s*?font-family\s*?\"\:.+?\}(,?)/gm;

                        let tmpl =  response.data?.post?.options?.data;

                        let dataLayout;
                        if(typeof tmpl === 'string'){
                            dataLayout = tmpl.replace(/"tab":"(active|hover)"/gm,'"tab":"normal"')

                            if(payload?.params?.remove_typo)
                            {
                                dataLayout = dataLayout.replace(regex, function(...agrs) {
                                    return agrs[1] && agrs[2] ? agrs[2] : '';
                                });
                            }
                            try{
                                dataLayout = JSON.parse(dataLayout);
                            }
                            catch(e){
                                console.log('Error')
                                dataLayout = JSON.parse(tmpl);
                            }
                        }

                        let data = {
                            sections: dataLayout.sections,
                            index: target.index
                        };
                        commit('builder/setPopup',
                            {
                                show: false,
                                loading: true,
                                message: 'Processing...',
                                closeable: true
                            },{ root: true});

                        commit('open', false);

                        if (payload?.params?.overwrite) {
                            commit('page/setSections', {data: data?.sections } , { root: true });
                        } else {
                            commit('page/insertSections',  data, { root: true });
                        }

                        (data?.sections??[]).forEach((sec) => {
                            sec.stats = {
                                type: response.data.post.type,
                                id: response.data.post.id
                            };
                        });

                        if(dataLayout?.custom_code) {
                            let settings = rootGetters['page/settings'];
                            if(settings) {
                                settings.custom_code = dataLayout.custom_code;
                            } else {
                                settings = {
                                    custom_code: dataLayout.custom_code
                                };
                            }

                            commit('page/setSettings',settings, { root: true });
                        }

                        // if(dataLayout.global_css) {
                        //     if(dataLayout.global_css.themeSettings)
                        //     {
                        //         delete dataLayout.global_css.themeSettings;
                        //     }
                        //     dispatch('page/setDataGlobal',dataLayout.global_css,{root:true});
                        // }

                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                    }
                }else{
                    throw new Error(response.data.message ? response.data.message : 'Unknow error');
                }
            })
            .catch((e) => {
                commit('open', false);
                commit(
                    'global/setProgress', { show: false, percent: 100 }, { root: true }
                );
                commit('builder/setPopup',
                    {
                        show: false,
                        loading: true,
                        message: 'Processing...',
                        closeable: true
                    },{ root: true});

                commit('builder/setPopup',
                    {
                        show: true,
                        loading: false,
                        message: e.message,
                        closeable: true
                    },{ root: true});
                // dispatch('global/flash',{
                //     type: 'error',
                //     message: e.message,
                //     open: true
                // },{ root: true});

            });
        },

        insertTemplates({commit, dispatch, getters,rootGetters  }, payload) {

            commit(
                'global/setProgress', { show: true, percent: 10 }, { root: true }
            );

            commit('builder/setPopup',
                {
                    show: true,
                    loading: true,
                    message: 'Processing...',
                    closeable: true
                },{
                    root: true
                }
            );

            let target = payload?.target ?? getters['open'];

            commit('open', false);

            this.$worker.fetch(
                window.route('builder.template',payload.id)
            ).then((response) => {

                if (response.data.status === 'success') {

                    commit(
                        'global/setProgress', {
                            show: false, percent: 100
                        }, {
                            root: true
                        }
                    );

                    const regex = /\{([^\{]+)?\"font-family\"\:\{([^\}]+)\}([^\}]+)\}/gm;
                        let tmpl =  response.data?.data?.options?.data;
                        let dataLayout;

                        if(typeof tmpl === 'string'){
                            dataLayout = tmpl.replace(/"tab":"(active|hover)"/gm,'"tab":"normal"')
                            if(payload?.params?.remove_typo)
                            {
                                dataLayout = dataLayout.replace(regex,'{}');
                            }
                            try{
                                dataLayout = JSON.parse(dataLayout);
                            }
                            catch(e){
                                dataLayout = JSON.parse(tmpl);
                            }
                        }


                    commit('builder/setPopup', {
                            show: false,
                            loading: true,
                            message: 'Processing...',
                            closeable: true
                        },
                        {
                            root: true
                        }
                    );

                    if (payload?.params?.overwrite) {
                        commit('page/setSections', {
                            data: dataLayout.sections
                        }, {
                            root: true
                        });
                    } else {
                        commit('page/insertSections', {
                            sections: dataLayout.sections,
                            index: target.index
                        }, {
                            root: true
                        });
                    }

                    // if (dataLayout.global_css) {
                    //     if (dataLayout.global_css.themeSettings) {
                    //         delete dataLayout.global_css.themeSettings;
                    //     }
                    //     dispatch('page/setDataGlobal',dataLayout.global_css,{root:true});
                    // }

                    if(rootGetters['page/params']?.template_id)
                    {
                        commit('page/setSettings', {
                            title: response.data?.data?.title,
                            layout: 'theme',
                            custom_code: {}
                        },{root:true});
                    }
                    commit('open', false);

                } else {
                    throw new Error(response.data.message ? response.data.message : 'Unknow error');
                }
            })
            .catch((e) => {
                commit('open', false);
                commit(
                    'global/setProgress', {
                        show: false, percent: 100
                    }, {
                        root: true
                    }
                );
                commit('builder/setPopup',
                {
                    show: false,
                    loading: true,
                    message: 'Processing...',
                    closeable: true
                },{ root: true});
                dispatch('global/flash',{
                    type: 'error',
                    message: e.message,
                    open: true
                },{ root: true});
            });

        },
        handleEditTask({commit, dispatch,rootGetters}, payload) {
            commit(
                'global/setProgress', { show: true, percent: 10 }, { root: true }
            );
            commit('builder/setPopup',
            {
                show: true,
                loading: true,
                message: 'Processing...',
                closeable: true
            },{ root: true});
            commit('open', false);

            this.$worker.fetch(window.route('builder.tasks',payload))
                .then((response) => {
                    commit('open', false);
                    if (response.data.status === 'success') {
                        let params = rootGetters['page/params'];
                        dispatch('page/setQueryParams', {post_id: payload, page: params.page},{ root:true});
                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                        commit('builder/setPopup',
                            {
                                show: false,
                                loading: true,
                                message: 'Processing...',
                                closeable: true
                            },{ root: true});
                        if(response.data.task.options && response.data.task.options.data)
                        {
                            let dataLayout = JSON.parse(response.data.task.options.data);

                            dataLayout = typeof dataLayout === 'string' ? JSON.parse(dataLayout) : dataLayout; //Remove after 1-2 motn
                            commit('page/setSections', {data: dataLayout.sections}, { root: true });
                            if(dataLayout.global_css)
                            {
                                if(dataLayout.global_css.themeSettings)
                                {
                                    delete dataLayout.global_css.themeSettings;
                                }
                                dispatch('page/setDataGlobal',dataLayout.global_css,{root:true});
                            }
                            if(dataLayout.custom_code)
                            {
                                commit('page/setSettings', {
                                    custom_code: dataLayout.custom_code
                                }, {root: true});
                            }

                        }
                        else
                        {
                            commit('page/setSections', {data: []}, { root: true });
                        }

                    }
                    else {
                        throw new Error(response.data.message? response.data.message: 'Unknow error');
                    }
                })
                .catch((e) => {
                    commit('open', false);
                    commit(
                        'global/setProgress', { show: false, percent: 100 }, { root: true }
                    );
                    dispatch('global/flash',{
                        type: 'error',
                        message: e.message,
                        open: true
                    },{ root: true});

            });

        },
    }
};