export default ({ reverse, screens }) => {
    let result = [];
    for (let k in screens) {
        let screen = screens[k];
        if (reverse && k !== 'desktop') {
            result[0] = result[0] ?? [];
            result[0].push(
                {
                    name: 'flex-direction' + ((k !== 'desktop') ? ('__' + k) : ''),
                    label: 'Reverse (' + screen.title + ')',
                    type: 'popup',
                    value: 'row',
                    options: {
                        type: 'dropdown',
                        preview: 'title',
                        oneline: true,
                        values: {
                            row: 'Default',
                            'column-reverse': 'Vertical reverse',
                            'row-reverse': 'Horizontal reverse',
                            'column': 'Vertical',

                             
                             
                        },
                        warnings: 
                            k == 'tablet' ? {
                                content: '<strong>The reverser only works properly when each column is on a line.</strong>',
                            } : []
                    },
                    css: {
                        properties: {
                            'flex-direction': ''
                        },
                        selector: ' div.core__row--columns',
                    }
                }
            );
        }
        result[1] = result[1] ?? [];
        result[1].push({
            name: 'display' + ((k !== 'desktop') ? ('__' + k) : ''),
            label: 'Hide On ' + screen.title,
            type: 'toggle',
            value: 'block',
            options: {
                oneline: true,
                
                values: {
                    on: {
                        label: 'YES',
                        value: 'none'
                    },
                    off: {
                        label: 'NO',
                        value: 'block'
                    }
                },
            },
            css: {
                properties: {
                    display: ''
                }
            }
        });
    }
    return result.map((item, i) => {
        if (i < result.length - 1) {
            return [...item, { type: 'line' }];
        }
        return item;
    }).flat();
};