export const ecResizeImage = function(t: String, r: String): String {
    try {
        if (t.indexOf('cdn.ecomposer.app') === -1)
            return t;
        if (!r || "original" == r ||  "full" == r || "master" == r)
            return t;
        var o = t.match(/\.(jpg|jpeg|gif|png|bmp|bitmap|tiff|tif)((\#[0-9a-z\-]+)?(\?v=.*)?)?$/igm);
        if (null == o)
            return '';
        var i = t.split(o[0])
          , x = o[0];
        return i[0] + "_" + r + x;
    } catch (o) {
        return t
    }
}
function copy(obj, cs = false) {
    if (!obj || typeof obj !== 'object') {
        return obj;
    }
	// If it's not an array or an object, returns null
	if (typeof obj !== 'object' || obj === null) {
		return obj;
	}

	let cloned,
        i,
        ignores = ['id', 'firstinit', 'tab_active','auto_apply', 'hasElementRequestLiquid'];

    if (cs) {
        ignores.push('css');
    }

	// Handle: Date
	if (obj instanceof Date) {
		cloned = new Date(obj.getTime());
		return cloned;
	}

	// Handle: array
	if (obj instanceof Array) {
		let l;
		cloned = [];
		for (i = 0, l = obj.length; i < l; i++) {
			cloned[i] = copy(obj[i]);
		}

		return cloned;
	}

	// Handle: object
	cloned = {};
	for (i in obj) if (obj.hasOwnProperty(i)) {
        if(
            ignores.indexOf(i) === -1 &&
            (
                !cs ||
                (obj[i].constructor === Array && obj[i].length > 0) ||
                (obj[i].constructor === Object && Object.keys(obj[i]).length > 0)
            )
        ) {
            cloned[i] = copy(obj[i]);
        }
	}

	return cloned;
}

function randid() {
    return 'ecom-' + Math.random().toString(36).substr(2, 20);
}

function makeId(length: number): string {
    var result = [];
    var characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
        result.push(characters.charAt(Math.floor(Math.random() * charactersLength)));
    }
    return result.join('');
}

function mergeArrByIndex(arr1, arr2, index) {
    if ((index < arr1.length && index + arr2.length < arr1.length) || !arr2.length) {
        return;
    }

    if (index >= arr1.length) {
        arr1[index] = null;
    }

    arr1.splice(index, arr1.length, ...arr2);
}

function setValByKey(values, strKey, value, separator = '||', overwrite = true) {
    let keys = strKey.split(separator);
    if (typeof values === 'object' && keys.length) {
        let parentValue = keys.slice(0, -1).reduce((o, n) => (o[n] = o[n] ?? {}), values);
        let lastKey = keys.slice(-1);
        if (
            parentValue[lastKey] === undefined ||
            (parentValue[lastKey] !== undefined && overwrite)
        ) {
            parentValue[lastKey] = value;
        }
    }
    return values;
}

function addObjToFirstArr(arr, obj) {
    let arrClone = JSON.parse(JSON.stringify(arr));
    let objClone = JSON.parse(JSON.stringify(obj));
    arrClone.splice(0, 0, objClone);
    return arrClone;
}

function resizeImage(t, payload) {
    try {
        if (
                !payload ||
                typeof payload !== 'object'
            ) {
            return t;
        }
        if(
            t.indexOf('cdn.shopify.com') !== -1 ||
            t.indexOf('burst.shopifycdn.com') !== -1 ||
            t.indexOf('/cdn/shop/') !== -1
        )
        var url = new URL(t);


        for(let k in payload) {
            if(payload[k] !== undefined && payload[k] !== '') {
                url.searchParams.set(k, payload[k]);
            }
        }
        if((payload.width !== undefined || payload.height !== undefined) &&
            !payload.crop) {
                url.searchParams.set('crop', 'center');
        }
        if(payload.width == undefined && payload.height === undefined && payload.crop) {
            url.searchParams.delete('crop');
        }

        return url.toString();
    } catch (o) {
        return t;
    }

    return t;
}

function delByKey(strKs, o, s = '||', cb) {
    let ks = strKs.split(s);
    let lastK = ks.slice(-1)[0];
    let op = ks.slice(0, -1).reduce((o, n) => o?.[n] ?? null, o);
    if (op) {
        if (op[lastK] && cb) {
            cb(op[lastK]);
        }
        delete op[lastK];
    }
}

function valueByKey(strKey, values, separator = '.') {
    let keys = strKey.split(separator);
    return keys.reduce((o, n) => o?.[n] ?? null, values);
}

function scrollTo(to, bh) {
    let idoc = window.EComposerBuilder.contentDocument,
        html = idoc.querySelector('html'),
        body = idoc.body;

    html.scrollTo({ top: to, behavior: bh !== false ? 'smooth' : '' });
    body.scrollTo({ top: to, behavior: bh !== false ? 'smooth' : '' });
}

const file = {
    name: (url) => {
        if (url) {
            var m = url.toString().match(/.*\/(.+?)\./);
            if (m && m.length > 1) {
                return m[1];
            }
        }
        return '';
    },
    ext: (url) => {
        return url?.split(/[#?]/)[0].split('.').pop().trim() ?? '';
    }
};

export function getAllowNames(fields, values ) {

    if(fields?.constructor !== Array) {
        return;
    }

    let allowNames = [];
    let addName = (s) => {
        if(s) {
            if (allowNames.indexOf(s) === -1) {
                allowNames.push(s);
            }
        }
    };

    fields.forEach(f => {
        if(f?.name) {
            addName(f.name);
        }
        else if(f?.group_name) {
            addName(f?.group_name);
        }
        else if(f?.params?.constructor === Array) {
            f?.params.forEach(p => {
                if(p?.name) {
                    addName(p.name);
                }
            });
        }
    });
    let regScreen =/__([a-zA-Z0-9]+)$/g;
    let ignoreNames = ['product', 'collection', 'blog', 'article'];
    for(let k in values) {
        k = k.replace(regScreen, '');
        if(allowNames.indexOf(k) === -1 && ignoreNames.indexOf(k) === -1) {
            delete values[k];
        }
    }
}



function formatFields(params: (GroupFieldType | AliasFieldType | FieldType)[]) {
    if(!params) {
        return;
    }
    const convert = (params: (GroupFieldType | AliasFieldType | FieldType)[]) => {
        params.forEach((param: any, index: number) => {
            let newField;
            if((param.group_alias ||
                param.alias) &&
                (newField = aliases(param)))
            {
                if(newField?.constructor === Array) {
                    let newFieldAsField = newField as FieldType[];
                    params.splice(index, 1, ...newFieldAsField);
                }
                else {
                    params.splice(index, 1, <FieldType | GroupFieldType>newField);
                }
                convert(params);
            }
            else if(param?.params && param?.type !== 'group') {
                convert(param.params);
            }

        });
    }
    convert(params);
    //params = formatGroupType(params);

    return params;
}


function setDefaultSettings(obj) {

    if (
        obj.data && (
            obj.exporting === false ||
            obj.exporting === undefined
        )
    ) {

        // Hold check allows name in settings, style, advance
        //getAllowNamesEl(obj, data);

        let fromFactory = false;

        if (obj.data.id === undefined) {
            obj.data.id = randid();
            fromFactory = true;
        }

        if (obj.data.IDInit === true) {
            fromFactory = true;
            delete obj.data.IDInit;
        }

        let getParams = (obj, type) => {
            if (obj?.layout?.[type]) {
                return (typeof obj.layout[type] == 'function' ? obj.layout[type]() : obj.layout[type]);
            } else {
                return (typeof obj[type] == 'function' ? obj[type]() : obj[type]);
            }
        };

        ['settings', 'style', 'advanced','template','extension_id','preset'].forEach((sch) => {

            if (!obj.data?.firstinit && (sch == 'settings' || sch == 'style')) {
                obj.data[sch] = defaultValues(
                    sch == 'advanced' ? Selectors.advanced.default(obj) : getParams(obj, sch),
                    obj.data[sch]
                );
            }

            if (fromFactory) {
                let obj_type = obj?.default?.[sch]?.constructor;
                if (obj_type === Object) {
                    Object.keys(obj.default[sch]).forEach((k) => {
                        if (obj.data[sch][k] === undefined) {
                            obj.data[sch][k] = obj.default[sch][k];
                        } else if (typeof obj.data[sch][k] == 'object' && typeof obj.default[sch][k] == 'object') {
                            obj.data[sch][k] = Object.assign(obj.default[sch][k], obj.data[sch][k]);
                        }
                    });
                }
                else if ( typeof obj?.default?.[sch] === 'string')
                {
                    obj.data[sch] =  obj?.default?.[sch];
                }
            }

        });

        obj.data.firstinit = true;

    }

}
function generateElementId(sections){
    function isRow(row){
        if(row.columns)
        {
            row.columns =  isColumns(row.columns)
        }
        return row;
    }
    function isElement(elm){
        elm.id = randid();
        return elm;
    }
    function isGroup(group){
        if(group && group.settings && group.settings.items)
        {
            group.settings.items = isColumns(group.settings.items);
        }
        return group;
    }
    function isBlocks(blocks){
        if(blocks && blocks.length)
        {
            blocks = blocks.map(function(block){
                if(block.row)
                {
                    block.row = isRow(block.row);
                }
                else if(block.group)
                {
                    block.group = isGroup(block.group);
                }
                else
                {
                    return isElement(block);
                }
                return block;
            });

        }
        return blocks;

    }

    function isColumns(columns){
        if(columns && columns.length)
        {
            columns = columns.map(function(column){
                if(column.blocks)
                {
                    column.blocks = isBlocks(column.blocks);
                }
                else if(column.row)
                {
                    column.row = isRow(column.row);
                }
                return column;
            });
        }
        return columns;
    }

    if(sections &&  sections.constructor ===  Object)
    {
        if(sections.columns)
        {
            sections.columns = isColumns(sections.columns);
        }
        else if( sections.row)
        {
            sections.row = isRow(sections.row);
        }
    }
    else if(sections && sections.constructor === Array)
    {
        sections = sections.map(function(section){
            if(section.id)
            {
                section.id = randid();
            }
            if(section.row && !section.columns&& section.row.columns)
            {
                //Force row to columns
                section.columns = section.row.columns;
                delete section.row;
            }
            if(section.columns)
            {
                section.columns = isColumns(section.columns);
            }
            else if(section.row)
            {
                section.row = isRow(section.row);
            }
            return section;

        })
    }

    return sections;

}

function mixS (s1, s2, rootS) {
    let rs = [];
    s1 = s1 || '';
    s2 = s2 || '';
    s1.split(',').forEach(s1 => {
        s2.split(',').forEach(s2 => {
            if(s2.indexOf('root') > -1) {
                s2 = s2.replace(/root/g, rootS);
                rs.push(s2);
            }
            else {
                rs.push(s1 + s2);
            }
        });
    });

    return rs.join(',');
}

function isGroup(f) {
    return !!(f.type != 'group' && f.params);
}

function defaultValues(params, current) {

    if (current === undefined) {
        current = {};
    }

    let values = {};
    let fieldRelation = [];

    if (params?.constructor !== Array) {
        return values;
    }

    const setValue = (group_name, field) => {
        if(
            field?.value !== '' &&
            field?.value !== null &&
            field?.value !== undefined &&
            field?.name
        ) {
                if(field?.options?.visible) {
                    fieldRelation.push({
                        group_name, field
                    });
                }

                let v;
                if (group_name) {
                    values[group_name] = values[group_name] ?? {};
                    v = values[group_name];
                } else {
                    v = values;
                }

                v[field.name] = field.value;

            }
    };

    params.forEach(ps => {
        if(ps?.type !== 'group' && ps?.params) {
            ps.params.forEach(p => {
                setValue(ps.group_name, p);
            });
        } else if(ps.name && ps.type) {
            setValue('', ps);
        }
    });


    fieldRelation.forEach( g => {
        let { field, group_name } = g;
        let { visible } = field.options;
        let condition = visible.constructor === Object ?
                            visible.condition : visible;
        let gValues = group_name ? values[group_name]?.data : values;
        if(!condition(gValues, values) &&  typeof  gValues === 'object' && gValues.hasOwnProperty(field.name)) {
            delete gValues[field.name];
        }
    });

    let newValues = {};

    Object.keys({...values, ...current}).forEach(key => {
        if (current[key] === undefined) {
            newValues[key] = values[key];
        } else {
            newValues[key] = current[key];
        }
    });

    return newValues;

}

function renImageResponsive(item, ss, lazy = true, css = null) {

    let ssAsc = Object.entries(ss).sort(([, b1], [, b2]) => b1.width - b2.width);
    if(!item.alt && item.name) {
        item.alt = item.name;
    }
    let html = '', style = '';
    let attrs = [];
    for (let i in ssAsc) {
        for (let k in item) {
            if (k.startsWith('value_')) {
                let s = /__(\w+)/.exec(k)?.[1];
                if (s) {
                    if (s === ssAsc[i][1].name) {
                        html += `<source media="(max-width: ${ssAsc[i][1].width}px)" srcset="${item[k]}">`;
                    }
                }
            } else if (k !== 'value') {
                let attr = k + '="' + item[k] + '"';
                if (attrs.indexOf(attr) === -1) {
                    attrs.push(attr);
                }
            }
        }
    }

    if(css) {
        style = ' style="';
        style += Object.keys(css).map(k => `${k}:${css[k]?.replace('-', ' ')};`).join('');
        style += '"';
    }

    html += `<img ${lazy ? `loading="lazy"` : ``} src="${item.value || '/images/placeholder.png'}" ${attrs.join(' ')}${style}>`;
    return html;
}

function isPreviewImage(str: string) {
    if(!str ||  str.indexOf('ecom-no-replace') > -1) {
        return false;
    }
    return str.match(/https?:\/\/(.*)\.(jpe?g|png|webp|bmp|tiff)(\?|.*&)ecom-need-replace([^"' ]*)/g);
}

export function varGlobal (params) {
    let css = {}, strCss = '';
    for (let type in params ?? {}) {
        if (type === 'colors' || type === 'typography') {
            let data = params[type];
            css[':root'] = css[':root'] ?? '';
            let key = '--ecom-global-' + type;
            let item;
            for (item in data) {
                let newK = key + '-' + item;
                let value = data[item].value;
                if (value && typeof value === 'string') {
                    css[':root'] += newK + ':' + value + ';';
                } else if (typeof value === 'object') {
                    let k;
                    for (k in value) {
                        if(k === 'value') continue;
                        if (value[k]) {
                            let v = k === 'font-family' ? value[k].name : value[k];
                            css[':root'] += newK + '-' + k + ':' + v + ';';
                        }
                    }
                }
            }
        }
    }
    for (let k in css) {
        if (css[k] && css[k].length > 0) {
            strCss += k + '{' + css[k] + '}';
        }
    }
    return strCss;
}

export function parseSectionCss(css) {
    let _css = '';
    let _keyframes = [],
        _fonts = [];

    if (typeof css === 'string') {
        _css += css;
    } else if (css.constructor === Array) {
        css.flat().forEach((c) => {
            let k = c.keyframes,
                f = c.fonts;
            if (k && k.length) {
                _keyframes.push(...k);
            }
            if (f && f.length) {
                _fonts.push(...f);
            }
            _css += c.style ?? '';
        });
    } else if (css.constructor === Object) {
        let k = css.keyframes,
            f = css.fonts;
        if (k && k.length) {
            _keyframes.push(...k);
        }
        if (f && f.length) {
            _fonts.push(...f);
        }
        _css += css.style ?? '';
    }

    _keyframes = [...new Set(_keyframes)];
    _fonts = [...new Set(_fonts)];
    _css = _fonts.join('') + _css;
    _css = _keyframes.join('') + _css;

    return _css.trim();
}
function getGlobalInCss(css) {
    if(!css) {
        return;
    }
    let rs = {};
    if(css && typeof css === 'object') {
        css = JSON.stringify(css);
    }
    let r = /global-(colors|typography)-(\w+)/gm,
        ms = [...css.matchAll(r)];
    ms.forEach(m => {
        let [, t, v] = m;
        rs[t] = rs[t]?.constructor === Array ? rs[t] : [];
        if(rs[t].indexOf(v) === -1)
        {
            rs[t].push(v);
        }
    });
    return rs;
}
function renderCss(id, css) {
    if (!css) {
        return;
    }

    let idoc = window.EComposerBuilder?.contentDocument;
    const addStyleToIframe = (css) => {
        css = parseSectionCss(css) ?? '';
        css  = moveTopMediaCss(css);
        return new Promise(function(resolve){
            let styleExist = idoc.querySelector('#' + id + '.ecom-style-loading');
            if (styleExist) {
                styleExist.innerHTML = css;
                resolve(styleExist);
            } else {
                let styleel = idoc.createElement('style');
                styleel.setAttribute('type', 'text/css');
                styleel.setAttribute('class', 'ecom-style-loading');
                styleel.setAttribute('id', id);
                styleel.onload = function(){
                    resolve(styleel);
                };
                styleel.onerror = function(){
                    resolve();
                };
                styleel.innerHTML = css;

                idoc.querySelector('head').appendChild(styleel);
            }

        });
    };

    const getPreviousSiblings = (elem) => {
        if(elem) {
            let id = elem.id;
            let parentElem = elem.parentNode;
            if(parentElem) {
                return [...parentElem.querySelectorAll('[id="'+id+'"]')].filter(el => el !== elem);
            }
        }
        return [];
    };

    new Promise((resolve) => {
        if(idoc)
        {
            resolve(idoc)
        }
        else
        {
            let myitv =  setInterval(() => {
                idoc = window.EComposerBuilder?.contentDocument;
                if(idoc)
                {
                    clearInterval(myitv);
                    resolve(idoc);
                }
            }, 500);
        }
    })

    .then(()=>{
        addStyleToIframe(css);
    })
    .then((el) => {
            dispatchResize();
            el.removeAttribute('class');
            getPreviousSiblings(el).forEach(sibling => {
               setTimeout(function(){
                sibling.remove();
               },500);
            });
    })
    .catch(e=>{
        // console.log(e);
    })
}
function getLinkFont(name, url) {
    if (!name) {
        return '';
    }
    if (url.indexOf('cdn.shopify.com') > -1) {
        let formats = {
            eot: 'embedded-opentype',
            woff2: 'woff2',
            woff: 'woff',
            ttf: 'truetype',
            svg: 'svg',
            otf: 'opentype'
        };
        let ext = file.ext(url);
        let format = formats[ext] ? formats[ext] : 'ttf';

        return encodeURI(
            `data:text/css;charset=utf-8,@font-face {font-family: '${name}';src: url('${url}') format('${format}');}`
        );
    } else if (url.startsWith('blob')) {
        return '';
    } else {
        return (
            'https://fonts.googleapis.com/css?family=' +
            name.replace(/\s/, '+') +
            ':100,200,300,400,500,600,700,800,900'
        );
    }
}
function validObject(obj, keys) {
    let rs = {};
    if (obj) {
        keys.forEach((k) => {
            if (k in obj) {
                rs[k] = obj[k];
            }
        });
    }
    return rs;
}

function str2key(str) {
    let key = '',
        len = 50;

    if (str.replace(/[^\w\d_]*/g, '').length <= len) {
        return str.replace(/[^\w\d_]*/g, '');
    }

    str.replace(/^([\w\d])|\s([\w\d])/g, (...m) => {
        key += m[1] ? m[1] : m[2];
    });

    return key.substr(0, len);
}

function hex2rgb(hex) {
    hex = /^#/.test(hex) ? hex.slice(1) : hex;

    if (hex.length !== 3 && hex.length !== 6) {
        return [0, 0, 0];
    }

    var digit = hex.split('');

    if (digit.length === 3) {
        digit = [digit[0], digit[0], digit[1], digit[1], digit[2], digit[2]];
    }
    var r = parseInt([digit[0], digit[1]].join(''), 16);
    var g = parseInt([digit[2], digit[3]].join(''), 16);
    var b = parseInt([digit[4], digit[5]].join(''), 16);

    return [r, g, b];
}

let dispatchResizeTimmer = 0;
function dispatchResize() {
    clearTimeout(dispatchResizeTimmer);
    dispatchResizeTimmer = setTimeout(() => {
        if (typeof Event === 'function') {
            window['EComposerBuilder'].contentWindow.dispatchEvent(new window['EComposerBuilder'].contentWindow.Event('resize'));
        }
    }, 500);
}

export function moveTopMediaCss(css) {
    let meds = [];
    let imports = [];
    css = css.replace(/@import url\(([^\\)]+?)\);?|@font-face {([^}]+?)}/g, (...m) => {
        if(meds.indexOf(m[0]) === -1 && imports.indexOf(m[0] === -1)  ){
            if(m[0].includes('font-face'))
            {
                meds.push(m[0]);
            }
            else
            {
                imports.push(m[0]);
            }
        }
        return '';
    });
    css = css.replaceAll( '/**ECOM-INSERT-CSS**/', '');
    return imports.join('') + meds.join('')  + '/**ECOM-INSERT-CSS**/' + css;
}

function setDataObject(obj, payload) {
    if(obj && typeof payload === 'object') {
        for(let k in payload) {
            if(payload[k] !== undefined && payload[k] !== null) {
                obj[k] = payload[k];
            }
        }
    }
}

function shortcuts(e, shortcuts, isidoc) {
    Object.values(shortcuts).forEach((fn) => {
        fn(e, isidoc);
    });
}

function canDrop(drop, to) {

    if (!drop || !to || drop === to) {
        return false;
    }

    let tois = to?.is ?? '',
        dropis = drop?.is ?? '',
        todeep = to?.deep ?? 0;//,
        //dropdeep = drop?.deep ?? 0;

    return (
        (
            tois == 'section' &&
                dropis == 'section'
        )
        ||
        (
            tois == 'row' &&
                ['block', 'blocks', 'row', 'group'].indexOf(dropis) > -1
        )
        ||
        (
            tois == 'blocks' &&
                ['block', 'row', 'group'].indexOf(dropis) > -1 &&
                (dropis != 'row' || todeep < 4)
        )
        ||
        (
            tois == 'column' &&
                dropis == 'column'
        )
        ||
        (
            tois == 'block' && (
                dropis == 'block' ||
                (dropis == 'row' && todeep < 4)
            )
        )
        ||
        (
            tois == 'group' && (
                dropis == 'block' ||
                (
                    ['group', 'row'].indexOf(dropis) > -1 &&
                    todeep == 1
                )
            )
        )
    );
}

function getPathImage(obj, path = [],images) {
    for(let k in obj) {
        let srcs;
        if(typeof obj[k] === 'string' && (srcs = isPreviewImage(obj[k]))) {
            srcs.forEach(src => {
                images.push({
                    src: src, path: [...path, k]
                });
            });
        }
        else if(obj[k] && typeof obj[k] === 'object' && isPreviewImage(JSON.stringify(obj[k]))) {
            getPathImage(obj[k], [...path, k], images);
        }
    }
}

function intBetween(num, min, max) {
    return Math.max(min, Math.min(max, num));
}

const convertImgPlaceholder = async (sections, noBlob = true) => {
    let imgPlaceholder;
    function drawImagePlaceholder(ctx, img, w, h) {
        let { width: wP , height: hP } = imgPlaceholder;
        let wD, hD;
        if(w >= h) {
            wD = intBetween(wP > w ? w : wP, 400, 2000);
            hD = wP / hP * wD;
        }
        else {
            hD = intBetween(hP > h ? hP : h, 400, 2000);
            wD = wP / hP * hD;
        }
        ctx.globalCompositeOperation = 'destination-over';
        ctx.drawImage(imgPlaceholder, 0, 0, wD, hD );
        ctx.drawImage(img, 0,0 );
    }
    function drawText(ctx, w, h) {
        let textSize = `${w} x ${h}`;
        let fsSize = intBetween(w * 0.06, 12, 100);
        let fontSize = '700 ' + fsSize + 'px Arial';
        ctx.font = fontSize;
        let measureSize = ctx.measureText(textSize);
        let wTextSize = measureSize.width;
        ctx.fillStyle = '#8787879e';
        ctx.fillRect(w / 2 - wTextSize / 2 - wTextSize * 0.1 , h / 2 - fsSize / 2 - fsSize * 0.3, wTextSize + wTextSize * 0.2, fsSize * 0.8 + fsSize * 0.6);
        ctx.textBaseline = 'middle';
        ctx.textAlign = 'center';
        ctx.fillStyle = 'white';
        ctx.fillText(textSize , w / 2, h / 2);

    }
    function urlImageToBlob(src, quality = 0.25) {
        return new Promise((resolve) => {
            let img = new Image(),
                ext = file.ext(src) || 'jpeg';
                ext = ext === 'png' ? ext : 'jpeg';
            img.setAttribute('crossOrigin', 'anonymous');
            img.onload = function() {
                let { width: w, height: h} = this;
                if(noBlob) {
                    resolve({src, placeholder: `https://placeholder.ecomposer.app/faker.png?width=${w}&height=${h}`});
                    return;
                }
                let canvas = document.createElement('canvas'),
                ctx = canvas.getContext('2d');
                canvas.width = w;
                canvas.height = h;
                drawText(ctx, w, h);
                drawImagePlaceholder(ctx, img, w, h);
                canvas.toBlob(function(blob) {
                    let url = URL.createObjectURL(blob) + `#width=${w}&height=${h}`;

                    canvas.remove();
                    resolve({src, placeholder: url});
                }, 'image/' + ext, quality);
            };
            img.onerror = function(e) {
                resolve({src, placeholder: 'https://placeholder.ecomposer.app/faker.png?width=100&height=100'});
            };
            img.src = src;
        });


    }
    function imagePlaceholder() {
        return new Promise((resolve, reject) => {
            let img = new Image();
            img.crossOrigin= 'anonymous';
            img.onload = () => resolve(img);
            img.onerror = reject;
            img.src = '/images/placehoder_image.png';
        });
    }
    if(typeof sections !== 'object') {
        return sections;
    }


    try {
        let images = [];
        getPathImage(sections, [], images);
        let uniImages = images.filter((value, index, self) => {
                return self.findIndex(s => s.src === value.src) === index;
            });
        //eslint-disable-next-line

        if(uniImages.length) {
            if(!noBlob) {
            imgPlaceholder = await imagePlaceholder();
                if(!imgPlaceholder) {
                    throw new Error('');
                }
            }
            let result = await Promise
                .all(uniImages.map(c => urlImageToBlob(c.src)));
            result.forEach(r => {
                let curImages = images.filter(image => image.src === r.src);
                curImages.forEach(image=> {
                    //let {path} = image;
                    image.placeholder = r.placeholder;
                    // let f = new Function('sections', 'src' , 'sections' + path.map(p => '["'+p+'"]').join('') + '=src');
                    // f(sections, r.placeholder);
                });
            });
        }
        return { sections, images };
    }catch(e) {
        return { sections, images: [] };
    }

};

const escapeHtml = (unsafe) =>
{
    return unsafe
         .replace(/&/g, '&amp;')
         .replace(/</g, '&lt;')
         .replace(/>/g, '&gt;')
         .replace(/"/g, '&quot;')
         .replace(/'/g, '&#039;');
};
const replacePlaceholder = (text) => {
    let isObj = false;
    isObj = typeof text === 'object';
    if(isObj) {
        text = JSON.stringify(text);
    }
    // eslint-disable-next-line no-useless-escape
    let r = /blob:([\w:\/\.-]+)#width=([\d]+)(&amp;|&)height=([\d]+)/gm;
    let ms = text.matchAll(r);
    if(ms) {
        [...ms].forEach(m => {
            text = text.replace(m[0], 'https://placeholder.ecomposer.app/faker.png?width='+m[2]+'&height='+m[4]);
        });
    }
    if(isObj) {
        text = JSON.parse(text);
    }
    return text;
};

export function removeEmptyKeyObj(obj) {
    let r = /(,?)\s*"([\d\w]+?)"\s*:\s*(\[\s*\]|{\s*}|"")\s*(,?)/g;
    if(obj && typeof obj === 'object') {
        let str = JSON.stringify(obj);
        for(let i = 0 ; i < 10; i++) {
            if(r.exec(str)) {
                str = str.replace(r, (...m) => {
                    if (m[1] && m[4]) {
                        return ',';
                    }
                    return '';
                }).replace(/,\s*([\]}])/g, '$1');
            }
            else {
                break;
            }
        }
        return JSON.parse(str);
    }
    return obj;
}

export function removeCssInData(sections) {
    if(sections?.constructor !== Array) {
        return;
    }
    const allowKeys = ['columns', 'blocks', 'group','row'];
    const run = (obj) => {
        if(!obj || typeof obj !== 'object') {
            return;
        }
        if(obj['css']) {
            delete obj['css'];
        }
        if (obj['firstinit']) {
            delete obj['firstinit'];
        }
        let k;
        for(k in obj) {
            if((allowKeys.indexOf(k) > -1 && typeof obj[k] === 'object') || obj.constructor === Array) {
                if(k === 'group') {
                    if(obj[k]['css']) {
                        delete obj[k]['css'];
                    }
                    if (obj[k]['firstinit']) {
                        delete obj[k]['firstinit'];
                    }
                    run(obj[k].settings.items);
                }
                else
                {
                    run(obj[k]);
                }
            }
        }
        return obj;
    };
    run(sections);
    return sections;
}

export function confirmExit(ask) {
    if (isdev) {
        return; // Not need in development
    }
    if (ask) {
        window.onbeforeunload = function() {
            return 'Leaving this page will reset the wizard';
        };
    } else {
        window.onbeforeunload = null;
    }
}

function breakpointsSwiper(settings) {

    const screensStore = store.getters['builder/screens'];
    let screens = (() => {
        if (!screensStore) {
            return [];
        }
        return Object.entries(screensStore).sort(([, b1], [, b2]) => b1.width - b2.width);
    })();

    const screenMinW = () => {
        let rs = [];
        for (let i in screens) {
            let screen = copy(screens[i]);
            screen[1].width = i === '0' ? 0 : parseInt(screens[i - 1][1].width) + 1;
            rs.push(screen);
        }
        return rs;
    }
    let bp = {},
        ops = [
            {
                key: 'slider_items',
                name: 'slidesPerView'
            },
            {
                key: 'slider_group',
                name: 'slidesPerGroup'
            },
            {
                key: 'slider_spacing',
                name: 'spaceBetween'
            }
        ];
    screenMinW().forEach(([, v]) => {
        ops.forEach((op) => {
            let c = Object.keys(settings).filter((k) => k.startsWith(op.key));
            if (c.length === 1 && settings[op.key]) {
                bp[op.name] = settings[c[0]];
            } else {
                bp['breakpoints'] = bp['breakpoints'] ?? {};
                bp['breakpoints'][v.width] = bp['breakpoints'][v.width] ?? {};
                let n = op.key + (v.name === 'desktop' ? '' : '__' + v.name);
                if (n in settings) {
                    bp['breakpoints'][v.width][op.name] = settings[n];
                }
            }
        });
    });
    return bp;

}

function optionSwiper(settings) {
    if(!settings) {
        return '';
    }
    let breakpoints = breakpointsSwiper(settings),
        autoplay = {
            enabled: settings.slider_autoplay ?? false,
            delay: settings.slider_autoplay_speed ?? 0,
            pauseOnMouseEnter: settings.slider_pause ?? false,
            disableOnInteraction: false
        },
        slider_effect = settings.slider_effect,
        creative = {},
        direction = settings.slider_style ?? 'horizontal';
    switch (settings.slider_effect){
        case 'creative':
            creative = Object.assign({},);
            direction === 'horizontal'
                ? creative = {
                    prev: {
                        shadow: true,
                        translate: [0, 0, -400],
                    },
                    next: {
                        translate: ["100%", 0, 0],
                    }
                }
                : creative = {
                    prev: {
                        shadow: true,
                        translate: [0, 0, -400],
                    },
                    next: {
                        translate: [0, "100%", 0],
                    }
                }
            break;
        case 'creative2':
            slider_effect = 'creative';
            direction === 'horizontal'
                ? creative = {
                    prev: {
                        shadow: true,
                        translate: ["-120%", 0, -500],
                    },
                    next: {
                        shadow: true,
                        translate: ["120%", 0, -500],
                    }
                }
                : creative = {
                    prev: {
                        shadow: true,
                        translate: [0, "-120%", -500],
                    },
                    next: {
                        shadow: true,
                        translate: [0, "120%", -500],
                    },
                }
            break;
        case 'creative3':
            slider_effect = 'creative';
            direction === 'horizontal'
                ? creative = {
                    prev: {
                        shadow: true,
                        translate: ["-20%", 0, -1],
                    },
                    next: {
                        translate: ["100%", 0, 0],
                    }
                }
                : creative = {
                    prev: {
                        shadow: true,
                        translate: [0, "-20%", -1],
                    },
                    next: {
                        translate: [0, "100%", 0],
                    }
                }
            break;
        case 'creative4':
            slider_effect = 'creative';
            direction === 'horizontal'
                ? creative = {
                    prev: {
                        shadow: true,
                        translate: [0, 0, -800],
                        rotate: [180, 0, 0],
                    },
                    next: {
                        shadow: true,
                        translate: [0, 0, -800],
                        rotate: [-180, 0, 0],
                    }
                }
                : creative = {
                    prev: {
                      shadow: true,
                      translate: [0, 0, -800],
                      rotate: [0, 180, 0],
                    },
                    next: {
                      shadow: true,
                      translate: [0, 0, -800],
                      rotate: [0, -180, 0],
                    }
                }
            break;
        case 'creative5':
            slider_effect = 'creative';
            direction === 'horizontal'
                ? creative = {
                    prev: {
                        shadow: true,
                        translate: ["-125%", 0, -800],
                        rotate: [0, 0, -90],
                    },
                    next: {
                        shadow: true,
                        translate: ["125%", 0, -800],
                        rotate: [0, 0, 90],
                    }
                }
                : creative = {
                    prev: {
                        shadow: true,
                        translate: [0, "-125%", -800],
                        rotate: [0, 0, -90],
                    },
                    next: {
                        shadow: true,
                        translate: [0, "125%", -800],
                        rotate: [0, 0, 90],
                    }
                }
            break;
        case 'creative6':
            slider_effect = 'creative';
            direction === 'horizontal'
                ? creative = {
                    prev: {
                        shadow: true,
                        origin: "left center",
                        translate: ["-5%", 0, -200],
                        rotate: [0, 100, 0],
                    },
                    next: {
                        origin: "right center",
                        translate: ["5%", 0, -200],
                        rotate: [0, -100, 0],
                    }
                }
                : creative = {
                    prev: {
                        shadow: true,
                        origin: "bottom center",
                        translate: [0, "-5%", -200],
                        rotate: [100, 0, 0],
                    },
                    next: {
                        origin: "top center",
                        translate: [0, "5%", -200],
                        rotate: [-100, 0, 0],
                    }
                }
            break;
    }

    let data = {
        direction: direction,
        loop: settings.slider_loop,
        breakpoints: breakpoints,
        autoplay,
        speed: settings.slider_speed,
        effect: slider_effect,
        grabCursor: true,
        slideActiveClass: 'ecom-box-active',
        centeredSlides: settings.slider_center || false,
        watchOverflow: true,
        autoHeight: true,
        cubeEffect: {
            slideShadows: true,
            shadowOffset: 20
        },
        creativeEffect: creative,
        fadeEffect: {
            crossFade: true
        },
        pagination: {
            el: '.ecom-swiper-pagination',
            type: 'bullets',
            clickable: true
        },
        navigation: {
            nextEl: '.ecom-swiper-button-next',
            prevEl: '.ecom-swiper-button-prev'
        },
    };

    if((settings?.items?.length < settings?.slider_items * 2) || (settings?.items?.length < settings?.slider_group * settings?.slider_items * 2) || settings?.slider_autoplay) {
        data.loop = false;
    }

    for (let k in breakpoints) {
        data[k] = breakpoints[k];
    }
    return JSON.stringify(data);
}

export const componentName = function(key) {
    if (!key || typeof key !== 'string') {
        return key;
    }
    key = key.match(/([^\/]*\/[^\/]+?\/[^\/]+?)\.vue/)?.[1] ?? key;
    //eslint-disable-next-line
    key = key.replace(/[\s\-_]([a-z])/g, m => {
            return m[1].toUpperCase();
        }).replace(/[^\w\d]/g, '');
    return key.substr(0, 1).toUpperCase()+key.substr(1);
}

import { defineAsyncComponent } from 'vue';
import LoadingComponent from '@/components/Builder/Components/Core/Layout/Loading.vue';
import ErrorComponent from '@/components/Builder/Components/Core/Layout/Error.vue';
import { Liquid, Javascript, Element } from '@/mixins';
export const loadComponent = function(url:String, mixins:Array<any>) {

    return  defineAsyncComponent({
            loader: () => {

                return new Promise((solve, reject)=>{
                    window.fetch(url)
                    .then(res => res.text())
                    .then(res => {
                        let script = '';
                        res.replace(/<script[\s\S]+?export default\s*([\s\S]*)<\/script>/g, (...m) => {
                            script = ' return ' + m[1];
                        });
                        try{
                            script= script ?  new Function(script)(): {};
                        }catch(e)
                        {
                            console.error(`Component ${url} is invalid.`, e.message);
                        }
                        //eslint-disable-next-line
                        res.replace(/<template[^\>]*?>([\s\S]+)<\/template\>/g, (...m) => {
                            script.template = m[1];
                        });
                        if (mixins && mixins.length > 0) {
                            script.mixins = mixins;
                        } else {
                            script.mixins = [ Element ];
                            if (typeof script?.computed?.javascript == 'function') {
                                script.mixins.push(Javascript);
                            }
                            if (script?.computed?.liquids) {
                                script.mixins.push(Liquid);
                            }
                        }
                        solve(script);
                    })
                    .catch(e=>{
                        console.error(e.message);
                        reject(e);
                    });
                });
            },
            loadingComponent: LoadingComponent,
            errorComponent: ErrorComponent,
            delay: 0,
            timeout: 60000,
            suspensible: true,
            onError (error, retry, fail, attempts) {
                if (error.message.indexOf('cdn.shopify.com') && attempts <= 3) {
                    // retry on fetch errors, 3 max attempts
                    retry();
                } else {
                    // Note that retry/fail are like resolve/reject of a promise:
                    // one of them must be called for the error handling to continue.
                    fail();
                }
            },
        });
}
