import { vendors } from '@/constants';
const createVendor = function (name) {
    let ifr = window.EComposerBuilder,
        stack = store.getters['builder/elementVendors'],
        url = vendors[name],
        type = url && url.match(/.js([?#]|$)/) ? 'js' :
        (url && url.match(/.css([?#]|$)/) ? 'css' : ''),
        key = 'vendor-' + type + '-' + name;
    if (
        type !== '' &&
        url &&
        ifr.contentWindow[key] === undefined
    ) {
        let elem = type == 'js' ?
            ifr.contentDocument.createElement('script') :
            ifr.contentDocument.createElement('link');
        elem.id = key;
        if (type == 'js') {
            elem.type = 'text/javascript';
            elem.src = url;
            elem.onerror = () => {
                store.commit(
                    'global/flash',
                    {
                        open: true,
                        message: 'The js resouce could not load "' + vendors[name]+'"',
                        type: 'error',
                    }
                );
            };
            ifr.contentDocument.body.appendChild(elem);
        } else {
            elem.rel = 'stylesheet';
            elem.media = 'all';
            elem.href = url;
            ifr.contentDocument.head.appendChild(elem);
        }
        stack.load[name] = false;
        elem.onload = () => {
            stack.load[name] = true;
            elem.setAttribute('data-loaded', 'true');
        };
    }
};

export const loadVendors = function(vendors, element) {
    let stack = store.getters['builder/elementVendors'];
    stack.register[element] = [];
    vendors.forEach(name => {
        createVendor(name);
        if (stack.register[element].indexOf(name) === -1) {
            stack.register[element].push(name);
        }
    });
}

