export const parseQueryParams = (params:Object):Object => {
    const params_data = Object.assign({}, params);

    if (params_data.hasOwnProperty("hmac")) {
        delete params_data.hmac;
    }
    if (params_data.hasOwnProperty("token")) {
        delete params_data.token;
    }
    if (params_data.hasOwnProperty("session")) {
        delete params_data.session;
    }

    return params_data;
}

