<template>
    <span v-html="svg" :class="size"></span>
</template>

<script>
    export default {
        props: {
            source: {
                type: String,
                default: '',
            },
            color: {
                type: String,
                default: '',
            },
            size: {
                type: String,
                default: 'w-5'
            }
        },
        computed: {
            svg(){
                var regex = /fill="([^"]*)"/g;
                var updatedSVGString = regex ? this.source?.replace(regex, 'fill="currentColor"') : this.source?.replace('<svg ', '<svg fill="currentColor" ');

                var regex_width = /width="([^"]*)"/g;
                var updatedSVGWidth = updatedSVGString?.replace(regex_width, '');

                var regex_height = /height="([^"]*)"/g;
                var updatedSVGHeight = updatedSVGWidth?.replace(regex_height, '');


                return updatedSVGHeight?.replace('<svg', `<svg class="inline-flex ${this.size} ${this.color ? this.color : ''}"`)
            }
        }
    }
</script>