import advanced from '@/components/Builder/Helpers/CssGroup/advanced';
import { convertCss } from '@/components/Builder/Helpers/Css';
import { loadVendors } from '@/utils/helpers';

export default {
    inject: ['exporting'],
    data() {
        return {
            oldData: {},
            timeout: {},
            active_child_elenent: true
        };
    },
    computed: {
        canMultipleLanguages() {
            return this.getPermission('translates',false);
        },
        fieldSettings() {
            return this.formatFields(this?.layout?.settings ? this.layout.settings : this.settings);
        },
        fieldStyle() {
            return this.formatFields(this?.layout?.style ? this.layout.style : this.style);
        },
        fieldAdvanced() {
            return this.formatFields(advanced(this));
        },
        params(){
            return this.$store.getters['page/params'];
        },
        translationPrefix(): String{
            return this.params.page === 'menu' ? 'ecomposer.ECOM_REPLACE_SECTION_NAME.' : 'sections.ECOM_REPLACE_SECTION_NAME.';
        },
        stacks() {
            return this.$store.getters['builder/stacks'];
        },
        editingElement() {
            return this.$store.getters['builder/editingElement'];
        },
        isFullScreen() {
            return this.$store.getters['builder/isFullScreen'];
        },
        getPermission() {
            return this.$store.getters['global/getPermission'];
        },
        screen(){
            return this.$store.getters['builder/screen'];
        }
    },
    watch: {
        'data.style': {
            deep: true,
            handler(val) {
                clearTimeout(this.timeout['style']);
                this.timeout['style'] = setTimeout(() => {
                    this.reactiveConvertCss('style', val);
                }, 10);
            }
        },
        'data.settings': {
            deep: true,
            handler(val) {
                let v = this.$helpers.copy(val);
                if(v?.items?.[0]?.blocks) {
                    delete v.items;
                }
                clearTimeout(this.timeout['settings']);
                this.timeout['settings'] = setTimeout(() => {
                    this.reactiveConvertCss('settings', v);
                }, 10);
            }
        },
        'data.advanced': {
            deep: true,
            handler(val) {
                if(!val) return;

                let $el = null;

                if((this?.deep == 1 && this?.is !== 'tabs' ) || (this?.deep >= 2 && this?.is == 'row')) {
                    $el = this.$el instanceof HTMLElement ? this.$el : null
                } else if(this?.deep >= 2 && this?.is == 'column') {
                    $el = this.$el instanceof HTMLElement ? this.$el.querySelector('.core__column--wrapper') : null
                }  else {
                    $el = this.$el.parentNode;
                }
                //Margin, padding
                let spacingName = 'spacing' + (this.screen === 'desktop' ? '' : ('__' + this.screen));
                if(val.hasOwnProperty(spacingName) && $el) {

                    let spacing = Object.assign({}, val[spacingName]),
                        margin = spacing?.margin || '',
                        padding = spacing?.padding || '';

                    if(padding) {
                        const {top, bottom, left, right} = padding;
                        top ? $el.style.setProperty('padding-top', top, 'important') : ''
                        bottom ? $el.style.setProperty('padding-bottom', bottom, 'important') : ''
                        //bottom ? $el.style.paddingBottom = bottom : ''
                        left ? $el.style.paddingLeft = left : ''
                        right ? $el.style.paddingRight = right : ''
                    }
                    if(margin) {
                        const {top, bottom, left, right} = margin;
                        top ? $el.style.marginTop = top : ''
                        bottom ? $el.style.marginBottom = bottom : ''
                        left ? $el.style.marginLeft = left : ''
                        right ? $el.style.marginRight = right : ''
                    }

                }
                //Position

                if(val.hasOwnProperty('left') && $el) $el.style.left = val.left;
                if(val.hasOwnProperty('right') && $el) $el.style.right = val.right;
                if(val.hasOwnProperty('top') && $el) $el.style.top = val.top;
                if(val.hasOwnProperty('bottom') && $el) $el.style.bottom = val.bottom;

                clearTimeout(this.timeout['advanced']);
                this.timeout['advanced'] = setTimeout(() => {
                    this.reactiveConvertCss('advanced', val);
                }, 10);


                clearTimeout(this.timeout['styleInline']);
                this.timeout['styleInline'] = setTimeout(() => {
                    if(val.hasOwnProperty(spacingName) && $el) {
                        $el.style.padding = '';
                        $el.style.margin = '';
                    }

                    if(val.hasOwnProperty('left') && $el) $el.style.left ='';
                    if(val.hasOwnProperty('right') && $el) $el.style.right = '';
                    if(val.hasOwnProperty('top') && $el) $el.style.top = '';
                    if(val.hasOwnProperty('bottom') && $el) $el.style.bottom = '';

                }, 500)


            }
        }
    },

    beforeCreate() {
        ['settings', 'style', 'advanced'].forEach((sch) => {
            if (this.data?.[sch]?.constructor !== Object) {
                this.data[sch] = {};
            }
        });
    },

    mounted() {
        if (this.exporting !== false) {
            return;
        }


        let name = (this.data?.name ?? this.$options?.name).toLowerCase();

        if (this.$options.vendors) {
            loadVendors(this.$options.vendors, name);
        }

        if (
            this.data && this.css
        ) {

            this.$store.commit('page/storeCssBase', {
                name: name,
                data: this.css
            });
        }

        if (this.data.id == this.editingElement?.data?.id) {
            this.$store.commit('builder/setEditingElement', this);
        }

        this.$helpers.setDefaultSettings(this);

        ['settings', 'style', 'advanced'].forEach((sch) => {
            if (this.data[sch]) {

                let val = this.data[sch];

                if (sch == 'settings') {
                    val = this.$helpers.copy(val);
                    if(val?.items?.[0]?.blocks) {
                        delete val.items;
                    }
                }

                this.reactiveConvertCss(sch, val);

            }
        });
        if(this.$options.hasChild)
        {
            this.childElementHelper();
        }

    },

    methods: {

        lang(str: String, key,options= null) :String{

            if (!str) {
                return '';
            }

            if (str.length > 998) {
                return str;
            }

            if (!this.exporting || this.exporting === 'preview') {
                if(options && Object.keys(options).length)
                {
                    Object.keys(options).forEach(k => {
                        str = str.replace(new RegExp('{{\\s*?' + k + '\\s*?}}', 'g'), '{{ ' + options[k] + '}}');
                    });
                }
                if(options && options.escape){
                    return escape(str);
                }
                return str;
            }
            if(!this.canMultipleLanguages)
            {
                return str;
            }
            const element_id = this.data?.id?.split('-')?.pop();
            key =  key && typeof key === 'string' ? this.$helpers.str2key(key) +'_'+  element_id: this.$helpers.str2key(str)  +'_'+  element_id;
            key = key+'_html';
            this.$store.commit('page/setLanguage', {id: key, text: str});
            if (str && options && Object.keys(options).length) {
                let values =   Object.keys(options).map(k=> `${k}:${options[k]}`);
                let vals  = values.join(',');
                if(options && options.escape)
                {
                    return `{{ '${this.translationPrefix}${key}' | t: ${vals} | escape }}`;
                }
                return `{{ '${this.translationPrefix}${key}' | t: ${vals} }}`;
            }
            return `{{ '${this.translationPrefix}${ key }' | t }}`;
        },

        formatFields(fields) {
            let settings = typeof fields == 'function' ?
                fields() :
                fields;
            return this.$helpers.formatFields(settings);
        },

        reactiveConvertCss(type, value) {
            if (
                this.data.id &&
                this.oldData[type] !== JSON.stringify(value) &&
                this.exporting === false
            ) {
                this.convertCss(type, value);
                this.oldData[type] = JSON.stringify(value);
            }
        },

        convertCss(type, value) {

            let id = this.$parent?.data?.group ?
                    this.$parent.data.id : this.data.id,
                    rootSelector = '.ecom-core'  + (id ? ('.' + id) : ''),
                    fields = (type === 'style' ?  this.fieldStyle :
                        (
                            type === 'settings' ? this.fieldSettings : this.fieldAdvanced
                        )
                    );
            if(!this.data.css)
            {
                this.data.css = {};
            }
            this.data.css[type] = convertCss(rootSelector, fields, value);

        },

        idoc() {
            return window.EComposerBuilder.contentDocument;
        },
        iwin() {
            return window.EComposerBuilder.contentWindow;
        },
        childElementHelper(){
            try {
                window.EComposerBuilder.contentWindow.Function(
                    `
                    var _this = this,$el=this.$el;
                    $el.querySelectorAll && this.$el.querySelectorAll('.ecom-child-element').forEach(elm=>{
                        elm.addEventListener('click', function(e){
                            e.preventDefault()
                            $el.querySelectorAll('.ecom-child-element-active').forEach(el=>{
                                el.classList.remove('ecom-child-element-active');
                            })
                            elm.classList.add('ecom-child-element-active');
                            $el.dispatchEvent(new window.CustomEvent('setactive',{detail: elm.dataset.childName}));

                        });

                    });
                    $el.addEventListener('click', function(e){
                        if(!e.target.closest('.ecom-child-element'))
                        {
                            $el.dispatchEvent(new window.CustomEvent('setactive',{detail:  null}));
                            $el.querySelectorAll('.ecom-child-element-active').forEach(el=>{
                                el.classList.remove('ecom-child-element-active');
                            })
                        }
                    });

                    `
                ).call({
                    $el: this.$el,

                    id: this.data.id
                });
            } catch (ex) {
                this.$store.commit(
                    'global/flash',
                    {
                        open: true,
                        message: 'An error occurred while executing the javascript code of element <strong>'+this.$options.name+'</strong>',
                        type: 'error',
                    }
                );
                console.error('[ECOMPOSER ERROR] process javascript of element "' + this.$options.name + '": ', ex);
            }
        },
        setActiveElement(e){
            this.active_child_elenent = e.detail;
        }
    }
};