<template>
	<li
		v-detectVisible="detectVisible"
		class="relative p-2 transition cursor-pointer select-none item"
        :class="classItem(item)"
	>
		<div
			class="relative"
			:class="[
				layout == 'grid' ? 'pb-100p' : '',
				item.fn == 'upload' ? 'pb-20' : 'pb-10',
			]"
		>
			<div
				v-if="show"
				class="absolute inset-0 group"
			>
				<template v-if="item.fn == 'upload'">
					<span
						class="absolute top-0 left-0 flex flex-col items-center justify-center w-full h-full font-medium text-gray-500 transition bg-white dark:bg-dark dark:text-dark-color border-2 border-gray-300 border-dashed rounded group-hover:text-primary-700 group-hover:border-primary-700 dark:border-gray-600"
					>
						<Icon :source="icons.ExportMinor" :size="'w-4'"></Icon>
						<text>Upload</text>
					</span>
					<input
						class="absolute top-0 left-0 z-10 w-full h-full opacity-0 cursor-pointer"
						type="file"
						:accept="acceptInput"
						multiple
						@change="$emit('upload', $event)"
					>
				</template>
				<div v-else class="flex items-center h-full">
					<div
						class="flex items-center w-full h-full"
						:class="{
								'opacity-30': item.loading
								}"
						@click="toggleItem(item)"
					>
						<template v-if="layout == 'list'">
							<span class="block w-10 h-10 mr-2 text-gray-700 dark:text-dark-color fill-current svg-full">
								<span
									v-html="item.thumbnail"
								/>
							</span>
							<span class="flex-1 block text-sm text-gray-500 dark:text-dark-color truncate uppfirst">
								{{ item.name }}
							</span>
						</template>
						<div
							v-else
							class="relative flex flex-col items-center justify-center w-full h-full overflow-hidden transition bg-white dark:bg-gray-500 rounded shadow-content"
							:title="item.reload ? 'Reload to choose' : item.name"
							:class="{
								'scale-90 transform':
									isActive(item.value) > -1 &&
									layout == 'grid',
							}"
						>
							<span
								v-if="!item.thumbnail.startsWith('<')"
								class="w-10 h-10 text-gray-700 dark:text-dark-color fill-current svg-full bg-gray-50 border border-gray-200 rounded flex justify-center items-center"
							>
								<div
									class="w-4 h-4 bg-auto bg-center"
									:style="{ 'mask-image': `url(${item.thumbnail})`,'background-image':`url('${item.thumbnail}')`,  'mask-repeat': 'no-repeat', 'mask-position' : 'center','mask-size':'contain' }"
								></div>
							</span>
							<span
								v-else
								class="w-10 h-10 text-gray-700 dark:text-dark-color fill-current svg-full bg-gray-50 border border-gray-200 rounded flex justify-center items-center p-3"
								v-html="item.thumbnail"
							/>
							<!-- <p
								class="text-center text-xs mt-0.5 max-w-full px-1 block truncate uppfirst text-gray-600 dark:text-dark-color"
							>
								<span>{{
									item.reload ? 'Reload to choose' : item.name
								}}</span>
							</p> -->
						</div>
					</div>
					<span
						class="absolute flex items-center justify-center w-5 h-5 p-px transition bg-white dark:bg-dark dark:text-dark-color border-2 rounded-full opacity-0"
						:class="[
							isActive(item.value) > -1
								? 'bg-primary-600 text-white border-primary-600 opacity-100'
								: 'border-gray-400 text-gray-400',
							layout == 'grid'
								? 'right-0 bottom-0 z-10'
								: 'top-1/2 right-0 transform -translate-y-1/2 group-hover:opacity-100',
						]"
					>
						<Icon :source="icons.CircleTickMinor" :size="'w-6'" :color="'text-primary-600'"></Icon>
					</span>
					<span
						v-if="item.loading"
						class="absolute transform -translate-x-1/2 -translate-y-1/2 top-1/2 left-1/2"
					>
						<Loading type="spin"/>
						<span
							class="absolute text-sm transform -translate-x-1/2 -translate-y-1/2 top-1/2 left-1/2"
						>
							{{ item.per }}
						</span>
					</span>
					<template v-if="isActive(item.value) === -1 && (item.id + '').startsWith('gid://')">
						<template v-if="!item.removing">
							<svg
								class="absolute w-4 h-4 text-gray-500 dark:text-dark-color transition top-1 right-1 z-1 hover:text-red-600 opacity-0 group-hover:opacity-100"
								@click="$emit('remove', item)"
								xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" aria-hidden="true"
							>
								<path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"></path>
							</svg>
						</template>

						<Loading v-else
							:size="4"
							type="spin"
							class="absolute w-4 h-4 text-gray-500 dark:text-dark-color transition top-1 right-1 z-1"
						></Loading>
					</template>
				</div>
			</div>
		</div>
	</li>
</template>

<script lang="ts">
import { detectVisible } from '../../directives';
import { ExportMinor, CircleTickMinor } from "@/assets/icons";

export default {
    name: 'IconContent',
	directives: {
		detectVisible
	},
    components: {
    },

    props: {
        value: {
            type: Array,
						default(){
							return [];
						}
        },
        item: {
            type: Object,
						default(){
							return {};
						}
        },
        layout: {
            type: String,
						default: ''
        }
    },
	emits: ['upload', 'remove', 'toggleItem', 'reload'],

    data(){
        return {
            show: false,
            info: {},
			icons: {
				ExportMinor, CircleTickMinor
			}

        };
    },

    computed: {
        acceptInput(){
            return '.svg';
        },
				hasReload() {
            return this.item.reload;
        }
    },
		watch: {
        hasReload(val) {
            if (val) {
                setTimeout(() => {
                    this.$emit('reload');
                }, 1000);
            }
        }
    },
    methods: {
        fileName(data){
                let name = this.$helpers.file.name(data);
                return name ? name :  data;
            },
        isActive(value) {
            return this.value.reduce((acc, cur, index) => {
                if (cur.value == value) {
					acc = index;
				}
                return acc;
            }, -1);
        },
		classItem(item){
			let cl = '';
			if(this.layout == 'grid'){
				cl += 'w-1/5 pt-0 px-1 ';
			}else{
				cl += 'py-2.5 pl-4 w-full border-b border-gray-300 dark:border-gray-600 bg-white dark:bg-gray-700 dark:text-dark-color';
				if(item.fn === 'item'){
					cl += 'h-15';
				}else{
					cl += 'h-25';
				}
				if(!item.reload){
					cl += ' cursor-pointer';
				}
			}
			if(item.reload){
				cl += ' opacity-50 pointer-events-none transform scale-90';
			}
			return cl;

        },
		detectVisible(){
			this.show = true;
		},
		async getContentSVG(url) {
			let res = await fetch(url);
			return (await res.text()).replace(/([\n])/g, '').replace(/([\t])/g, '').replace(/([ ],{2,})/g,' ').replace(/<!--(.*?)-->/g,'');
		},
		async toggleItem(item){
			const formatSvg = (htmlSVG) => {
				var div = document.createElement('div');
				div.innerHTML = htmlSVG;
				let svg =  div.querySelector('svg');
				if(!svg) {
					return htmlSVG;
				}
				let stroke = svg.getAttribute('stroke');
				if(stroke && stroke !== 'none') {
					svg.setAttribute('stroke', 'currentColor');
				}
				else {
					svg.setAttribute('fill', 'currentColor');
				}
				return svg.outerHTML;
			};
			if(!item?.value?.startsWith('<')){
				let dataSVG = await this.getContentSVG(item.value);
				dataSVG = formatSvg(dataSVG);
				item.thumbnail = dataSVG;
				item.value = dataSVG;
			}
			this.$emit('toggleItem', item);
		}
    }
};
</script>

<style>
 .svg-full svg{
	width: 100%;
	height: 100%;
 }
 .min-w-1\/3{
	min-width: 33.333333%;
 }
 .max-w-1\/3{
	max-width: 33.333333%;
 }
</style>
