export const EComDate = {
    names: {
        month: [
            {name: 1, title:'Jan'},
            {name: 2, title:'Feb'},
            {name: 3, title:'Mar'},
            {name: 4, title:'Apr'},
            {name: 5, title:'May'},
            {name: 6, title:'Jun'},
            {name: 7, title:'Jul'},
            {name: 8, title:'Aug'},
            {name: 9, title:'Sep'},
            {name: 10, title:'Oct'},
            {name: 11, title:'Nov'},
            {name: 12, title:'Dec'}
        ],
        day: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
        times: Array.from({ length: 48 }, (_, i) =>
            (i / 2 % 1 === 0) ? (i / 2  + ':00') : (parseInt(i / 2) + ':30')
        ),
        gmts: ['-0100', '-0200', '-0300', '-0400', '-0500', '-0600', '-0700', '-0800', '-0900', '-1000', '-1100',
                '+0100', '+0200', '+0300', '+0330', '+0400', '+0430', '+0500', '+0530', '+0545', '+0600', '+0700', '+0800', '+0900', '+0930', '+1000', '+1100', '+1200', '+1300',]
    },
    startIndex(d, m, y) {
        return new Date(m + '-' + d + '-' + y).getDay();
    },
    isLeap(year) {
        if ((year % 4) || ((year % 100 === 0) && (year % 400))) {
            return 0;
        }
        return 1;
    },
    daysIn(month, year) {
        return (month === 2) ? (28 + this.isLeap(year)) : 31 - (month - 1) % 7 % 2;
    },
    days(month, year) {
        let days = [];
        const genDays = (length , start = 1) => {
            return Array.from({ length }, (_, i) => i + start);
        };
        let startIndex = this.startIndex(1, month, year);
        if(startIndex !== 0) {
            let prevMonth = month == 0 ? 12 : (month - 1),
                prevYear = month == 0 ? (year - 1) : year,
                daysIn = this.daysIn(prevMonth, prevYear);
            days.splice(0, startIndex + 1, ...genDays(startIndex, daysIn - startIndex));
        }
        days.push(...genDays(this.daysIn(month, year)));
        ///days.push(...genDays(42 - days.length, 1));
        return days;
    }
};
