import helpers from '@/utils/helpers';

import { store } from '@/store';
import { ROOT_STORE } from '@/store/constants';

export default {
    namespaced: true,
    state: {
        pages_allowed: ['page'],
        type_templates: ['home','ajax-cart','quickview'],
        params:{},
        sections: [],
        settings: {
            title: 'Untitled',
            layout: 'theme',
            published: 'published',
            status: '',
            custom_code: {
                js: '',

                css: ''
            }
        },
        languages: {}, // Store all lang() for exporting
        sectionCss: {}, // Store dynamic css of each section
        cssBases: {}, // Store all css base of elements
        javascripts: {}, // Store all javascript callback of elements
        globalUsed: {},
        drafts: [],
        layouts: [],
        layout: null,
        main_layout: null,
        layoutTrending: [],
        loading: false,
        extension_elements:{},
        ecosystem_apps: [],
        popup:false,
        importPopup: false,
        handle: ''
    },
    mutations: {
        setPopup(state,payload)
        {
            state.popup = payload;
        },
        setHandle(state,payload)
        {
            state.handle = payload;
        },
        setImportPopup(state,payload)
        {
            state.importPopup = payload;
        },
        setSettings(state,payload){
            if(payload.hasOwnProperty('custom_code') && payload.custom_code.constructor !== Object) {
                payload.custom_code = {
                    js: '',
                    css: ''
                }
            }
            state.settings = Object.assign({layout: 'theme',title: 'Untitled',custom_code: {js: '',css:''} }, payload) ;
        },
        setUser(state, payload) {
            state.user = payload;
        },
        setSections(state, payload) {
            let data;
            if(payload.keepId){

                //Check if has duplicate ID, auto generate ID again
                const uniqueValues = new Set(payload.data.map((v:any) => v.id));
                if (uniqueValues.size < payload.data.length) {
                    data = this.$helpers.generateElementId(payload.data);
                } else {
                    data = payload.data;
                }

            }else
                data = this.$helpers.generateElementId(payload.data);


            setTimeout(()=>{
                state.sections = data;
                if (payload.data?.columns?.length > 0) {
                    store.commit(ROOT_STORE.MUTATIONS.SET_TAB, 'navigator')
                }

            },1000);
        },
        addSection(state, payload) {
            state.sections.push(this.$helpers.generateElementId(payload));
        },
        insertSections(state, payload) {
            let section_data = this.$helpers.generateElementId(payload.sections);
            state.sections = state.sections.filter((section)=>section.columns && section.columns.length);
            if (payload.index === undefined) {
                state.sections.push(...section_data);
            } else {
                state.sections.splice(
                    payload.index,
                    (state.sections[payload.index]?.columns?.length ?? 0 > 0 ? 0 : 1),
                    ...section_data
                );
            }
            store.commit(ROOT_STORE.MUTATIONS.SET_TAB, 'navigator')
        },
        storeCssBase(state, payload) {
            if (!state.cssBases[payload.name] && payload.data) {
                state.cssBases[payload.name] = payload.data;
            }
        },
        revokeCssBase(state, payload) {
            if (state.cssBases[payload] !== undefined) {
                delete state.cssBases[payload];
            }
        },
        setQueryParams(state, payload) {
            state.params = payload;
        },
        setLayouts(state, payload){
            state.layouts = payload;
        },
        setMainLayout(state, payload){
            state.main_layout = payload;
        },
        setDrafts(state, payload){
            state.drafts = payload;
        },
        setLanguage(state, payload) {
            if (payload.id && typeof payload.text == 'string') {
                state.languages[payload.id] = payload.text;
            }
        },
        resetLanguages(state) {
            state.languages = {};
        },
        storeJavascript(state, payload) {
            if (payload.id && payload.data) {
                state.javascripts[payload.id] = payload;
            }
        },
        revokeJavascript(state, id) {
            delete state.javascripts[id];
        },
        revokeSectionCss(state, id) {
            delete state.sectionCss[id];
        },
        layoutTrending(state, payload){
            state.layoutTrending = payload;
        },

        setLayout(state,payload)
        {
            if(payload && payload.options && payload.options.sources)
            {
                payload.options.sources = null;
            }
            state.layout = payload;
        },
        setGlobalUsed(state, {id, payload}) {
            state.globalUsed[id] = state.globalUsed[id] ?? {};
            for(let k in payload) {
                state.globalUsed[id][k] = payload[k];
            }
        },
        setGlobalSectionData(state,payload){
            if(payload && payload.key)
            {
                state.sections.forEach((s,i) => {
                    if(state.sections[i].settings && state.sections[i].settings[payload.key]?.value !== payload.value?.value)
                    {
                        state.sections[i].settings[payload.key] = payload.value;
                    }
                });
            }
        }

    },
    getters: {
        popup(state){
            return state.popup;
        },
        handle(state){
            return state.handle;
        },
        importPopup(state){
            return state.importPopup;
        },
        settings(state) {
            return state.settings;
        },
        sections(state) {
            return state.sections;
        },
        user(state) {
            return state.user;
        },
        section: (state) => (id) => {
            return state.sections.find((section) => section.id === id);
        },
        scripts: (state) => (id) => {
            let component = state.components[id];
            if (!component.parsed_scripts) {
                component.parsed_scripts = {};
            }
            return component.parsed_scripts;
        },
        allNodes: (state) => {
            let res = {
                    names: ['section', 'row', 'column'],
                    vendors: [],
                    ids: [],
                    theme_extensions: []
                },
                getcss = (data) => {

                    let d = data.group ? data.group : data,
                        id = data ? data.id : null;
                    if (d && id && (d.settings || d.style || d.advanced)) {
                        let css = [];

                        if (d.css?.settings) {
                            css.push(d.css?.settings);
                        }
                        if (d.css?.style) {
                            css.push(d.css?.style);
                        }
                        if (d.css?.advanced) {
                            css.push(d.css?.advanced);
                        }
                        return css;
                    }

                    return [];

                },
                loop = (data, sec) => {
                    if (data === sec && sec.id) {
                        state.sectionCss[sec.id] = getcss(sec);
                    }
                    let doloop = (elms) => {
                        let hasLiquid = false;

                        if (elms) {
                            elms.forEach((elm) => {
                                if (!elm) {
                                    return;
                                }
                                if (elm.hasRequestLiquid) {
                                    if(!elm.hasElementRequestLiquid )
                                    {
                                        sec.hasElementRequestLiquid = true;
                                    }

                                    hasLiquid = true;
                                }
                                if (elm.vendors) {
                                    elm.vendors.forEach((name) => {
                                        if (res.vendors.indexOf(name) === -1) {
                                            res.vendors.push(name);
                                        }
                                    });
                                }
                                let comName = elm?.name ?? elm?.element ?? elm?.$?.$options?.name ?? false;
                                if (comName && res.names.indexOf(comName.toLowerCase()) === -1) {
                                    res.names.push(comName.toLowerCase());
                                }

                                if (elm.row) {
                                    loop(elm.row, sec);
                                    let tmp = getcss(elm.row);
                                    if(tmp && tmp.length)
                                    {
                                        state.sectionCss[sec.id].unshift(tmp);
                                    }
                                } else if (elm?.group?.settings?.items) {
                                    loop(elm.group.settings, sec);
                                    if(['shopifyProductBox','shopifyArticleBox'].includes(elm.name))
                                    {
                                        elm.group.settings.items[0].hasElementRequestLiquid = true;
                                    }
                                    res.ids.push(elm.group.id);
                                }

                                else if(elm?.template === 'theme-app-extensions' && elm?.visibility !== false)
                                {
                                    if(elm?.settings?.extension_id_global && state.params.page != 'product') {
                                        elm.extension_id = elm?.settings?.extension_id_global;
                                    }
                                    res.theme_extensions.push(elm);
                                }
                                if (elm.id) {
                                    res.ids.push(elm.id);
                                }
                                let tmp = getcss(elm);
                                if(tmp && tmp.length)
                                {
                                    state.sectionCss[sec.id].unshift(tmp);
                                }

                                if(['shopifyProductBox','shopifyArticleBox'].includes(elm?.name)) {
                                    hasLiquid = true;
                                }

                            });
                        }
                        return hasLiquid;
                    };
                    if (state.sectionCss[sec.id] && data.columns) {
                        data.columns.forEach((col) => {
                            if (col.blocks) {
                                doloop(col.blocks);
                            }
                            let tmp = getcss(col);
                            if(tmp && tmp.length )
                            {
                                state.sectionCss[sec.id].unshift( tmp);
                            }

                            res.ids.push(col.id);
                        });
                    }
                    if (state.sectionCss[sec.id] && data.items) {
                        data.items.forEach((item) => {
                            if (item.blocks) {
                                let hasLiquid = doloop(item.blocks);
                                if(hasLiquid !== item.hasElementRequestLiquid)
                                {
                                    item.hasElementRequestLiquid = hasLiquid;
                                }
                            }
                        });
                        let tmp = getcss(data);
                        if(tmp && tmp.length)
                        {
                            state.sectionCss[sec.id].unshift(tmp);
                        }

                    }
                    res.ids.push(data.id);
                    // if (data === sec && sec.id) {
                    //     //state.sectionCss[sec.id] = moveTopMediaCss(state.sectionCss[sec.id]);
                    // }
                };

            state.sections.forEach((sec) => {
                if(sec.hasElementRequestLiquid)
                {
                    sec.hasElementRequestLiquid = false;
                }
                loop(sec, sec);
            });

            return res;

        },
        cssBases: (state) => {
            return state.cssBases;
        },
        sectionCss: (state) => {
            return state.sectionCss;
        },
        javascripts: (state) => {
            return state.javascripts;
        },
        params(state) {
            return state.params;
        },
        mainLayout(state){
            return state.main_layout;
        },
        layouts(state){
            return state.layouts;
        },
        drafts(state){
            return state.drafts;
        },
        layoutTrending(state){
            return state.layoutTrending;
        },
        languages(state) {
            return state.languages;
        },
        dataExport(state, getters, rootState, rootGetters) {
            return {
                sections: state.sections,
                settings: getters.settings,
                // created: new Date().getTime(),
                global: getters.globalUsed,
                custom: getters.custom_code,
                theme: rootGetters['settings/themeSettings'],
            };

        },
        loading(state) {
            return state.loading;
        },
        layout(state)
        {
            return state.layout;
        },
        type_templates(state){
            return state.type_templates;
        },
        isTemplateDefault(state)
        {

            if(state.layout && state.layout.is_default)
            {
                return  true;
            }
            let page = state.params.page;
            if(!state.type_templates.includes(page))
            {
                return false;
            }
            if(state.layouts.length)
            {

                let find = state.layouts.find(e=>e.type === page);
                return find ? false : true;
            }
            return false;

        },
        extension_elements(state){
            return state.extension_elements;
        },
        ecosystem_apps(state){
            return state.ecosystem_apps;
        },
        globalUsed(state, getters, rootState, rootGetters) {
            let global = rootGetters['settings/globalDatas'],
            globalUsed = state.globalUsed;
            let result = {};

            let nameDefault = ['primary','secondary','text','accent'],
                types = ['colors', 'typography'];
            types.forEach(t => {
                nameDefault.forEach(n => {
                    if(!result?.[t]?.[n] && global?.[t]?.[n]) {
                        result[t] = result[t] ?? {};
                        result[t][n] = global[t][n];
                    }
                });
            });
            let id, k;
            for(id in globalUsed) {
                for(k in globalUsed[id]) {
                    result[k] = result[k] ?? {};
                    if(globalUsed[id][k]?.constructor === Array) {
                        globalUsed[id][k].forEach(key => {
                            if(global?.[k]?.[key]) {
                                result[k][key] = global[k][key];
                            }
                        });
                    }
                }
            }

            return result;
        },
        custom_code(state) {
            return state.settings.custom_code;
        }
    },
    actions: {

        init({ commit, getters, dispatch, state,rootGetters }) {
            this.$worker.fetch(window.route('builder.init', state.params))
                .then((response) => {
                    if (response.data.status === 'success') {
                        //Fetch task to show

                        if(state.params.post_id)
                        {
                            dispatch('library/handleEditTask',state.params.post_id,{ root: true});
                        }
                        else if(state.params.template_id)
                        {
                            setTimeout(()=>{
                                dispatch('library/insertTemplates',{id: state.params.template_id, type: 'accept'},{ root: true});
                            },2000)
                        }

                        commit(
                            'global/setUser', response.data.user, { root: true }
                        );
                        commit(
                            'global/setConfig', response.data.configs, { root: true }
                        );
                        dispatch(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                        state.pages_allowed = response.data.pages_allowed;
                        if(response.data?.extension_elements)
                        {
                            dispatch('insertExtensionElements', response.data?.extension_elements);
                        }

                        if(state.params.params)
                        {
                            try{
                                let configs = JSON.parse(window.atob(state.params.params));
                                if(typeof configs === 'object')
                                {
                                    commit('setSettings',configs);
                                }
                                dispatch('setQueryParam',{name:'params'})
                            }
                            catch(e)
                            {
                                console.info(e.message)
                            }
                        }

                        if(response.data?.ecosystem_apps)
                        {
                            dispatch('insertEcoSystemApps', response.data?.ecosystem_apps)
                        }
                        dispatch('setDataGlobal',response.data.css_global);
                        commit('setLayouts',response.data.layouts);
                        commit('setMainLayout', response.data.main_layout);
                        let layout = response.data.layout;

                        if(state.params.page === 'page' && layout) {
                            if(!layout.options?.settings?.published) {
                                layout.options.settings.published = "published";
                            }
                        }

                        let hash = window.location.hash;
                        if(hash.indexOf('#library') === -1) {
                            commit('builder/setPopup',
                            {
                                show: false,
                                loading: true,
                                message: 'Processing...',
                                closeable: false
                            },{ root: true});
                        }

                        let page_settings = response.data.theme_settings;
                        if(response.data?.custom_code && response.data?.custom_code.constructor === Object) {

                            page_settings.custom_code = response.data.custom_code;
                        }
                        if(Array.isArray(page_settings?.body)) {
                            page_settings.body = Object.assign({}, page_settings.body);
                        }

                        commit('settings/setThemeSettings', page_settings, { root: true });
                        if (layout && !state.params.post_id) {
                            let theme_layout = layout.options?.settings?.layout??'theme';
                            if(['footer','header','section','quickview','ajax-cart', 'menu', 'password'].includes(layout.type))
                            {
                                theme_layout = 'ecom'
                            }
                            if(layout.type === 'product' || layout.type === 'collection') {
                                commit('setHandle', layout.options.settings?.handle);
                            }
                            dispatch('setQueryParams', {
                                page: layout.type,
                                id: layout._id,
                                layout: theme_layout
                            });

                            if (layout.options) {

                                if (layout.options.data) {
                                    if(typeof layout.options.data === 'string')
                                    {
                                        layout.options.data = JSON.parse(layout.options.data.replace(/"tab":"(active|hover)"/gm,'"tab":"normal"'));
                                    }
                                    layout.options.keepId = true;
                                    dispatch('setSections', layout.options);
                                }

                                commit('setSettings',layout.options && layout.options.settings ?  layout.options.settings : {

                                });

                            }

                            if(layout?.status) {
                                state.settings.status = layout.status;
                            }

                            if(response.data.drafts && response.data.drafts.length)
                            {
                                commit('setDrafts', response.data.drafts);
                            }

                            //Set data of current layout to state;
                            commit('setLayout', layout);

                        }
                        else {
                            if(state.params.page === 'menu')
                            {
                                dispatch('setSections', {data: [{"columns":[{"width":100,"settings":{"width":"100%","width__mobile":"100%"}}],"hasElementRequestLiquid":false}]});

                            }
                            commit('setLayout', null);
                            if(state.params.assign_for && response.data.page_settings && Object.keys(response.data.page_settings).length)
                            {
                                commit('setSettings',response.data.page_settings);
                            }

                            /*
                            if(!state.params.assign_for) {
                                const settings_temp = Object.assign({}, getters['settings']);
                                if(['home', 'footer','header', 'quickview', 'password'].includes(state.params.page)) {
                                    settings_temp.assign_for = 'default';
                                } else if(['product', 'collection', 'list-collections', 'blog', 'article'].includes(state.params.page)) {
                                    settings_temp.assign_for = 'none';
                                }

                                commit('setSettings', settings_temp);
                            }
                            */


                            if(!state.params.status) {
                                const settingTemps = Object.assign({}, getters['settings']);
                                if(state.params.page !== 'page') {
                                    settingTemps.status = 'public';
                                }

                                commit('setSettings', settingTemps);
                            }


                            dispatch('setQueryParam',{ name: 'id',value: undefined});
                            dispatch('setQueryParam',{ name: 'layout',value: state.params?.layout??'theme'});
                            if(!state.params.page || !state.pages_allowed.includes(state.params.page))
                            {
                                dispatch('setQueryParam',{ name: 'page',value:'page'});
                            }


                        }
                        if(state.params?.token)
                        {
                            dispatch('setQueryParam',{ name: 'token',value: undefined});
                        }

                        const page_allowed = rootGetters['global/getPermission']('pages');
                        const max_limit = rootGetters['global/getPermission']('max_limit',10);
                        const limit = rootGetters['global/getPermission']('limit',10);
                        const user = rootGetters['global/user'];
                        const planName = user?.plan?.name??'Free';

                        if(!page_allowed || !page_allowed.includes(state.params.page))
                        {
                            let lim = 0;
                            try{
                                // lim = sessionStorage.getItem('ecom-popup-notice-page-allowed');
                            }
                            catch(e) {

                            }
                            if(!lim) {
                                try{
                                    sessionStorage.setItem('ecom-popup-notice-page-allowed', '1');
                                }
                                catch(e){

                                }
                                commit('builder/setPopup',
                                    {
                                        show: true,
                                        type: 'error',
                                        loading: false,
                                        title: 'Information!',
                                        message: `Please notice that **you cannot publish ${state.params.page}** on ${planName} plan. You can still create and save it to publish later on`,
                                        closeable: true,
                                        cancel: 'Ok, got it'
                                    },{ root: true});
                            }


                            // return false;
                        }
                        else  if(response.data.layouts.length > max_limit)
                        {
                            let lim = 0;
                            try{
                                lim = sessionStorage.getItem('ecom-popup-notice-page-publish');
                            }
                            catch(R){}

                            if(!lim) {
                                sessionStorage.setItem('ecom-popup-notice-page-publish', '1');
                                commit('builder/setPopup',
                                    {
                                        show: true,
                                        type: 'error',
                                        loading: false,
                                        title: 'Opps! Exceed limit',
                                        message: `You are reach out of limit **(${max_limit})** for ${planName} plan.\n You still able to edit and save but can't publish.
                                        Please [upgrade](${window.route('app.plans')}) plan to continue`,
                                        closeable: true
                                    },{ root: true});
                            }

                            return false;
                        }
                        else if (!response.data.layouts.length)
                        {
                            var lim = 0;
                            try{
                                lim = localStorage.getItem('ecom-popup-notice-page-limit');
                            }
                            catch (e) {}
                            if(!lim) {
                               try{
                                localStorage.setItem('ecom-popup-notice-page-limit', '1');
                               }catch(e){

                               }
                                commit('builder/setPopup',
                                {
                                    show: true,
                                    type: 'warning',
                                    loading: false,
                                    title: 'Information!',
                                    message: `Please notice that you can publish ${limit} pages/sections in ${planName} plan (for any page type). After running out of ${limit} slots, you can still create & save the others.`,
                                    closeable: true,
                                    cancel: 'Ok, got it'
                                },{ root: true});
                            }

                        }

                    }
                })
                .catch((e) => {
                    console.log(e);
                });

        },

        setSections({ commit }, payload) {
            commit('setSections', payload);
        },

        addSection({ commit }, payload) {
            commit('addSection', payload);
        },

        clearSections({ state, commit, dispatch }) {
            dispatch('history/addStack', {
                type: 'pages',
                name: '',
                action: 'clear',
                data: state.sections
            }, { root: true });

            if(state.params.page === 'menu')
            {
                state.sections = [{"columns":[{"width":100,"settings":{"width":"100%","width__mobile":"100%"}}],"hasElementRequestLiquid":false}];
            }
            else
                state.sections = [];



        },

        setQueryParams({  commit }, data) {
            const url = new URL(window.location.href);
            let params = Object.assign({},url.searchParams, data);

            url.search = new URLSearchParams(params).toString();
            window.history.pushState({}, '', url);
            commit('setQueryParams', data);
            if(window.ECOM_INIT?.APPBRIDGE)
            {
                var AppBridge = window['app-bridge'];
                var actions = AppBridge.actions;

                var History = actions.History;
                const history = History.create(window.app);

                history.dispatch(History.Action.PUSH, url.pathname + url.search);
            }
            if (data.page) {
                commit('builder/setEditingElement', null, {root: true});
                commit('global/openCategories', data.page, {root: true});
            }
        },

        setQueryParam({ state, dispatch }, payload) {
            let vars = state.params;
            if (payload.value === undefined) {
                delete vars[payload.name];
            }
            else {
                vars[payload.name] = payload.value;
            }
            dispatch('setQueryParams',vars);

        },
        fetchTrending({state, commit,dispatch}){
            if(state.layoutTrending && state.layoutTrending.length)
            {
                return false;
            }
            state.loading = true;
            this.$worker.fetch(window.route('builder.pages'), {
                metadata: {
                    paged: 1,
                    limit: 8,
                    type:'newest',
                    category:  null,
                    category_slug: ['product','collection','blog','article'].includes(state.params.page) ? state.params.page : null
                }
            }, 'POST').then((response) => {
                if(response && response.data.status == 'success')
                {
                    state.loading = false;
                    commit('layoutTrending', response.data.items.data);
                }
                else
                {
                    throw new Error(response.data.message);
                }
            })
            .catch((e) => {
                state.loading = false;
                    dispatch('global/flash',{
                    type: 'error',
                    message: e.message,
                    open: true
                },{root: true});
                return false;
            });
        },
        preview({ dispatch, getters,commit,state}, payload){


            var handle = null;
            let section_length = state.sections.length;
            if( section_length )
            {
                for(let j=0;j< section_length; j++)
                {
                    if('settings' in state.sections[j] && state.sections[j]?.settings[getters.params.page]?.value)
                    {
                        handle = state.sections[j].settings[getters.params.page].value;
                        break;
                    }
                }
            }
            let params = getters.params;
            if(handle)
            {
                params.handle = handle;
            }
            helpers.convertImgPlaceholder(payload.sections)
            .then(({images = []}) => {

                let { preview = '', output = '', css = '' } = payload;

                for(let k in {preview, output, css}) {
                    if(payload[k]) {
                        images.forEach(image => {
                            payload[k] = payload[k].replaceAll(image.src, image.placeholder);
                            payload[k] = payload[k].replaceAll(helpers.escapeHtml(image.src), image.placeholder);
                        });
                    }
                }
                this.$worker.fetch(window.route('builder.preview',params), payload,'POST')
                .then(response=>{
                    commit('builder/setPopup',
                    {
                        show: false,
                        closeable: true
                    },{ root: true}) ;
                    dispatch('global/setProgress', { show: false, percent: 100 },{ root: true});
                    if(response.data.status === 'success')
                    {
                        let layout = getters.settings.layout;
                        layout = layout ? layout : 'theme';

                        let link = response.data.link + '&layout=' + layout + (handle  ? '&handle=' + handle : '');

                        let w = window.open(link ,'ecom-preview');
                        if(!w || w.closed || typeof w.closed=='undefined')
                        {
                            alert('Pop-up Blocker is enabled! Please add this site to your exception list.');

                        }
                        else
                        {
                            w.focus();
                        }
                        if(response.data.password)
                        {
                            commit('builder/setPopup',{
                                type: 'warning',
                                title: 'Password enabled',
                                message: 'Your storefront password is enabled. Enter your password in online store then click [this link]('+link + ') again. If you already entered password once, just click link to preview next time.',
                                show: true,
                                closeable: true,
                                action:  null
                            },{ root: true});
                        }
                    }
                    else
                    {
                        throw new Error(response.data && response.data.message ? response.data.message : 'Unknow error');
                    }
                })
                .catch(e=>{
                    dispatch('global/setProgress', { show: false, percent: 100 },{ root: true});
                    commit('builder/setPopup',
                    {
                        show: true,
                        loading: false,
                        closeable: true,
                        message: e.message,
                        type: 'error',
                        title: 'Opps!'
                    },{ root: true}) ;

                });
            });
        },
        save({ dispatch, getters, commit,state}, payload){
            if(!payload.data?.settings?.published) {
                payload.data.settings.published = 'published';
            }
            let { type, data, action, autoDraft = false } = payload;

            let isTemplateDefault = getters.isTemplateDefault ;

            let params = {
                status: type,
                ...data,
                action,
                autoDraft,
                extensions: getters.allNodes.theme_extensions
            };

            helpers.convertImgPlaceholder(params.sections)
            .then(({images = []}) => {

                let { preview = '', output = '', css = '' } = params;

                for(let k in {preview, output, css}) {
                    if(params[k]) {
                        images.forEach(image => {
                            params[k] = params[k].replaceAll(image.src, image.placeholder);
                            params[k] = params[k].replaceAll(helpers.escapeHtml(image.src), image.placeholder);
                        });
                    }
                }
                let handle = getters.handle;

                if(!handle && params?.settings?.handle) {
                    handle = params.settings.handle;
                }
                this.$worker.fetch(window.route('builder.save',getters.params),{...params, handle: handle },'POST')
                .then(response => {
                    dispatch('global/setProgress', { show: false, percent: 100 },{ root: true});

                    if (response.data.status === 'success')
                    {
                        let layout = getters.settings.layout,
                            typename = ['page', 'home'].indexOf(response.data.data.type) > -1 ? 'page' : 'template';

                        layout = layout ? layout : 'theme';
                        if(['footer','header','section','quickview','ajax-cart'].includes(response.data.data.type))
                        {
                            layout = 'ecom'
                        }

                        // let section_length = state.sections.length;
                        // let page = response.data?.data?.type;
                        // if( section_length && page )
                        // {

                        //     for(let j=0;j< section_length; j++)
                        //     {
                        //         if(state.sections[j]?.settings && state.sections[j]?.settings[page]?.value)
                        //         {
                        //             handle = state.sections[j].settings[page].value;
                        //             break;
                        //         }
                        //     }
                        // }

                        commit('builder/setPopup', {
                            type: 'success',
                            title: ['public','publish'].includes(type) ? `Your ${ typename } has been ${ action ==='publish' &&(isTemplateDefault ||  response.data.data.type !== 'page' || response?.data?.data?.options?.settings?.published === 'published' ) ? (response.data.data?.status === 'draft' ? 'unpublished' : 'published') : 'saved' } successfully!` : `Your ${ typename } has been saved to draft!`,
                            message: response.data.password && type !== 'draft' && response.data.data?.status !== 'draft' ? 'Your storefront password is enabled. You may have to fill out your password and click the view button one again to Preview the '+typename : 'You can access it anytime later to edit & publish.',
                            show: true,
                            closeable: true,
                            action: type === 'public' && action === 'publish' && response.data.data?.status !== 'draft' ?  {
                                title: 'View',
                                link: window.route('builder.view',{
                                    id: response.data.data.id,
                                    page: response.data.data.type,
                                    layout,
                                    handle


                                })
                            }: null,
                        },{ root: true});
                        commit('setLayout', response.data.data);
                        dispatch('setQueryParams',{
                            id: response.data.data.id,
                            page: response.data.data.type
                        });

                        commit('setLayouts', response.data.layouts);

                        if (response.data.drafts) {
                            // console.log(response.data.drafts);
                            commit('setDrafts', response.data.drafts);
                        }
                        commit('page/setPopup', false, {root: true})

                        helpers.confirmExit(false);
                    }
                    else
                    {
                        if(response.data?.type === "plan_permision") {
                            let key = 'EComCallback' + new Date().getTime();

                            window[key] = (res,params) => {
                                if(res === 'accept') {
                                    payload.action = 'save';
                                    payload.type = 'draft';
                                    dispatch('save',payload);
                                    commit('builder/setPopup',
                                    {
                                        show: true,
                                        loading: true,
                                        closeable: false,
                                        title: 'Saving, please wait...',
                                    },{ root: true});

                                }
                            };
                            commit('builder/setPopup',{
                                type: 'error',
                                title: 'Opps!',
                                show: true,
                                message: response.data.message,
                                callback: key,
                                action: {
                                    title: 'Save to publish later'
                                },
                                closeable: true
                            },{ root: true});
                        } else {
                            throw new Error(response.data && response.data.message ? response.data.message : 'Unknow error');
                        }
                    }
                })
                .catch( (e:any)=>{
                    dispatch('global/setProgress', { show: false, percent: 100 },{ root: true});
                    const mess = e.message && e.message.includes('256') ? `Template content exceeds 256Kb limit. [Learn more](https://help.ecomposer.io/docs/faqs/what-should-you-do-when-see-the-template-content-exceeds-256kb-limit-message/)` : e.message;
                    commit('builder/setPopup',{
                        type: 'error',
                        title: 'Opps!',
                        show: true,
                        message: mess,
                        action: '',
                        closeable: true
                    },{ root: true});
                });
            });
        },

        destroyDrafts({state, dispatch, commit}, payload) {
            // state.drafts = [];
            let params = state.params;
            if(payload && params) {
                params.draftType = payload.draftType;
            }
            if(!params.id)
            {
                return false;
            }
            this.$worker.fetch(window.route('builder.drafts',{...params,_method: 'DELETE'}),null,'DELETE')
            .then(response=>{
                dispatch('global/setProgress',{
                    show: false,
                    percent: 0
                },{ root: true});
                if(response.data.status === 'success')
                {
                    commit('setDrafts', response.data.drafts);
                    dispatch('global/flash',{
                        open: true,
                        type: 'error',
                        message: 'Destroyed all drafts!'
                    },{ root: true});
                }
                else
                {
                    throw new Error(response.data.message ? response.data.message : 'Unknow error');
                }
            })
            .catch(e=>{
                dispatch('global/setProgress',{
                    show: false,
                    percent: 0
                },{ root: true});
                dispatch('global/flash',{
                    open: true,
                    type: 'error',
                    message: e.message
                },{root: true});
            });

        },

        destroyDraft({dispatch, commit},payload){
            // state.drafts = [];

            if(!payload)
            {
                return false;
            }
            this.$worker.fetch(window.route('builder.draft',{id: payload,_method: 'DELETE'}),null, 'DELETE')
            .then(response=>{
                dispatch('global/setProgress',{
                    show: false,
                    percent: 0
                },{ root: true});
                if(response.data.status === 'success')
                {
                    commit('setDrafts', response.data.drafts);
                    dispatch('global/flash',{
                        open: true,
                        type: 'success',
                        message: 'Draft destroyed!'
                    },{ root: true});
                }
                else
                {
                    throw new Error(response.data.message ? response.data.message : 'Unknow error');
                }
            })
            .catch(e=>{
                dispatch('global/setProgress',{
                    show: false,
                    percent: 0
                },{ root: true});
                dispatch('global/flash',{
                    open: true,
                    type: 'error',
                    message: e.message
                },{root: true});
            });

        },

        import({ commit,dispatch }, payload){

            if(payload.settings)
            {
                commit('setSettings', payload.settings);
            }
            if(payload.sections)
            {
                if (payload?.overwrite) {
                    commit('insertSections',{ sections: payload.sections});
                } else {
                    commit('setSections',{ data: payload.sections});
                }
            }
            if(payload.global)
            {
                dispatch('settings/appendDatasGlobal', {payload: payload.global, overwrite: true}, { root: true });
            }
            if(payload.theme)
            {
                dispatch('settings/setThemeSettings', payload.theme, { root: true });
            }
        },
        actions({ commit,dispatch,state}, payload){
            dispatch('global/setProgress', { show: true, percent: 10 },{ root: true});
            commit('builder/setPopup',
            {
                show: true,
                loading: true,
                message: 'Processing...',
                closeable: false
            },{ root: true});
            this.$worker.fetch(window.route('builder.actions',{id: payload.id}),payload, 'POST')
            .then(response=>{
                dispatch('global/setProgress', { show: false, percent: 100 },{ root: true});

                if (response.data.status === 'success')
                {
                    let act =payload.action ;
                    act =  act.charAt(0).toUpperCase() + act.slice(1)
                    dispatch('global/flash',{
                        open: true,
                        type: 'success',
                        message: act + ' successfully!'
                    },{ root: true});
                    commit('builder/setPopup',
                    {
                        show: false,
                        loading: false,
                        message: 'Processing...',
                        closeable: true
                    },{ root: true});
                    if(payload.action === 'delete' && payload.id ===  state.params.id)
                    {
                        commit(
                            'global/setProgress', { show: true, percent: 10 },{ root: true}
                        );

                        dispatch('setQueryParam',{name: 'id',value: undefined});
                        commit('setSettings', {
                            title: 'Untitled',
                            layout: 'theme',
                            custom_code: {}
                        });
                        dispatch('setSections', { data: [] });
                        dispatch('init');

                    }
                    else if(payload.action === 'rename' && payload.id ===  state.params.id)
                    {
                        state.settings.title = payload.title;
                    }
                    commit('setLayouts', response.data.layouts);
                }
                else
                {
                    throw new Error(response.data && response.data.message ? response.data.message : 'Unknow error');
                }

            })
            .catch(e=>{
                dispatch('global/setProgress', { show: false, percent: 100 },{ root: true});
                commit('builder/setPopup',{
                    type: 'error',
                    title: 'Opps!',
                    show: true,
                    message: e.message,
                    action: '',
                    closeable: true
                },{ root: true});
            });
        },
        insertExtensionElements({state,commit},payload){
            state.extension_elements = payload;
            if(payload.author || payload.logo)
            {

                commit('library/addSource',{
                    id: 'theme',
                    title: payload?.author,
                    logo: payload?.logo,
                    logo_white: payload?.logo_white,
                    icon_white: payload?.icon_white,
                    icon: payload?.icon,
                    description: payload?.description,
                    website: payload?.website,
                    name: payload?.name || 'Kalles',
                    url: payload?.url || 'https://1.envato.market/x9GM5O'
                },{root:true});
                if(payload.categories)
                {
                    commit('library/setSourceCategories',payload.categories,{ root: true});
                }
            }
            commit('builder/insertExtensionElements',payload.elements,{ root: true});
        },
        setDataGlobal({commit},payload){

                let cssGlobal = payload;
                if(cssGlobal)
                {
                    if(typeof cssGlobal.typography === 'object') {
                        for(let k in cssGlobal.typography) {
                            if(cssGlobal.typography[k]?.value?.constructor !== Object) {
                                cssGlobal.typography[k].value = {};
                            }
                        }
                    }
                }
                commit('settings/setDataGlobal',cssGlobal,{ root:true});

        },
        setGlobalUsed({commit}, payload) {
            commit('setGlobalUsed', payload);
        },

        insertEcoSystemApps( {state, commit}, payload) {
            const elms = {};
            if (payload && payload.length) {
                state.ecosystem_apps = payload;
                payload.forEach((elm)=>{
                     const key = helpers.componentName(elm.slug);
                    if(elm.options.data)
                    {
                        elms[key] =  {
                            title: elm.title,
                            icon: elm.options?.schema?.icon??'',
                            category: ['app'],
                            name: key,
                            data: {
                                title: elm.title,
                                name: key,
                                settings: {

                                }
                            }
                        }
                        if(elm.options?.schema?.types)
                        {
                            elms[key].data.settings.types = elm.options.schema.types;
                            elms[key].types = elm.options.schema.types;
                        }
                    }
                })
            }

            commit('builder/insertExtensionElements',{
                ...elms,
                extensions:{
                    title: 'Add more',
                    category: ['app'],
                    icon: '<svg xmlns="http://www.w3.org/2000/svg" class="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">\n  <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M17 14v6m-3-3h6M6 10h2a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v2a2 2 0 002 2zm10 0h2a2 2 0 002-2V6a2 2 0 00-2-2h-2a2 2 0 00-2 2v2a2 2 0 002 2zM6 20h2a2 2 0 002-2v-2a2 2 0 00-2-2H6a2 2 0 00-2 2v2a2 2 0 002 2z" />\n</svg>',
                    action: 'extensions'
                }
            },{root: true})
        },
        insertEcoSystemApp( {state, commit}, payload) {
            const elms = {};
            if (payload ) {
                state.ecosystem_apps.splice(state.ecosystem_apps.length - 1 ,0,payload);
                const key = this.$helpers.componentName(payload.slug);
                commit('builder/insertExtensionElement',{
                    name: key ,
                    value: {

                        title: payload.title,
                        icon: payload.options?.schema?.icon??'',
                        category: ['app'],
                        types: payload.options?.schema?.types??null,
                        name: key,
                        data: {
                            title: payload.title,
                            name: key,
                            settings: {

                            }
                        }

                    }

                },{root: true})

            }

        },
        uninstallEcoSystemApp({state, commit}, payload){
            state.ecosystem_apps = state.ecosystem_apps.filter(ext=>{
                return ext.slug !== payload.slug;
            });
            const key = this.$helpers.componentName(payload.slug);
            commit('builder/removeExtensionElement', key,{root: true})
        }
    }
};
