import { ROOT_STORE } from "@/store/constants";

import type { ActionTree } from "vuex";
import type { ControlsTabsItemType, RootActionsTypes, RootStateType } from "@/store/interfaces";

export const actions: ActionTree<RootStateType, RootStateType> & RootActionsTypes = {
    [ROOT_STORE.ACTIONS.SET_TAB]({ commit }, payload: ControlsTabsItemType) {
        commit(ROOT_STORE.MUTATIONS.SET_TAB, payload.id);
    },
    [ROOT_STORE.ACTIONS.UPDATE_REFRESH]({ commit }) {
        commit(ROOT_STORE.MUTATIONS.UPDATE_REFRESH);
    },
    [ROOT_STORE.ACTIONS.UPDATE_VERSION]({ commit }, payload: string) {
        commit(ROOT_STORE.MUTATIONS.UPDATE_VERSION, payload);
    }
};
