export default {
    namespaced: true,
    state: {
        progress: {
            show: true,
            percent: 10,
        },
        progress_timeout: null,
        banner: {
            show: false,
            message: '',
            timeout: 10000,
            title: '',
            type: 'error',
        },
        flash: {
            open: false,
            message: '',
            title: '',
            type: 'success',
        },
        sidebar: {
            open_categories: {}
        },
        user: null,
        configs: {
            icons: null,
            tinify: null
        },
        doc: null,
        loading: true,
    },
    getters: {
        progress(state) {
            return state.progress;
        },
        flash(state) {
            return state.flash;
        },
        sidebar(state) {
            return state.sidebar;
        },
        user(state) {
            return state.user;
        },
        configs(state) {
            return state.configs;
        },
        isDesigner(state) {
            if (state.user && state.user.roles && state.user.roles[0].permissions && state.user.roles[0].permissions.some(permission => permission.name === 'design')) {
                return true;
            } else {
                return false;
            }
        },
        configIcons(state) {
            return state.configs.icons;
        },
        getPermission(state){
            return  (key,def = null)=>{
                return state.user?.plan_permissions?.permissions?.[key] ?? def;
            };
        },
        doc(state){
            return state.doc;
        },
        loading(state){
            return state.loading;
        }
    },
    mutations: {
        setProgress(state, payload) {
            state.progress = payload;
        },
        setBanner(state, payload) {
            state.banner = payload;
        },
        flash(state, payload) {
            state.flash = payload;
        },
        setUser(state, payload) {
            state.user = payload;
        },
        setConfig(state, payload) {
            state.configs = payload;
        },
        openCategories(state, payload) {
            state.sidebar.open_categories = {};
            state.sidebar.open_categories[['page', 'home'].indexOf(payload) > -1 ? 'basic' : payload] = true;
        }
    },
    actions: {
        setLoading({state},payload){
            state.loading = payload;
        },
        setDoc({state}, payload){
            state.doc = payload;
        },
        setProgress({ commit, state }, payload) {
            clearTimeout(state.progress_timeout);
            if (!payload.show) {
                state.progress.percent = 100;
                setTimeout(() => {
                    commit('setProgress', payload);
                }, 500);
            } else {
                state.progress_timeout = setInterval(() => {
                    if (state.progress.percent >= 90 || !state.progress.show) {
                        clearTimeout(state.progress_timeout);
                    }

                    state.progress.percent += 1;
                }, 100);
                commit('setProgress', payload);
            }
        },
        flash({ commit }, payload) {
            if(!('open' in payload))
            {
                payload.open = true;
            }
            commit('flash', payload);
        },
        setBanner({ commit }, payload) {
            commit('setBanner', payload);
        },
    },
};