/**
 * ! Need define params type
 * @param obj
 * @param objs
 * @returns
 */
export const mergeObj = (obj: any, ...objs: any) => {
    const setValue = (root: any, obj: any) => {
        if (obj && typeof obj === 'object') {
            let k;
            for (k in obj) {
                if (typeof obj[k] === 'object') {
                    root[k] = root[k] ?? (Array.isArray(obj[k]) ? [] : {});
                    setValue(root[k], obj[k]);
                } else {
                    if (root[k] === undefined) {
                        root[k] = obj[k];
                    }
                }
            }
        }
    };
    objs.forEach((item: any) => {
        setValue(obj, item);
    });
    return obj;
}
