export const generateUniqueID = () => {
    return Math.floor((1 + Math.random()) * 0x10000)
            .toString(8)
            .substring(1);
}

export const markdown = (markdownText: string) => {
    if (!markdownText) {
        return '';
    }
    const regex = /(?:https?:)?(?:\/\/)?(?:[0-9A-Z-]+\.)?(?:youtu\.be\/|youtube(?:-nocookie)?\.com\S*?[^\w\s-])([\w-]{11})(?=[^\w-]|$)(?![?=&+%\w.-]*(?:['"][^<>]*>|<\/a>))[?=&+%\w.-]*/gim;

    const htmlText = markdownText
        .replace(/^### (.*$)/gim, '<h3 class="text-base font-bold text-gray-800 dark:text-dark-color">$1</h3>')
        .replace(/^## (.*$)/gim, '<h2 class="text-lg font-bold text-gray-800 dark:text-dark-color">$1</h2>')
        .replace(/^# (.*$)/gim, '<h1 class="text-xl font-bold text-gray-800 dark:text-dark-color">$1</h1>')
        .replace(/^\\> (.*$)/gim, '<blockquote>$1</blockquote>')
        .replace(/\*\*([^\*]+)\*\*/gim, '<strong class="text-gray-800 dark:text-dark-color font-medium">$1</strong>')
        .replace(/\*(.*)\*/gim, '<i>$1</i>')
        .replace(/!\[(.*?)\]\((.*?)\)/gim, '<img alt=\'$1\'  src=\'$2\' />')
        .replace(regex,`<iframe src="https://www.youtube-nocookie.com/embed/$1" class="aspect-video aspect-w-16 aspect-h-9" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allow-fullScreen></iframe>`)
        .replace(
            /\[(.*?)\]\((#.*?)\)/gim,
            `
        <a href='$2' class="ecom-markdown-link font-medium text-primary-600 underline hover:no-underline hover:text-primary-700 ecom-field-open-settings">$1
        </a>`
        )
        .replace(
            /\[(.*?)\]\((.*?)\)/gim,
            `
        <a href='$2' class="ecom-markdown-link font-medium text-primary-600 underline hover:no-underline hover:text-primary-700 break-words" target='_blank'>$1
            <svg xmlns="http://www.w3.org/2000/svg" class="inline-flex h-4 w-4" viewBox="0 0 20 20" fill="currentColor">
                <path d="M11 3a1 1 0 100 2h2.586l-6.293 6.293a1 1 0 101.414 1.414L15 6.414V9a1 1 0 102 0V4a1 1 0 00-1-1h-5z" />
                <path d="M5 5a2 2 0 00-2 2v8a2 2 0 002 2h8a2 2 0 002-2v-3a1 1 0 10-2 0v3H5V7h3a1 1 0 000-2H5z" />
            </svg>
        </a>`
        )
        .replace(/\n$/gim, '<br />');

    return htmlText.trim();
}
export const slugify = (str: string)=>{
    str = str.replace(/^\s+|\s+$/g, ''); // trim
    str = str.toLowerCase();

    // remove accents, swap  for n, etc
    var from = "/_,:;";
    var to   = "aaaaaaaaaeeeediiiiiiooooouuuuunc------";
    for (var i=0, l=from.length ; i<l ; i++) {
        str = str.replace(new RegExp(from.charAt(i), 'g'), to.charAt(i));
    }

    str = str.replace(/[^a-z0-9 -]/g, '') // remove invalid chars
        .replace(/\s+/g, '-') // collapse whitespace and replace by -
        .replace(/-+/g, '-'); // collapse dashes

    return str;
}