<template>
    <div class="bottom-0 left-0 w-full bg-white dark:bg-gray-700 dark:text-dark-color rounded-lg rounded-t-none footer-current relative">
        <div
            v-show="!isBlank && type !== 'image'"
            class="relative flex items-center h-16 px-3 text-sm border-b border-gray-300 dark:border-gray-600"
        >
            <template v-if="type == 'font'">
                <span
                    class="text-lg text-gray-800 dark:text-dark-color"
                    :style="{fontFamily: data?.[0]?.name}"
                >
                    {{ data?.[0]?.name }}
                </span>
            </template>
            <template v-else-if="type == 'product_type' || type == 'product_vendor'">
                <span class="text-gray-800 text-md">
                    {{ data?.[0]?.name }}
                </span>
            </template>
            <template v-else>
                <div v-if="type !== 'blog'" class="w-10 h-10">
                    <span
                        v-if="type === 'icon'"
                        class="text-gray-700 fill-current svg-full dark:text-dark-color"
                        v-html="data?.[0]?.thumbnail || data?.[0]?.value"
                    />
                    <img
                        v-else-if="type"
                        class="block object-cover w-full h-full border border-gray-300 rounded"
                        :src="currentImage(type, data?.[0]) ?? '/images/placeholder.png'"
                        :alt="data?.[0]?.name"
                    >
                </div>
                <div class="absolute text-sm text-gray-700 dark:text-dark-color transform -translate-y-1/2 top-1/2 right-3 left-4" :class="{'pl-12': type !== 'blog'}">
                    <p class="text-gray-500 dark:text-dark-color">
                        Currently selected
                    </p>
                    <h6 class="truncate">
                        {{ data?.[0]?.name }}
                    </h6>
                </div>
            </template>
        </div>
        <div class="text-sm py-2 flex items-center justify-end px-3.5" :class="{'hidden': type == 'font'}">
            <Button
                :text="text[0]"
                class="mr-3"
                @click="$emit('clickCancel')"
            />
            <Button
                :text="text[1]"
                :type="!isChange || data?.length <= 0 ? 'disabled' : 'primary'"
                :class="!isChange || data?.length <= 0 ? 'border-transparent' : ''"
                @click="$emit('clickSelect')"
            />
        </div>
    </div>
</template>

<script lang="ts">
    import { fileMixin } from '../../mixins';
    export default {
        name: 'PickerFooter',
        mixins: [ fileMixin ],
        props: {
            // eslint-disable-next-line vue/require-default-prop
            data: [String, Array],
            multiple: {
                type: Boolean,
                default: false
            },
            isChange: {
                type: Boolean,
                default: false
            },
            type: {
                type: String,
                default: ''
            },
            textSelect: {
                type: String,
                default: 'Select'
            }

        },
        emits: ['clickCancel', 'clickSelect'],
        computed: {
            text () {
                return ['Cancel', this.textSelect];
            },
            isBlank () {
                if (!this.data) {
                    return true;
                }
                return this.multiple
                    || !this.data.length
                    || (this.data.length == 1
                        && this.data[0].name === false
                    );
            }
        }
    };
</script>
<style>
    .footer-current:before,
    .footer-current:after {
      content: '';
      position: absolute;
      right: 0;
      left: 0;
      display: block;
      height: 0.25rem;
      pointer-events: none;
      transition: opacity 0.2s cubic-bezier(0.64, 0, 0.35, 1);
      top: -0.25rem;
    }
    /* .footer-current:before {
      background: linear-gradient(0deg, #dfe3e8 0.0625rem, transparent 0);
    } */
    .footer-current:after {
      background: linear-gradient(0deg, rgba(64, 64, 64, 0.1), transparent 0.25rem);
    }
</style>
