<template>
    <div ref="wrapper" class="fixed top-0 left-0 z-50" :class="popclass" @click="closePopup($event.target === $refs.wrapper)">
        <div class="inset-0  flex flex-col overflow-hidden rounded relative popup-container transition-all transform duration-400 opacity-0 -translate-x-8" :class="wrpclass" :style="popupSize">
            <div v-if="$slots.topbar" class="relative flex w-full ecom-popup-topbar z-2">
                <div class="relative z-30 flex flex-shrink-0 w-full h-14 font-medium text-gray-900 dark:text-dark-color" :class="{'border-b dark:border-gray-700 border-gray-200': borderTop}">
                    <slot name="topbar" />
                    <div class="absolute top-0 right-0 z-10 flex items-center h-14">
                        <ecom-button @click="closePopup(true)" data-tooltip="Close (Esc)" :icon="CancelMajor" outline class="border-0 !shadow-none !bg-transparent transition hover:rotate-180 transform"></ecom-button>
                    </div>
                </div>
            </div>
            <div v-else class="relative w-full">
                <div class="absolute top-0 right-2 z-10 flex items-center h-14">
                    <ecom-button @click="closePopup(true)" data-tooltip="Close (Esc)" :icon="CancelMajor" outline class="border-0 !shadow-none !bg-transparent transition hover:rotate-180 transform"></ecom-button>
                </div>
            </div>
            <div class="ecom-popup-list_wrapper flex relative flex-grow flex-col flex-1 border-content z-1 dark:text-dark-color dark:border-gray-600" :class="[bodyclass, height != 'auto' ? 'h-pop' : 'h-10']" @scroll="onScroll">
                <slot name="body" />
            </div>
            <div class="w-full flex ecom-popup-footer z-2" v-if="hasFooterSlot">
                <div class="relative z-30 flex flex-shrink-0 w-full rounded-b font-medium text-gray-900 bg-white dark:bg-dark dark:text-dark-color">
                    <div class="flex items-center h-14 justify-end w-full">
                        <slot name="footer" />
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
    import EcomButton from './EcomButton.vue';
    import { CancelMajor } from "@/assets/icons";
    export default {
  components: { EcomButton },
        data() {
            return {
                CancelMajor,
                itv:null
            }
        },
        props: {
            width: {
                type: [Number, String],
                default: 1400
            },
            height: {
                type: [Number, String],
                default: 0
            },
            popclass: {
                type: String,
                default: 'h-full w-full bg-gray-900 bg-opacity-30 flex justify-center items-center'
            },
            wrpclass: {
                type: String,
                default: 'items-center my-5 bg-white dark:bg-dark dark:text-dark-color'
            },
            bodyclass: {
                type: String,
                default: 'overflow-y-auto py-5 px-8 w-full'
            },
            borderTop: {
                type: Boolean,
                default: false
            },

        },
        emits: ['close','scroll-end'],

        computed: {
            hasFooterSlot(){
                return !!this.$slots['footer'];
            },
            popupSize() {
                return `width: ${typeof this.width == 'number' ? this.width + 'px' : this.width};

                    ${
                    this.height ? (
                        typeof this.height == 'number' ?
                        'height: '+this.height+'px;margin-top: calc(50vh - '+(50+(this.height/2))+'px)' :
                        (this.height == 'auto' ? 'max-height: 88vh;margin-top: -10vh;margin-bottom:-10vh;' : 'height:'+this.height)
                    ) : 'height: calc(70vh - 2.5rem)'}`;
            },
            tokenClose() {
                return this.popup.tokenClose;
            },
            popup(){
                return this.$store.getters['builder/popup'];
            }
        },

        watch: {
            // tokenClose() {
            //     this.closePopup(true);
            // }
        },
        methods: {
            onScroll(e){
                let target = e.target;
                if(target.offsetHeight+target.scrollTop + 50 >= target.scrollHeight){
                    clearTimeout(this.itv);
                    this.itv = setTimeout(()=>{
                        this.$emit('scroll-end');
                    },400);
                }
            },
            closePopup (e) {
                if (e) {
                    this.$emit('close', e);
                }
            },
        },
        mounted() {
            setTimeout(() => {
                const wrapper = this.$refs.wrapper;
                if(wrapper) {
                const container = wrapper.querySelector('.popup-container');
                if(container) {
                    container.classList.remove('-translate-x-8', 'opacity-0');
                    container.classList.add('translate-x-0', 'opacity-100');
                }
                }
            }, 10);

        },
    };
</script>
