import { mapGetters } from 'vuex';
export default {
    inject: ['exporting'],
    components: {
        Liquid: {
            inject: ['exporting'],
            props: {
                data: {
                    type: String,
                    default: ''
                }
            },
            template: `{{ data && exporting === true ? 'liquid[['+encodeURIComponent(data)+']]' : '' }}`
        }
    },
    data() {
        return {
            currentEid: ''
        }
    },
    computed: {
        edata() {
            return this.data;
        },
        liquidData() {
            return this.getLiquids[this.edata.id];
        },
        reactiveLiquidRaw() {
            let rawdata = '',
                raws = {};
            if (!this?.data?.id) {
                return '';
            }
            if (this.liquids) {
                let isEmpty = true;
                Object.keys(this.liquids).forEach((lq) => {
                    if (this.liquids[lq].code && this.liquids[lq].code !== '') {
                        isEmpty = false;
                    }
                    rawdata += `<ecom-embed-start id="${this.edata.id}" name="${lq}" />`;
                    rawdata += `${this.liquids[lq].code}`;
                    rawdata += `<ecom-embed-end id="${this.edata.id}" name="${lq}" />`;
                    raws[lq] = this.liquids[lq].code;
                });
                return {
                    before: this.shopifyWrapper?.liquidBefore ?? '',
                    after: this.shopifyWrapper?.liquidAfter ?? '',
                    raw: isEmpty ? '' : rawdata,
                    raws: isEmpty ? {} : raws
                };
            }
            return '';
        },
        shopifyWrapper() {
            let get = (o) => {
                return o ? (o.shopify_type ? o : get(o.$parent)) : null;
            };
            return get(this);
        },
        ...mapGetters('builder', ['getLiquids', 'stacks'])
    },
    methods: {
        parent(is) {
            let get = (o) => {
                return o ? (o.is == is ? o : get(o.$parent)) : null;
            };
            return get(this);
        },
        liquid(name) {
            if (this.exporting === true) {
                if(this.data.id !== this.currentEid) {
                    this.reactiveLiquid();
                }
                return 'liquid[['+encodeURIComponent((this.liquidData?.raws??{})[name] ?? '')+']]';
            }
            return (this.liquidData?.content??{})[name] ?? (this?.liquids?.[name]?.preview ?? '');
        },
        reactiveLiquid() {

            let val = this.reactiveLiquidRaw;

            if (
                !this.liquids
            ) {
                return;
            }

            let id = this.data.id;

            if (!this.getLiquids[id]) {
                this.getLiquids[id] = {};
            }

            let myLiquid = this.getLiquids[id];

            if (this.exporting !== false) {
                myLiquid.raws = val.raws;
                this.currentEid = id;
                return;
            }

            if (!myLiquid.content) {
                myLiquid.content = {};
                Object.keys(this.liquids).forEach((l) => {
                    // Use preview if the liquid content of name is not loaded
                    if (!myLiquid.content[l]) {
                        myLiquid.content[l] = this.liquids[l].preview;
                    }
                });
            }

            if (
                val === '' || !val.raw ||
                (
                    myLiquid.raw === val.raw &&
                    myLiquid.before === val.before &&
                    myLiquid.after === val.after
                )
            ) {
                return;
            }

            myLiquid.raw = val.raw;
            myLiquid.raws = val.raws;
            myLiquid.before = val.before;
            myLiquid.after = val.after;

            this.shopifyWrapper.processLiquid(this.edata);

        }
    },
    watch: {
        reactiveLiquidRaw() {
            this.reactiveLiquid();
        }
    },
    mounted() {

        if (this.exporting !== false) {
            return;
        }
        if (!this.edata.id) {
            this.edata.id = this.$helpers.randid();
        }

        if (
            this.requestShopifyType &&
            !this.shopifyWrapper.acceptRequest(this.requestShopifyType)
        ) {
            this.$store.commit(
                'global/flash', {
                    open: true,
                    message: 'This element type cannot be dropped into a '+this.shopifyWrapper.shopify_type+' section. Please drop it to another section.',
                    type: 'error',
                }, { root: true }
            );
            return setTimeout(() => {
                this.$parent.dropOut();
            }, 10);
        }
        this.reactiveLiquid();
    }
};
