export default {
    namespaced: true,
    state: {
        presets: {
            library: {},
            my_presets: {}
        },
        loading: {
            data: false,
            add: false
        },
        optionsAccept: [ 'advanced', 'settings', 'style', 'global'],
        actived_element: null
    },
    getters: {
        loading(state) {
            return state.loading;
        },
       
        presets(state) {
            let { type, category } = state.current;
            return state.presets[type]?.[category] ?? {};
        },
        optionsAccept(state){
            return state.optionsAccept;
        },
        isLoadmore(state, getters) {
            return getters.presets.page !== null &&
                    getters.presets.page !== undefined;
        }
    },
    mutations: {
        setCurrent(state, payload) {
            state.current = payload;
        },
      
        setLoading(state, {type, value}){
            state.loading[type] = value;
        },
        setPresets(state, {page, items = []}) {
            let { type, category } = state.current;
            state.presets[type] = state.presets[type] ?? {};
            state.presets[type][category] = state.presets[type][category] ?? {};
            state.presets[type][category]['items'] = state.presets[type][category]['items'] ?? [];
            state.presets[type][category]['page'] = page;
             state.presets[type][category]['items'] = items;
            // itemsPreset.splice(itemsPreset.length, 0, ...items);
            // itemsPreset = items;
            
        },
        addNewPreset(state, payload) {
            let {type, category} = state.current;
            if(type && category) {
                state.presets[type][category] = state.presets[type][category] ?? {};
                let stateCate = state.presets[type]?.[category];
                stateCate.page = stateCate.page ?? null;
                stateCate.items = stateCate.items ?? [];
                stateCate.items.splice(0, 0 , payload);
            }
        },
        deletePreset(state, id) {
            let {type, category} = state.current;
            let items = state.presets?.[type]?.[category]?.items;
            if(items) {
                let i = items.findIndex(x => x.id === id);
                if(i > -1) {
                    items.splice(i, 1);
                }
            }
        }
    },
    actions: {
        setCurrent({commit, dispatch}, payload){
            commit('setCurrent', payload);
            // dispatch('getPreset');
        },
        async getPreset({dispatch, getters, commit, state}, loadmore = false){
            // commit(
            //     'global/setProgress', { show: true, percent: 10 }, { root: true }
            // );
            let dataPreset = getters.presets ?? {};
            if((dataPreset.items || dataPreset.page === null) && !loadmore) {
                return ;
            }
            commit('setLoading', {type: 'data', value: true});
            let { page } = dataPreset;
            let { type, category } = state.current;

         
            return this.$worker.fetch( window.route(type === 'library' ? 'builder.presets' : 'builder.my_templates'),{
                metadata: {
                    paged: page,
                    limit:20,
                    category
                }
            },'POST')
            .then(res => {
                if(res.data.status === 'success') {
                    let itemsData = res.data.items,
                        { current_page, last_page, data : items = [] } = itemsData,
                        page  = current_page < last_page ? (current_page + 1) : null;
                    commit('setPresets', { page, items });
                }
                else {
                    commit('setPresets', { page: null, items: [] });
                }
            })
            .catch(e => {
                dispatch('global/flash',{
                    type: 'error', 
                    message: e.message,
                    open: true
                },{ root: true});
            })
            .then(() => {
                commit('setLoading', {type: 'data', value: false});
            });  
        },
        createPreset({ commit, dispatch, getters }, payload) {
            commit('setLoading', {type: 'add', value: true});
            commit(
                'global/setProgress', { show: true, percent: 10 }, { root: true }
            );
            payload.data = this.$helpers.validObject(payload.data, getters.optionsAccept);
                this.$worker
                    .fetch(window.route('builder.template.create'),payload,'POST')
                .then((response) => {
                    if (response.data.status === 'success') {
                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                        dispatch('global/flash',{
                            type: 'success', 
                            message: response.data.message,
                            open: true
                        },{ root: true});
                        commit('addNewPreset', response.data.data);
                        commit('setLoading', {type: 'add', value: false});
                    }
                    else {
                        throw new Error(response.data.message);
                    }
                })
                .catch((e) => {
                    console.log(e);
                    commit(
                        'global/setProgress', { show: false, percent: 100 }, { root: true }
                    );
                    dispatch('global/flash',{
                        type: 'error', 
                        message: e.message,
                        open: true
                    },{ root: true});
                    commit('setLoading', {type: 'add', value: false});

            });        
        },
        updateTitle({commit, getters, dispatch}, { id, formData }){
            let itemEdit = (() => {
                let items = getters.presets.items;
                if(items){
                    return items.filter(x => x.id === id)[0];
                }
                return null;
            })();
            if(!id || !itemEdit) {
                dispatch('global/flash',{
                    type: 'error', 
                    message: 'ID may not be blank or invalid',
                    open: true
                },{ root: true});
                return;
            }
            commit(
                'global/setProgress', { show: false, percent: 10 }, { root: true }
            );
            itemEdit.editingTitle = true;
            this.$worker
                .fetch(window.route('builder.template',id), formData,'POST')
                .then((response) => {
                    if (response.data.code === 200) {
                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                        dispatch('global/flash',{
                            type: 'success', 
                            message: 'The preset name has been updated successfull!',
                            open: true
                        },{ root: true});
                        itemEdit.editingTitle = false;
                    }else{
                        throw new Error(response.data.message ? response.data.message : 'Unknow error');
                    }
                })
                .catch((e) => {
                    console.log(e);
                    commit(
                        'global/setProgress', { show: false, percent: 100 }, { root: true }
                    );
                    dispatch('global/flash',{
                        type: 'error', 
                        message: e.message,
                        open: true
                    },{ root: true});
                });
        },
      
        deletePreset({commit, dispatch, getters }, id = ''){
            let itemDelete = (() => {
                let items = getters.presets.items;
                if(items){
                    return items.filter(x => x.id === id)[0];
                }
                return null;
            })();
            if(!id || !itemDelete) {
                dispatch('global/flash',{
                    type: 'error', 
                    message: 'ID may not be blank or invalid',
                    open: true
                },{ root: true});
                return;
            }
            commit(
                'global/setProgress', { show: true, percent: 10 }, { root: true }
            );
            itemDelete.deletting = true;
            this.$worker
                .fetch(window.route('builder.template.delete',{ template: id }), {}, 'DELETE')
                .then((response) => {
                    if (response.data.status === 'success') {
                        commit(
                            'global/setProgress', { show: false, percent: 100 }, { root: true }
                        );
                        dispatch('global/flash',{
                            type: 'success', 
                            message: response.data.message,
                            open: true
                        },{ root: true});
                        commit('deletePreset', id);
                    }
                })
                .catch((e) => {
                    console.log(e);
                    commit(
                        'global/setProgress', { show: false, percent: 100 }, { root: true }
                    );
                    dispatch('global/flash',{
                        type: 'error', 
                        message: e.message,
                        open: true
                    },{ root: true});

            });  
        }

    },
};