import { mapGetters } from 'vuex';

export default {
    computed: {
        ...mapGetters('builder', ['screen']),
    },
    methods: {
        fieldName(field) {
            let name = field.name,
                responsive = field?.options?.responsive;
            let rs = name;
            if (responsive &&
                    (field.type !== 'picker' ||
                    (field.type === 'picker' &&
                    field.options?.type !== 'image'))
                ) {
                if (this.screen !== 'desktop') {
                    rs += '__' + this.screen;
                }
            }
            return rs;
        }
    }
};
