//eslint-
import { nextTick } from 'vue'
import { createI18n } from 'vue-i18n'
const locales = import.meta.globEager('../../locales/*.json');
const langs = {};
Object.keys(locales).forEach(key => {
    const locale = key.split('/').pop().split('.')[0]
    langs[locale] = locales[key].default;
});
import type { I18n, I18nOptions, Locale, VueI18n, Composer,  } from 'vue-i18n'

export const SUPPORT_LOCALES = window.ECOM_INIT?.locales || [];

export function getLocale(i18n: I18n): string {
  return i18n.mode === 'legacy'
    ? (i18n.global as unknown as VueI18n).locale
    : (i18n.global as unknown as Composer).locale.value
}

export function setLocale(i18n: I18n, locale: Locale): void {
  if (i18n.mode === 'legacy') {
    ;(i18n.global as unknown as VueI18n).locale = locale
  } else {
    ;(i18n.global as unknown as Composer).locale.value = locale
  }
}

export function setupI18n(options: I18nOptions = { locale: 'en' }): I18n {
    console.log(new Date().getTime(), 'setupI18n')  ;
  const i18n = createI18n(options)
  setI18nLanguage(i18n, options.locale!)
  return i18n;
}

export function setI18nLanguage(i18n: I18n, locale: Locale): void {
  setLocale(i18n, locale)
 
  document.querySelector('html')!.setAttribute('lang', locale)
  let dir = 'ltr';
  if(['ar','arc','dv','fa','ha','he','khw','ks','ku','ps','ur'].includes(locale))
  {
    dir = 'rtl';
  }
  document.querySelector('html')!.setAttribute('dir', dir)
}

// eslint-disable-next-line @typescript-eslint/no-explicit-any
const getResourceMessages = (r: any) => r.default || r

export   function loadLocaleMessages(i18n: I18n, locale: Locale) {

//   const messages = await import(`../../locales/${locale}.json`).then(getResourceMessages)
    const messages =langs[locale];
   
  // set locale and locale message
  i18n.global.setLocaleMessage(locale, messages)

  return nextTick()
}

var currentLocale = window.ECOM_INIT?.locale || 'en';
 
if (!SUPPORT_LOCALES.includes(currentLocale)) {
    currentLocale = 'en';
  }


export const i18n = setupI18n({
  legacy: true,
  locale:  currentLocale,
  globalInjection: true,
  fallbackLocale: 'en',

})
loadLocaleMessages(i18n, currentLocale)

  // set i18n language
setI18nLanguage(i18n, currentLocale);
export const $t =  i18n.global.t;