import { store } from '@/store';
import background from './background.js';
import transform from './transform.js';
import responsive from './responsive.js';
import position from './position.js';
export default function(el) {

    let name = el.$options?.name ?? '',
        iscol = (name == 'Column'),
        selector = '';

    if (iscol) {
        let secid = el?._section?.data?.id ?? '';
        selector = `html body .ecom-core.${secid} root>div.core__column--wrapper`;
    }


    const isDev = ['admin','manager','designer','supporter', 'tester'].includes(store.getters['global/user']?.roleName??'');
    const scrollingAnimation = {
        group_title: 'Scrolling Animation',
        params: [
            {
                type: "popup",
                label: "Horizontal position",
                name: "scolling_horizontal",
                options: {
                    type: "scrolling-animation-px",
                    reset: true
                }
            },
            {
                type: "popup",
                label: "Vertical position",
                name: "scolling_vertical",
                options: {
                    type: "scrolling-animation-px",
                    reset: true
                }
            },
            {
                type: "popup",
                label: "Rotate",
                name: "scrolling_rotate",
                options: {
                    type: "scrolling-animation-deg",
                    reset: true
                }
            },
            {
                type: "popup",
                label: "Opacity",
                name: "scrolling_opacity",
                options: {
                    type: "scrolling-animation-opacity",
                    reset: true
                }
            },
            {
                type: "popup",
                label: "Scale",
                name: "scrolling_scale",
                options: {
                    type: "scrolling-animation-scale",
                    reset: true
                }
            },
            {
                type: "popup",
                label: "Blur",
                name: "scrolling_blur",
                options: {
                    type: "scrolling-animation-blur",
                    reset: true
                }
            },
            {
                type: 'line'
            },
            {
                name: 'animation_duration',
                label: 'Animation Duration',
                type: 'number',
                value: 100,
                options: {
                    reset: true,
                    slider: true,
                    units: {
                        ms: {
                            min: 0,
                            max: 10000,
                            step: 100
                        }
                    }
                },
                css: {
                    properties: {
                        'animation-duration': ""
                    },
                    important: true
                }
            },
            {
                name: "timing",
                label: "Timing function",
                type: "popup",
                options: {
                    type: "dropdown",
                    default: false,
                    values: {
                        "ease": "Ease",
                        "ease-in": "Ease-in",
                        "ease-out": "Ease-out",
                        "ease-in-out": "Ease-in-out",
                        "linear": "Linear",
                        "step-start": "Step-start",
                        "step-end": "Step-end"
                    },
                    reset: true
                },
                css: {
                    properties: {
                        'transition-timing-function': ""
                    }
                }
            }
        ],
    };

    return [{
            group_title: 'Advanced',
            selector: selector,
            reset: true,
            params: [
                {
                    name: 'spacing',
                    type: 'dimension',
                    label: 'Spacing',
                    options: {
                        responsive: true,
                        units: {
                            margin: {
                                px: {
                                    min: -1000,
                                    max: 1000
                                },
                                '%': {
                                    min: -100,
                                    max: 100
                                },
                                em: {
                                    min: -100,
                                    max: 100
                                },
                                rem: {
                                    min: -100,
                                    max: 100
                                }
                            },
                            padding: {
                                px: {
                                    min: 0,
                                    max: 1000
                                },
                                '%': {
                                    min: 0,
                                    max: 100
                                },
                                em: {
                                    min: 0,
                                    max: 100
                                },
                                rem: {
                                    min: 0,
                                    max: 100
                                }
                            }

                        }
                    },
                    css: {
                        important: true
                    }
                },
                ...position(['Section', 'Row', 'Column'].indexOf(name) === -1),
                ...transform
            ],
            css: true
        },
        ['Section', 'Row', 'Column'].indexOf(name) === -1 ? {
            group_title: 'Background',
            selector: selector,
            reset: true,
            params: background,
            css: true
        } : null,
        {
            group_title: 'Visibility',
            params: responsive({
                reverse: name === 'Section' ||
                    name === 'Row',
                screens: store.getters['builder/screens']
            }),
            css: true
        },
        {
            group_title: 'Entrance Animation',
            params: [
                {
                    type: "popup",
                    label: "Animation Type",
                    name: "animation_type",
                    options: {
                        type: "dropdown",
                        preview: 'title',
                        values: "entrance-animation",
                        size: "small",
                        'icon_type': "animation"
                    },
                    css: {
                        selector: ".ecom-animated",
                        properties: {
                            'entrance-animation': ""
                        }
                    }
                },
                {
                    name: 'animation_duration',
                    label: 'Animation Duration',
                    type: 'number',
                    value: 0,
                    options: {
                        slider: true,
                        units: {
                            ms: {
                                min: 0,
                                max: 10000,
                                step: 100
                            }
                        }
                    },
                    css: {
                        selector: ".ecom-animated",
                        properties: {
                            'animation-duration': ""
                        },
                        important: true
                    }
                },
                {
                    name: 'animation_delay',
                    label: 'Animation Delay <span class="lowercase">(ms)</span>',
                    type: 'number',
                    value: 0,
                    options: {
                        slider: true,
                        units: {
                            ms: {
                                min: 0,
                                max: 10000,
                                step: 100
                            }
                        }
                    },
                    css: {
                        selector: ".ecom-animated",
                        properties: {
                            'animation-delay': "%value%",
                            'transition-delay': "%value%",
                            'transition-property': 'opacity'
                        }
                    }
                },
                {
                    type: 'toggle',
                    label: 'Loop',
                    name: 'loop',
                    value: 'unset',
                    options: {
                        values:{
                            on:{
                                label: 'Yes',
                                value: 'infinite'
                            },
                            off: {
                                label: 'No',
                                value: 'unset'
                            }
                        }
                    },
                    css: {
                        selector: ".ecom-animated",
                        properties: {
                            'animation-iteration-count': ''
                        }
                    }
                }
            ],
        },
        scrollingAnimation,
        {
            group_title: 'Attributes',
            selector: selector,
            params: [{
                name: 'id',
                type: 'text',
                label: '<span class="inline-block w-24">CSS ID</span>',
                options: {
                    placeholder: 'Unique ID'
                },
                css: { isCss: false }
            },
            {
                name: 'classes',
                type: 'text',
                label: '<span class="inline-block w-24">CSS Classes</span>',
                css: { isCss: false }
            },
            {
                name: 'attributes',
                label: 'Custom HTML Attributes',
                type: 'textarea',
                description: 'Add custom HTML attributes to the element',
                options: {
                    placeholder: `attribute:value \n attribute-name:value2`,
                    height: 120,
                    update: 'onchange'
                },
                css: { isCss: false }
            }, {
                type: 'textarea',
                name: 'custom-css',
                label: 'Custom CSS for this element',
                description: 'Use "&" as root selector',
                options: {
                    height: 220,
                    update: 'onchange',
                    placeholder: `& {\n // css for the element itself\n}\nselector {\n // css for the children inside\n}`,
                },
                css: {
                    isCss: true
                }
            }],

        }
    ].filter(x => x);
}
