import { mapGetters } from 'vuex';
export default {
    computed: {
        ...mapGetters('builder', ['screens', 'screen']),
        screensASC() {
            return Object.entries(this.screens).sort(([, b1], [, b2]) => 
                b1.width - b2.width
            );
        }
    },
    methods: {
        nameScreen(screen) {
            screen = screen ?? this.screen;
            return screen === 'desktop' ?
                    '' :
                    ('__' + screen);
        },
        currentImage(type, item, isThumbnail = true) {
            if(!item) {
                return null;
            }
            let k = isThumbnail ? 'thumbnail' : 'value';
            if(type === 'image') {
                let preview = item[k + this.nameScreen()] || item['value' + this.nameScreen()];
                if(!preview) {
                    let i = this.screensASC.findIndex(s => s[0] === this.screen);
                    for(i + 1; i < this.screensASC.length; i++){
                        let s = this.screensASC[i];
                        let p = item[k + this.nameScreen(s[1].name)] || item['value' + this.nameScreen(s[1].name)];
                        if(p) {
                            preview = p;
                            break;
                        }
                    }
                }
                return preview;
            }
            return item[k];
        },
        
    }
};