import helpers from "@/utils/helpers";
export default {
    namespaced: true,
    state: {
        show_revision: false,
        stack: [],
        cursor: 0,
        limit: 50,
        timmer: 0,
        section_current: null
    },
    getters: {
        showRevision(state){
            return state.show_revision;
        },
        stack(state) {
            return state.stack;
        },
        cursor(state) {
            return state.cursor;
        },
        canRedo(state) {
            return state.stack[state.cursor]?.redo?.key || state.stack[state.cursor + 1]?.redo?.key
        },
        canUndo(state) {
            return state.cursor > 0;
        },
        current(state) {
            return state.stack?.[state.cursor] ?? null;
        },
        section_current(state) {
            return state.section_current;
        },
    },
    mutations: {
        undo(state) {
            state.cursor--;
        },
        redo(state) {
            state.cursor++;
        },
        stacks(state, payload) {
            state.stack = payload;
        },
        cursor(state, payload) {
            state.cursor = payload;
        },
        stack(state, payload) {

            let stack = state.stack.forEach((s:any) => {
                //Update section same ID
                if(s.section_id == payload.section_id) {
                    s.section_id = payload.new_id;
                }
                //Update section from same ID case Move
                if(s?.section_from == payload.section_id) {
                    s.section_from = payload.new_id;
                }
                //Update redo section on stack
                if(s?.redo?.section_id == payload.section_id) {
                    s.redo.section_id = payload.new_id;
                }
            })
            state.stack == stack;
        },
        section_current(state, payload) {
            state.section_current = payload;
        }
    },
    actions: {
        showRevision({state, rootGetters}){
            const popup = rootGetters['builder/popup'];
            if(popup?.show) {
                setTimeout(() => {
                    state.show_revision  = true;
                }, 1500)
            } else {
                state.show_revision  = true;
            }

        },
        hideRevision({state}){
            state.show_revision  = false;
        },

        history({ commit, getters, rootGetters, state, dispatch }, payload) {
            if (getters.canUndo || getters.canRedo) {

                if(payload.action == 'undo') {
                    commit("undo");
                }

                let stacks = getters.stack,
                            cursor = getters.cursor,
                            stack = stacks[cursor];

                if(stack) {
                    const key = payload.action == 'redo' ? stack.redo.key : stack.key,
                        section_id =  payload.action == 'redo' ? stack.redo.section_id : stack.section_id;
                    var section = null,sessionData =  null ;
                    try{
                       let sessionData = null;
                       try{
                        sessionData =  window.sessionStorage[key]
                       }catch(e){

                       }
                       if(sessionData) {
                            section = JSON.parse(sessionData);
                        }
                    }
                    catch(error){
                        console.error(error)
                    }


                    const sections = rootGetters["page/sections"];
                    if(!section) return;

                    if(section.type == 'pages') { // Case clear all
                        dispatch("addStack",
                            {
                                data: [],
                                action: 'redo',
                                type: 'pages',
                                undo: true
                            }
                        );
                        commit('page/setSections', {data:section.data, keepId: true}, {root: true});
                    } else if(section.type == 'stack') {
                        const builderStack = rootGetters['builder/stacks'];
                        if(builderStack && builderStack.hasOwnProperty(section_id)) {
                            const current_stack = builderStack[section_id];
                            if(payload.action == 'undo' && !section.undo && !stack.hasOwnProperty('undo')) {
                                const redo_payload = {
                                    data: current_stack.data[section.name],
                                    section_id: section_id,
                                    name: section.name,
                                    undo: true,
                                    type: section?.type
                                }
                                dispatch("addStack",redo_payload);
                            }

                            current_stack.data[section.name] = section.data;
                            const new_id = helpers.randid();
                            current_stack.data.id = new_id;
                            commit('stack',
                                {
                                    section_id: stack.section_id,
                                    new_id: new_id
                                }
                            );
                        }
                    } else {
                        let undoSection = sections.findIndex( (s:any) => s.id == section_id);
                        //If section, insert section to Sections stack
                        if(section.type === 'section' && undoSection == -1) {
                            if(section.action === 'delete') {
                                sections.splice(section.index, 0, section.data);
                                if(stacks.length - 1 == cursor && !section.undo) {
                                    dispatch("addStack",
                                        {
                                            data: section,
                                            index: section.index,
                                            action: 'add',
                                            type: 'section',
                                            undo: true
                                        }
                                    );
                                }
                            } else if(section.action === 'add') {
                                sections.splice(section.index, 1);
                                if(stacks.length - 1 == cursor && !section.undo) {
                                    dispatch("addStack",
                                        {
                                            data: section,
                                            index: section.index,
                                            action: 'delete',
                                            type: 'section',
                                            undo: true
                                        }
                                    );
                                }
                            }

                        } else if(section.type !== 'section' && undoSection > -1) {
                            //If undo, add current setion to redo stack
                            if(payload.action == 'undo' && !section.undo && !stack.hasOwnProperty('undo')) {
                                const redo_payload = {
                                    data: sections[undoSection],
                                    section_id: section_id,
                                    undo: true,
                                    type: section?.type
                                }
                                //Move case
                                if(section.type == 'block' && stack.section_from && section.section_from_data) {
                                    let section_from = sections.findIndex( (s:any) => s.id == stack.section_from);
                                    if(section_from > -1) {
                                        redo_payload.section_from = stack.section_from;
                                        redo_payload.section_from_data = sections[section_from];
                                    }
                                }
                                dispatch("addStack",redo_payload);
                            }

                            //Move case
                            if(section.type == 'block' && stack.section_from && section.section_from_data) {
                                let section_from = sections.findIndex( (s:any) => s.id == stack.section_from);
                                if(section_from > -1) {
                                    sections[section_from] = section.section_from_data;
                                    const from_new_id = helpers.randid();
                                    sections[section_from].id = from_new_id;
                                    commit('stack',
                                        {
                                            section_id: stack.section_from,
                                            new_id: from_new_id,
                                        }
                                    );
                                }
                            }
                            sections[undoSection] = section.data;
                            const new_id = helpers.randid();
                            sections[undoSection].id = new_id;

                            commit('stack',
                                {
                                    section_id: stack.section_id,
                                    new_id: new_id
                                }
                            );
                        }
                    }

                    if(payload.action == 'redo') {
                        commit("redo");
                    }

                    commit("builder/setEditingElement", null, { root: true });
                    state.timmer = new Date().getTime();
                }

            }
        },

        addStack({state, getters, commit}, payload) {
            if (state.timmer > new Date().getTime() - 1000) {
                return;
            }
            const key = helpers.randid();

            payload.created = new Date().toString();

            let stack = {
                key,
                section_id: payload?.section_id
            },
            cursor = getters.cursor,
            stacks = getters.stack;

            if(!payload?.undo) {
                stacks.splice(state.cursor)
            }

            //If move element, take Section ID from.
            if(payload?.section_from) {
                stack.section_from = payload?.section_from;
            }

            //If undo case, add new property: redo to current stack cursor
            if(payload?.undo) {
                let redo_stack = stacks[cursor];
                if(redo_stack) {
                    redo_stack.redo = {
                        key: key,
                        section_id: payload.section_id,
                        section_from: payload?.section_from
                    }
                }
            } else {
                stacks.push(stack);
            }

            if(!payload?.undo) {
                cursor = stacks.length;
            }



            //Delete stack if > 30
            if(stacks.length > 30) {
                const elDelete = stacks.shift();
                if(elDelete) {
                    try{
                        window.sessionStorage.removeItem(elDelete.key);
                    }
                    catch(e) {
                        console.error(e);
                    }
                }
            }
            //Delete if
            commit('stacks', stacks);
            commit('cursor', cursor);

            try{
                window.sessionStorage.setItem(key, JSON.stringify(payload));
            }
            catch(e) {
                console.error(e);
            }

            helpers.confirmExit(true);

        }
    }
};
