<template>
    <li
        v-detectVisible="detectVisible"
        class="relative p-2 transition cursor-pointer select-none item"
        :class="classItem(item)"
    >
        <div class="relative">
            <div v-if="show">
                <div class="flex items-center group h-28">
                    <div
                        class="flex items-center w-full h-full relative ecom-cat-free transition-all overflow-hidden rounded"
                        :class="{ 'opacity-30': item.loading }"
                        @click="$emit('loadFreeImg', item)"
                    >
                        <img
                            class="block object-cover w-full h-full rounded-sm"
                            :src="item.thumbnail ?? '/images/placeholder.png'"
                            :alt="item.title"
                        />
                        <div
                            class="absolute top-1/2 transform -translate-y-1/2 w-full text-center z-10"
                        >
                            <span
                                class="text-2xl font-semibold text-center text-white"
                                v-html="item.title"
                            />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </li>
</template>

<script lang="ts">
import Toggle from "../../fields/toggle/Toggle.vue";
import { detectVisible } from "../../directives";
import { fileMixin } from "../../mixins";
import { Popmenu } from "@/mixins";
// import { faGear, faUpload, faCheck } from "@fortawesome/pro-light-svg-icons";
export default {
    name: "ItemCategory",
    directives: {
        detectVisible
    },
    components: {
        Toggle
    },
    mixins: [Popmenu, fileMixin],
    props: {
        type: {
            type: String,
            default: "menu"
        },
        value: {
            type: Array,
            default() {
                return [];
            }
        },
        compress: {
            type: Boolean,
            default: false
        },
        item: {
            type: Object,
            default() {
                return {};
            }
        },
        layout: {
            type: String,
            default: ""
        }
    },
    emits: ["loadFreeImg"],

    data() {
        return {
            show: false,
            info: {},
            // icons: { faGear, faUpload, faCheck }
        };
    },

    computed: {
        hasReload() {
            return this.item.reload;
        },
        showThumb() {
            return this.type === "icon" || this.layout !== "list";
        }
    },
    watch: {
        hasReload(val) {
            if (val) {
                setTimeout(() => {
                    this.$emit("reload");
                }, 1000);
            }
        }
    },
    methods: {
        classItem(item) {
            let cl = "";

            cl += "px-4 w-1/2 bg-white dark:bg-gray-700 dark:text-dark-color";
            if (item.fn === "item") {
                cl += "py-2";
            } else {
                cl += "py-2";
            }
            if (!item.reload) {
                cl += " cursor-pointer";
            }

            if (item.reload) {
                cl += " cursor-pointer";
            }
            return cl;
        },
        detectVisible() {
            this.show = true;
        }
    }
};
</script>
<style>
.bg-img-default {
    background-position: 0 0, 0.6rem 0.6rem;
    background-size: 1.2rem 1.2rem;
    background-image: linear-gradient(
            45deg,
            #dfe3e8 25%,
            transparent 0,
            transparent 75%,
            #dfe3e8 0,
            #dfe3e8
        ),
        linear-gradient(45deg, #dfe3e8 25%, #fff 0, #fff 75%, #dfe3e8 0, #dfe3e8);
    box-shadow: inset 0 0 0 0 transparent, 0 0 0 1px #c4cdd5;
}
.dark .bg-img-default {
    box-shadow: inset 0 0 0 0 transparent, 0 0 0 1px #4c4e50;
}
.ecom-cat-free::after {
    content: "";
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 1;
}
.ecom-cat-free:hover::after {
    opacity: 0;
}
</style>
