<template>
    <div>
        <div v-if="hasGroupTitle(group)" class="relative transition-all pl-2">
            <button v-if="group.params.length > 0" type="button"
                class="!outline-none relative flex items-center justify-between transition-all w-full text-sm font-semibold text-gray-900 dark:text-dark-color focus:outline-none before:content-[''] before:mr-0.5 before:rounded-md before:w-1.5 before:absolute before:-left-2.5 before:h-full"
                :class="[
                    $parent.activeGroup === index ? 'before:border-r-3 before:border-primary-600 bg-primary-100 dark:bg-primary-600 dark:before:border-primary-600 '+(deep === 0 ? 'rounded' : '') : (deep === 0 ? 'rounded' : ''),
                    $parent.isSettings ? 'my-1' : 'py-2 mb-1 bg-gray-100 dark:bg-gray-700 hover:bg-gray-200 dark:hover:bg-gray-700',
                    $parent.isSettings ? 'pl-0 !bg-transparent before:!hidden' : ''
                ]" @click="$parent.activeGroup = ($parent.activeGroup != index ? index : null)">
                <div class="flex items-center justify-start content-start w-full" :class="$parent.activeGroup === index ? 'dark:text-white' : ''">
                    
                    <span v-if="!$parent.isSettings" class="ml-1 py-1.5 -my-2 rounded transition mr-1">
                        <Icon :source="icons.CaretDownMinor"
                            class="flex justify-center items-center transform origin-center text-gray-500 dark:text-dark-color"
                            :class="$parent.activeGroup === index ? 'rotate-0' : '-rotate-90'"></Icon>
                    </span>
                    <span v-if="group.icon" class="mr-3 " v-html="group.icon" />
                    <span class="uppfirst line-clamp-1 flex-1 text-left">{{ group.group_title || 'Untitled' }}</span>
                    <span v-if="$parent.isSettings"
                        class="cursor-pointer flex items-center py-2 px-1 rounded dark:text-dark-color ml-auto">
                        <Icon :source="icons.CaretDownMinor"
                            class="flex justify-center items-center transform origin-center text-gray-500"
                            :class="$parent.activeGroup === index ? '-rotate-180' : 'rotate-0'"></Icon>
                    </span>
                </div>
            </button>
            <button v-if="canResets[index] && (group.group_name || group.reset)" type="button"
                class="absolute text-gray-500 transition transform -translate-y-1/2 top-1/2 hover:text-gray-800 dark:text-dark-color dark:hover:text-primary-100"
                :class="$parent.isSettings ? 'right-8' : 'right-4'"
                data-tooltip="Reset" @click="resetGroupName(group)">
                <Icon :source="icons.ecRefresh" :size="'w-4'"></Icon>
            </button>
        </div>
        <keep-alive>
            <div v-if="$parent.activeGroup === index" class="bg-white dark:bg-dark dark:text-dark-color mb-2 pl-2"
                :class="$parent.activeGroup === index ? 'py-4 ' + (
                    deep === 0 ? (
                        hasGroupTitle(group) ? 'rounded' : 'rounded'
                    ) : ''
                ) : ''">
                <div v-if="group.description" class="mb-4 text-sm text-gray-500 dark:text-dark-color"
                    v-html="$helpers.markdown(group.description)" />
                <div class="flex flex-wrap -mx-2">
                    <GroupFields :data="group" :gparams="gparams" :field-values="fieldValues" :freeform="freeform"
                        @update:data="updateData" @update:delete="updateData" />
                </div>
                <div v-if="!hasGroupTitle(group) && canResets[index] && (group.group_name || group.reset)"
                    class="inline-block mt-5 ml-2">
                    <a href="#" class="flex items-center text-red-500 hover:underline" @click="resetGroupName(group)">
                        <Icon :source="icons.ecRefresh" :size="'w-4'"></Icon>
                        <span class="text-sm font-normal ml-2">Discard changes</span>
                    </a>
                </div>
            </div>
        </keep-alive>
    </div>
</template>



<script lang="ts">
    import GroupFields from './Group.vue';
    import {CaretDownMinor, ecRefresh} from '@/assets/icons';
    export default {
        components: {
            GroupFields
        },
        props: {
            data: {
                type: Object,
                default() {
                    return {}
                }
            },
            fieldValues: {
                type: Object,
                default() {
                    return {};
                }
            },
            fields: {
                type: Array,
                default() {
                    return [];
                }
            },
            index: {
                type: Number,
                default: 0
            },
            freeform: {
                type: Boolean,
                default: false
            }
        },
        emits: ['update:label', 'update:delete', 'update:clicked', 'update:data'],
        data() {
            return {
                valueDeleted: {},
                originalValues: {},
                canResets: {},
                icons: {
                    CaretDownMinor, ecRefresh
                }
            }
        },
        computed: {
            group() {
                return this.data;
            },
            gparams(){
                return this.group.params.filter((p) => {
                        return p.type && this.$parent.fieldVisible(this.group, p);
                    });
            },
            deep() {
                return this.$parent.deep;
            },
            values() {
                return this.fieldValues;
            },
        },
        mounted() {
            this.originalValues = this.$helpers.copy(this.values);
        },
        methods: {
            onUpdateRefresh(){
                this.$store.commit('UPDATE_REFRESH');
            },
            hasGroupTitle(group) {
                return (group.group_title && group.group_title !== '') || this.fields.length > 1;
            },
            resetGroupName (tab) {
                if (tab.group_name) {
                    if (this.originalValues[tab.group_name]) {
                        this.values[tab.group_name] = this.$helpers.copy(this.originalValues[tab.group_name]);
                    } else {
                        delete this.values[tab.group_name];
                    }
                } else if (tab.params){
                    tab.params.forEach((p) => {
                        if (p.name) {
                            if (this.originalValues[p.name]) {
                                this.values[p.name] = this.$helpers.copy(this.originalValues[p.name]);
                            } else {
                                delete this.values[p.name];
                            }
                        }
                    });
                }
                delete this.canResets[this.index];
                this.onUpdateRefresh();
            },
            updateData(field) {
                // console.log(val);
                if(!field || field.type !== 'tab')
                    this.canResets[this.index] = true;
            }
        }
    };
</script>
<style>
    #sidebar-theme-settings-form  .-mx-2.relative.p-3{
        margin-left: 0px;
    }
</style>