<template>
    <div class="relative flex flex-col items-center justify-center p-3 border-2 border-dashed rounded group bg-gray-50 dark:bg-gray-700 dark:text-dark-color" @drop="dragFile">
        <Icon v-if="!file" :source="PageUpMajor" :size="'w-8'" class="transition text-gray-500 group-hover:text-primary-600 dark:group-hover:text-white"></Icon>
        <Text v-else :color="'text-gray-500 dark:text-dark-color dark:group-hover:text-white'" :fontWeight="'medium'">
            {{ file.name }}
        </Text>
        <Text :size="'small'" class="transition group-hover:text-gray-600 dark:group-hover:text-white mt-1">
            <span class="text-primary-600">Upload a file </span>or drag and drop
        </Text>
        <input type="file" class="absolute inset-0 w-full h-full opacity-0 cursor-pointer dark:bg-gray-700" :accept="field.options?.accept ?? null" @change="uploadFile">
    </div>
</template>

<script lang="ts">
    import { PageUpMajor } from "@/assets/icons";
    export default {
        name: 'UploadField',
        props: {
            field: {
                type: Object,
                default() {
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data() {
            return {
                file: null,
                PageUpMajor
            };
        },
        watch: {
            file(val) {
                this.$emit('update:data', val);
            }
        },
        methods: {
            uploadFile(e) {
                this.addFile(e.target.files);
            },
            dragFile(e) {
                this.addFile(e.dataTransfer.files);
            },
            addFile(files) {
                if(files.length) {
                    this.file = files[0];
                }
                else {
                    this.file = null;
                }
            }
        }
    };
</script>
