import {store} from '@/store';
export const upload = async ({type, file, compress}, onUploadProgress = null, _) => {
    let formData = new FormData();
    formData.append('type',(type === 'icon' || type === 'font') ? 'FILE' : type.toUpperCase());
    formData.append('file', file)

    if(compress !== undefined && compress !== null) {
        formData.append('compress', compress)
    }
    try {
        console.log('Upload data', formData);
        const res = await _.$worker.fetch(
            route(type === 'icon' ? 'builder.icons' : 'builder.files'),
            formData,
            'POST'
        );
        if (res.data.status  === 'success') {
            let resData = res.data;
            return resData;
        }
        throw new Error(res.data.message);
    }
    catch(err) {
        store.commit(
            'global/flash',
            {
                open: true,
                message: err.message,
                type: 'error',
            },
            { root: true }
        );

        return false;
    }

};

export const fetch = async (type, subtype, query, next, limit, _) => {
    const getRoute = (params: null) => {
        switch (type) {
            case 'video':
            case 'audio':
                return route( 'builder.files', params);
            case 'image':
            case 'font':
                return route((subtype === 'free' || subtype === 'google') ? 'builder.bridge' : 'builder.files',params);
            case 'icon':
                return route('builder.icons',params);
            default:
                return route('builder.picker',params);
        }
    };

    const getParams = () => {
        let params = {};
        switch (type) {
            case 'icon': {
                params.type = subtype;
                if(query){
                    params.query = query;
                }
                if(next){
                    params.page = next;
                }
                params.limit = limit;
                break;
            }
            default: {
                if(subtype === 'free'){
                    if(next){
                        params.url = next;
                    }else{
                        params.url = 'https://burst.shopify.com/api/photos'
                                        + ( query ? ('/search?q=') + query : '');
                    }
                }
                else if(subtype === 'google'){
                    params.url = 'https://www.googleapis.com/webfonts/v1/webfonts?key=AIzaSyCrsTDigL61TFHYPHTZduQP1cGi8CLfp90&sort=popularity';
                }
                else{
                    params.type = type;
                    params.limit = limit;
                    if(query){
                        params.query = query;
                    }
                    if(next){
                        params.page = next;
                    }
                }
                break;
            }
        }
        return params;
    };

    const formatResData = (resData) => {
        return resData.map(item => {
            if(type === 'image' || type === 'font'){
                if(subtype === 'free'){
                    let {id, title: name, url: value, url} = item;
                    let thumbnail = _.$helpers.resizeImage(url, {width: 150, height: 150,format: 'pjpg'});
                    return {
                        fn: 'item', id, name, value, url, thumbnail
                    };
                }
                else if(subtype === 'google'){
                    const url = (name) => 'https://fonts.googleapis.com/css?family='+name.replace(/\s/g, '+')+':100,200,300,400,500,600,700,800,900';
                    let {family: name, family: thumbnail} = item;
                    return {
                        fn: 'item',
                        id: _.$helpers.makeId(8),
                        name,
                        thumbnail,
                        value: url(name)
                    };
                }else{
                    let k = type === 'font' ? 'url' : 'src';
                    let {id, [k]: value, [k]: url, [k]: thumbnail} = item;
                    let name = _.$helpers.file.name(value);
                    let ext = _.$helpers.file.ext(value);
                    return {
                        fn: 'item', id, name, ext, value, url, thumbnail
                    };
                }
            }
            else if(type === 'icon'){
                let {keywords, name,  url, url: value, url: thumbnail} = item;
                if(!name){
                    name = _.$helpers.file.name(value);
                }

                let rs = {
                    id: item.id ?? _.$helpers.makeId(8),
                    fn: 'item', name, value, url, thumbnail
                };
                if(subtype !== 'uploaded') {
                    let cate = keywords.split(',')[0];
                    rs['cate'] = cate;
                }
                return rs;
            }
            else{
                let {id, title: name, image: url, handle: value} = item;
                let thumbnail = _.$helpers.resizeImage(url, {width: 150, height: 150,format: 'pjpg'});
                return {
                    fn: 'item', id, name, value, url, thumbnail
                };
            }
        });
    };

    const processing = (res) => {
        let after = false, before = false, items = [];

        if(subtype === 'free'){
            ({next: after = false, previous: before = false} = res?.result?.metadata?.paging ?? {});
        }
        else {
            ({next: after = false, previous: before = false} =  res?.data?.pageInfo ?? {});
        }
        items = formatResData(  res?.data?.files ??
                                res?.data?.items ??
                                res?.result?.data ??
                                res?.result?.items ??
                                res?.icons ??
                                []);
        return {after, before, items};
    };

    if(!type){
        return {items: [], before: false, after: false};
    }

    let params = getParams();
    const res = await _.$worker.fetch(getRoute(params), null, 'GET');


    if (res.status == 200) {
        let resData = res.data;
        if(resData['status'] === 'success'){
            return processing(resData);
        }
    }
    return {items: []};
};

export const remove = async (id, _) => {
    let formData = new FormData();
    formData.append('ids', id);
    formData.append('_method', 'DELETE');
    const res = await _.$worker.fetch(route('builder.files'),formData, 'DELETE');
    if (res.data.status === 'success') {
        let resData = res.data;
        return resData;
    }
    return false;
};
