<template>
    <div
        v-for="(field, index) in gparams"
        :key="(field.name) || index"
        class="px-2"
        :class="field.options?.half ? 'w-1/2' : 'w-full'"
    >
        <div v-for="(warning, iWarning) in warnings(group, field)" :key="iWarning" class="flex w-full p-3 mb-3 text-sm rounded-md text-gray-600 border border-yellow-500 shadow-sm bg-yellow-50 font-normal">
            <Icon :source="CircleAlertMajor" class="mr-2" :color="'text-yellow-600'"></Icon>
            <div v-html="warning" /> 
        </div>
        <Field
            :data="field"
            :group="group"
            :field-values="fieldValues"
            :freeform="freeform"
            :class="{'mb-4' : index < gparams.length - 1}"
            @update:data="$emit('update:data', $event)"
            @update:delete="$emit('update:delete', $event)"
        />
    </div>
</template>

<script lang="ts">
import { defineAsyncComponent } from '@vue/runtime-core';

import { relationMixin } from '../../mixins';
import {CircleAlertMajor} from '@/assets/icons'

export default {
    components: { Field:  defineAsyncComponent(() => import('./Field.vue')) },
    mixins: [relationMixin],
    data(){
        return {
            CircleAlertMajor
        }
    },
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        },
        fieldValues: {
            type: Object,
            default() {
                return {};
            }
        },
        gparams: {
            type: Array,
            default() {
                return [];
            }
        },
        freeform: {
            type: Boolean,
            default: false
        }
    },
    emits: ['update:label', 'update:delete', 'update:clicked', 'update:data'],
    computed: {
        group () {
            return this.data;
        },
        values() {
            return this.fieldValues;
        }
    },
    methods: {
        warnings (g, f) {
            let wns = f.options?.warnings;
            let rs = [];
            if(wns && typeof wns === 'object') {
                let p = this.pathField(g, f),
                    v = this.$helpers.valueByKey(p, this.values, '||');
                if(wns.constructor === Object) {
                    wns = [wns];
                }
                wns.forEach(wn => {
                    let {content, condition} = wn;
                    if(content) {
                        if(!condition || condition(v)) {
                            rs.push(content);
                        }
                    }
                });
            }
            return rs;
        }
    }
};
</script>
