type PositionType = {
    selector: string;
    isBefore?: boolean;
};
type VisibleType = {
    relation: string;
    status: string;
};

export const refactorArray = (arr: FieldType[], index: number, item?: FieldType) => {
    let array = [...arr];
    if (item) {
        array.splice(index, 1, item);
    } else {
        array.splice(index, 1);
    }
    return array;
};

export const addSwitch = (
    fieldsDTO: FieldType[],
    visibleDTO: string | VisibleType,
    positionDTO?: string | PositionType
) => {
    const array: FieldType[] = [...fieldsDTO];
    for (let i in fieldsDTO) {
        array[i] = { ...fieldsDTO[i] };
        if (array[i]) {
            // add visible option field
            let status = "";
            let relation = "";
            if (typeof visibleDTO === "object") {
                status = visibleDTO.status;
                relation = visibleDTO.relation;
            } else {
                status = visibleDTO;
                relation = "tab";
            }
            array[i].name = fieldsDTO[i].name + status + "mode";
            array[i].options = {
                ...fieldsDTO[i].options,
                visible: {
                    keep_data: true,
                    condition: (data: any) => {
                        return data && data[relation] === status;
                    }
                }
            };

            // handle selector
            if (positionDTO) {
                let css: CssFieldType = {};
                let selector = "";
                const str = (fieldsDTO[i].css as CssFieldType)?.selector;

                if (typeof fieldsDTO[i].css === "object") {
                    css = { ...(fieldsDTO[i].css as CssFieldType) };
                    delete css.selector;
                }

                if (str && typeof str === "string") {
                    selector = handleString(str.split(","), status, positionDTO);
                } else {
                    selector = typeof positionDTO == "object" ? positionDTO.selector : positionDTO;
                }

                array[i].css = {
                    ...css,
                    selector
                };
            }
        }
    }
    return array;
};

const handleString = (selectors: string[], status: string, position: string | PositionType) => {
    let newSelector = "";
    const length: number = selectors.length;
    if (length > 1) {
        for (let i in selectors) {
            let check = parseInt(i) < length - 1 ? "," : "";
            if (typeof position == "string") {
                if (status == "active") {
                    newSelector += selectors[i] + position + check;
                } else {
                    newSelector += position + selectors[i] + check;
                }
            } else {
                if (!position.isBefore) {
                    newSelector += selectors[i] + position.selector + check;
                } else {
                    newSelector += position.selector + selectors[i] + check;
                }
            }
        }
    } else {
        if (typeof position == "string") {
            newSelector = status == "active" ? selectors[0] + position : position + selectors[0];
        } else {
            newSelector = !position.isBefore
                ? selectors[0] + position.selector
                : position.selector + selectors[0];
        }
    }
    return newSelector;
};
