<template>
	<li v-detectVisible="detectVisible" class="relative p-4 transition cursor-pointer select-none item bg-white dark:bg-gray-700" :class="[classItem(item), item.fn == 'upload' ? 'w-full' : '']">
		<div class="relative" :class="[layout == 'grid' ? 'pb-100p' : '', item.fn == 'upload' ? 'pb-10' : 'pb-10', item.fn == 'upload' ? '!pb-0 h-48 flex flex-col' : '']">
			<div v-if="show" class="inset-0 group" :class="item.fn == 'upload' ? 'relative flex-grow' : 'absolute'">
				<template v-if="item.fn == 'upload'">
					<span class="absolute top-0 left-0 flex items-center justify-center w-full h-full font-medium text-gray-500 transition border border-gray-300 border-dashed rounded-md group-hover:text-primary-700 group-hover:border-primary-600 dark:border-gray-600">
						<text class="text-primary-800 bg-primary-400 px-3 py-1 rounded-sm">Upload Image</text>
					</span>
					<input class="absolute top-0 left-0 z-10 w-full h-full opacity-0 cursor-pointer dark:border-gray-600" type="file" :accept="acceptInput" @change="$emit('upload', $event)">
				</template>
				<div v-else class="flex items-center h-full group">
					<div class="flex items-center w-full h-full" :class="{ 'opacity-30': item.loading}" @click="$emit('toggleItem',item)">
						<div v-if="type == 'font'" class="flex items-center w-full h-full rounded" :class="{ 'bg-img-default': layout == 'grid' }">
							<link rel="stylesheet" :href="fontURL(item)">
							<Text :size="'large'" :truncate="true" :class=" [layout == 'grid' ? 'text-center' : '', isActive(item.value) > -1 && layout != 'grid' ? 'text-primary-600' : 'text-gray-800 dark:text-dark-color' ]" :style="{ fontFamily:item.name }">
								{{ item.name }}
							</Text>
						</div>
						<div v-else-if="type == 'product_type' || type == 'product_vendor'" class="flex items-center w-full h-full rounded" :class="{ 'bg-img-default': layout == 'grid' }">
							<Text :size="'small'" :truncate="true" :class="{ 'text-center': layout == 'grid' }">
								{{ item.name }}
							</Text>
						</div>
						<template v-else>
							<template v-if="layout == 'list'">
								<span class="block w-10 h-10 p-1 mr-2" v-if="showThumb">
									<span v-if="type === 'icon'" v-html="item.thumbnail" />
									<img v-else class="block object-cover w-full h-full rounded-sm"  :src="item.thumbnail ?? '/images/placeholder.png'" :alt="item.name">
								</span>
								<Text :truncate="true" :class="{'pl-2': !showThumb}">
									{{ item.name }}
								</Text>
							</template>
							<div v-else class="relative flex items-center justify-center w-full h-full pb-6 overflow-hidden transition duration-100 rounded bg-img-default group" :title="item.name" :class="{ 'scale-90 transform': isActive(item.value) > -1 && layout == 'grid', 'opacity-50 hover:opacity-80': item.reload}">
								<span v-if="type === 'icon'" class="block w-10 h-10" v-html="item.thumbnail" />
								<img v-else class="absolute block object-cover max-w-full max-h-full transform -translate-x-1/2 -translate-y-1/2 rounded top-1/2 left-1/2" :class="{ '-mt-3': type != 'image', 'h-full': !item.thumbnail, 'opacity-0': item.loading && type == 'video' }" :src="item.thumbnail ?? '/images/placeholder.png'" :alt="item.name">
								<p v-if="item.reload" class="absolute left-0 right-0 flex items-center justify-center h-8 mx-1 text-xs text-center text-white transform -translate-y-1/2 bg-black rounded opacity-0 group-hover:opacity-100 top-1/2" @click="$emit('reload')">
									<span class="px-0.5 truncate">{{ item.reload ? 'Reload to choose' : item.name }}</span>
								</p>
								<p v-else-if="type != 'image'" class="bg-white dark:bg-dark dark:text-dark-color absolute bottom-0 left-0 w-full text-sm truncate px-2 py-0.5" @click="$emit('reload')">
									{{ item.reload ? 'Reload to choose' : item.name }}
								</p>
							</div>
						</template>
					</div>
					<span v-if="isActive(item.value) > -1 && type != 'font'" class="absolute flex items-center justify-center w-5 h-5 p-px transition bg-white dark:bg-dark dark:text-dark-color border-2 rounded-full opacity-0" :class="[isActive(item.value) > -1 ? 'bg-primary-600 text-white border-primary-600 opacity-100' : 'border-gray-400 text-gray-400', layout == 'grid' ? 'right-0 bottom-0 z-10' : 'top-1/2 right-0 transform -translate-y-1/2',]">
						<Icon :source="icons.CircleTickMajor" :size="'w-5'" :color="'text-primary-600'"></Icon>
					</span>
					<button type="button" v-if="isActive(item.value) > -1 && type == 'font'" class="absolute right-0 inline-flex justify-center items-center font-medium smoothing h-7 text-xs px-2 ecom-save-settings gap-1 text-white bg-primary-600 hover:bg-primary-500 focus:outline-none rounded  smoothing" @click.prevent="$emit('clickSelect')">
						Select
					</button>
					<span v-if="item.loading" class="absolute transform -translate-x-1/2 -translate-y-1/2 top-1/2 left-1/2">
						<Loading :size="layout === 'grid' ? 12 : 10" type="spin" />
						<Text size="'small'" class="absolute transform -translate-x-1/2 -translate-y-1/2 top-1/2 left-1/2">
							{{ item.per }}
						</Text>
					</span>
					<span v-if="isActive(item.value) === -1 && (item.id + '').startsWith('gid://')" class="absolute right-0 block w-5 h-5 text-gray-500 dark:text-dark-color transition z-1" :class="[item.removing ? '' : 'hover:text-red-600 opacity-0 group-hover:opacity-100', layout == 'grid' ? 'bottom-1 right-1' : 'top-1/2 right-0 transform -translate-y-1/2']">
						<template v-if="!item.removing">
							<span class="bg-danger-600 rounded flex text-white p-0.5" @click="$emit('remove', item)">
								<svg class="w-full h-full text-current" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" aria-hidden="true">
									<path stroke-linecap="round" stroke-linejoin="round" stroke-width="2"
										d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16">
									</path>
								</svg>
							</span>
						</template>
						<Loading v-else :size="5" type="spin" class="w-full h-full text-current"></Loading>
					</span>
				</div>
			</div>
			<label v-if="item.fn == 'upload' && type === 'image'" class="flex items-center min-w-40 my-3" @click="checkCompress">
				<Toggle :field="{
						label: 'Compress',
						name: 'compress',
						type: 'toggle',
						options: {
							values: {
								on: { value: true },
								off: { value: false },
							}
						}
					}" v-model:data="_compress" />
				<Text :as="'span'" :siz="'small'" class="ml-2 cursor-pointer">Compress image</Text>
			</label>
		</div>
	</li>
</template>

<script lang="ts">
import Toggle from '../../fields/toggle/Toggle.vue';
import { detectVisible } from '../../directives';
import { fileMixin  } from '../../mixins';
import { Popmenu  } from '@/mixins';
import { FolderUpMajor, SettingsMajor, CircleTickMajor } from "@/assets/icons";
export default {
    name: 'ItemContent',
	directives: {
		detectVisible
	},
    components: {
		Toggle
    },
	mixins: [ Popmenu, fileMixin ],
    props: {
        type: {
            type: String,
            default: 'menu'
        },
        value: {
            type: Array,
			default(){
				return [];
			}
        },
		compress: {
			type: Boolean,
			default: false
		},
        item: {
            type: Object,
			default(){
				return {};
			}
        },
        layout: {
            type: String,
			default: ''
        }
    },
	emits: ['update:compress', 'upload', 'remove', 'toggleItem', 'reload', 'clickSelect'],

    data(){
        return {
            show: false,
            info: {},
            icons: { 
				FolderUpMajor, SettingsMajor, CircleTickMajor
			}
        };
    },

    computed: {
        acceptInput(){
            if(this.type.indexOf('image') > -1){
				return '.jpg,.jpeg,.png,.gif,.webp,.svg';
			}
			if(this.type.indexOf('font') > -1){
				return '.woff,.woff2,.ttf,.otf';
			}
			if(this.type.indexOf('video') > -1){
				return '.mp4,.mov,.webm';
			}
			if(this.type.indexOf('audio') > -1){
				return 'audio/*';
			}
            return '';
        },
        hasReload() {
            return this.item.reload;
        },
		isTinify() {
            let cfg = this.$store.getters['global/configs']
			return cfg.tinify && cfg.tinify.enable;
		},
		_compress: {
			get() {
				return this.compress;
			},
			set(val : any) {
				this.$emit('update:compress', val);
			}
		},
        showThumb(){
            return this.type === 'icon' || this.layout !=='list';
        }
    },
    watch: {
        hasReload(val) {
            if (val) {
                setTimeout(() => {
                    this.$emit('reload');
                }, 1000);
            }
        }
    },
    methods: {
         fontURL({name, value:src}){
                return this.$helpers.getLinkFont(name, src);
        },
        isActive(value) {
			return this.value.findIndex(v => {
				v = this.type === 'image' ?
							this.currentImage(this.type, v, false) :
							v.value;
				return v === value;
			});
        },
		checkCompress(e) {
			if (!this.isTinify) {
				e.preventDefault();
				window.location.hash = '#extensions/2';
			}
		},
		classItem(item){
            // return this.layout == 'grid' ?
            //         'w-1/2 pt-0 px-1' :
            //         'py-2.5 pl-4 w-full border-b border-gray-300 bg-white ' +
            //         ((item.fn == 'item') ?
            //             'h-15' : 'h-25');
			let cl = '';
			if(this.layout == 'grid'){
				cl += 'w-1/2 pt-0 px-2 ';
			}else{
				cl += 'px-4 w-full border-b border-gray-100 dark:border-gray-600 bg-white dark:bg-gray-700 dark:text-dark-color';
				if(item.fn === 'item'){
					cl += 'py-2';
				}else{
					cl += 'py-2';
				}
				if(!item.reload){
					cl += ' cursor-pointer';
				}
			}
			if(this.type == 'image'){
				cl += 'w-1/5 ';
			}
			if(item.reload){
				cl += ' cursor-pointer';
			}
			return cl;

        },
		detectVisible(){
			this.show = true;
		}
    }
};
</script>
<style>
    .bg-img-default{
        background-position: 0 0,.6rem .6rem;
        background-size: 1.2rem 1.2rem;
        background-image: linear-gradient( 45deg ,#dfe3e8 25%,transparent 0,transparent 75%,#dfe3e8 0,#dfe3e8),linear-gradient( 45deg ,#dfe3e8 25%,#fff 0,#fff 75%,#dfe3e8 0,#dfe3e8);
        box-shadow: inset 0 0 0 0 transparent, 0 0 0 1px #c4cdd5;
    }
		.dark .bg-img-default{
        box-shadow: inset 0 0 0 0 transparent, 0 0 0 1px #4c4e50;
    }
</style>
