<template>
    <span v-if="uploadImage" class="bg-black opacity-20 fixed left-0 top-0 w-screen h-screen z-40" @click="$emit('close')"></span>
    <div
        class="z-50 h-full p-3 transition duration-200 transform max-w-popup ecom-picker__wrp"
        :class="[
            position === 'absolute' && header.type !== 'image' ? 'absolute top-0 left-0 w-full'  : 'fixed top-14 mt-3 max-h-picker-fixed w-68 ' + (largeScreen ? 'right-7' : 'left-3'),
            uploadImage ? 'fixed !w-[980px] !h-screen max-w-none !left-1/2 !right-auto -translate-x-1/2' : ''
        ]"
    >
        <div class="relative flex flex-col w-full h-full bg-white dark:bg-gray-700 dark:text-dark-color rounded-lg shadow-popup">
            <header class="relative flex items-center justify-between h-12 px-2 text-sm border-b rounded-t-lg z-1 border-form-border dark:border-gray-600" :class="uploadImage ? 'pl-5 pr-4 py-7' : ''">
                <ecom-button v-if="header.back" tooltiplocation="bottom" tooltip="Go back" @click="$emit('back')" :icon="icons.ChevronLeftMinor" class="!bg-transparent"></ecom-button>
                <ecom-button v-if="header.reload" :class="header.type ===  'image' ? 'order-1' : ''" tooltip="Refresh" tooltiplocation="bottom" @click="$emit('reload')" :icon="icons.ecRefresh" class="!bg-transparent"></ecom-button>
                <span v-else-if="!header.alignLeft" class="block" :class="header.reload ? 'w-16 mr-2' : 'w-10'" />
                <div class="text-sm font-medium text-gray-700 dark:text-dark-color uppfirst" :class="[{'pl-3': header.alignLeft}, header.type ===  'image' ? 'order-0 mr-auto font-semibold text-xl' : '']" v-html="header.title" />
                <div :class="header.type ===  'image' ? 'order-2 ml-2' : ''" >
                    <ecom-button tooltip="Close" tooltiplocation="bottom" @click="$emit('close')" :icon="icons.CancelMajor" class="!bg-transparent"></ecom-button>
                </div>
            </header>
            <div :class="uploadImage ? 'flex border-b h-full dark:border-dark' : 'flex flex-col flex-grow h-full'">
                <template v-if="header?.tabs?.list?.length" >
                    <template v-if="uploadImage">
                        <div class="w-60 h-full bg-gray-100 pl-3 pr-2 py-3 dark:bg-dark">
                            <button v-for="(item, index) in header.tabs.list" @click="$emit('update:tabs', item.value)" :key="index" class="h-9 w-full px-3 mb-1 text-left text-sm rounded-[4px] relative hover:bg-primary-300 before:content-[''] before:w-0.75 before:rounded-r-sm before:absolute before:-left-3 before:top-0 before:h-full transition dark:text-dark-color dark:hover:bg-gray-700"
                                :class="header?.tabs?.cur == item.value ? 'before:bg-primary-600 bg-primary-300 dark:bg-gray-700' : 'before:bg-transparent bg-transparent'"
                            >
                                {{ item.text }}
                            </button>
                        </div>
                    </template>
                    <div v-else class="px-3 mt-2">
                        <ul class="flex flex-wrap border-b dark:border-primary-600">
                            <li v-for="tab in header.tabs.list" :key="tab.value" class="flex-1 ecom-item-tab-picker-hover">
                                <button type="button" class="flex items-center justify-center w-full py-2 text-sm text-center capitalize align-middle transition rounded h-7-5 whitespace-nowrap"
                                    :class=" header?.tabs?.cur == tab.value ? 'text-primary-600' : 'text-gray-600 dark:text-dark-color'" @click="$emit('update:tabs', tab.value)">
                                    {{ tab.text }}
                                </button>
                                <span :class="header?.tabs?.cur == tab.value ? ' bg-primary-600': 'bg-transparent'" class="flex w-full rounded-t-md" style="height: 3px" />
                            </li>
                        </ul>
                    </div>
                </template>
                <div class="flex-grow h-full flex flex-col">
                    <div v-if="header?.tabs?.cur == 'uploaded' && header?.type == 'icon'" class="px-3 mt-4">
                        <p v-if="is_shopify_trial" class="text-sm font-medium text-red-600 italic text-center">Not support SVG upload on Shopify trial plan.</p>
                        <p v-else class="text-sm font-medium text-red-600 italic text-center">Only SVG format allowed</p>
                    </div>
                     <div v-if="header?.tabs?.cur == 'uploaded' && header?.type == 'font' && is_shopify_trial" class="px-3 mt-4">
                        <p class="text-sm font-medium text-red-600 italic text-center">Not support SVG upload on Shopify trial plan.</p>
                    </div>
                    <div v-if="header?.search?.show && (!freeImgCat || header?.tabs?.cur == 'uploaded')" class="p-3 mt-1 text-sm z-20 border-b border-gray-200 dark:border-dark">
                        <CSearch :search="header.search.value" :cates="cates" @update:search="$emit('update:search', $event)"/>
                    </div>
                    <div v-if="uploadImage && freeImgCat && header?.tabs?.cur != 'uploaded'" class="flex p-2 rounded-md mt-1">
                        <ecom-button @click="back2FreeCat" data-tooltip="Back to categories" data-tooltip-location="left-center" :icon="icons.ArrowLeftMinor"></ecom-button>
                        <div class="flex flex-grow self-center text-base ml-1">
                            <span class="">{{ freeImgCat?.title }}</span>
                        </div>
                    </div>
                    <div class="relative flex-1 overflow-x-hidden" :class="uploadImage ? 'flex-grow' : '' ">
                        <slot name="content" />
                    </div>
                </div>
            </div>
            <slot name="footer" />
        </div>
    </div>
</template>

<script lang="ts">
import { mapGetters } from 'vuex';
import {ChevronLeftMinor, ecRefresh, CancelMajor, ArrowLeftMinor } from '@/assets/icons';

import CSearch from '../molecules/Search/Search.vue';

export default {
    name: 'PickerFrameChild',
    components: {
        CSearch
    },
    data() {
        return {
            icons: {
                ChevronLeftMinor, ecRefresh, CancelMajor, ArrowLeftMinor
            }
        }
    },
    props: {
        header: {
            type: Object,
            default() {
                return {};
            }
        },
        position: {
            type: String,
            default: 'absolute'
        }
    },
    emits: ['back', 'reload', 'close', 'update:search', 'update:tabs', 'loadFreeImg'],
    computed: {
        ...mapGetters('picker', ['categories', 'loading', 'freeImgCat']),
        ...mapGetters('builder', ['largeScreen']),
        cates() {
            let { type, subtype } = this.header;
            return this.categories({
                type,
                subtype
            });
        },
        is_shopify_trial() {
            let global = this.$store.getters['global/user'];
            return global?.shopify_plan == 'trial';
        },
        uploadImage(){
            return this.header?.type === 'image';
        }
    },
    methods: {
        back2FreeCat() {
            this.$store.commit('picker/freeImgCat', null);
            this.$emit('loadFreeImg');
        }
    },
};
</script>

<style>
.shadow-img {
    box-shadow: 0 1px 0 0 #dfe3e8, 0 3px 4px -4px rgb(64 64 64 / 30%);
}
.h-3px {
    height: 3px;
}
.max-w-popup {
    max-width: calc(20rem + 15px);
}
.pt-17 {
    padding-top: 4.25rem;
}
.ecom-item-tab-picker-hover:hover span {
    opacity: 1;
}
.max-h-picker-fixed {
    max-height: calc(100% - 4.25rem);
}
</style>