<template>
    <div
        ref="wrapper"
        class="core__blocks"
        :class="{
            'core__blocks--empty': visibleBlocks.length === 0 && !isFullScreen,
            'drop__placeholder': visibleBlocks.length === 0 && isDropTo && !isFullScreen
        }"
        @mouseenter="overBlocks"
        @mouseleave="outBlocks"
    >
        <div v-if="visibleBlocks.length > 0" class="core__blocks--body">
            <BlockElement
            v-for="(block, i) in visibleBlocks"
                :key="block.id"
                :data="block"
                :deep="deep"
                :index="i"
                :control="control"
                :section_id="section_id"
            />
        </div>
        <div
            v-else-if="!isFullScreen"
            class="core__empty--placeholder"
            @click="editColumn"
        >
            <span @click.stop="backtoElements">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" fill="currentColor"><path d="M240 352V272H160C151.2 272 144 264.8 144 256C144 247.2 151.2 240 160 240H240V160C240 151.2 247.2 144 256 144C264.8 144 272 151.2 272 160V240H352C360.8 240 368 247.2 368 256C368 264.8 360.8 272 352 272H272V352C272 360.8 264.8 368 256 368C247.2 368 240 360.8 240 352zM512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256zM256 32C132.3 32 32 132.3 32 256C32 379.7 132.3 480 256 480C379.7 480 480 379.7 480 256C480 132.3 379.7 32 256 32z"/></svg>
            </span>
        </div><template v-else />
    </div>
</template>



<script lang="ts">
import { defineAsyncComponent } from 'vue';
import { Core } from '@/mixins';
import LoadingComponent from './Loading.vue';
export default {
    name: 'Blocks',
    components: {
        BlockElement: defineAsyncComponent({
            loader: () => import('./Block.vue'),
            loadingComponent: LoadingComponent,
            delay:0,
            timeout: 60000
        })
    },
    mixins: [ Core ],
    props: {
        'data': {
            type: Array,
            default: () => {
                return [];
            }
        },
        'deep': {
            type: Number,
            default: 0
        },
        'control': {
            type: Boolean,
            default: true
        },
        'section_id': {
            type: String,
            default: ''
        }

    },
    emits: ['active'],
    data() {
        return {
            is: 'blocks',
            over: false
        };
    },
    computed: {
        blocks() {
            return this.data;
        },
        visibleBlocks() {
            return this.blocks.filter((block) => {
                if (block.id === undefined) {
                    block.id = this.$helpers.randid();
                    block.IDInit = true;
                }
                if(block?.group) {
                    block.IDInit = false;
                }
                return block.visibility !== false && (
                    !block.row || block.row.visibility !== false
                );
            });
        },
        stack() {
            return this.data;
        },
        canDrop() {
            return this.$helpers.canDrop(this.dragCurrent, this);
        },
        sections() {
            return this.$store.getters['page/sections'];
        }
    },
    methods: {
        onSetTabDefault(){
            this.$store.commit('SET_TAB', 'elements');
        },
        overBlocks(e) {
            if (this.isFullScreen) {
                return;
            }

            this.over = true;

            if (
                this.canDrop
            ) {
                if (
                    this.blocks.length > 0 &&
                    e.target === this.$refs.wrapper
                ) {
                    if (
                        (
                            this.dragCurrent.$parent === this ||
                            this.dragCurrent.$parent.$parent === this
                        )
                    ) {
                        // Reverse drag element back to current column
                        this.$store.commit('builder/setDropTo', null);
                    } else {
                        let ins = this.stacks[this.blocks[this.blocks.length-1].id];
                        if (ins) {
                            this.$store.commit('builder/setDropTo', ins);
                            this.$store.commit('builder/setDropPos', 'bottom');
                            ins.placeholder = 'bottom';
                        }
                    }
                } else if (
                    this.blocks.length === 0 &&
                    e.target === e.currentTarget
                ) {
                    this.$store.commit('builder/setDropTo', this);
                }
            }
        },

        outBlocks() {
            if (!this.isFullScreen) {
                this.timmer = setTimeout(() => {
                    if (!this.dragCurrent || !this.$parent.over) {
                        this.over = false;
                    }
                }, 250);
            }
        },

        activeCol(e) {
            if (e.target.classList.contains('core__empty--placeholder')) {
                this.$emit('active');
            }
        },

        backtoElements() {
            this.$store.commit('builder/setDragging', { timmer: new Date().getTime() });
            this.$store.commit('builder/setEditingElement', null);
            this.$store.commit('SET_TAB', 'elements');
        },

        editColumn() {
            if(this?.$parent?.is === 'column') {
                this.$store.commit('builder/setActiveElement', this.$parent);
                this.$store.commit('builder/setEditingElement', this.$parent);
            }
        },

        dropIn(current) {

            if(this.section_id) {
                const action = this.section_id == current.section_id ? 'add' : 'move',
                    section = this.sections.find((s:any) => s.id == this.section_id);
                let payload = {
                    type: 'block',
                    data: section,
                    section_id: this.section_id,
                    action: action
                }
                if(action == 'move') {
                    payload.section_from = current.section_id;
                    payload.section_from_data = this.sections.find((s:any) => s.id == current.section_id);
                }
                this.$store.dispatch('history/addStack', payload);
            }

            this.stack.push(current.dropOut());
            if (this.$parent.is == 'column') {
                this.$parent.column.refresh = this.$helpers.randid();
            }
        }
    }
};
</script>
