<template>
    <div
        class="relative text-sm"
        :class="id"
    >
        <Icon  v-if="!searchData?.length" :source="icons.SearchMajor" :size="'w-4'" class="absolute w-4 p-px text-gray-400 transition transform -translate-y-1/2 left-3 top-1/2 z-1"></Icon>
        <Icon v-else @click="searchData = ''" :source="icons.CancelMajor" :size="'w-4'" class="absolute w-4 text-gray-400 transition transform -translate-y-1/2 rounded-full cursor-pointer hover:text-gray-600 left-3 top-1/2 dark:text-dark-color dark:hover:text-primary-700"></Icon>
        <button
            v-if="brands.length"
            class="absolute transition cursor-pointer right-3 top-2"
            :class="isopen ? 'text-primary-600' : 'text-gray-500 dark:text-dark-color dark:hover:text-primary-700 hover:text-gray-700'"
            @click="open"
        >
            <Icon :source="icons.AdjustMinor" :size="'w-4'"></Icon>
        </button>

        <input
            v-model="searchData"
            type="text"
            class="block w-full h-9 border outline-interactive-600 transition border-gray-300 dark:bg-dark dark:text-dark-color rounded shadow-sm pl-9 sm:text-sm  dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent focus:border-gray-300 dark:border-gray-600"
            placeholder="Search"
            @keydown.enter="searchData = $event.target.value.trim()"
        >
        <div
            v-if="isopen"
            class="absolute z-10 w-full px-3 py-1 overflow-y-auto bg-white dark:bg-dark dark:text-dark-color rounded max-h-full-17 shadow-lg"
            :class="largeScreen ? 'left-0 right-full top-full mt-2' : 'right-0 left-full top-0 ml-2' "
        >
            <div class="flex justify-between items-center py-3 mb-1 text-gray-800 border-b border-gray-200 dark:border-gray-600">
                <h3 class="uppercase text-xs font-semibold dark:text-dark-color">Categories</h3>
                <Icon @click="isopen=false" :source="icons.CancelMajor" :size="'w-4'" class="w-5 h-5 p-px text-gray-500 transition rounded-full cursor-pointer hover:text-gray-600 dark:text-dark-color dark:hover:text-primary-700"></Icon>
            </div>
            <ul class="inline-block w-full">
                <li
                    class="relative p-1 py-2 text-sm capitalize cursor-pointer transition-search"
                    @click="toggleOpen('')"
                >
                    <label
                        class="cursor-pointer"
                        :class="
                            selected.cate ? 'text-gray-700 hover:text-gray-800 dark:text-dark-color dark:hover:text-primary-700' : 'text-primary-600'
                        "
                    >
                        All icons ({{ cateCount }})
                    </label>
                </li>
                <li
                    v-for="(brand) in brands"
                    :key="brand"
                    class="relative py-1.5 text-sm capitalize transition-search border-t border-gray-100 dark:border-gray-600"
                >
                    <label
                        class="flex items-center p-1 text-gray-700 cursor-pointer hover:text-gray-800 dark:text-dark-color dark:hover:text-primary-700"
                        @click="toggleOpen(brand)"
                    >
                        {{ brand.replace(/-/, ' ') }} ({{ catesComp[brand]['All'] }})
                        <Icon :source="openComp === brand ? icons.MinusMajor : icons.MobilePlusMajor" :size="'w-4'" class="ml-auto text-gray-500 dark:text-dark-color"></Icon>
                        <!-- <Icon :source="icons.ChevronDownMinor" :size="'w-4'" :class="{'transform rotate-180': openComp === brand}"></Icon> -->
                    </label>
                    <ul
                        v-if="keys(catesComp[brand]).length && openComp === brand"
                        class="py-0.5 mt-1 -mb-1.5 pl-3 overflow-y-auto"
                    >
                        <li
                            v-for="(countCate, cate, iCate) in catesComp[brand]"
                            :key="cate"
                            class="relative p-1 cursor-pointer transition-search"
                            @click="setSelectedCatgory(brand + '|||' +cate)"
                        >
                            <label
                                class="capitalize cursor-pointer text-bm"
                                :class="(brand + '|||' +cate === selected.cate) ? 'text-primary-600' : 'text-gray-500 dark:text-dark-color dark:hover:text-primary-700 hover:text-gray-700'"
                            >
                                {{ cate.replace(/-/, ' ') }} ({{ countCate }})
                            </label>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
    </div>
</template>

<script lang="ts">
    import {CancelMajor, SearchMajor, AdjustMinor, ChevronDownMinor, MobilePlusMajor, MinusMajor} from '@/assets/icons';
    import { Popmenu } from '@/mixins';
    export default {

        props: {
            search: {
                type: String,
                default: ''
            },
            cates: {
                type: Object,
                default(){
                    return {};
                }
            }
        },
        mixins: [ Popmenu ],
        emits: ['update:search'],
        data() {
            return {
                searchData: this.search,
                selected: {
                    cate: null
                },
                openTab: '',
                itv: null,
                icons: {
                    CancelMajor, SearchMajor, AdjustMinor, ChevronDownMinor, MobilePlusMajor, MinusMajor
                }
            };
        },
        computed: {
            catesComp(){
                let cates = this.$helpers.copy(this.cates);
                for(let k in cates) {
                    let cate = cates[k];
                    let keys = this.keys(cate);
                    if(!keys['All']){
                        let count = this.sum(Object.values(cate));
                        cates[k] = Object.assign({All: count}, cate);
                    }
                }
                return cates;
            },
            brands(){
                return Object.keys(this.catesComp);
            },
            openComp: {
                get(){
                    return this.openTab ?? this.brands[0];
                },
                set(val){
                    this.openTab = val;
                }
            },
            cateCount(){
                return this.sum(this.brands.map(x => this.catesComp[x].All));
            },
            value(){
                return this.selected.cate ? (this.searchData + '|||' + this.selected.cate) : this.searchData;
            },
            largeScreen() {
                return this.$store.getters['builder/largeScreen'];
            }
        },
        watch: {
            search: function (val) {
                this.initData(val);
            },
            'selected.cate': function() {
                if(this.isEmit) {
                    this.$emit('update:search', this.value);
                }
                this.isEmit = true;
            },
            value: function (value) {

                clearTimeout(this.itv);

                this.itv = setTimeout(() => {
                    this.$emit('update:search', value);
                }, 500)
            },
            brands: {
                deep: true,
                handler: function(val){
                    if(!val.length) {
                        this.selected.cate = '';
                    }
                }
            }
        },
        created () {
            this.initData(this.search, true);
        },
        methods: {
            setSelectedCatgory(val){
                this.selected.cate = val;
                this.isopen = false;
            },
            keys(obj){
                if(typeof obj !== 'object'){
                    return [];
                }
                return Object.keys(obj);
            },
            sum(numbers){
                return numbers.reduce((a, b) => a + b, 0);
            },
            toggleOpen(brand){
                if(this.openTab === brand){
                    this.openTab = '';
                }
                else{
                    this.openTab = brand;
                    this.selected.cate = brand + '|||All';
                }
            },
            initData(val ,created){
                let qs = val.split('|||');
                if(qs.length >= 3){
                    this.searchData = qs.slice(0, -2).join('|||');
                    if(created) {
                        this.isEmit = false;
                    }
                    this.selected.cate = qs.slice(1).join('|||');
                    this.openTab = qs[1];
                }
                else{
                    this.searchData = val;
                }
            }
        }
    };
</script>
<style>
.component-fade-enter-active, .component-fade-leave-active {
  transition: all 0.5s cubic-bezier(0.47, -0.66, 0.18, 1.46);
}
.component-fade-enter, .component-fade-leave-to{
    transform: translateY(-20%);
    opacity: 0;
}
.transition-search {
    transition: all 0.5s cubic-bezier(0.47, -0.66, 0.18, 1.46)
}
.max-h-40{
    max-height: 10rem;
}
.max-h-full-17{
    max-height: calc(100vh - 17.3rem);
}
</style>
