import { addSwitch, refactorArray } from "../../utils";

const icon: GroupFieldType = {
    group_name: "icon",
    group_title: "Icon",
    params: [
        {
            name: "iconPrimaryColor",
            label: "Color",
            type: "color",
            options: {
                oneline: true,
                global: {
                    type: "colors"
                }
            },
            css: {
                properties: {
                    color: ""
                }
            }
        },
        {
            name: "iconFontSize",
            label: "Size",
            type: "number",
            options: {
                responsive: true,
                units: {
                    px: {
                        min: 0,
                        max: 300
                    }
                }
            },
            css: {
                selector: " svg",
                properties: {
                    height: "",
                    width: ""
                }
            }
        },
        {
            name: "iconTransform",
            label: "Rotate",
            type: "number",
            options: {
                responsive: true,
                min: 0,
                max: 360
            },
            css: {
                selector: " svg",
                properties: {
                    transform: "rotate(%value%deg)"
                }
            }
        }
    ]
};

const iconParamsDefault : FieldType[]= [
    {
        name: "iconPrimaryColor",
        label: "Color",
        type: "color",
        options: {
            oneline: true,
            global: {
                type: "colors"
            },
        },
        css: {
            properties: {
                color: ""
            }
        }
    },
    {
        name: "iconBackground",
        label: "Background",
        type: "background",
        options: {
            oneline: true,
            responsive: true,
        },
        css: {
            properties: {
                background: ""
            }
        }
    },
    {
        type: "popup",
        label: "Border",
        name: "iconBorder",
        options: {
            responsive: true,
            oneline: true,
            type: "border",
        },
        css: {
            properties: {
                border: ""
            }
        }
    },
    {
        name: "iconBoxShadow",
        label: "Box Shadow",
        type: "popup",
        options: {
            oneline: true,
            type: "box-shadow",
        },
        css: {
            properties: {
                "box-shadow": ""
            }
        }
    },
    {
        name: "iconBorderRadius",
        label: "Border Radius",
        type: "dimension",
        options: {
            responsive: true,
            type: "radius",
            units: {
                "%": {
                    min: 0,
                    max: 100
                },
                px: {
                    min: 0,
                    max: 1000
                }
            }
        },
        css: {
            properties: {
                "border-radius": ""
            }
        }
    },
]

let indexColor = icon.params.findIndex((item) => item.name === "iconPrimaryColor");

const iconHover = (selector: string = ':hover'): GroupFieldType => {
    return {
        group_name: "icon_hover",
        group_title: "Icon",
        params: [
            ...refactorArray(icon.params, indexColor),

            {
                name: "tab",
                type: "tab",
                value: "normal",
                options: {
                    tabs: [
                        {
                            name: "normal",
                            title: "Normal"
                        },
                        {
                            name: "hover",
                            title: "Hover"
                        }
                    ]
                },
                css: {
                    isCss: false
                }
            },
            ...addSwitch(iconParamsDefault,'normal'),
            ...addSwitch(iconParamsDefault,'hover',selector),
            {
                name: "iconAnimationHoverMode",
                label: "Hover Animation",
                type: "popup",
                options: {
                    type: "dropdown",
                    values: "animation",
                    size: "small",
                    visible: {
                        keep_data: true,
                        condition: (data: any) => {
                            return data["tab"] === "hover";
                        }
                    }
                },
                css: {
                    selector,
                    properties: {
                        animation: ""
                    }
                }
            },
            {
                name: "iconTransition",
                type: "number",
                label: 'Transition Duration <span class="lowercase">(ms)</span>',
                options: {
                    min: 0,
                    max: 1500,
                    visible: {
                        keep_data: true,
                        condition: (data: any) => {
                            return data["tab"] === "hover";
                        }
                    }
                },
                css: {
                    properties: {
                        transition: "all %value%ms ease"
                    }
                }
            }
        ]
    }
};

let indexTab = iconHover().params.findIndex((item) => item.name === "tab");

const iconActive  = (selector: string = ':hover'): GroupFieldType => {
    return {
        group_name:'icon_active',
        group_title:'Icon',
        params:[
            ...refactorArray(iconHover(selector).params, indexTab, {
                name: "tab",
                type: "tab",
                value: "normal",
                options: {
                    tabs: [
                        {
                            name: "normal",
                            title: "Normal"
                        },
                        {
                            name: "hover",
                            title: "Hover"
                        },
                        {
                            name: "active",
                            title: "Active"
                        }
                    ]
                },
                css: {
                    isCss: false
                }
            }),
            ...addSwitch(iconParamsDefault,'active','.ecom-icon-active')
        ]
    }
};

export default icon;
export const hover = iconHover;
export const active = iconActive;
