<template>
    <component
        :is="data?.settings?.tag ?? (is == 'section' ? 'section' : 'div')"
        v-bind="rowAttrs"
        @mouseenter="overRow"
        @mouseleave="outRow"
        @contextmenu="rightClickMenu"
        :class="exporting===false ? 'dom-relative' : ''"
    >
        <div v-if="exporting===false">
            <!-- <span @mouseup="editRow" class="mask-dom top"></span> -->
            <!-- <span @mouseup="editRow" class="mask-dom bottom"></span> -->
            <span @mouseup="editRow" class="mask-dom left"></span>
            <span @mouseup="editRow" class="mask-dom right"></span>
        </div>
        <div
            v-if="deep < 5"
            :data-deep="deep"
            class="core__row--columns"
            :class="data?.settings?.['content-width'] == 'full' ? 'core__row--full' : ''"
        >
            <ColumnElement
                v-for="(col, i) in visibleColumns"
                :key="col.id"
                :index="i"
                :data="col"
                :deep="deep"
                :section_id="section_id"
            />
        </div>
        <div
            v-else-if="exporting===false"
            class="core__notice--error"
            :data-deep="deep"
        >
            <span>Sorry, We do not support inner row inside the inner row!</span>
        </div>
        <transition
            enter-active-class="transition duration-300 ease-out"
            enter-from-class="opacity-0"
            enter-to-class="opacity-100"
            leave-active-class="transition duration-300 ease-in"
            leave-from-class="opacity-100"
            leave-to-class="opacity-0"
        >
            <div v-show="isActive" class="transition">
                <div class="label-section" :title="'Edit ' + is" :class="isEditing ? 'active' : ''" @mousedown="startDrag" @mouseup="editRow" :style="{marginBottom: '-1px'}">
                    <Icon :source="icons.SectionMajor" class="icon-builder"></Icon>
                    <span v-if="data?.title && data?.title !== ''">{{ data.title }}</span>
                    <span v-else>{{ is }} {{ index + 1 }} </span>
                </div>
                <div class="section__settings-edit" :style="{ marginBottom: shouldApplyMarginBottom ? '-28px' : '0' }" v-if="isEditing">
                    <!-- <span class="btn-in-section" :title="'Edit ' + is" :class="isEditing ? 'active' : ''" @mouseup="editRow">
                        <Icon :source="icons.EditMajor" class="icon-builder"></Icon>
                    </span> -->
                    <span class="btn-in-section" v-if="is == 'section' && params?.page !== 'menu'" :title="'Add ' + is" @click="addSection">
                        <Icon :source="icons.MobilePlusMajor" class="icon-builder"></Icon>
                    </span>
                    <span class="btn-in-section" v-if="is == 'section' && params?.page !== 'menu' && !isFirst" :title="'Move to previous position'" @click="moveSection($event, 'prev')">
                        <Icon :source="icons.ArrowUpMinor" class="icon-builder"></Icon>
                    </span>
                    <span class="btn-in-section" v-if="is == 'section' && params?.page !== 'menu' && !isLast" :title="'Move to next position'" @click="moveSection($event, 'next')">
                        <Icon :source="icons.ArrowDownMinor" class="icon-builder"></Icon>
                    </span>
                    <span v-if="params?.page !== 'menu'" :title="'Delete ' + is" class="btn-in-section section__settings--delete" @click="dropOut">
                        <Icon :source="icons.DeleteMajor" class="icon-builder"></Icon>
                    </span>
                </div>
                <div v-if="is == 'section' && params?.page !== 'menu'" class="section__settings--add" :class="isEditing ? 'active' : ''">
                    <span :title="'Double space to add section'" @click="addSectionAfter">
                        <Icon :source="icons.CirclePlusMajor" class="icon-builder"></Icon>
                    </span>
                </div>
            </div>
        </transition>
        <div v-if="['section', 'row'].includes(is)" class="ecom-section__overlay">
            <video
                class="ecom-video-background"
                :muted="true"
                defaultmuted
                playsinline
                preload="true"
                v-if="show_video_background && ['upload','external'].includes(data?.settings?.source)"
                :data-play-pause-inview="data.settings?.inview_playpause"
                :loop="data.settings.loop"
                :autoplay="data.settings.autoplay"
                :data-source="data.settings?.source"
                :src="video_url"
                ref="video"
            />
            <div
                class="ecom-video-background-youtube-wrapper"
                v-if="show_video_background && ['youtube','vimeo'].includes(data?.settings?.source)"
            >
                <div
                    class="ecom-video-background-youtube"
                >
                    <iframe
                        class="ecom-video-background"
                        frameborder="0"
                        allowfullscreen="1"
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                        style="border: 0; width: 100%; height: 100% !important"
                        :src="data?.settings?.source == 'vimeo' ? linkVimeo : linkYoutube"
                    />
                </div>
            </div>
            <div class="ecom-overlay" />
        </div>
        <template v-else />
        <template v-if="canDrop">
            <div
                class="placeholder__top"
                style="z-index:11"
                @mouseenter="placehover"
            />
            <div
                class="placeholder__bottom"
                style="z-index:11"
                @mouseenter="placehover"
            />
        </template>
        <template v-else />
        <div
            v-if="is == 'section' && !exporting && !isFullScreen && params?.page !== 'menu'"
            class="core__row--padding-bottom"
            @mousedown.stop="startDragPadding($event, 'bottom')"
        >
            <span
                v-if="isDragPadding"
                class="core__block--padding-value"
                v-html="paddingCurrentVal + 'px'"
            />
            <span
                v-if="isDragPadding"
                class="core__block--padding-markup"
                :style="{height: paddingCurrentVal + 'px'}"
            />
        </div>
    </component>
</template>

<script>
import ColumnElement from './Column.vue';
import { Core, Element } from '@/mixins';

import { EditMajor, MobilePlusMajor, DeleteMajor, ArrowUpMinor, ArrowDownMinor, SectionMajor, CirclePlusMajor } from '@/assets/icons';
export default {
    name: 'Row',
    docs: 'https://help.ecomposer.io/docs/elements/basic-elements/inner-row/',
    components: {
        ColumnElement,

    },
    mixins: [Core, Element],
    props: {
        data: {
            type: Object,
            default() {
                return {
                    columns: [],
                    settings: {
                        shopify_type: 'product'
                    }
                };
            }
        },
        section_id: {
            type: String,
            default: ''
        },
        deep: {
            type: Number,
            default: 0
        },
        index: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            shouldApplyMarginBottom: false,
            is: this.deep == 1 ? 'section' : 'row',
            name: this.deep == 1 ? 'section' : 'inner row',
            over: false,
            dragging: false,
            placeholder: '',
            rand: '094',
            timmer: 0,
            icons: {
                EditMajor, MobilePlusMajor, DeleteMajor, ArrowUpMinor, ArrowDownMinor, SectionMajor, CirclePlusMajor
            },
            isMuted: this.data?.settings?.muted,
            space: 0,
            position: 0,
            isDragPadding: false,
            paddingPosition: '',
            paddingCurrentVal: 0
        };
    },
    computed: {
        stack() {
            return this.is == 'section'
                ? this.$parent.$parent.sections
                : this.$parent?.$parent?.$parent?.blocks ?? [];
        },

        row() {
            return this.data;
        },

        columns() {
            return this.row.columns ? this.row.columns : [];
        },

        colids() {
            let ids = [];
            this.columns.forEach((col) => {
                ids.push(col.id);
            });
            return ids.join(',');
        },

        rowSettings() {
            return this.row.settings ? this.row.settings : {};
        },

        stretch() {
            return this.row.settings ? this.row.settings.stretch : false;
        },

        visibleColumns() {
            return this.columns.filter((col) => {
                if (col.id === undefined) {
                    col.id = this.$helpers.randid();
                    col.IDInit = true;
                }
                return col.visibility !== false;
            });
        },

        styleattr() {
            let bg = null;
            if(this.exporting && this.data?.settings?.dynamic_background && ['product','collection','article'].includes(this.params.page) )
            {
                bg = "{{"+this.params.page+" | img_url:\"master\" }}";
                bg = 'url(\'liquid[['+ encodeURIComponent(bg)+']]\')';
            }
            return {
                wrapper: {
                    // outline: this.isActive ? `${this.deep === 1 ? '2' : '1'}px solid ${this.isEditing ? 'rgb(var(--primary-color-active))' : 'rgba(var(--primary-color-light))'} `: '',
                    transition: 'all ease 250ms',
                    boxShadow: this.isActiveBorder ? `inset 0 0 0 ${this.isEditing ? '2px rgb(var(--primary-color-active))' : '1px rgba(var(--primary-color-light))'}` : '',
                    zIndex: this.exporting ? 'inherit' : (this.isActive ? 3 : 0),
                    backgroundImage: bg
                },
                settings: `display: ${this.isActive ? 'inline-block' : 'none'}, margin-bottom: ${this.shouldApplyMarginBottom ? '-28px' : '0'}`
            };
        },

        isActive() {
            return (
                this.exporting === false &&
                !this.isFullScreen &&
                (this.over) && !this.dragCurrent);
        },

        isActiveBorder() {
            return (
                this.exporting === false &&
                !this.isFullScreen &&
                (this.over || this.activeElement?.data?.id === this.data.id || this.editingElement?.data?.id === this.data.id) && !this.dragCurrent);
        },

        isEditing() {
            return this.editingElement?.data?.id === this.data.id;
        },

        isFirst() {
            return this.index == 0;
        },

        isLast() {
            return (
                this?.$parent?.visibleSections &&
                this.index == this?.$parent?.visibleSections?.length - 1
            );
        },

        isDraggingCol() {
            return this.dragCurrent && this.dragCurrent.is == 'column';
        },

        isDropTo() {
            return this.dropTo === this && this.canDrop && this.dragCurrent !== this;
        },

        canDrop() {
            return this.$helpers.canDrop(this.dragCurrent, this);
        },

        rowAttrs() {
            const attrs = {
                ref: 'wrapper',
                style: this.styleattr.wrapper,
                'data-placeholder': this.placeholder ? this.placeholder : null,
                key: this.row.refresh,
                class: {
                    'ecom-current-section-drag': this.sectionDrag,
                    'ecom-animated': !this.exporting,
                    'ecom-animation': this.data?.advanced?.animation_type,
                    'ecom-row ecom-core': true,
                    drop__placeholder: this.isDropTo,
                    drag__placeholder: this.isDragMe,
                    core__block: this.deep > 1,
                    'ecom-section': this.deep === 1,
                    'ecom-section__stretched': this.stretch,
                    'ecom-section__video-bg' : this.show_video_background && ['youtube','vimeo'].includes(this.data?.settings?.source),
                    ...this.classes
                },
                'data-id': this.data.id,
                ...this.attributes
            };

            if(this.scrollingAnimation) {
                attrs['ecom-scrolling-animation'] = this.scrollingAnimation;
            }

            return attrs;
        },
        video_url(){
            let source = this.data?.settings?.source;
            if(!['upload','external'].includes(source))
            {
                return '';
            }
            if(source === 'upload')
            {
                return this.data?.settings.video?.value??'';
            }
            return this.data?.settings?.url??'';
        },
        linkYoutube() {
            if (this.data?.settings?.srcYoutube) {
                const regExpYt = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|&v=)([^#&?]*).*/;
                let val = this.data?.settings?.srcYoutube.match(regExpYt);
                let videoIdYt = val && val[2].length === 11 ? val[2] : null;
                return `${location.protocol}//www.youtube.com/embed/${videoIdYt}?version=3?${
                    this.data?.settings?.start_time ? "&start=" + this.data?.settings?.start_time : ""
                }${this.data?.settings?.end_time ? "&end=" + this.data?.settings?.end_time : ""}&controls=0&mute=${
                    this.data?.settings?.muted ? 1 : 0
                }&showinfo=0&iv_load_policy=3&playlist=${videoIdYt}&autoplay=${this.data?.settings?.autoplay ? 1 : 0}&loop=${this.data?.settings?.loop ? 1 : 0}`;
            } else {
                return "";
            }
        },
        linkVimeo() {
            if (this.data?.settings?.srcVimeo) {
                const regExpVm = /(?:http?s?:\/\/)?(?:www\.)?(?:vimeo\.com)\/?(.+)/;
                let vals = this.data?.settings?.srcVimeo.match(regExpVm);
                let videoIdVm = vals ? vals[1] : null;
                return `${
                    location.protocol
                }//player.vimeo.com/video/${videoIdVm}?rel=0?version=3&controls=0&mute=${this.data?.settings?.muted ? 1 : 0}${
                    this.data?.settings?.start_time ? "&byline=1#t=00h00m" + this.data?.settings?.start_time + "ss" : ""
                }&showinfo=0&autoplay=${this.data?.settings?.autoplay ? 1 : 0}&loop=${this.data?.settings?.loop ? 1 : 0}`;
            } else {
                return "";
            }
        },
        params(){
            return this.$store.getters['page/params'];
        },
        settings() {
            let settings =  [
                {
                    group_title: 'Layout',
                    params: [
                        {
                            type: 'popup',
                            name: 'content-width',
                            label: 'Content Width',
                            options: {
                                type: 'dropdown',
                                values: {
                                    boxed: 'Boxed',
                                    full: 'Full Width'
                                }
                            }
                        },
                        {
                            name: 'max-width',
                            label: 'Max width',
                            type: 'number',
                            css: {
                                selector: '>div.core__row--columns'
                            },
                            value: '1140px',
                            options: {
                                units: {
                                    px: {
                                        min: 250,
                                        max: 1600,
                                        step: 1
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data['content-width'] === 'boxed';
                                    }
                                },
                                responsive: true
                            }
                        },
                        {
                            name: 'height',
                            label: 'Content height',
                            type: 'popup',
                            css: {
                                selector: '>div.core__row--columns'
                            },
                            options: {
                                type: 'dropdown',
                                values: {
                                    '100vh': 'Fix To Screen',
                                    auto: 'Min Height'
                                },
                                oneline: true
                            }
                        },
                        {
                            name: 'min-height',
                            label: 'Minimum height',
                            type: 'number',
                            css: {
                                selector: '>div.core__row--columns>.ecom-column.ecom-core>.core__column--wrapper>.core__blocks'
                            },
                            options: {
                                units: {
                                    px: {
                                        min: 0,
                                        max: 1440,
                                        step: 1
                                    },
                                    vh: {
                                        min: 0,
                                        max: 100,
                                        step: 1
                                    },
                                    wh: {
                                        min: 0,
                                        max: 100,
                                        step: 1
                                    }
                                },
                                responsive: true,
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        return data?.height == 'auto';
                                    }
                                }
                            }
                        },
                        {
                            name: 'padding',
                            label: 'Column gap',
                            type: 'popup',
                            css: {
                                selector: '>.core__row--columns>.ecom-column>.core__column--wrapper'
                            },
                            options: {
                                responsive: true,
                                values: {
                                    '0px': 'No gap',
                                    '5px': 'Narrow',
                                    '15px': 'Extended',
                                    '20px': 'Wide',
                                    '30px': 'Wider',
                                    inherit: 'Custom'
                                },
                                preview: 'title',
                                type: 'dropdown'
                            }
                        },
                        {
                            name: 'custom-padding',
                            label: 'Custom gap',
                            type: 'number',
                            css: {
                                selector: '>.core__row--columns>.ecom-column>div.core__column--wrapper',
                                properties: {
                                    padding: ''
                                }
                            },
                            options: {
                                responsive: true,
                                units: {
                                    px: {
                                        min: 0,
                                        max: 500,
                                        step: 1
                                    },
                                    '%': {
                                        min: 0,
                                        max: 100,
                                        step: 1
                                    },
                                    vh: {
                                        min: 0,
                                        max: 100,
                                        step: 1
                                    },
                                    vw: {
                                        min: 0,
                                        max: 100,
                                        step: 1
                                    }
                                },
                                visible: {
                                    keep_data: false,
                                    condition: (data) => {
                                        let key = 'padding';
                                        if(this.screen !== 'desktop')
                                        {
                                            key += `__${this.screen}`;
                                        }
                                        return data[key] === 'inherit';
                                    }
                                },
                                responsive: true
                            }
                        },
                        {
                            type: 'line'
                        },
                        {
                            type: 'paragraph',
                            content: '** Align columns inside **'
                        },
                        {
                            type: 'popup',
                            label: 'Align items',
                            name: 'align-items',
                            css: {
                                selector: '>div.core__row--columns>.ecom-core.ecom-column>.core__column--wrapper>div.core__blocks>div.core__blocks--body',
                            },
                            options: {
                                responsive: true,
                                values: {
                                    'flex-start': 'Top',
                                    center: 'Middle',
                                    'flex-end': 'Bottom',
                                    'baseline': 'Base line',
                                    stretch: 'Stretch'
                                },
                                preview: 'title',
                                type: 'dropdown'
                            }
                        },
                        (this.columns.length > 1 ? {
                            type: 'popup',
                            label: 'Align content',
                            name: 'align-content',
                            css: {
                                selector: '>div.core__row--columns>.ecom-core.ecom-column>.core__column--wrapper>div.core__blocks>div.core__blocks--body',
                                properties: {
                                    'align-items':''
                                }
                            },
                            options: {
                                responsive: true,
                                values: {
                                    'flex-start': 'Top',
                                    center: 'Middle',
                                    'flex-end': 'Bottom',
                                    'space-between': 'Space Between',
                                    'space-around': 'Space Around',
                                    'space-evenly': 'Space Evenly'
                                },
                                preview: 'title',
                                type: 'dropdown'
                            }
                        } : {}),
                        (this.screen != 'desktop' || this.columns.length === 1 ? {
                            type: 'popup',
                            label: 'Horizontal align',
                            name: 'justify-content',
                            css: {
                                selector: '>div.core__row--columns'
                            },
                            options: {
                                responsive: true,
                                values: {
                                    'left': 'Left',
                                    'center': 'Center',
                                    'right': 'Right',
                                },
                                type: 'dropdown'
                            }
                        } : {}),
                        {
                            type: 'line'
                        },
                        (this.is == 'section'
                        ? {
                            type: 'toggle',
                            label: 'Stretch section',
                            name: 'stretch',
                            /*description: 'Stretch the section to the full width of the page using JS',*/
                            options: {
                                values: {
                                    on: {
                                        label: 'YES',
                                        value: true
                                    },
                                    off: {
                                        label: 'NO',
                                        value: false
                                    }
                                },
                                oneline: true
                            }
                        } : {}),

                        {
                            type: 'toggle',
                            label: 'Overflow hidden',
                            name: 'overflow',
                            css: {
                                selector: '>div.core__row--columns'
                            },
                            options: {
                                values: {
                                    on: {
                                        label: 'YES',
                                        value: 'hidden'
                                    },
                                    off: {
                                        label: 'NO',
                                        value: ''
                                    }
                                },
                                oneline: true
                            }
                        },
                    ]
                },
                {
                    group_title: 'Structure',
                    params: [
                        {
                            name: 'tag',
                            label: '<span class="uppercase">HTML</span> Tag',
                            type: 'popup',
                            options: {
                                type: 'dropdown',
                                values: {
                                    div: 'Div',
                                    header: 'Header',
                                    footer: 'Footer',
                                    main: 'Main',
                                    article: 'Article',
                                    section: 'Section',
                                    aside: 'Aside',
                                    nav: 'Nav'
                                }
                            }
                        },
                        {
                            label: 'Structure',
                            type: 'structure',
                            structure: this.columns
                        }
                    ]
                },

            ];
            if(['section', 'row'].includes(this.is))
            {
                settings.push({
                    group_title: 'Video background',
                    params: [
                        {
                            type:'popup',
                            name:'source',
                            label:'Video source',
                            options: {
                                type: 'dropdown',
                                default: false,
                                values: {
                                    none: 'None',
                                    upload: 'Upload video',
                                    external: 'Custom video url',
                                    youtube: "Youtube",
                                    vimeo: "Vimeo",
                                }
                            }
                        },
                        {
                            type: 'picker',
                            label: 'Choose video <i>(maximum 10Mb)</i>',
                            name: 'video',
                            options: {
                                type: 'video',
                                visible:function(data){ return data['source'] === 'upload'}
                            }
                        },
                        {
                            type: 'text',
                            label: 'Enter your custom video url',
                            name: 'url',
                            placeholder: 'https://the-domain.com/*/video.mp4',
                            options: {
                                visible:function(data){ return data['source'] === 'external'}
                            }
                        },
                        {
                            name: "srcYoutube",
                            label: "Youtube link",
                            type: "text",
                            options: {
                                visible:function(data){ return data['source'] === 'youtube'}
                            }
                        },
                        {
                            name: "srcVimeo",
                            label: "Vimeo link",
                            type: "text",
                            options: {
                                visible:function(data){ return data['source'] === 'vimeo'}
                            }
                        },
                        {
                            name: "start_time",
                            label: "Start time (s)",
                            type: "number",
                            options: {
                                half: true,
                                slider: false,
                                min: 0,
                                max: 1000,
                                visible:function(data){ return data['source'] === 'vimeo' || data['source'] === 'youtube'}
                            }
                        },
                        {
                            name: "end_time",
                            label: "End time",
                            type: "number",
                            options: {
                                slider: false,
                                min: 0,
                                max: 1000,
                                half: true,
                                visible:function(data){ return data['source'] === 'youtube'}
                            }
                        },
                        {
                            type:'toggle',
                            name: 'loop',
                            label:'Loop',
                            options:{
                                values:{
                                    on:{
                                        label:'On',
                                        value: true
                                    },
                                    off: {
                                        label: 'Off',
                                        value: false
                                    }
                                },
                                //visible:function(data){ return data['source'] === 'external' || data['source'] === 'upload'}
                            }
                        },
                        {
                            type:'toggle',
                            name: 'autoplay',
                            label:'Autoplay',
                            options:{
                                values:{
                                    on:{
                                        label:'On',
                                        value: true
                                    },
                                    off: {
                                        label: 'Off',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'paragraph',
                            content: 'Note: Chromium browsers do not allow autoplay in most cases. However, muted autoplay is always allowed.'
                        },
                        {
                            type:'toggle',
                            name: 'muted',
                            label:'Muted',
                            options:{
                                values:{
                                    on:{
                                        label:'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                },
                                visible:function(data){ return data['source'] != 'vimeo'}
                            }
                        },
                        {
                            type:'toggle',
                            name: 'inview_playpause',
                            label:'Auto play/pause when in view',
                            options:{
                                values:{
                                    on:{
                                        label:'Yes',
                                        value: true
                                    },
                                    off: {
                                        label: 'No',
                                        value: false
                                    }
                                }
                            }
                        },
                        {
                            type: 'paragraph',
                            content: 'Note: This featured not working on Safari.'
                        },
                    ]
                })
                if(['collection','product','article'].includes(this.params.page))
                {
                    settings.push({
                        group_title: 'Dynamic background',
                        params: [
                            {
                                type: 'paragraph',
                                content: `Show current ${this.params.page} featured image as background.\n
                                You can config background properties on tab "Design" in "Background" group\n
                                **The feature image only work when publish on live site**`
                            },
                            {
                                type: 'toggle',
                                label: 'Show featured image as background',
                                name: 'dynamic_background',
                                /*description: 'Stretch the section to the full width of the page using JS',*/
                                options: {
                                    values: {
                                        on: {
                                            label: 'YES',
                                            value: true
                                        },
                                        off: {
                                            label: 'NO',
                                            value: false
                                        }
                                    },
                                    oneline: true
                                }
                            }
                        ]
                    })
                }
            }
            return settings;
        },
        show_video_background(){
            return ['section', 'row'].includes(this.is) && ['upload','external', 'youtube', 'vimeo'].includes(this.data.settings?.source);
        },
        style() {
            return [
                {
                    group_alias: 'background',
                    options:{
                        group_title: 'Background',
                        css: true,
                        reset: true
                    }
                },
                (['section', 'row'].includes(this.is)
                    ? {
                        group_alias: 'background:overlay',
                        options:{
                            group_title: 'Background Overlay',
                            group_name: 'backgroud_overlay',
                            selector: '>div.ecom-section__overlay'
                        },
                        modify: {
                            remove: {
                                index: 5,
                                length: 1
                            },
                            params: [
                                {
                                    position:6,
                                    fields:[
                                        {
                                            type: "background",
                                            label: "Background",
                                            name: "sectionBackgroundHover",
                                            options: {
                                                oneline: true,
                                                responsive: true,
                                                types: ["classic", "gradient"]
                                            },
                                            css: {
                                                selector: "root:hover>.ecom-section__overlay>.ecom-overlay",
                                                properties: {
                                                    background: ""
                                                }
                                            }
                                        },
                                    ]
                                }
                            ]
                        }
                      }
                    : null
                ),
                {
                    group_alias: 'border',
                    options:{
                        group_title: 'Border',
                        css: true,
                        reset: true
                    }
                },
                {
                    group_alias: 'typography',
                    options:{
                        group_title: 'Typography',
                        css: true,
                        reset: true,
                    }
                }
            ].filter(p => p);
        },

        css() {
            return `
                .ecom-row {
                    width: 100%;
                    position: relative;
                }
                .ecom-video-background-youtube-wrapper {
                    overflow: hidden
                }
                .ecom-section__overlay > .ecom-overlay, .ecom-section__overlay .ecom-video-background-youtube, .ecom-video-background-youtube-wrapper{
                    position:absolute;
                    top:0;
                    left:0;
                    bottom:0;
                    right:0;
                }
                .ecom-row:not(.ecom-section) {
                    float: left;
                }
                .ecom-row.ecom-section {
                    clear: both;
                }
                .ecom-row:hover {
                    z-index: 1;
                }
                .ecom-row>div.core__row--columns.core__row--full {
                    max-width: 100%;
                }
                @media (min-aspect-ratio: 16/9) {
                    .ecom-section__overlay .ecom-video-background-youtube  { height: 300%; top: -100%; }
                }
                @media (max-aspect-ratio: 16/9) {
                    .ecom-section__overlay .ecom-video-background-youtube  { width: 300%; left: -100%; }
                }
                @supports not (aspect-ratio: 16 / 9) {
                    .ecom-video-background-youtube:before {
                        display: block;
                        content: "";
                        width: 100%;
                        padding-top: 56.25%;
                    }
                    .ecom-video-background-youtube .ecom-video-background {
                        position: absolute;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        left: 0;
                    }
                    .ecom-video-background {
                        -o-object-fit: unset !important;
                        object-fit: unset !important;
                    }
                }
                @media (max-width: 767px) {
                    .ecom-section__video-bg .ecom-video-background-youtube {
                        display: none;
                    }
                }
                `;
        },

        nogap() {
            return this.rowSettings?.['remove-edge-gap'];
        },

        stacks(){
            return this.$store.getters['builder/stacks'];
        },
        screen(){
            return this.$store.getters['builder/screen'];
        },
        sections(){
            return this.$store.getters['page/sections'];
        },
        sectionDrag() {
            const sectionDrag = this.$store.getters['builder/sectionDrag'];
            return sectionDrag === this.section_id;
        }
    },

    watch: {
        // colids() {
        //     this.$store.dispatch('history/addStack', {
        //         type: 'columns',
        //         name: '',
        //         action: 'Changed'
        //     });
        // },
        stretch: {
            immediate: true,
            handler() {
                this.$helpers.dispatchResize();
            }
        },
        rowSettings: {
            deep: true,
            handler() {
                this.$helpers.dispatchResize();
            }
        },
        'data.settings.autoplay': {
            deep: false,
            handler(autoplay) {
                if(this.$refs.video) {
                    if(autoplay) {
                        this.$refs.video.load();
                        this.$refs.video.setAttribute('muted', '');
                    } else {
                        this.$refs.video.pause();
                        this.$refs.video.removeAttribute('muted');
                    }
                }
            }
        }
    },

    updated() {
        if (this.row.columns === undefined) {
            this.row.columns = [];
        }
    },

    mounted() {
        if(this.$refs.video && this.data?.settings?.autoplay) {
            this.$refs.video.setAttribute('muted', '');
        }
        if (this.exporting === false) {
            this.stacks[this.data.id] = this;
        }
    },

    methods: {
        moveSection(e, position) {
            let realIndex= this.stack.findIndex((s) => s.id == this.data.id),
                index = realIndex > -1 ? realIndex : this.index;
            const sections = this.$parent.sections.splice(index, 1);
            const newIndex = position === 'prev' ? index -1 : index + 1;
            this.$parent.sections.splice(newIndex, 0, sections[0]);

            setTimeout(() => {
                const html = this.idoc().querySelector('html');
                const element =  this.$refs.wrapper;
                const elementRect = element.getBoundingClientRect();
                const absoluteElementTop = elementRect.top + window.pageYOffset;
                const middle = absoluteElementTop - (html.offsetHeight / 2);

                this.scroll(middle);
            }, 100)

        },
        startDragPadding(e, name) {
            this.position = e['clientY'];
            this.paddingPosition = name;
            this.idoc().onmouseup = this.onMouseUp;
            this.idoc().onmousemove = this.onMouseMove;
        },
        onMouseMove(e) {
            const $el = this.$el,
                    advance = Object.assign({}, this.data.advanced);

            let padding = this.paddingPosition == 'bottom' ? advance?.spacing?.padding?.bottom : advance?.spacing?.padding?.top;
            let space = 0, result = '';
            padding = padding ? parseInt(padding.replace(/[^0-9.]/g, "")) : 0;

            this.idoc().body.style.cursor = 'ns-resize';

            let newP = e['clientY'];
            space = newP - this.position;

            if(Math.abs(space) > 3) {
                this.paddingCurrentVal = padding;
                this.isDragPadding = true;

                let padding_current = space + padding >= 0 ? space + padding : 0;
                if(this.paddingPosition == 'bottom') {
                    $el.style.setProperty('padding-bottom', (padding_current) + 'px', 'important');
                } else {
                    //$el.style.paddingTop = Math.abs(padding_current) + 'px';
                    $el.style.setProperty('padding-top', Math.abs(padding_current) + 'px', 'important');
                }
                this.paddingCurrentVal = result = Math.abs(padding_current);
            }

            this.space = result == 0 || result == '' ? 0 : result + 'px';
        },
        onMouseUp() {
            const currentScreen = this.currentScreen(), currentScreenName = currentScreen.name == 'desktop' ? '' :  currentScreen.name,
                    spacing_res = currentScreenName ? 'spacing__' + currentScreenName : 'spacing';

            const element = this, $el = element.$el.parentElement,
                    advance = Object.assign({}, this.data.advanced);
            if(!advance.hasOwnProperty(spacing_res)) {
                advance[spacing_res] = {
                    padding: {

                    }
                }
            } else if(!advance[spacing_res].hasOwnProperty('padding')) {
                advance[spacing_res].padding = {}
            }
            const space = this.space;
            this.space = 0;
            if(this.paddingPosition == 'bottom') {
                advance[spacing_res].padding.bottom = space;
            } else {
                advance[spacing_res].padding.top = space;
            }

            Object.assign(this.data.advanced, advance);

            setTimeout(() => {
                $el.style.paddingBottom = null;
                $el.style.paddingTop = null;
            }, 1000)
            this.isDragPadding = false;
            this.paddingCurrentVal = 0;
            this.idoc().body.style.cursor = 'default';
            this.idoc().onmouseup = null;
            this.idoc().onmousemove = null;
        },
        currentScreen(){
            return this.$store.getters['builder/currentScreen'];
        },
        overRow() {
            if (!this.isFullScreen) {
                this.over = true;
                this.parentsOut();
                clearTimeout(this.timmer);
            }
        },

        outRow() {
            if (!this.isFullScreen) {
                clearTimeout(this.timmer);
                this.timmer = setTimeout(() => {
                    this.over = false;
                }, 50);
            }
        },

        placehover(e) {
            if (this.isFullScreen || !this.canDrop) {
                return;
            }
            let pos = e.target.classList.contains('placeholder__top') ? 'top' : 'bottom';
            this.$store.commit('builder/setDropTo', this);
            this.$store.commit('builder/setDropPos', pos);
            this.placeholder = pos;
        },

        editRow() {
            let inst = this.is == 'section' ? this.$parent : this;
            console.log(this);
            this.$store.commit('builder/setActiveElement', this);
            this.$store.commit('builder/setEditingElement', inst);

            const rect = this.$el.getBoundingClientRect();
            const isNearTop = rect.top <= 50; // Kim tra nt c st mp trn mn hnh khng
            console.log(isNearTop);
            this.shouldApplyMarginBottom = isNearTop;

        },
        edit(){
            this.editRow();
        },

        addSection() {
            let realIndex= this.stack.findIndex((s) => s.id == this.data.id),
                index = realIndex > -1 ? realIndex : this.index;
            this.$parent.sections.splice(index, 0, {
                config: {},
                columns: []
            });

            this.$parent.indexofnew = index;
            this.over = false;
        },

        addSectionAfter() {
            let realIndex= this.stack.findIndex((s) => s.id == this.data.id),
                index = realIndex > -1 ? realIndex : this.index;
            this.$parent.sections.splice(realIndex + 1, 0, {
                config: {},
                columns: []
            });

            this.$parent.indexofnew = realIndex + 1;
            this.over = false;
        },

        refreshCols() {

            if (this.columns.length === 0) {
                this.$parent.$parent[this.is == 'row' ? 'blocks' : 'sections']?.splice(
                    this.$parent.index,
                    1
                );
            }

            this.columns.forEach((c) => {
                c.width = parseFloat((100 / this.columns.length).toFixed(2));
                c.settings.width = c.width+'%';
            });

            //this.row.refresh = this.$helpers.randid();

            this.dispatchResize();
        },

        duplicate() {
            let data = this.$helpers.generateElementId(this.$parent.cloneData());

            let height = this.$refs.wrapper.offsetHeight;
            if (this.is == 'section') {
                this.stack.splice(this.index + 1, 0, data);
            } else if (this.is == 'row') {
                this.stack.splice(this.index + 1, 0, data);
            }
            this.scroll(height);
        },

        paste(clipboard) {
            if(!clipboard) {
                return;
            }
            let data = this.$helpers.copy(clipboard.data);
            data = this.$helpers.generateElementId(data);
            switch (clipboard.is) {
                case 'section':
                    if (this.is == 'section') {
                        this.stack.splice(this.index + 1, 0, data);
                        this.scroll(this.$refs.wrapper.offsetHeight);
                    }
                    break;
                case 'column':
                    if (this.columns.length >= 10) {
                        this.notice({
                            message: 'Can not insert more than 10 columns in a ' + this.is,
                            type: 'error'
                        });
                    } else {
                        this.columns.splice(this.index + 1, 0, data);
                        this.refreshCols();
                    }
                    break;
                case 'block':
                    if ((data.row || data.group) && this.is == 'row') {
                        this.$parent.$parent.blocks.splice(this.$parent.index + 1, 0, data);
                    } else {
                        this.columns[0].blocks.push(data);
                    }
                    this.scroll(this.$refs.wrapper.offsetHeight / 2);
                    break;
            }
        },

        insertCol() {
            if (this.columns.length >= 10) {
                this.notice({
                    message: 'Can not insert more than 10 columns in a row',
                    type: 'error'
                });

            } else {
                this.columns.push({
                    width: Math.round(100 / (this.columns.length + 1)),
                    settings: {
                        width: '50%'
                    },
                    blocks: []
                });
                this.refreshCols();
            }
        },

        dropOut() {
            if (this.data.id == this.editingElement?.data?.id) {
                setTimeout(() => {
                    this.$store.dispatch('builder/setEditingElement', null);
                }, 50);
            }
            let id = this.data.id;
            if(this?.is == 'row' && this?.deep >= 2) {
                id = this.$parent?.data?.id;
            }

            let realIndex= this.stack.findIndex((s) => s.id == id),
                new_index = realIndex > -1 ? realIndex : this.index;
            //History
            let section = this.is == 'row'  ? this.sections.find(s => s.id == this.section_id) : this.stack[new_index];

            if(section) {
                this.$store.dispatch('history/addStack', {
                    type: this.is == 'row' ? 'row' : 'section',
                    index: new_index,
                    data:  section,
                    section_id: this.is == 'row' ? this.section_id : section.id,
                    action: 'delete'
                });
            }
            return this.stack.splice(new_index, 1)[0];

        },

        dropIn(current, pos) {
            if (this.is == 'section') {
                this.stack.splice(this.index + pos, 0, current.dropOut());
                this.$store.dispatch('history/addStack', {
                    type: 'section',
                    name: '',
                    action: 'Moved'
                });
            }
        }
    }
};
</script>