export const fetchFiles = async (type, subtype, query, next, limit, _, cat = '', loadmore = false) => {
    const getRoute = (params = null) => {
        switch (type) {
            case 'video':
            case 'audio':
                return route('builder.files', params);
            case 'image':
            case 'font':
                return route((subtype === 'free' || subtype === 'freeImgCat' || subtype === 'google') ? 'builder.bridge' : 'builder.files', params);
            case 'icon':
                return route('builder.icons', params);
            default:
                return route('builder.picker' , params);
        }
    };

    const getParams = () => {
        let params = {};
        switch (type) {
            case 'icon': {
                params.type = subtype;
                if(query){
                    params.query = query;
                }
                if(next){
                    params.page = next;
                }
                params.limit = limit;
                break;
            }
            default: {
                if(subtype === 'free'){
                    if(next && loadmore){
                        params.url = next;
                    }else{
                        if(query) {
                            params.url = 'https://burst.shopify.com/api/photos'
                                        + ( query ? ('/search?q=') + query : '');
                        } else {
                            params.url = `https://burst.shopify.com/api/categories/${cat}/photos?litmit=30&offset=0`
                        }
                    }
                } else if(subtype === 'freeImgCat'){
                    params.url = 'https://burst.shopify.com/api/categories?litmit=20&offset=0'
                }
                else if(subtype === 'google'){
                    params.url = 'https://www.googleapis.com/webfonts/v1/webfonts?key=AIzaSyCrsTDigL61TFHYPHTZduQP1cGi8CLfp90&sort=popularity';
                }
                else{
                    params.type = type;
                    params.limit = limit;
                    if(query){
                        params.query = query;
                    }
                    if(next){
                        params.page = next;
                    }
                }
                break;
            }
        }
        return params;
    };

    const formatResData = (resData) => {

        return resData.map(item => {
            if(['font','image'].includes(type)){

                if(subtype === 'free'){
                    let {id, title: name, url: value, url} = item;
                        let thumbnail = _.$helpers.resizeImage(url, {width: 150, height: 150, format: 'pjpg'});
                        return {
                            fn: 'item', id, name, value, url, thumbnail
                        };
                } else if(subtype === 'freeImgCat') {
                    let {id, name: title, handle, cover_photo} = item;
                    let thumbnail = _.$helpers.resizeImage(cover_photo.url, {width: 284, height: 126, format: 'pjpg'});
                    return {
                        fn: 'item', id, title, handle, thumbnail
                    };
                }
                else if(subtype === 'google'){
                    const url = (name) => 'https://fonts.googleapis.com/css?family='+name.replace(/\s/g, '+')+':100,200,300,400,500,600,700,800,900';
                    let {family: name, family: thumbnail} = item;
                    return {
                        fn: 'item',
                        id: _.$helpers.makeId(8),
                        name,
                        thumbnail,
                        value: url(name)
                    };
                }else{

                    let k = type === 'font' ? 'url' : 'src';
                    let {id, [k]: value, [k]: url, [k]: thumbnail, width, height} = item;
                    let name = _.$helpers.file.name(value);
                    let ext = _.$helpers.file.ext(value);
                    thumbnail = type === 'image' ? _.$helpers.resizeImage(url, {width: 250, height: 250, format: 'pjpg'}) : thumbnail;
                    return {
                        fn: 'item', id, name, ext, value, url, thumbnail, width, height
                    };
                }
            }
            else if(type ==='video')
            {
                let { preview,id,sources} = item;
                if(sources) {
                    let source_video = sources.find( (video: any) => {
                        return video.mimeType.includes('video')
                    } );
                    let url = source_video?.url ? source_video.url : '';

                    return {
                        fn: 'item', id, name: url.split('/').pop().split('?')[0], value: url, url, thumbnail: preview.status === 'READY'? preview.image.url : null
                    };
                }
            }
            else if(type ==='audio')
            {
                let { preview,id,url,name} = item;
                return {
                        fn: 'item', id, name, value: id, url, thumbnail: preview.status === 'READY'? preview.image.url : null
                    };
            }
            else if(type === 'icon'){
                let {keywords, name,  url, url: value, url: thumbnail} = item;
                if(!name){
                    name = _.$helpers.file.name(value);
                }

                let rs = {
                    id: item.id ?? _.$helpers.makeId(8),
                    fn: 'item', name, value, url, thumbnail
                };
                if(subtype !== 'uploaded') {
                    let cate = keywords.split(',')[0];
                    rs['cate'] = cate;
                }
                return rs;
            }
            else{
                let {id, title: name, image: url, handle: value} = item;
                let thumbnail = _.$helpers.resizeImage(url, {width: 500, height: 500});
                return {
                    fn: 'item', id, name, value, url, thumbnail
                };
            }
        });
    };

    const processing = (res) => {
        let after = false, before = false, items = [];

        if(subtype === 'free'){
            ({next: after = false, previous: before = false} = res?.result?.metadata?.paging ?? {});
        }
        else {
            ({next: after = false, previous: before = false} =  res?.data?.pageInfo ?? {});
        }

        items = formatResData(  res?.data?.files ??
                                res?.data?.items ??
                                res?.result?.data ??
                                res?.result?.items ??
                                res?.icons ??
                                []);

        return {after, before, items};
    };

    if(!type){
        return {items: [], before: false, after: false};
    }

    let params = getParams();

    const res = await _.$worker.fetch(getRoute(params) );


        let resData = res.data;
        if(resData['status'] === 'success'){
            return processing(resData);
        }
        else
        {
            _.$store.dispatch('global/flash',{
                open: true,
                type: 'error',
                message:resData?.message??''
            });
        }

    return {items: []};
};
