export default {
    worker: null,
    queues: {},
    async init() {
        if (!this.worker) {
            this.worker = new Worker('/worker.js', { type: 'module' });
            this.onmessage();
        }
    },
    onmessage(){
        this.worker.onmessage = (e) => {
            let data = e.data;
            if (this.queues[data.id]) {
                if(data.status == 'success'){
                    this.queues[data.id].resolve(data.result);
                }
                else{
                   
                    this.queues[data.id].reject(new Error(data.result));
                }
                delete this.queues[data.id];
            }
        }
    },
    generateId(){
        return Math.random().toString(36).substring(2, 9);
    },
    async getAll() {
        await this.init();
        return new Promise((resolve,reject) => {
            this.worker.onmessage = e => {
                resolve(e.data);
            };
            let id = this.generateId();
            this.worker.postMessage({ action: 'getAll',id });
            this.queues[id] = {resolve,reject};
        });
    },
    async delete(params) {
        await this.init();
        return new Promise((resolve,reject) => {
             
            let id = this.generateId();
            this.worker.postMessage({ action: 'delete',id,params });
            this.queues[id] = {resolve,reject};
        });
    },
    async deleteByKeys(params) {
        await this.init();
        return new Promise((resolve,reject) => {
            let id = this.generateId();
            this.worker.postMessage({ action: 'deleteByKeys',id,params });
            this.queues[id] = {resolve,reject};
        });
    },
    async update(params) {
        await this.init();
        return new Promise((resolve,reject) => {
            let id = this.generateId();
            this.worker.postMessage({ action: 'update',id,params });
            this.queues[id] = {resolve,reject};
        });
    },
    async add(params) {
        await this.init();
        return new Promise((resolve,reject) => {
            let id = this.generateId();
            this.worker.postMessage({ action: 'add',id,params });
            this.queues[id] = {resolve,reject};
        });
    },
    async find(key,limit) {
        await this.init();
        return new Promise((resolve,reject) => {
            let id = this.generateId();
            this.worker.postMessage({ action: 'find',id,params:{key,limit} });
            this.queues[id] = {resolve,reject};
        });
    },
    async fetch(route: string, params:  any = null, method: string = 'GET') {
        await this.init();
        if(window.ECOM_INIT.APPBRIDGE)
        {
            let url = new URL(route);

            url.searchParams.append('token',window.sessionToken);
            url.searchParams.append('host',window.route().params.host);
            url.searchParams.append('shop',window.route().params.shop);
            route = url.href;
        }
        return new Promise((resolve,reject) => {
            let id = this.generateId();
            let tmp = null;
            let isForm = false;
            if(params instanceof FormData){
                tmp = {};
                isForm = true;
                for (let key of params.keys()) {
                    tmp[key] = params.get(key);
                    if(typeof tmp[key] === 'object' && !(tmp[key] instanceof File)){
                        tmp[key] = JSON.parse(JSON.stringify(tmp[key]));
                    }
                }
            }
            else if(params)
            {
                tmp = {...params}
                for(let key in tmp){
                    if(typeof tmp[key] === 'object' && !(tmp[key] instanceof File)){
                        tmp[key] = JSON.parse(JSON.stringify(tmp[key]));
                    }
                }
                // tmp = JSON.stringify(tmp);
            }
            // let tmp = method !== 'GET' && typeof params === 'object'  && !(params instanceof FormData) ? JSON.stringify(params) : params;
            this.worker.postMessage({ action: 'fetch',id,params: tmp, route, method,isForm });
            this.queues[id] = {resolve,reject};
        });
    }

    



}