import { mapGetters } from 'vuex';

export default {
    inject: ['exporting'],
    components: {
        Liquid: {
            inject: ['exporting'],
            props: {
                data: {
                    type: String,
                    default: ''
                }
            },
            template: `{{ data && exporting === true ? 'liquid[['+encodeURIComponent(data)+']]' : '' }}`
        }
    },
    data() {
        return {
            combinedLiquid: {
                timer: 0,
                timeout: 0,
                elements: {},
                sheduled: {},
                processId: 0
            }
        };
    },
    computed: {

        allowTypes () {
            return ['section', 'product', 'page', 'article', 'blog', 'cart', 'collection'];
        },

        liquidBefore () {
            let settings = this.data.settings;
            if (!settings) {
                return '';
            }
            let shopify_type = this.shopify_type;
            switch (shopify_type) {
                case 'product':
                    return `
                    {% comment %}
                        // Reactive
                        * this.data.settings.product
                    {% endcomment  %}
                    {% liquid
                        if ${(this.data?.template === 'featured_product') || this?.isProductBox ? ' true ' : "request.page_type != 'product' or product == blank" }
                            assign selected_product = '${this.selected_product}'
                            if selected_product == blank or selected_product == '' or all_products[selected_product] == blank
                                assign product = collections['all'].products.first
                            else
                                assign product = all_products[selected_product]
                                if product.id == blank
                                    assign product = collections['all'].products.last
                                endif
                            endif
                        endif
                    %}
                    {%- if product != blank -%}
                        {%- form 'product', product,class: "ecom-product-form ecom-product-form--single ecom-click", product_id: product.id, data-product_id: product.id,data-product-id: product.id, data-handle: product.handle-%}

                 `;
                case 'collection':
                    return `
                    {% comment %}
                        // Reactive
                        * this.data.settings.collection
                    {% endcomment  %}
                    {% liquid
                        if request.page_type != 'collection' or collection == blank
                            assign selected_collection = '${this.selected_collection}'
                            if selected_collection == blank or selected_collection == '' or collections[selected_collection] == blank
                                assign collection = collections.all
                            else
                                assign collection = collections[selected_collection]
                                if collection.id == blank
                                    assign collection = collections.all
                                endif
                            endif
                        endif
                    %}
                 `;
                case 'cart':
                    return `
                    <form action="{{ routes.cart_url }}" class="ecom-cart-form" method="post" enctype="multipart/form-data">
                `;
                case 'blog':
                    return `
                    {%- capture blog_handle -%}${this.data.settings?.blog?.value ? this.data.settings.blog.value: ''}{%-  endcapture-%}
                    {% assign blog_handle = blog_handle | strip %}
                    {%- if request.page_type != 'blog' and blog_handle != blank -%}
                        {%- assign blog = blogs[blog_handle]-%}
                    {%- endif -%}
                `;
                case 'article':
                    return `
                    {%- capture article_selected -%}${this.data.settings?.article?.value ? this.data.settings.article.value: ''}{%-  endcapture-%}
                    {% assign article_selected = article_selected | strip %}
                    {%- if request.page_type != 'article' and article_selected != blank  -%}
                        {%- assign article = articles[article_selected]-%}
                        {% assign blog_handle = article_selected | split: '/' | first %}
                        {% assign  blog = blogs[blog_handle] %}
                    {%- endif -%}
                `;
                default:
                    return '';
            }

        },

        liquidAfter () {


            let settings = this.data.settings;
            if (!settings) {
                return '';
            }
            let shopify_type = this.shopify_type;
            let tagName = 'script';
            switch (shopify_type) {
                case 'product':
                    return `

                    {% unless ecom_has_variant_picker %}
                    {%- capture ecom_product_json -%}
                        {
                        "id":  {{ product.id }},
                        "title":  {{ product.title | json }},
                        "handle":  {{ product.handle | json }},
                        "price":  {{ product.price| json }},
                        "price_min":  {{ product.price_min| json }},
                        "price_max":  {{ product.price_max| json }},
                        "available":  {{ product.available | json }},
                        "price_varies":  {{ product.price_varies| json }},
                        "compare_at_price":  {{ product.compare_at_price| json }},
                        "compare_at_price_min":  {{ product.compare_at_price_min| json }},
                        "compare_at_price_max":  {{ product.compare_at_price_max| json }},
                        "compare_at_price_varies":  {{ product.compare_at_price_varies| json }},
                        "variants": [
                            {%- for variant in product.variants -%}
                                {
                                "id":  {{ variant.id }},
                                "title": " {{ variant.option1 | handleize }}{%- if product.options.size == 2 -%} / {%- endif -%} {{ variant.option2 | handleize }}{%- if product.options.size == 3 -%} / {%- endif -%} {{ variant.option3 | handleize }}",
                                "option1":  {{ variant.option1  | json }},
                                "option2": {%- if product.options.size == 1 -%}null{%-else-%} {{ variant.option2  | json }}{%-endif-%},
                                "option3": {%- if product.options.size == 1 or product.options.size == 2 -%}null{%-else-%} {{ variant.option3  | json }}{%-endif-%},
                                "sku":  {{ variant.sku | json }},
                                "requires_shipping":  {{ variant.requires_shipping }},
                                "taxable":  {{ variant.taxable |json }},
                                "selling_plan_allocations":{%- if variant.selling_plan_allocations and variant.selling_plan_allocations.size > 0 -%}[{% for allocation in variant.selling_plan_allocations %}{"price":{{allocation.price}},"selling_plan":{"id":{{allocation.selling_plan.id}}, "name":{{allocation.selling_plan.name | json }} } }{%unless forloop.last%},{% endunless%}{%- endfor -%}]{%- else -%}null{%- endif -%},
                                "featured_image": {%- if variant.image.id != blank -%}{
                                        "id":  {{ variant.image.id | json }},
                                        "product_id":  {{ product.id | json }},
                                        "width":  {{ variant.image.width | json }},
                                        "height":  {{ variant.image.height | json }},
                                        "src":  {{ variant.image.src | json }},
                                        "position": {{ variant.image.position }}
                                    }{%- else-%}null{%-endif-%},
                                "available":  {{ variant.available }},
                                "public_title": " {{ variant.option1 | handleize }}{%- if product.options.size == 2 -%} / {%- endif -%} {{ variant.option2 | handleize }}{%- if product.options.size == 3 -%} / {%- endif -%} {{ variant.option3 | handleize }}",
                                "options":   {{ variant.options | json }},
                                "price":  {{ variant.price| json }},
                                "weight":  {{ variant.weight| json }},
                                "compare_at_price":  {{ variant.compare_at_price | json }},
                                "inventory_quantity":  {{ variant.inventory_quantity | json }},
                                "inventory_management":  {{ variant.inventory_management | json }},
                                "inventory_policy":  {{ variant.inventory_policy | json }},
                                "next_incoming_date": {% if variant.next_incoming_date %}{{variant.next_incoming_date | time_tag | json }}{% else%}null{% endif %},
                                "barcode":  {{ variant.barcode | json }}
                                }{%- if forloop.last == false -%},{%- endif -%}
                                {%- endfor -%}
                                ],
                                "featured_image":  {{ product.featured_image | json }},
                                "options": [
                                {%- for option in product.options -%}
                                {{ option | json }}{%- if forloop.last == false -%},{%- endif -%}
                                {%-endfor -%}
                            ]
                        }
                    {%- endcapture -%}
                        <input type="hidden" name="id" value="{{product.selected_or_first_available_variant.id}}" />
                        <${tagName} type="application/json" id="product-json-{{product.id}}-${this.data.id}">
                                {{ ecom_product_json}}
                        </${tagName}>
                    {% endunless %}
                    {%- endform -%}
                    {% assign ecom_has_variant_picker = false %}

                    {%- endif -%}
                `;
                case 'collection':

                    return `

                `;
                case 'cart':
                    return `<input type="hidden" name="checkout" value="ec" /></form>`;
                case 'blog':
                    return `

                `;
                case 'article':
                    return ``;
                default:
                    return '';

            }
        },

        isShopify () {
            return this.shopify_type != 'section';
        },

        selected_product () {
            return 'product' in this?.data?.settings ?? [] ? this?.data?.settings?.product?.value ?? '' : '';
        },

        selected_collection () {
            return 'collection' in this?.data?.settings ?? [] ? this?.data?.settings?.collection?.value ?? '' : '';
        },

        self() {
            return this.data;
        },

        shopify_type () {
            return (
                this.data &&
                this.data.shopify_type &&
                this.allowTypes.indexOf(this.data.shopify_type) > -1 &&
                this.data.hasElementRequestLiquid
            ) ? this.data.shopify_type : 'section';
        },
        params(){
            return this.$store.getters['page/params'];
        },
        ...mapGetters('builder', ['stacks', 'getLiquids']),

    },
    methods: {

        acceptRequest (req) {

            if (!this.data.settings) {
                this.self.settings = {};
            }

            let res = true;

            Object.keys(req).forEach((re) => {
                if (re == 'shopify_type') {
                    if (
                        this.shopify_type == 'section' ||
                        this.shopify_type == req[re]
                    ) {
                        this.self[re] = req[re];
                    } else {
                        res = false;
                    }
                } else if (this.self?.settings?.constructor === Object) {
                    this.self.settings[re] = req[re];
                }
            });

            return res;

        },

        processLiquid (element) {

            if (this.exporting !== false) {
                return;
            }

            if (!this.isShopify) {
                // if return, only allow children element run liquid inside section has type is Shopify
                //return;
            }

            // Unbounce loading multiple request
            let now = new Date().getTime(),
                combined = this.combinedLiquid;

            // Neu 1 element day yeu cau cho section de xu ly liquid

            combined.elements[element.id] = element;
            combined.timer = now;
            clearTimeout(combined.timeout);
            combined.timeout = setTimeout(() => {
                this.doProcessLiquid();
            }, 500);

        },

        doProcessLiquid() {

            let combined = this.combinedLiquid,
                rawcode = `{% assign EComBuilderMode = true %} ` + this.liquidBefore;

            if (
                !combined ||
                !combined.elements ||
                Object.keys(combined.elements).length === 0
            ) {
                return;
            }
            // if(this.params?.page === 'product') {
            //     rawcode += `{% assign product = all_products['${this.selected_product}']%}`;
            // }

            let processId = Math.random();

            Object.keys(combined.elements).forEach((id) => {
                if (
                    this.getLiquids[id].raw &&
                    combined.sheduled[id] === undefined
                ) {
                    combined.sheduled[id] = [];
                }
                rawcode += this.getLiquids[id].raw;
                combined.sheduled[id].push(processId);
            });

            rawcode += this.liquidAfter;

            // Combined liquid code and post to server
            this.$store.dispatch(
                'global/setProgress', { show: true, percent: 10 }
            );
            this.$worker.fetch(window.route('builder.scripts', this.params?.page === 'product' ? {...this.params, handle: this.selected_product} : this.params), {
                layout: 'none',
                code: '<process id="'+processId+'" />'+rawcode,
                settings: JSON.parse(JSON.stringify(this.data.settings || {}))
            },'POST').then((res) => {
                this.$store.dispatch(
                    'global/setProgress', { show: false, percent: 100 }
                );
                if (res.data.status === 'success') {
                    let processId = res.data.result.match(/<process id="([\d\.]*)"\s?\/?>/);
                    if (processId === null) {
                        this.$store.commit(
                            'global/flash',
                            {
                                open: true,
                                message: 'Error while process Liquid',
                                type: 'error',
                            },
                            { root: true }
                        );
                        return;
                    }
                    processId = processId[1];
                    Object.keys(combined.sheduled).forEach((id) => {
                        let processIds = combined.sheduled[id];
                        if (processIds[processIds.length-1] != processId) {
                            return; // This is not latest request of this element
                        }
                        if (res.data.result.indexOf(id) > -1) {
                            let reg = new RegExp('<ecom-embed-start id="' + id + '" name="([\\s\\S]+?)"\\s?\\/?>([\\s\\S]*)<ecom-embed-end id="' + id + '" name="\\1"\\s?\\/?>', 'g');

                            res.data.result.replace(reg, (...m) => {
                                if (this.getLiquids?.[id]?.content) {
                                    this.getLiquids[id].content[m[1]] = m[2];
                                }
                            });
                        } else {
                            this.getLiquids[id].content = {};
                            /*this.$store.commit(
                                'global/flash',
                                {
                                    open: true,
                                    message: 'Error while process Liquid on element "' + combined.elements[id].name + '"',
                                    type: 'error',
                                },
                                { root: true }
                            );*/
                        }
                        if (combined.elements[id]) {
                            combined.elements[id].refresh = this.$helpers.randid();
                            delete combined.elements[id];
                        }
                        delete combined.sheduled[id];
                    });
                }
                else {
                    throw new Error(res.data.message ? res.data.message : 'Server error');
                }
            })
            .catch(e => {
                console.log(e);
                this.$store.dispatch(
                    'global/setProgress', { show: false, percent: 100 }
                );
                this.$store.dispatch('global/flash', {
                    open: true,
                    type: 'error',
                    message: e.message,
                    title: 'Render liquid error'
                });
            });
        }
    }
};
