<template>
    <div class="ecom-aync-component-error core__notice--error">
        <span>Error when loading <strong>{{ componentName }} </strong> Please try again</span>
    </div>
</template>
<script>
export default {
    props:{
        'data': {
                type: Object,
                default () {
                    return {};
                }
            },
    },
    computed:{
        componentName(){
            if(this?.data?.title)
            {
                return this.data.title;
            }
            if(this?.data?.name)
            {
                return this.data.name;
            }
            return '';
        }
    }
};
</script>