import type { Preview, StoryContext } from "@storybook/vue3";
import { setup } from '@storybook/vue3';
import { fieldTheme } from "./fieldTheme.decorator";
import { store } from '@/store';

import '@/assets/styles/common.css';
import { worker } from '@/services/api';
import initGComponent from '@/plugins/gComponent';
import { i18n } from '@/plugins/i18n' 

import { focusDirectives } from '@/directives';

import helpers from '@/utils/helpers';


import EComFields from '@/plugins/fields'
const preview: Preview = {
    parameters: {
        actions: { argTypesRegex: "^on[A-Z].*" },
        controls: {
            matchers: {
                color: /(background|color)$/i,
                date: /Date$/
            }
        },
        docs: {
            source:{
                language: 'json',
                type: 'dynamic',
                transform: (code: String, storyContext: StoryContext) =>{
                    return JSON.stringify(storyContext.args, null, 2);
                }
            },
            
        }
        
    }
};

setup((app) => {
    // Registers your app's plugins into Storybook
   
// app.config.globalProperties.$axios = axios;
app.config.globalProperties.$worker = worker;
app.config.globalProperties.$helpers = helpers;
/**
 * ! Todo
 */
// @ts-ignore


app.use(i18n);
    store.$helpers = helpers;


    store.dispatch('settings/initGlobal');
    /**
     * ! Todo
     */
    // @ts-ignore
    // store.$axios = axios;
    store.$worker = worker;


    /* Init Directives */
    focusDirectives(app);

    /* Init Plugins */
    initGComponent(app);
    app.use(EComFields)
    app.use(store);
  });

export const decorators = [fieldTheme]; 
export default preview;
