<template>
    <div class="relative flex flex-col items-start">
        <div v-for="(item,index) in Object.keys(list)" :key="index" class="flex items-center w-full mb-2" >
            <span class="mr-2">
                <input :id="item" v-model="value" type="checkbox" :value="item" class="w-5 h-5 cursor-pointer opacity-0 absolute z-10 ecom-checkbox peer">
                <span class="w-5 h-5 border rounded flex justify-center items-center peer-checked:bg-interactive-600 peer-checked:border-transparent">
                    <Icon :source="icons.TickSmallMinor" :size="'w-5'" :color="'text-white'" class="w-5 h-5 flex"></Icon>
                </span>
            </span>
            <label class="text-sm font-normal leading-5 cursor-pointer text-gray-600 dark:text-dark-color" :for="item">
                {{ list[item] }}
            </label>
        </div>
    </div>
</template>
<script lang="ts">
import { TickSmallMinor } from "@/assets/icons";
export default {
    props: {
        field: {
            type: Object,
            default(){
                return {};
            }
        },
        // eslint-disable-next-line vue/require-default-prop
        data: [Array]
    },
    emits: ['update:data'],
    data () {
        return {
            value: this.data ?? this.field?.value ?? [],
            list: this.field.options?.values,
            status: true,
            emit: false,
            icons: {
                TickSmallMinor
            }
        };
    },
    watch: {
        data:{
            immediate: true,
            handler:function(val){
                if(this.status){
                    this.emit = false;
                    if (typeof val != 'undefined') {
                        this.value = val;
                    }
                }
                this.status = true;
            }
        },
        value: {
            immediate: true,
            handler: function (val) {
                if (this.emit) {
                    this.status = false;
                    this.$emit('update:data', val);
                }
                this.emit = true;
            },
        }
    },
};
</script>
