import { fetchFiles } from '@/utils';

// q: query
const filterItem = (arr, q) => {
    let [query, brand, category] = q.split('|||');
    let arrFiltered = [];
    arrFiltered = arr.filter(item => {
        let check = true;
        if(brand) {
            check &= item.brand === brand;
        }
        if(category && category !== 'All') {
            check &= item.cate === category;
        }
        if(query) {
            check &= item.name.toLowerCase().indexOf(query.toLowerCase()) > -1;
        }
        return check;
    });
    return arrFiltered;
};
export default {
    namespaced: true,
    state: {
        limit: {
            icon: 39
        },
        loading: {
            loadmore: false,
            data: false
        },
        types: {},
        freeImgCat: null
    },
    getters: {
        freeImgCat(state) {
            return state.freeImgCat;
        },
        data(state){
            let itemUpload = {
                'fn':'upload',
                'id': 'item_uplad',
                'value':'',
                'url':'',
                'thumbnail':''
            };
            return async function(type, subtype = '', search, loadmore = false, reload = false) {
                let cates = [];
                if(typeof subtype === 'object'){
                    let firstKey = Object.keys(subtype)[0];
                    cates = subtype[firstKey];
                    subtype = firstKey;
                }
                else{
                    cates = [subtype];
                }
                state.types[type] = state.types[type] ?? {};
                let stateType = state.types[type];
                if(subtype && typeof subtype === 'string'){
                    stateType[subtype] = stateType[subtype] ?? {};
                    stateType = stateType[subtype];
                }
                if(stateType['search'] !== search){
                    Object.assign(stateType , {
                        search, page:1
                    });
                    if(!stateType.loadAll) {
                        stateType.after = '';
                    }
                    delete stateType['scroll_top'];
                }
                stateType.items = stateType.items ?? [];
                let items = stateType.items;

                let limit = state.limit[type] ?? 30;

                if(loadmore) {
                    state.loading.loadmore = true;
                    stateType.page ++;
                }else {
                    state.loading.data = true;
                }

                let posStart = ( stateType.page - 1 ) * limit;
                let dataFilted = filterItem(items, stateType.search);

                const resDataExists = (arr, length) => {
                    let rs = arr.slice(loadmore ? posStart : 0, length);
                    if(subtype == 'uploaded'){
                        return this.$helpers.addObjToFirstArr(rs, itemUpload);
                    }
                    return rs;
                };
                let inData = posStart + limit <= dataFilted.length && !reload;

                if(reload) {
                    items.splice(0, items.length);
                }
                if(subtype === 'free' && state.freeImgCat) {
                    inData = false;
                }

                if (stateType.after !== false || subtype === 'freeImgCat') {
                    if (inData || (subtype === 'freeImgCat' && items.length)) {
                        return resDataExists(dataFilted, posStart + limit);
                    } else {
                        let ress = { items: [] };
                        for(let i = 0 ; i < cates.length ; i ++){
                            let res = await fetchFiles(type, cates[i], stateType['search'], stateType.after, limit, this, state?.freeImgCat?.handle || '', loadmore );

                            let {before, after, items} = res;

                            items.forEach(item => {
                                if(item) {
                                    item.brand = cates[i]
                                }
                            });

                            Object.assign(ress, { before, after });
                            ress.items.push(...items);
                        }
                        this.$helpers.mergeArrByIndex(items, ress.items, posStart);
                        stateType.after = ress.after;
                        if(ress.after === false && !stateType.search) {
                            stateType.loadAll = true;
                        }
                        let rs = ress.items.slice(0, limit);
                        if(posStart == 0 && subtype == 'uploaded'){
                            return this.$helpers.addObjToFirstArr(rs, itemUpload);
                        }
                        return rs;
                    }
                }else{
                    return resDataExists(dataFilted, posStart + limit);
                }

            };
        },
        categories(state){
            return function({ type, subtype }) {
                let cates = {};
                let stateData = state.types?.[type]?.[subtype];
                if(stateData){
                    let { items = [] } = stateData;
                    items.forEach(item => {
                        if(item) {
                            let {brand, cate} = item;
                            if(brand && cate){
                                cates[brand] = cates[brand] ?? {};
                                let cBrand = cates[brand];
                                cBrand[cate] = (cBrand[cate] ?? 0) + 1;
                            }
                        }
                    });
                }
                return cates;
            };
        },
        loading(state){
            return state.loading;
        },
        isLoadmore(state){
            return function(type, subtype){
                if(subtype === 'freeImgCat') {
                    return false;
                }

                let stateType = state.types[type];
                if(subtype) {
                    stateType = stateType[subtype];
                }
                let { search, items } = stateType;
                let dataFilted = items;
                if(search) {
                    dataFilted = filterItem(items, search);
                }
                return stateType.after || stateType.page * (state.limit[type] ?? 30) < dataFilted.length;
            };
        },
        search(state){
            return function(type, subtype){
                let stateType = state.types[type];
                if(subtype) {
                    stateType = stateType[subtype];
                }
                return stateType?.search ?? '';
            };
        },
        scrollTop(state){
            return ({type, subtype, layout}) => {
                let stateType = state.types[type];
                if(subtype) {
                    stateType = stateType[subtype];
                }
                return stateType?.scroll_top?.[layout] ?? 0;
            };
        }

    },
    mutations: {
        freeImgCat(state, payload) {
            state.freeImgCat = payload;
        },
        setScrollTop(state, {type, subtype, layout, top}){
            let stateType = state.types[type];
            if(subtype) {
                stateType = stateType[subtype];
            }
            if(stateType){
                stateType.scroll_top = stateType.scroll_top ?? {};
                stateType.scroll_top[layout] = top;
            }
        },
        removeItem(state, { type, id }) {
            let data = state.types?.[type]?.['uploaded']?.['items'];
            if(data) {
                let i = data.findIndex(d => d.id === id);
                if(i > -1) {
                    data.splice(i, 1);
                }
            }
        },
        addFirstItem(state, {type, subtype,  item}){
            let stateType = state.types[type];
            if(subtype) {
                stateType = stateType[subtype];
            }
            stateType.items.splice(0, 0, item);

        },
        reloadItems(state, { type, subtype }){
            let stateType = state.types[type];
            if(subtype) {
                stateType = stateType[subtype];
            }
            if(stateType){
                //stateType.items.splice(0, stateType.items.length);
                stateType.after = '';
                stateType.page = 1;
            }
        },
        removeLoading(state){
            state.loading = {};
        }
    },
    actions: {
        setScrollTop({commit}, payload){
            commit('setScrollTop', payload);
        },
        removeItem({commit}, payload){
            commit('removeItem', payload);
        },
        addFirst({commit}, payload){
            commit('addFirstItem', payload);
        },
        reload({commit}, payload){
            commit('reloadItems', payload);
        },
        removeLoading({commit}) {
            commit('removeLoading');
        }
    }
};
