<template>
    <div
      v-if="layout == 'list'"
    >
      <div :class="`flex flex-col space-y-${size}`">
        <div role="status" v-for="index in size" :key="index" class="p-4 space-y-4 w-full rounded border border-gray-200 divide-y divide-gray-200 shadow animate-pulse dark:divide-gray-700 md:p-6 dark:border-gray-700">
            <div class="flex justify-between items-center">
                <div>
                    <div class="h-2.5 bg-gray-300 rounded-full dark:bg-gray-600 w-24 mb-2.5"></div>
                    <div class="w-32 h-2 bg-gray-200 rounded-full dark:bg-gray-700"></div>
                </div>
                <div class="h-2.5 bg-gray-300 rounded-full dark:bg-gray-700 w-12"></div>
            </div>
        </div>
      </div>
    </div>
    <div
      v-else
      ref="skeleton"
      class="grid py-5 mb-5 grid-cols-5 md:grid-cols-3 sm:grid-cols-2"
      :class="`gap-x-${size} gap-y-${size} lg:grid-cols-${size}`"
    >
      <div v-for="index in size" class="transition-all transform duration-300 opacity-100 scale-90 translate-y-2">
        <div
          v-if="type === 'card'"
          class="bg-gray-100 dark:bg-gray-700 rounded max-w-lg w-full"
        >
          <div class="animate-pulse flex">
            <div class="w-full rounded h-96">
              <div class="h-48 rounded-t bg-gray-200 dark:bg-gray-700" :class="index"></div>
              <div class="flex-1 px-4 py-8 space-y-4 sm:p-8 bg-gray-200 dark:bg-gray-900">
                <div class="w-full h-6 rounded bg-gray-100 dark:bg-gray-700"></div>
                <div class="w-full h-6 rounded bg-gray-100 dark:bg-gray-700"></div>
                <div class="w-3/4 h-6 rounded bg-gray-100 dark:bg-gray-700"></div>
              </div>
            </div>
          </div>
        </div>
        <div
          v-else
          class="bg-gray-100 dark:bg-gray-400 rounded max-w-sm w-full"
          >
          <div class="animate-pulse flex">
            <div class="bg-gray-300 dark:bg-gray-600 w-full rounded h-96"></div>
          </div>
        </div>
      </div>
    </div>
</template>

<script lang="ts">
export default {
  props: {
      size: {
          type: Number,
          default: 5
      },
      type: {
          type: String,
          default: ''
      },
      layout: {
          type: String,
          default: ''
      }
  },
  computed: {

  },
  mounted() {
      setTimeout(() => {
        const skeleton = this.$refs.skeleton;
        if(skeleton) {
          const div = skeleton.querySelectorAll('.transition-all');
          if(div) {
            div.forEach(el => {
              el.classList.remove('scale-90', 'translate-y-2');
              el.classList.add('scale-100', 'translate-y-0');
            });
          }
        }
      }, 10)

  },
};
</script>

