<template>
    <div class="pl-2">
        <div class="relative flex items-center">
            <Icon :source="SearchMajor" :size="'w-4'" class="absolute top-3 left-2 flex items-center text-gray-500 dark:text-dark-color"></Icon>
            <input
                :id="id"
                v-model="getvalue"
                v-focus="autofocus ? 'select' : false"
                type="search"
                :placeholder="placeholder"
                class="block w-full pl-8 border bg-transparent border-gray-200 rounded outline-interactive-600 h-10 sm:text-sm dark:focus:ring-offset-transparent focus:ring-primary-400 dark:focus:ring-transparent dark:focus:border-gray-600 focus:border-gray-400 border-opacity-70 dark:bg-dark dark:text-dark-color"
                :class="[shortkey !== '' ? 'pr-10' : 'pr-2 ' , borderDark ? ' dark:border-gray-700' : ' dark:border-gray-600 '] + classList"
                @keyup="$emit('keyup', $event.target.value)"
                @keydown="$emit('keydown', $event.target.value)"
                @blur="$emit('blur', $event.target.value)"
                @change="$emit('change', $event.target.value)"
            >
            <div
                v-if="shortkey !== ''"
                class="absolute inset-y-0 right-0 flex py-1.5 pr-1.5"
            >
                <kbd class="inline-flex items-center px-2 font-sans text-sm text-gray-600 font-medium dark:text-dark-color">
                    {{ shortkey }}
                </kbd>
            </div>
        </div>
    </div>
</template>

<script lang="ts">
    import { SearchMajor } from "@/assets/icons";
    export default {
        data() {
            return {
                SearchMajor
            }
        },
        props: {
            classList: {
                type: String,
                default: ''
            },
            shortkey: {
                type: String,
                default: ''
            },
            placeholder: {
                type: String,
                default: ''
            },
            id: {
                type: String,
                default: null
            },
            value: {
                type: String,
                default: ''
            },
            borderDark: {
                type: Boolean,
                default: false
            },
            autofocus: {
                type: Boolean,
                default: false
            }
        },
        emits: ['keydown', 'keyup', 'blur', 'change'],
        computed: {
            getvalue() {
                return this.value;
            }
        }
    };
</script>

<style>
    input[type="search"]::-webkit-search-decoration,
    input[type="search"]::-webkit-search-cancel-button,
    input[type="search"]::-webkit-search-results-button,
    input[type="search"]::-webkit-search-results-decoration {
        -webkit-appearance:none;
    }
</style>
