import { addSwitch, refactorArray } from "../../utils";

const box : GroupFieldType = {
    group_name:'box',
    group_title:'Box',
    params:[
        {
            name: 'boxBackground',
            label: 'Background',
            type: 'color',
            options: {
                oneline: true,
                global: {
                    type: 'colors'
                }
            },
            css:{
                properties:{
                    'background-color': ''
                }
            },
        },

            {
                type: 'popup',
                label: 'Box Shadow',
                name: 'boxShadow',
                options: {
                    type: 'box-shadow',
                    oneline: true
                },
                css:{},
            }
        ,
        {
            type: 'popup',
            label: 'Border',
            name: 'boxBorder',
            options: {
                type: 'border',
                oneline: true
            },
            css:{},
        },
        {
            name: 'boxBorderRadius',
            label: 'Border Radius',
            type: 'dimension',
            options: {
                responsive: true,
                type: 'radius',
                units: {
                    px: {
                        min: 0,
                        max: 1000
                    },
                    '%': {
                        min: 0,
                        max: 100
                    }
                }
            },
            css:{
                properties:{
                    'border-radius': '',
                    'overflow': 'hidden'
                }
            }
        },
    ]
}

const boxHover : GroupFieldType = {
    group_name:'box_hover',
    group_title:'Box',
    params:[
        {
            name: "tab",
            type: "tab",
            value: "normal",
            options: {
                tabs: [
                    {
                        name: "normal",
                        title: "Normal"
                    },
                    {
                        name: "hover",
                        title: "Hover"
                    }
                ]
            },
            css: {
                isCss: false
            }
        },
        ...addSwitch([...box.params],'normal'),
        ...addSwitch([...box.params],'hover',':hover'),
        {
            type: 'number',
            label: 'Transition Duration <span class="lowercase">(ms)</span>',
            name: 'transition',
            options: {
                min: 0,
                max: 1500,
                visible:{
                    keep_data: true,
                    condition: (data: any) => {
                        return data && data["tab"] === "hover";
                    }
                }
            },
            css: {
                properties: {
                    transition: 'all %value%ms ease'
                }
            }
        },
    ]
}

let indexTab = boxHover.params.findIndex((item) => item.name === "tab");

const boxActive : GroupFieldType = {
    group_name:'box_active',
    group_title:'Box',
    params:[
        ...refactorArray(boxHover.params,indexTab,{
            name: "tab",
            type: "tab",
            value: "normal",
            options: {
                tabs: [
                    {
                        name: "normal",
                        title: "Normal"
                    },
                    {
                        name: "hover",
                        title: "Hover"
                    },
                    {
                        name: "active",
                        title: "Active"
                    }
                ]
            },
            css: {
                isCss: false
            }
        }),
        ...addSwitch(box.params,'active','.ecom-box-active')
    ]
}

export default box;
export const hover = boxHover;
export const active = boxActive;
