<template>
    <span
        v-if="loading.data"
        class="absolute block z-1 inset-0"
    >
        <Loading type="spin" class="bg-white dark:bg-gray-700 dark:text-dark-color p-3 rounded text-gray-700 bg-opacity-90" />
    </span>
    <div v-if="isEmpty" class="w-full text-center text-xs">
        <span class="text-xs">No result</span>
    </div>

    <ul
        v-else-if="!loading.data"
        ref="list"
        class="absolute top-0 flex flex-wrap w-full max-h-full overflow-y-auto text-sm"
        :class="{'py-2 px-2.5' : layout == 'grid'}"
        @scroll.passive="eventChange"
    >
        <component
            :is="type === 'icon' ? 'IconContent' : (isFreeImgCat ? 'ItemCategory' : 'ItemContent')"
            v-for="item in getlist"
            :key="item.id"
            :item="item"
            :value="value"
            :layout="layout"
            :type="type"
            v-model:compress="compress"
            @upload="upload"
            @remove="remove"
            @toggleItem="toggleItem"
            @reload="reloadPicker"
            @clickSelect="$emit('clickSelect')"
            @loadFreeImg="loadFreeImg"
        />
        <li
            v-if="isNext && !loading.data"
            class="w-full mx-auto mt-3 mb-5 text-center"
            :class="{'px-1': layout == 'grid'}"
        >
            <Loading
                v-if="loading.loadmore"
                type="spin"
            />
            <button
                v-else
                class="inline-flex justify-center items-center font-medium smoothing h-10 text-sm px-3  border border-gray-300  text-gray-500 shadow-sm bg-white dark:bg-dark dark:border-gray-700 dark:text-dark-color dark:hover:bg-dark dark:active:bg-dark hover:bg-gray-100 active:bg-gray-200 focus:ring-primary-600 rounded smoothing"
                @click="getData(true)"
            >Load more
            </button>
        </li>
        <link
            v-if="url.length"
            :href="url"
            rel="stylesheet"
        >
    </ul>
</template>

<script lang="ts">
    import { mapGetters } from 'vuex';

    import ItemContent from './PickerItemContent.vue';
    import IconContent from './PickerIconContent.vue';
    import ItemCategory from './PickerCategory.vue';

    import { fileMixin } from '../../mixins';
    import { upload, remove } from '../../utils';
    export default {
        name: 'PickerContent',
        components: {
            ItemContent,
            IconContent,
            ItemCategory,

        },
        mixins: [ fileMixin ],
        emits: ['clickSelect'],
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            search: {
                type: String,
                default: '',
            },
            value: {
                type: Array,
                default () {
                    return [];
                },
            },
            multiple: {
                type: Boolean,
                default: false,
            },
            responsive: {
                type: Boolean,
                default: false,
            },
            type: {
                type: String,
                default: 'menu',
            },
            layout: {
                type: String,
                default: 'grid',
            },
            tab: {
                type: String,
                default: '',
            }
        },
        data () {
            return {
                init: false,
                data: {
                    init: false,
                    list: []
                },
                compress: false,
                $list: null
            };
        },
        computed: {
            ...mapGetters('global', ['configIcons']),
            ...mapGetters('picker', { 'pickerData': 'data' }),
            ...mapGetters('picker', ['isLoadmore', 'scrollTop', 'loading', 'freeImgCat']),
            ...mapGetters('builder', ['screen']),
            isTinify() {
                return !!this.$store.getters['global/configs']?.tinify?.enable;
            },
            getlist() {
                return this.data.list.filter(item => item != null);
            },
            isNext () {
                return this.isFreeImgCat ? false : this.isLoadmore(this.type, this.tab);
            },
            isFreeImgCat () {
                return !this.freeImgCat && this.tab == 'free' && !this.search
            },
            valueComp () {
                return this.value;
            },
            url () {
                if (this.type == 'font' && this.tab == 'google') {
                    if (this.data.list.length) {
                        return 'https://fonts.googleapis.com/css2?' +
                            this.data.list
                                .map(item => {
                                    return 'family=' + item.name.replace(/\s/g, '+');
                                }).join('&');
                    }
                }
                return '';
            },
            isEmpty(){
                return !this.data.list ||!this.data.list.length
            }
        },
        watch: {
            show: function (val) {
                if (val) {
                    if (!this.init && !this.loading.data) {
                        this.getData();
                    }
                    this.setScroll();
                    this.eventChange();

                }
            },
            search: function (val) {
                if(val === '') {
                    this.reloadPicker();
                } else {
                    this.getData();
                }

            },
            'loading.data': function (val) {
                if (!val) {
                    this.reinitScroll();
                }
            },
            tab: function () {
                this.reinitScroll();
            },
            isTinify: {
                immediate: true,
                handler(val) {
                    this.compress = val;
                }
            },
        },
        created () {
            if (this.show && !this.init) {
                this.getData();
                this.init = true;
            }
        },
        methods: {
            loadFreeImg(cat) {
                this.$store.commit('picker/freeImgCat', cat);
                this.getData(false, false);
            },
            loadFreeCat() {
                this.getData(false, false);
            },
            fileName (data) {
                let name = this.$helpers.file.name(data);
                return name ? name : data;
            },
            reinitScroll () {
                this.$nextTick(() => {
                    this.$list = this.$refs.list;
                    this.setScroll();
                    this.eventChange();
                });
            },
            isActive (value) {
                return this.value.findIndex(v => {
                    v = this.type === 'image' ?
                                this.currentImage(this.type, v, false) :
                                v.value;
                    return v === value;
                });
            },
            setScroll () {
                this.$list &&
                    (this.$list.scrollTop = this.scrollTop({
                        type: this.type,
                        subtype: this.tab,
                        layout: this.layout
                    }));
            },
            reload () {
                // this.$list &&
                //     (this.$list.scrollTop = 0);

                this.getData(false, true);
            },
            reloadPicker() {
                this.$store.dispatch('picker/reload', {
                    type: this.type,
                    subtype: this.tab
                });
                this.reload();
            },
            getData (loadmore = false, reload = false) {
                let subtype = this.tab;
                if (this.type === 'icon' && this.tab === 'system') {
                    subtype = {
                        system: Object.keys(this.configIcons)
                    };
                }

                if(this.isFreeImgCat && !this.search) {
                    subtype = 'freeImgCat';
                }

                let res = this
                    .pickerData(
                        this.type,
                        subtype,
                        this.search,
                        loadmore,
                        reload
                    );

                res.then((data) => {
                    if(data.status === 'error')
                    {
                        throw new Error(data.message);
                    }
                    if (loadmore) {
                        this.data.list
                            .splice(
                                this.data.list.length,
                                0,
                                ...data
                            );
                    } else {
                        this.data.list
                            .splice(
                                0,
                                this.data.list.length,
                                ...data
                            );
                    }
                    if(reload && this.$list) {
                        setTimeout(() => {
                            this.$list.scrollTop = 0;
                        });
                    }
                    this.$store.dispatch('picker/removeLoading');
                    this.init = true;
                    this.eventChange();

                })
                .catch(e=>{
                    this.$store.dispatch('global/flash',{
                        open: true,
                        type: 'error',
                        message: e.message
                    });
                })

            },
            removeItem (value) {
                let res = this.valueComp.filter((x) => {
                    return x.value != value;
                });
                this.valueComp = res;
            },
            searchItem (id) {
                return this.getlist.filter(x => x.id == id)?.[0] ?? false;
            },
            toggleItem ({ id, name, ext, cate, thumbnail, value, reload, loading, url, width, height}) {
                if (reload || loading) {
                    return;
                }

                let kValue = 'value',
                    kThumb = 'thumbnail';
                let item = { id, name };
                if(ext) {
                    item.ext = ext;
                }
                if(cate) {
                    item.cate = cate;
                }
                if(url) {
                    item.url = url;
                }
                if(this.responsive && !this.multiple) {
                    if (this.screen !== 'desktop') {
                        kValue += '__' + this.screen;
                        kThumb += '__' + this.screen;
                    }
                    let v = this.valueComp[0];
                    if(v) {
                        if(width) {
                            v.width = width;
                        }
                        if(height) {
                            v.height = height;
                        }
                        if(v[kValue] === value) {
                            delete v[kValue];
                            delete v[kThumb];
                        }
                        else {
                            if(this.screen === 'desktop') {
                                v.name = name;
                            }
                            v[kValue] = value;
                            v[kThumb] = value;
                        }
                    }
                    else {
                        item[kValue] = value;
                        item[kThumb] = value;
                        this.valueComp.push(item);
                    }
                }
                else {
                    item.value = value;
                    item.thumbnail = thumbnail;
                    let isActive = this.isActive(value);
                    if (isActive > -1) {
                        this.valueComp.splice(isActive, 1);
                    } else {
                        if (this.multiple) {
                            this.valueComp.push(item);
                        } else {
                            this.valueComp.splice(0, this.valueComp.length, item);
                        }
                    }
                }
            },
            deleteItem (id) {
                this.data.list = this.getlist
                    .filter(x => x.id != id);
            },
            editItem (id, data, add = false) {
                let items = this.getlist.filter(x => x.id == id);
                if (items.length) {
                    items.forEach(el => {
                        Object.keys(data).forEach(key => {
                            el[key] = data[key];
                        });
                        if (add) {
                            this.toggleItem(el);
                        }
                    });
                }
            },
            async upload (e) {
                let files = e.target.files;
                if (files.length) {
                    for (let i = 0; i < files.length; i++) {
                        let file = files[i];
                        let id = this.$helpers.makeId(10);
                        let newItem = {
                            id,
                            fn: 'item',
                            loading: true,
                            name: file.name,
                            value: URL.createObjectURL(file),
                            thumbnail: URL.createObjectURL(file),
                        };
                        this.data.list.splice(1, 0, newItem);
                        this.eventChange();
                        let onUploadProgress = (e) => {
                            let { loaded, total } = e;
                            let per = Math.round(loaded / total * 100) + '%';
                            this.editItem(id, { per });
                        };
                        let res = await upload({type: this.type, file, compress: this.compress}, onUploadProgress, this);
                        if (res.status === 'success') {
                            setTimeout(()=>{
                                this.editItem(id,
                                    {
                                        loading: false,
                                        reload: true
                                    }
                                );
                                this.$store
                                    .dispatch(
                                        'picker/addFirst',
                                        {
                                            type: this.type,
                                            subtype: this.tab,
                                            item: this.searchItem(id)
                                        }
                                    );
                                if(res.status === 'success') {
                                    this.$store.commit(
                                        'global/flash',
                                        {
                                            open: true,
                                            timeout: 10000,
                                            title: this.type != 'image' ? 'The uploaded file is being processed in the background' : '',
                                            message: this.type != 'image' ? 'They may take a few minutes to appear in your file list. Refresh to fetch new items' : res.message,
                                            type: 'success',
                                        },
                                        { root: true }
                                    );
                                }
                                else
                                throw new Error(res.message ?? 'Unknow error');
                            },3000)
                        } else {
                            if(res.message) {
                                this.$store.commit(
                                    'global/flash',
                                    {
                                        open: true,
                                        message: res.message,
                                        type: 'error',
                                    },
                                    { root: true }
                                );
                            }
                            this.deleteItem(id);
                        }
                    }
                    e.target.value = '';
                }
            },
            remove(item) {
                let { id, removing } = item;
                if(removing) {
                    return;
                }
                item.removing = true;
                remove(id, this).then(res => {
                    if(res.status === 'success') {
                        let i = this.data.list.findIndex(d => d.id === id);
                        this.data.list.splice(i, 1);
                        this.$store.dispatch('picker/removeItem',
                            {
                                type: this.type,
                                id
                            }
                        );
                    }
                    else {
                        item.removing = false;
                    }
                });
            },
            eventChange () {
                let itv = null;
                clearTimeout(itv);

                itv = setTimeout(() => {
                    let top = this.$list?.scrollTop ?? 0;
                    this.$store.dispatch('picker/setScrollTop', {
                        type: this.type,
                        subtype: this.tab,
                        layout: this.layout,
                        top
                    });
                }, 50)
            }
        }
    };
</script>
<style>
    .pb-100p {
      padding-bottom: 100% !important;
    }
    .h-15 {
      height: 3.75rem;
    }
    .h-25 {
      height: 6.25rem;
    }
</style>
