<template>
    <div
        class="w-full"
    >
        <hr
            v-if="field.type === 'line'"
            class="border-gray-200 dark:border-gray-700"
        >
        <template v-else>
            <div
                :class="{
                    'flex justify-between' : isOneLine,
                    'flex-row-reverse': isReverse
                }"
            >
                <div
                    class="relative flex items-center group"
                    :class="
                        isOneLine ? ('flex-1') + (!isReverse ? ' mr-2' : ' ml-2') :
                        (field.options?.half ? 'mb-1' : (field.label ? 'mb-2' : ''))
                    "
                >
                    <div
                        v-if="field.label"
                        class="flex items-center justify-between pr-1 field-title gap-2"
                        :class="field?.options?.editLabel ? 'w-full': ''"

                    >
                        <template v-if="field?.options?.editLabel !== true">
                            <label
                                :for="pathField(group, field)"
                                class="text-sm text-gray-600 dark:text-dark-color"
                                :class="field.type == 'toggle' ? 'cursor-pointer' : ''"
                                v-html="$helpers.markdown(field.label)"


                            />
                            <Icon :source="icons.ecInfoLine" v-if="field.tooltip" :data-tooltip="field.tooltip" :size="'w-4'" class="text-gray-500 dark:text-dark-color"></Icon>
                        </template>
                        <div
                            v-else
                            contenteditable="true"
                            class="w-full p-1.5 text-gray-900 dark:text-dark-color h-9 flex items-center truncate border rounded text-xss focus:outline-none focus:ring-0 focus:border-gray-300"
                            :class="freeform ? 'font-medium' : ''"
                            :data-name="field.name"
                            @blur="changeLabel"
                            v-html="$helpers.markdown(field.label)"
                        />
                    </div>
                    <span
                        v-if="field?.options?.delete"
                        class="ml-2 opacity-0 cursor-pointer group-hover:opacity-100 flex"
                        :data-tooltip="field.options?.delete !== false ? 'Delete' : (('System '+(field.type === 'color' ? 'color' : 'typography')+' can\'t be deleted'))"
                        data-tooltip-location="top-left"
                        @click="deleteItem(field)"
                    >
                        <Icon :source="icons.DeleteMajor" :size="'w-3.5'" :class="field.options?.delete ? 'hover:!text-red-600' : ''" class="ml-2 flex text-gray-500 dark:text-dark-color"></Icon>
                        <!-- <Icon :source="icons.ecRefresh" :color="'text-gray-500 dark:text-dark-color'" :size="'w-3.5'" class="ml-2 flex"></Icon> -->
                    </span>
                    <button
                        v-if="canReset && getData(group, field)"
                        class="mx-1 text-gray-600 hover:text-gray-900 dark:text-dark-color dark:hover:text-primary-700"
                        data-tooltip="Reset value"
                        data-tooltip-location="top"
                        @click="updateData(group, field, '__delete__')"
                    >
                        <Icon :source="icons.ecRefresh" :size="'w-3.5'" :color="'text-gray-500 dark:text-dark-color'"></Icon>
                    </button>
                    <SwitchDevice v-if="field?.options?.responsive" />
                    <div
                        v-if="valuePopup(group, field)"
                        @click="doOpenPopup()"
                        class="ml-auto cursor-pointer bg-primary-500 text-gray-700 py-1 px-2 text-xs absolute right-0 top-1 rounded-full max-w-16 hover:max-w-none"
                    >
                        <span class="block truncate capitalize" v-html="valuePopup(group, field)" />
                    </div>
                    <template v-else />
                </div>
                <component
                    v-if="field.type === 'dimension'"
                    :is="field.type + 'Field'"
                    :key="'dimension'"
                    :field="field"
                    :path="pathField(group, field)"
                    :data="getData(group, field)"
                    :responsiveData="getResponsiveData(group, field)"
                    :ref="field.type + 'Field'"
                    @update:data="updateData(group, field, $event)"
                    @update:delete="$emit('update:delete', $event)"
                    @update:clicked="$emit('update:clicked', $event)"
                />
                <component
                    v-else
                    :is="field.type + 'Field'"
                    :key="refresh"
                    :field="field"
                    :path="pathField(group, field)"
                    :data="getData(group, field)"
                    :ref="field.type + 'Field'"
                    @update:data="updateData(group, field, $event)"
                    @update:delete="$emit('update:delete', $event)"
                    @update:clicked="$emit('update:clicked', $event)"
                />
            </div>
            <div
                v-for="(desc, i) in descs(group, field)"
                :key="i"
                class="mt-1 text-sm text-gray-500 dark:text-dark-color whitespace-normal w-full"

            >
                <div class="des" v-html="$helpers.markdown(desc)" />
                <!-- <button
                    class="btn-des-readmore text-primary-500 hover:text-primary-700 transition"
                    @click="$event.target.parentNode.classList.toggle('max-2-line')"
                /> -->
            </div>
        </template>
    </div>
</template>



<script lang="ts">
import SwitchDevice from '../molecules/DeviceOptions/DeviceOptions.vue';
import { mapGetters } from 'vuex';

import AllFields from '@/plugins/fields/fields';
import { fieldMixin, relationMixin } from '../../mixins';

import { types as valuesDropdown } from '@/constants';
import {ecRefresh, DeleteMajor, ecInfoLine} from '@/assets/icons';

export default {
    components: {
        SwitchDevice,
        ...AllFields
    },
    mixins: [fieldMixin, relationMixin],
    props: {
        data: {
            type: Object,
            default() {
                return {};
            }
        },
        group: {
            type: Object,
            default() {
                return {};
            }
        },
        fieldValues: {
            type: Object,
            default() {
                return {};
            }
        },
        canResets: {
            type: Object,
            default() {
                return {};
            }
        },
        freeform: {
            type: Boolean,
            default: false
        },
        ispop: {
            type: Boolean,
            default: false
        }
    },
    emits: [
        'update:label',
        'update:delete',
        'update:clicked',
        'update:data',
        'fieldClick'
    ],
    data() {
        return {
            valueDeleted: {},
            icons: {
                DeleteMajor,
                ecRefresh,
                ecInfoLine
            }
        };
    },
    computed: {
        refresh(){
            return this.$store.getters.REFRESH;
        },
        values() {
            return this.fieldValues;
        },
        field() {
            return this.data;
        },
        getCanResets() {
            return this.canResets;
        },
        canReset() {
            return (this.field?.options?.reset ?? false) === true ||
                    (
                        ['number', 'dimension'].indexOf(this.field?.type) > -1 &&
                        this.field?.options?.reset !== false
                    ) ||
                    (
                        this.field?.type == 'picker' &&
                        this.field?.options?.type == 'icon' &&
                        this.field?.options?.reset !== false
                    );
        },
        ctrfields() {
            return ['choose', 'toggle', 'popup', 'color', 'background', 'icon'];
        },
        isOneLine() {
            return this.field?.options?.oneline || this.ctrfields.indexOf(this.field.type) > -1 || this.field.options?.type === 'icon';
        },
        isReverse() {
            return this.isOneLine && (this.ispop !== true || this.ctrfields.indexOf(this.field.type) > -1 || (this.field.type === 'picker' && !this.field.options?.multiple && this.field.options?.type === 'icon'));
        },
        ...mapGetters('builder', ['editingElement']),
        ...mapGetters('settings', [ 'globalData' ]),
    },
    // created() {

    //     if(this.data?.params?.length) {
    //         const checkBgField = this.data.params.find(field => field.type === 'background' && field?.options?.toNative);
    //         if(checkBgField) {
    //             this.fieldValues[this.data?.name].forEach((field, index) => {
    //                 if(checkBgField?.name && field[checkBgField?.name] && !field[checkBgField?.name]?.native) {

    //                     this.fieldValues[this.data?.name][index]['value'].native = this.convertNativeColor(field);
    //                 }
    //             })
    //         }
    //     }
    //     console.log(this.fieldValues);
    // },
    methods: {
        doOpenPopup() {
            if(this.$refs['popupField']) {
                this.$refs['popupField'].open();
            }
        },
        // convertNativeColor(val) {
        //     return Object.keys(val.value?.gradient).length
        //     ? convertBg({
        //             gradient: val?.value?.gradient
        //         })
        //     : "";
        // },
        changeLabel(e){
            this.$emit('update:label', [e.target.dataset.name, e.target.innerText])
        },
        valueGlobal(values, field) {
            let k;
            let v;
            delete field?.options?.global?.value;
            for (k in values) {
                if (k.startsWith('global-')) {
                    let value = values[k];
                    let type = /^global-([\w]+)/g.exec(k)?.[1];
                    let globalValue = this.globalData(type)?.[value]?.value;
                    if (globalValue) {
                        if (typeof globalValue === 'object') {
                            v = globalValue;
                        } else if (typeof globalValue === 'string') {
                            v = globalValue;
                        }
                        field.options.global.value = value;
                    }
                }
            }
            return v ?? values;
        },
        descs (g, f) {
            let descs = f.description;
            let rs = [];
            if(descs) {
                if(typeof descs === 'object') {
                    let p = this.pathField(g, f, false),
                        v = this.$helpers.valueByKey(p, this.values, '||');
                    if(descs.constructor === Object) {
                        descs = [descs];
                    }
                    descs.forEach(wn => {
                        let {content, condition} = wn;
                        if(content) {
                            if(!condition || condition(v, this.values)) {
                                rs.push(content);
                            }
                        }
                    });
                }
                else if(typeof descs === 'string') {
                    rs.push(descs);
                }
            }
            return rs;
        },
        getData(group, field) {
            let name = this.fieldName(field);
            let v = group?.group_name ?
                    this.values?.[group.group_name]?.[name] :
                    this.values?.[name];
            return this.valueGlobal(v, field);
        },
        getResponsiveData(group, field) {
            let name = this.fieldName(field);
            let value_desktop = {};
            let v = group?.group_name ?
                    this.values?.[group.group_name]?.[name] :
                    this.values?.[name];
            if(name.includes('__tablet') || name.includes('__mobile')) {
                 let name_desktop = name.replace('__tablet', '').replace('__mobile', '');

                value_desktop = group?.group_name ? this.values?.[group.group_name]?.[name_desktop] : this.values?.[name_desktop];
                return v ?  mergeDeep(this.$helpers.copy(value_desktop), this.$helpers.copy(v)) : this.$helpers.copy(value_desktop);
            }

            return v;

            function isObject(item) {
                return (item && typeof item === 'object' && !Array.isArray(item));
            }

            function mergeDeep(target, ...sources) {
                if (!sources.length) return target;
                const source = sources.shift();

                if (isObject(target) && isObject(source)) {
                    for (const key in source) {
                    if (isObject(source[key])) {
                        if (!target[key]) Object.assign(target, { [key]: {} });
                        mergeDeep(target[key], source[key]);
                    } else {
                        Object.assign(target, { [key]: source[key] });
                    }
                    }
                }

                return mergeDeep(target, ...sources);
            }

        },
        // getData(group, field) {
        //     let name = this.fieldName(field);
        //     let value_desktop = {};
        //     let v = group?.group_name ?
        //             this.values?.[group.group_name]?.[name] :
        //             this.values?.[name];
        //     if(name.includes('__tablet') || name.includes('__mobile')) {
        //          let name_desktop = name.replace('__tablet', '');

        //         value_desktop = group?.group_name ? this.values?.[group.group_name]?.[name_desktop] : this.values?.[name_desktop];
        //         console.log(value_desktop, v);
        //         let vn = Object.assign(value_desktop, v);

        //         console.log(vn);

        //     }
        //     //console.log(this.values, group?.group_name);

        //     return this.valueGlobal(v, field);
        // },
        deleteObj(group, fieldName) {
            let values = {};
            if(group.group_name) {
                values = this.values[group.group_name]?.data;
                if(values) {
                    delete values[fieldName];
                    if(Object.keys(values).length === 0) {
                        delete this.values[group.group_name];
                    }
                }
            }else {
                values = this.values;
                delete values?.[fieldName];
            }
        },
        isValidValue(value) {
            return  !(value == '__delete__' ||
                value == '__empty__' ||
                value === '' ||
                (!(value instanceof File) && typeof value === 'object' && Object.keys(value).length === 0)
            );
        },
        updateData(group, field, value) {
            let { group_name } = group;
            let field_name = this.fieldName(field);
            if(!field_name) {
                return ;
            }
            let values = null;
            if(group_name) {
                this.values[group_name] = this.values[group_name] ?? {};
                values = this.values[group_name];
            }
            else {
                values = this.values;
            }
            if(this.isValidValue(value)) {
                values[field_name] = value;
            }
            else {
                delete values?.[field_name];
            }
            this.$emit('update:data', field);

        },
        deleteItem(field) {
            if(field.options?.delete !== false) {
                this.$emit('update:delete', field.name);
            }
        },
        valuePopup(group, field) {
            if(field.type === 'popup' && field.options?.type === 'dropdown') {
                let { preview = 'value' } = field.options;
                let optionsValues = field.options?.values;

                let values = {};

                if(typeof optionsValues === 'string') {
                    values = valuesDropdown[optionsValues] ?? [];
                }
                else if(typeof optionsValues === 'object'){
                    values = optionsValues;
                }
                let value = this.getData(group, field);

                if(value === undefined) {
                    return 'Default';
                }
                let isArrValues = values.constructor === Array;
                value = isArrValues?
                        values.filter(it => it.name === value)[0] :
                        value;
                let rs;

                if(preview === 'value'){
                    rs = value?.name ?? value;
                }
                else {
                    rs = isArrValues ? value?.title : values[value];
                }
                return typeof rs === 'string' ? rs.replace(/_/g, ' ') : rs;
            }
        }
    }
};
</script>
<style>
    .max-2-line .des{
        display: block;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .btn-des-readmore:before {
        content: 'Read less'
    }
    .max-2-line .btn-des-readmore:before {
        content: 'Read more'
    }
</style>
