<template>
    <div class="relative" :class="[id,wrap, full ? 'w-full' : '']">
        <button :class="btnclass ? btnclass+(isopen && activeclass ? ' '+activeclass : '') : ['inline-flex hover:text-gray-800 active:bg-gray-300 dark:hover:text-primary-700 dark:active:bg-dark items-center justify-center px-2 py-3 text-sm font-normal focus:outline-none bg-white dark:bg-dark dark dark:text-dark-color', isopen ? 'text-gray-800 bg-gray-200 dark:bg-dark dark:text-dark-color' : 'text-gray-600 hover:bg-gray-200', extclass, rounded ? 'rounded' : '']" :data-tooltip="tooltip" @keyup.space.prevent :data-tooltip-location="dir == 'right' ? 'bottom-right' : 'bottom'" @click="open">
            <slot name="button" />
        </button>

        <transition enter-active-class="transition ease-out duration-10" enter-from-class="transform scale-90 opacity-0"
            enter-to-class="transform scale-100 opacity-100" leave-active-class="transition ease-out duration-15"
            leave-from-class="transform scale-100 opacity-100" leave-to-class="transform scale-90 opacity-0">
            <div v-if="isopen" class="absolute z-20 max-w-xs px-2 mt-3 sm:px-0 -translate-y-[2px]" :class="{ 'w-full max-w-none': full, 'left-0': dir == 'left', 'right-0': dir == 'right', 'left-1/2 w-0': dir == 'center', 'top-0 left-full': dir == 'custom', [popclass]: true}">
                <div class="inline-block p-2 overflow-auto text-base transition bg-white dark:bg-dark dark:text-dark-color rounded shadow-lg max-h-96 ring-1 ring-black ring-opacity-10 focus:outline-none sm:text-sm" :class="{'w-full': full, 'transform -translate-x-1/2': dir == 'center', [panelclass]: true}">
                    <slot name="content" class="whitespace-nowrap text-left w-full text-sm flex min-w-full px-2 py-2 mb-0.5 rounded text-gray-800 dark:text-dark-color dark:hover:bg-gray-600 hover:bg-gray-100 hover:text-gray-900 dark:hover:text-gray-200" />
                </div>
            </div>
        </transition>
    </div>
</template>
<script lang="ts">
    import { Popmenu } from '@/mixins'
    import { wrap } from 'module';
    export default {
        mixins: [ Popmenu ],
        props: {
            class: {
                type: String,
                default: ''
            },
            rounded: {
                type: Boolean,
                default: true,
            },
            activeclass: {
                type: String,
                default: ''
            },
            popclass: {
                type: String,
                default: ''
            },
            panelclass: {
                type: String,
                default: ''
            },
            extclass: {
                type: String,
                default: ''
            },
            dir: {
                type: String,
                default: 'center'
            },
            tooltip: {
                type: String,
                default: ''
            },
            close: {
                type: String,
                default: ''
            },
            full: {
                type: Boolean,
                default: true
            },
            wrap: {
                type: String,
                default: ''
            }
        },
        computed: {
            btnclass () {
                return this.class ? this.class : false;
            },
            keep() {
                return this.close != 'anyway';
            }
        },
        watch: {
            isopen: {
                deep: true,
                handler(newVal : any) {
                    if(!newVal)
                        this.$emit('hanldeClose');
                }
            }
        }
    };
</script>
