<template>
    <button v-if="is === 'button'" v-bind="bind">
        <slot name="before" />
        <span v-if="text !== ''" v-html="text" />
        <!-- <ecom-icon v-if="icon" class="w-5 h-5"  :icon="icons[icon+'Icon']" /> -->
        <slot />
    </button>
    <a v-else v-bind="bind">
        <span v-if="text !== ''" v-html="text" />

        <!-- <ecom-icon v-if="icon" class="w-5 h-5"  :icon="icons[icon+'Icon']" /> -->
        <slot />
    </a>
</template>

<script lang="ts">
import { defineComponent } from "@vue/runtime-core";
// import {faXmark,faTrash,faCheck,faSliders,faPlus} from '@fortawesome/pro-light-svg-icons'
export default defineComponent({

    props: {
        text: {
            type: String,
            default: ""
        },
        is: {
            type: String,
            default: "button"
        },
        type: {
            type: String,
            default: "secondary"
        },
        formtype: {
            type: String,
            default: "button"
        },
        icon: {
            type: String,
            default: null
        },
        class: {
            type: String,
            default: null
        },
        size: {
            type: String,
            default: "normal"
        },
        tooltip: {
            type: String,
            default: null
        },
        tooltiplocation: {
            type: String,
            default: null
        }
    },
    computed: {
        icons(){
            return {};
        },
        bind() {
            return {
                class:
                    "inline-flex justify-center items-center font-medium smoothing active:outline-none focus:outline-none " +
                    this.classButton,
                "data-tooltip": this.tooltip,
                "data-tooltip-location": this.tooltiplocation,
                type: this.formtype,
                title: this.text
            };
        },
        classButton() {
            let cl = [];
            if (this.type != "icon") {
                cl.push(
                    this.size == "small"
                        ? "h-8 text-sm"
                        : this.size == "tiny"
                        ? "h-7 text-xss"
                        : "h-9 text-sm"
                );
                if (!this.class || this.class.indexOf("px-") === -1) {
                    cl.push(this.size == "small" ? "px-2.5" : "px-3");
                }
            }
            cl.push(this.class);
            switch (this.type) {
                case "secondary":
                    cl.push(
                        "border border-gray-300 dark:border-gray-600 text-gray-500 shadow-sm bg-white dark:bg-gray-700 dark:border-gray-600 dark:text-dark-color dark:hover:bg-dark dark:active:bg-dark hover:bg-gray-100 active:bg-gray-200 focus:ring-primary-600"
                    );
                    break;
                case "danger":
                    cl.push(
                        "border border-gray-300 dark:border-gray-600 text-red-600 hover:text-red-800 dark:bg-gray-700 dark:border-gray-600 dark:text-dark-color shadow-sm bg-white hover:bg-gray-100 active:bg-gray-200 focus:ring-red-600 dark:hover:bg-dark dark:active:bg-dark"
                    );
                    break;
                case "disabled":
                    cl.push("border border-gray-300 dark:border-gray-600 bg-gray-100 text-gray-400 pointer-events-none dark:bg-gray-700 dark:border-gray-600 dark:text-dark-color");
                    break;
                case "icon":
                    cl.push("p-2 hover:bg-gray-200 active:bg-gray-300 hover:text-gray-800 dark:active:bg-gray-700 dark:text-dark-color dark:hover:bg-dark dark:hover:text-primary-700");
                    break;
                case "icon-button":
                    cl.push("p-2");
                    break;
                case "primary":
                    cl.push("text-white bg-primary-600 hover:bg-primary-700 active:bg-primary-800 focus:outline-none");
                    break;

                default:
                    break;
            }
            if (!this.class || this.class.indexOf("rounded") === -1) {
                cl.push("rounded font-medium smoothing");
            }
            return cl.join(" ");
        }
    }
});
</script>
