export default function(com) {
    const checkElement = com ? [{
            name: 'width-max',
            label: 'Width',
            type: 'popup',
            options: {
                size: 'small',
                values: {
                    '100%': 'Full Width',
                    'unset': 'Inline (auto)',
                    'custom': 'Custom'
                },
                preview: 'title',
                default: '',
                type: 'dropdown'
            }
        },
        {
            type: 'number',
            label: 'Custom width',
            name: 'custom-width',
            options: {
                responsive: true,
                units: {
                    px: { min: 0, max: 1000, step: 1 },
                    '%': { min: 0, max: 100, step: 1 },
                    vw: { min: 0, max: 100, step: 1 }
                },
                visible: {
                    keep_data: false,
                    condition: (data) => {
                        return data['width-max'] === 'custom';
                    }
                }
            }
        },
        {
            type: 'popup',
            label: 'Position',
            name: 'position',
            options: {
                values: {
                    'relative': 'Relative',
                    'absolute': 'Absolute',
                    'fixed': 'Fixed'
                },
                default: '',
                size: 'small',
                type: 'dropdown'
            }
        }
    ] : [{
        type: 'popup',
        label: 'Position',
        name: 'position',
        options: {
            values: {
                'relative': 'Relative',
                'absolute': 'Absolute',
                'fixed': 'Fixed'
            },
            default: '',
            type: 'dropdown'
        }
    }];

    return [
        ...checkElement,
        // {
        //     type: 'choose',
        //     label: 'Vertical align',
        //     name: 'align-self',
        //     options: {
        //         oneline: true,
        //         responsive: true,
        //         type: 'vertical-align',
        //         values: ['flex-start', 'center', 'flex-end'],
        //         visible: {
        //             keep_data: false,
        //             condition: (data) => {
        //                 return data['width-max'] &&
        //                     data['position'] !== 'absolute' &&
        //                     data['position'] !== 'fixed';
        //             }
        //         }
        //     }
        // },
        {
            type: 'choose',
            label: 'Horizontal Orientation',
            name: 'horizontal-orientation',
            value: 'left',
            options: {
                oneline: true,
                type: 'align-x',
                values: ['left', 'right'],
                visible: {
                    keep_data: false,
                    condition: (data) => {
                        return data && data['position'] && ['absolute', 'fixed', 'relative'].indexOf(data['position']) > -1;
                    }
                },
                warnings: {
                    content: '<strong>Please note!</strong> Custom positioning is not considered best practice for responsive web design and should not be used too frequently.'
                }
            },

            css: { isCss: false }
        },
        {
            type: 'number',
            label: 'Offset Left',
            name: 'left',
            options: {
                responsive: true,
                units: {
                    px: { min: -1000, max: 1000, step: 1 },
                    '%': { min: -200, max: 200, step: 1 },
                    vw: { min: -200, max: 200, step: 1 },
                    vh: { min: -200, max: 200, step: 1 }
                },
                visible: {
                    keep_data: false,
                    condition: (data) => {
                        return data && data['horizontal-orientation'] === 'left' && ['absolute', 'fixed', 'relative'].indexOf(data['position']) > -1;
                    }
                }
            }
        },
        {
            type: 'number',
            label: 'Offset Right',
            name: 'right',
            options: {
                responsive: true,
                units: {
                    px: { min: -1000, max: 1000, step: 1 },
                    '%': { min: -200, max: 200, step: 1 },
                    vw: { min: -200, max: 200, step: 1 },
                    vh: { min: -200, max: 200, step: 1 }
                },
                visible: {
                    keep_data: false,
                    condition: (data) => {
                        return data &&  data['horizontal-orientation'] === 'right' && ['absolute', 'fixed', 'relative'].indexOf(data['position']) > -1;
                    }
                }
            }
        },
        {
            type: 'choose',
            label: 'Vertical Orientation',
            name: 'verical-orientation',
            value: 'top',
            options: {
                oneline: true,
                type: 'align-y',
                values: ['top', 'bottom'],
                visible: {
                    keep_data: false,
                    condition: (data) => {
                        return data && data.position && ['absolute', 'fixed', 'relative'].indexOf(data['position']) > -1;
                    }
                }
            },
            css: { isCss: false }
        },
        {
            type: 'number',
            label: 'Offset Y',
            name: 'top',
            options: {
                responsive: true,
                units: {
                    px: { min: -1000, max: 1000, step: 1 },
                    '%': { min: -200, max: 200, step: 1 },
                    vw: { min: -200, max: 200, step: 1 },
                    vh: { min: -200, max: 200, step: 1 }
                },
                visible: {
                    keep_data: false,
                    condition: (data) => {
                        return data && data.position && data['verical-orientation'] === 'top' && ['absolute', 'fixed', 'relative'].indexOf(data['position']) > -1;
                    }
                }
            }
        },
        {
            type: 'number',
            label: 'Offset Y',
            name: 'bottom',
            options: {
                responsive: true,
                units: {
                    px: { min: -1000, max: 1000, step: 1 },
                    '%': { min: -200, max: 200, step: 1 },
                    vw: { min: -200, max: 200, step: 1 },
                    vh: { min: -200, max: 200, step: 1 }
                },
                visible: {
                    keep_data: false,
                    condition: (data) => {
                        return data && data.position && data['verical-orientation'] === 'bottom' && ['absolute', 'fixed', 'relative'].indexOf(data['position']) > -1;
                    }
                }
            }
        },
        {
            name: 'z-index',
            type: 'number',
            label: 'Z-Index',
            options: {
                control_width: '75%',
                slider: false,
                responsive: true,
                visible: {
                    keep_data: false,
                    condition: (data) => {
                        return data['position'] && data['position'] !== '';
                    }
                }
            },
            css: {
                important: true
            }
        }
    ];
}