export default {
    data() {
        return {
            id: this.$helpers.randid(),
            isopen: false,
            canClose: false,
            popupPosition: 'bottom',
            isShowPopup: false
        }
    },
    methods: {
        open(e) {
            if (this.isopen) {
                return this.isopen = false;
            }
            this.isopen = true;

            this.$nextTick(() => {
                const popupWrapper = this.$refs['ecom-fields__popup'];
                if(popupWrapper) {
                    const rect = popupWrapper.getBoundingClientRect();
                        if(rect.y + rect.height > window.innerHeight && rect.y > rect.height + 50) {
                            this.popupPosition = 'top';
                        }

                    this.isShowPopup = true;
                }
            })

            this.isShowPopup = false;
            this.popupPosition = 'bottom';

            document.addEventListener('mousedown', this.doClose);
            document.addEventListener('click', this.doClose);
        },
        doClose(e) {
            if (e && e.type == 'mousedown') {
                this.canClose = (!e.target.closest('.'+this.id) || this.keep === false);
                if (this.canClose) {
                    document.removeEventListener('mousedown', this.doClose);
                }
            } else if (
                e === undefined || this.canClose
            ) {
                this.isopen = false;
                this.canClose = false;
                document.removeEventListener('click', this.doClose);
                document.removeEventListener('mousedown', this.doClose);
            }
        }
    }
}