<template>
    <template v-if="link && url !== ''">
        <a v-bind:href="url" :class="[styleTypo, buttonSize, lightMode, darkMode]">
            <Icon v-if="icon !== ''" :source='icon' :size="sizeIcon" class="inline-flex justify-center items-center mx-1 px-0.5"></Icon>
            <slot></slot>
        </a>
    </template>
    <template v-else-if="!split">        
        <button class="outline-interactive-600 transition hover:transition duration-300" :class="[styleTypo, buttonSize, lightMode, darkMode]" @click="onAction">
            <slot name="before"></slot>
            <Icon v-if="icon !== ''" :source='icon' :size="sizeIcon" :class="colorIcon" class="inline-flex justify-center items-center mx-1 px-0.5"></Icon>
            <slot></slot>
            <slot name="after"></slot>
        </button>
    </template>
    <template v-else>
        <div class="inline-flex relative">
            <button :class="[styleTypo, buttonSize, lightMode, darkMode]" class="rounded-r-none border-r-0 !outline-none transition hover:transition duration-300 pr-3" @click="onAction">
                <slot name="before"></slot>
                <Icon v-if="icon !== ''" :source='icon' :size="'w-4'" class="inline-flex justify-center items-center mx-1 px-0.5"></Icon>
                <slot></slot>
                <slot name="after"></slot>
            </button>
            <div ref="mainSplit">
                <button :class="[styleTypo , buttonSize, lightMode, darkMode,btnSplitClass]" class="rounded-l-none !outline-none" @click="splitShow = !splitShow">
                    <Icon :source="CaretDownMinor" :size="'w-5'"></Icon>
                </button>
                <Transition
                    enter-active-class="transition ease-out duration-10"
                    enter-from-class="transform -translate-y-2 scale-90 opacity-0"
                    enter-to-class="transform translate-y-100 scale-100 opacity-100"
                    leave-active-class="transition ease-out duration-15"
                    leave-from-class="transform translate-y-100 scale-100 opacity-100"
                    leave-to-class="transform -translate-y-2 scale-90 opacity-0"
                >
                    <ul v-if="splitShow" class="absolute right-0 top-full shadow-lg bg-white rounded min-w-30 p-3 mt-1 dark:bg-dark dark:!shadow-dark">
                        <template v-for="(item, key) in actions">
                            <li v-if="item.hidden === false" :key="key" class="mb-1 last:mb-0">
                                <ecom-button fullWidth :icon="item.icon" @click="item.onAction" class="!justify-start border-0 whitespace-nowrap hover:bg-gray-100 dark:hover:bg-gray-600 !shadow-none transition hover:transition duration-300">{{ item.title }}</ecom-button>
                            </li>
                        </template>
                    </ul>
                </Transition>
            </div>
        </div>
    </template>
</template>

<script lang="ts">
    import { CaretDownMinor } from '@/assets/icons'
    export default {
        props: {
            outline: {
                type: Boolean,
                default: false,
            },
            primary: {
                type: Boolean,
                default: false,
            },
            destructive: {
                type: Boolean,
                default: false,
            },
            link: {
                type: Boolean,
                default: false,
            },
            url: {
                type: String,
                default: '',
            },
            icon: {
                type: String,
                default: '',
            },
            size: {
                type: String,
                default: 'medium',
            },
            fullWidth: {
                type: Boolean,
                default: false,
            },
            // small,medium,large
            split: {
                type: Boolean,
                default: false,
            },
            onAction: {
                type: Function,
                default: null,
            },
            actions: {
                type: Array,
                default: null,
            }
        },
        data(){
            return {
                CaretDownMinor: CaretDownMinor,
                splitShow: false,
                // darkMode: '',
                // styleTypo: '',
            }
        },
        computed: {
            sizeIcon(){
                switch (this.size) {
                    case 'small':
                        return 'w-3.5';
                        break;
                    case 'medium':
                        return 'w-5';
                        break;
                    case 'large':
                        return 'w-6';
                        break;
                    default:
                        break;
                }
                
            },
            colorIcon(){
                if(this.primary || this.destructive){
                    return 'text-white';
                }else{
                    return 'text-gray-500 dark:text-dark-color';
                }
            },
            lightMode(){
                if(this.outline){
                    return 'bg-transparent border-gray-200 hover:bg-gray-100';
                }

                if(this.primary){
                    return 'text-white bg-primary-600 hover:bg-primary-700 hover:shadow-lg';
                }
                
                if(this.destructive){
                    return 'text-white bg-danger-600 hover:bg-danger-700 hover:shadow-lg';
                }

                if(this.link){
                    return '';
                }

                return 'bg-white hover:bg-gray-100';
            },
            darkMode(){

                if(this.outline){
                    return 'dark:bg-transparent dark:text-dark-color dark:border-gray-700 dark:hover:bg-dark';
                }
                if(this.primary || this.destructive || this.link){
                    return '';
                }
                return 'dark:bg-dark dark:text-dark-color dark:hover:bg-dark';
            },

            styleTypo(){
                if(this.outline){
                    return 'inline-flex items-center justify-center font-medium text-sm border rounded shadow-sm py-1 px-1.5';
                }

                if(this.primary || this.destructive){
                    return 'inline-flex items-center justify-center font-medium text-sm shadow-sm border border-transparent rounded py-1 px-1.5';
                }

                if(this.link){
                    return 'inline-flex items-center justify-center font-medium border border-transparent text-sm underline hover:text-primary-700';
                }

                return 'inline-flex items-center justify-center font-medium text-sm rounded py-1 px-1.5';
            },
            buttonSize(){
                let tmp = '';
                switch (this.size) {
                    case 'small':
                        tmp += ' h-7 !text-xs';
                        break;
                    case 'medium':
                        tmp += ' h-9 !text-sm';
                        break;
                    case 'large':
                        tmp += ' h-10 !text-base';
                        break;
                    default:
                        break;
                }
                this.fullWidth ? tmp += ' !flex w-full' : '';
                return tmp;
            },
            btnSplitClass(){
                if(this.primary || this.destructive){
                    return 'ml-[1px]';
                }
            }
        },
        mounted() {
            document.addEventListener("click", this.handleClickOutside);
        },
        beforeDestroy() {
            document.removeEventListener("click", this.handleClickOutside);
        },
        methods: {
            handleClickOutside(event) {
                const mainSplitElement = this.$refs.mainSplit;
                if (mainSplitElement && !mainSplitElement.contains(event.target) && this.splitShow) {
                    this.splitShow = false;
                }
            },
        },
    }
</script>