import { getters } from './root/getters';
import { mapActions } from "vuex";
import { state } from "./root/state";

export default {
    namespaced: true,
    state: {
        library: {
            open: false,
            loading: true,
            detail: null,
            listLoading: true
        },
        queries: {
            page: 1,
            category: null,
            query: null,
            type: 'popular',
            tag: null
        },
        apps: null,
        categories: [],
        installed: [],
        toast: {
            open: false,
            type: 'success',
            message: ''
        },
        timeout: null
    },
    getters:{
        open( state ){
            return state.library.open;
        },
        categories(state){
            return state.categories;
        },
        apps(state){
            return state.apps;
        },
        queries(state){
            return state.queries;
        },
        installed(state){
            return state.installed;
        },
        library(state){
            return state.library;
        },
        detail(state) {
            return state.library.detail;
        },
        loading(state){
            return state.library?.loading;
        },
        listLoading(state) {
            return state.library.listLoading;
        },
        toast(state){
            return state.toast;
        },
    },
    mutations:{
        setApps(state,payload){
            state.apps = payload;
        },
        setCategories(state,payload){
            state.categories = payload;
        },
        setInstalled(state,payload){
            state.installed = payload;
        },
        setLoading(state, payload){
            state.library.loading = payload;
        }, 
        setListLoading(state, payload){
            state.library.listLoading = payload;
        },
        setDetail(state, payload) {
            state.library.detail = payload;
        },
        setQueryType(state, payload) {
            state.queries.type = payload;
        },
        setQueryPage(state, payload) {
            state.queries.page = payload;
        },
        setQueryQuery(state, payload) {
            state.queries.query = payload;
        },
        setQueryTag(state, payload) {
            state.queries.tag = payload;
        },
        setQueryCategory(state, payload) {
            state.queries.category = payload;
        }
    },

    actions: {

        open({state, dispatch}){
            state.library.open = true;
            if (state.apps === null) {
                state.library.loading = true;
                dispatch('load');
                if(!window.location.hash.includes('extensions'))
                {
                    window.location.hash='extensions'
                }
            }
        },

        load({state, dispatch, commit}) {
            commit('setLoading', true);
            this.$worker.fetch( window.route('builder.apps', state.queries) )
                .then(response=>{
                    commit('setLoading', false);
                    if(response.data.status === 'success')
                    {
                        commit('setCategories',response.data.categories);
                        commit('setApps',response.data.apps);
                        commit('setInstalled', response.data.installed);

                    }
                    else
                        throw new Error(response.data.message ? response.data.message: 'Unknow error');

                })
                .catch(e=>{
                    commit('setLoading', false);
                    dispatch('global/flash',{
                        type: 'error',
                        message: e.message,
                        open: true
                    },{root: true});
                    return false;
                });
        },

        close({state}) {
            state.library.open = false;
            if(window.location.hash.includes('extensions'))
            {
                window.location.hash=''
            }
        },

        setToast({state},payload) {
            state.toast = payload;
            clearTimeout(state.timeout)
            state.timeout = setTimeout(()=>{
                state.toast = {
                    open: false
                }
            }, 6000);
        },

        uninstall({ commit, dispatch,state }, payload) {
            dispatch('setLoading', {id: payload.id, status: true});
            this.$worker.fetch(window.route('builder.apps.uninstall',{ id: payload.id }), null, 'DELETE')
            .then(response => {
                commit('setLoading', false);
                if(response.data.status === 'success') {
                    commit('builder/setPopup',
                        {
                            show: true,
                            type: 'success',
                            loading: false,
                            title: 'Complete!',
                            message: 'Your extension has been uninstalled successfully.',
                            closeable: true
                        },{ root: true});
                    dispatch('load');
                    dispatch('page/uninstallEcoSystemApp', response.data.extension,{ root:true});
                    if (typeof payload.callback == 'function') {
                        payload.callback();
                    }
                } else {
                    throw new Error(response.data.message ? response.data.message : 'Unknow message');
                }
            })
            .catch(e => {
                commit('setLoading', false);
                commit('builder/setPopup',
                    {
                        show: true,
                        type: 'error',
                        loading: false,
                        title: 'Notice!',
                        message: e.message,
                        closeable: true
                    },{ root: true});
                if(state.apps?.data)
                {
                    state.apps.data.forEach(app=>{
                        if(app.id === payload.id)
                        {
                            app.loading = false;
                        }
                    })
                }
            })
        },

        install({ commit, dispatch, state }, payload) {
            let app = state.apps.data.find(app => app.id === payload.id);
            if(app) {
                if(app?.options?.schema?.handle_install === true) {
                    let key = 'EComCallback' + new Date().getTime();

                    window[key] = (payload,params) => {
                        if(payload === 'accept') {
                            installApp(this); 
                        }
                    }; 
                    commit('builder/setPopup',{
                        type: 'warning',
                        title: 'Opps!',
                        show: true,
                        message: `Please make sure you installed <b>${app?.options?.schema?.name || app.title}</b> app before using this extension`,
                        callback: key,
                        action: {
                            title: 'Yes, app installed '
                        },
                        second_action: {
                            title: 'Not yet, add app now',
                            link: app?.options?.schema?.link + '?utm_source=ecomposer&utm_medium=extension&ref=ecomposer' || 'https://apps.shopify.com/'
                        },
                        closeable: true 
                    },{ root: true});
                } else {
                    installApp(this);
                }
            }

            function installApp(_this) {
                dispatch('setLoading', {id: payload.id, status: true});
                _this.$worker.fetch(window.route('builder.apps.install',{ id: payload.id }),null, 'POST')
                .then(response=>{
                    commit('setLoading', false);
                    dispatch('setLoadingDetail', {id: payload.id, status: false});
                    dispatch('detail', {id: payload.id, tab: response.data.extension?.options?.schema?.settings ? 'settings': 'details'});
                    if(response.data.status === 'success') {
                        commit('builder/setPopup',
                            {
                                show: true,
                                type: 'success',
                                loading: false,
                                title: 'Congratulations!',
                                message: 'Your extension has been installed successfully.',
                                closeable: true
                            },{ root: true});
                        payload.tab = 'settings';
                        dispatch('load');

                        dispatch('page/insertEcoSystemApp', response.data.extension,{ root:true});
                        if (typeof payload.callback == 'function') {
                            payload.callback();
                        }
                        if(response.data.info)
                        {
                            commit('builder/setPopup',
                                {
                                    show: true,
                                    type: 'warning',
                                    loading: false,
                                    title: 'Action required!',
                                    message:  response.data.info,
                                    closeable: true
                                },{ root: true});
                        }

                    } else {
                        throw new Error(response.data.message ? response.data.message : 'Unknow message');
                    }
                })
                .catch(e => {
                    commit('setLoading', false);
                    commit('builder/setPopup',
                        {
                            show: true,
                            type: 'error',
                            loading: false,
                            title: 'Notice!',
                            message: e.message,
                            closeable: true
                        },{ root: true});
                    if(state.apps?.data)
                    {
                        state.apps.data.forEach(app=>{
                            if(app.id === payload.id)
                            {
                                app.loading = false;
                            }
                        })
                    }
                })
            }

        },

        detail({ commit,dispatch }, payload) {
            dispatch('setLoadingDetail', {id: payload.id, status: true});
            this.$worker.fetch(window.route('builder.apps.settings', { post: payload.id, page: payload.page }), null, 'GET')
            .then(response => {
                dispatch('setLoadingDetail', {id: payload.id, status: false});
                commit('setListLoading', false);
                if (response.data.status === 'success') {
                    window.location.hash='extensions/'+ payload.id;
                    response.data.app.type = 'detail';
                    response.data.app.tab = payload.tab;
                    commit('setDetail', { ...response.data.app, review: response.data.review,reviews: response.data.reviews});
                    if (typeof payload.callback == 'function') {
                        payload.callback();
                    }
                    if(response.data.info)
                    {
                        commit('builder/setPopup',
                            {
                                show: true,
                                type: 'warning',
                                loading: false,
                                title: 'Information!',
                                message:  response.data.info,
                                closeable: true
                            },{ root: true});
                    }
                } else {
                    throw new Error(response.app.message ? response.app.message : 'Unknow message');
                }
            })
            .catch(e=>{
                commit('setListLoading', false);
                dispatch('global/flash',{
                    type: 'error',
                    message: e.message,
                    open: true
                },{root: true});
            })
        },

        setLoading({state}, payload) {
            let app = state?.apps?.data?.constructor === Array ? state.apps.data.filter((a) => {return a.id === payload.id;}) : null;
            if (app?.[0]) {
                app[0].loading = payload.status;
            }
            if (state.library.detail) {
                state.library.detail.loading = payload.status;
            }
        },

        setLoadingDetail({state}, payload) {
            let app = state?.apps?.data?.constructor === Array ? state.apps.data.filter((a) => {return a.id === payload.id;}) : null;
            if (app?.[0]) {
                app[0].loadingDetail = payload.status;
            }
            if (state.library.detail) {
                state.library.detail.loading = payload.status;
            }
        },

        closeDetail({commit}){
            commit('setDetail', null);
            window.location.hash = 'extensions'
        }
    }
}