export default {
  namespaced: true,
  state: {
      open: false,
      show: false,
      loading: false,
      shopifyType: '',
      metafileds: {},
      dynamicSource:[]
  },
  getters: {
      open(state) {
          return state.open;
      },
      show(state) {
        return state.show;
      },
      loading(state) {
        return state.loading;
      },
      shopifyType(state) {
        return state.shopifyType;
      },
      metafileds(state) {
        return state.metafileds[state.shopifyType];
      },
      dynamicSource(state) {
        return state.dynamicSource
      }
  },
  mutations: {
      open(state, payload) {
          state.open = payload;
      },
      show(state, payload) {
        state.show = payload;
      },
      loading(state, payload) {
        state.loading = payload;
      },
      metafileds(state, payload) {
        state.metafileds[payload.shopify_type] = payload.data;
      },
      dynamicSource(state, payload) {
        state.shopifyType = payload;
        const source = {
          product: [
            {
              title: 'Title',
              key: 'product.title'
            },
            {
              title: 'Link Title',
              key: 'product.title | link_to: product.url'
            },
            {
              title: 'Description',
              key: 'product.description'
            },
            {
              title: 'Type',
              key: 'product.type'
            },
            {
              title: 'Image',
              key: 'product | img_tag'
            },
            {
              title: 'Price',
              key: 'product.price | money'
            },
            {
              title: 'Price Compare',
              key: 'product.compare_at_price | money'
            },
            {
              title: 'Tags',
              key: "product.tags | join: ', '"
            }
          ],
          collection: [
            {
              title: 'Title',
              key: 'collection.title'
            },
            {
              title: 'Description',
              key: 'collection.description'
            },
            {
              title: 'Tags',
              key: "collection.tags | join: ', '"
            },
            {
              title: 'All Tags',
              key: "collection.all_tags | join: ', '"
            },
            {
              title: 'All Type',
              key: "collection.all_types | join: ', '"
            },
            {
              title: 'All Vendor',
              key: "collection.all_vendors | join: ', '"
            },
            {
              title: 'Featured Image',
              key: 'collection.featured_image | img_tag'
            },
            {
              title: 'Products Count',
              key: 'collection.products_count | img_tag'
            },
          ],
          page: [
            {
              title: 'Title',
              key: 'page.title'
            },
            {
              title: 'Description',
              key: 'page.page_description'
            },
            {
              title: 'Content',
              key: 'page.content'
            },
            {
              title: 'Author',
              key: 'page.author'
            },

          ],
          article: [
            {
              title: 'Title',
              key: 'article.title'
            },
            {
              title: 'Link Title',
              key: 'article.title | link_to: article.handle'
            },
            {
              title: 'Excerpt',
              key: 'article.excerpt'
            },
            {
              title: 'Content',
              key: 'article.content'
            },
            {
              title: 'Author',
              key: 'article.author'
            },
            {
              title: 'Tags',
              key: "article.tags | join: ', '"
            },
          ],
          blog: [
            {
              title: 'Title',
              key: 'blog.title'
            },
            {
              title: 'Link Title',
              key: 'blog.title | link_to: blog.handle'
            },
            {
              title: 'All Tags',
              key: "article.all_tags | join: ', '"
            },
          ],
        }
        state.dynamicSource = source[payload];
      },
  },
  actions: {
    loadDynamicSources({ getters, state, commit, dispatch, rootGetters }) {
      commit('loading', true);
      const editingElement = rootGetters['builder/activeElement'], sections = rootGetters['page/sections'], section = sections.find((s:any) => s.id == editingElement?.section_id);
      let shopify_type = section?.shopify_type ?  section?.shopify_type : 'page';
      if(shopify_type) {
        commit('dynamicSource', shopify_type);
      }

      if(state?.metafileds && state.metafileds.hasOwnProperty(shopify_type)) {
        commit('loading', false);
        return;
      }
      console.log('Load Dynamic Resource')
      this.$worker
          .fetch(window.route('sources.load', {shopify_type: shopify_type}), null)
          .then((response) => {
              if (response.data.status === 'success') {
                commit('metafileds', {shopify_type: shopify_type, data: response.data.metafields});
                commit('loading', false);
              } else {
                  throw new Error(
                      response?.data?.message ? response.data.message : 'Unknow error'
                  );
              }
          })
          .catch((e) => {
            commit('loading', false);
        });
    }
  }
};