                        <button title="Delete item" class=" absolute top-1/2 right-7 transform -translate-y-1/2 p-2.5 text-gray-600 dark:text-dark-color dark:hover:text-primary-700 dark:hover:bg-dark transition-all opacity-0 cursor-pointer border-0 rounded hover:bg-gray-200" :class="{ 'group-hover:opacity-100': !isDraggingStarted }" @click="deleteItem(i)">
                            <Icon :source="icons.DeleteMajor" :size="'w-4'"></Icon>
                        </button>
                        <button title="Drag item" class="flex absolute top-1/2 right-1 transform -translate-y-1/2 py-2.5 px-1 text-gray-600 dark:text-dark-color dark:bg-dark transition-all opacity-0 cursor-pointer border-0 rounded hover:bg-gray-200 group-hover:opacity-100" :class="dragging ? 'cursor-grabbing' : 'cursor-grab'" @mousedown="dragItem">
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="w-3 h-4 -mr-1 text-gray-500 dark:text-dark-color transition opacity-0 group-hover:opacity-100">
                                <path d="M10 6a2 2 0 110-4 2 2 0 010 4zM10 12a2 2 0 110-4 2 2 0 010 4zM10 18a2 2 0 110-4 2 2 0 010 4z"/>
                            </svg>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" class="w-3 h-4 -ml-1 text-gray-500 dark:text-dark-color transition opacity-0 group-hover:opacity-100">
                                <path d="M10 6a2 2 0 110-4 2 2 0 010 4zM10 12a2 2 0 110-4 2 2 0 010 4zM10 18a2 2 0 110-4 2 2 0 010 4z"/>
                            </svg>
                        </button>
                    </template>
                </li>
            </ul>
            <template v-if="!showEditAlt && options.editAlt && options?.type == 'image'">
                <ecom-button :outline="'true'" :icon="icons.EditMajor" @click="open" class="absolute right-4 bottom-4 !h-7 !text-xs bg-white hover:bg-gray-100 dark:!bg-dark dark:hover:bg-gray-500">
                    <span>Edit</span>
                </ecom-button>
            </template>
        </div>

    <!-- <div
        v-if="!checkShopifyPicker"
        class="absolute top-0 bottom-0 left-0 right-0 border-2 border-transparent rounded cursor-pointer ecom-picker-ring z-1 border-primary-600 ring-offset-1"
    /> -->
        <div v-if="!showEditAlt && options.editAlt && options?.type == 'image'" class="relative z-20" :class="id">
            <transition
                enter-active-class="transition duration-200 ease-out"
                enter-from-class="transform scale-90 opacity-0"
                enter-to-class="transform scale-100 opacity-100"
                leave-active-class="transition duration-150 ease-in"
                leave-from-class="transform scale-100 opacity-100"
                leave-to-class="transform scale-90 opacity-0"
            >
                <div v-if="isopen" class="fixed w-full h-full left-0 ecom-fields__focal-wrapper right-0 top-0 z-10 mb-1 bottom-full ecom-fields__popup">
                    <div class="absolute ecom-fields__focal-inner w-2/5 left-0 right-0 bg-white dark:bg-dark dark:text-dark rounded shadow-popup z-20">
                        <div class="flex bg-white h-full dark:bg-dark dark:text-dark-color bottom-2 rounded" :class="isSingle ? 'flex-col' : ''">
                            <div class="block px-5 py-4 border-b border-gray-300 border-solid">
                                <div class="flex justify-between">
                                    <div class="text-xl"><h2>Preview and edit</h2></div>
                                    <span class="transition hover:rotate-180 transform cursor-pointer"
