                                                    >
                                                </div>
                                            </div>
                                        </div>
                                        <div class="flex gap-5 p-5 bg-gray-200">
                                            <div class="w-3/5 flex items-center">
                                                <h3 class="uppercase overflow-ellipsis overflow-hidden" v-html="valueProp[0].name">

                                                </h3>
                                            </div>
                                            <div class="btn_focal w-2/5 flex items-center justify-center">
                                                <span 
                                                    ref="btn_enable"
                                                    class="bg-primary-600 w-4/5 h-10 flex items-center justify-center rounded text-white cursor-pointer"
