                                    <div class="w-full px-5 mb-4">
                                        <span class="text-sm leading-none text-gray-900 dark:text-dark-color">Edit alt text</span>
                                        <div class="flex mt-2">
                                            <input
                                                ref="inputEditAlt"
                                                v-model="values[0].name"
                                                v-focus="'select'"
                                                type="text"
                                                class="w-full pl-2 transition border-gray-300 dark:bg-gray-700 dark:border-gray-600 dark:text-dark-color rounded shadow-sm h-9 sm:text-sm focus:ring-inset focus:ring-1 focus:ring-primary-400 dark:focus:ring-transparent focus:border-primary-400"
                                            >
                                        </div>
                                    </div>
                                </div>
