                            <div class="block px-5 py-4 border-t border-gray-300 border-solid">
                                <div class="flex justify-end">
                                    <div class="flex justify-end w-2/5 gap-10">
                                        <div class="py-1 px-4 flex justify-center cursor-pointer rounded text-gray-500 shadow-sm hover:bg-gray-100 bg-white border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-dark-color dark:hover:bg-dark"
                                            @click="cancelSet"
                                        > Cancel </div>
                                        <div class="py-1 px-4 flex justify-center cursor-pointer rounded text-white hover:bg-primary-700 bg-primary-600"
                                            @click="saveSet"
                                        > Save </div>
                                    </div>
                                </div>
                            </div>
                        </div>
