<template>
    <div class="inline-block w-full bg-white dark:bg-gray-700 text-black dark:text-dark-color rounded">
        <ul
            v-if="Object.keys(valueTypeFiltered).length > 0"
            class="p-2 overflow-auto max-h-60"
        >
            <button
                v-for="(item, key) in valueTypeFiltered"
                :key="key"
                as="li"
                type="button"
                class="
                    cursor-pointer
                    w-full
                    mb-0.5
                    hover:bg-gray-100
                    dark:hover:bg-dark
                    rounded-sm
                    transition
                    cursor-default
                    text-sm
                    select-none
                    relative
                    p-2
                    flex
                    items-center
                    rounded-sm
                    justify-between
                "
                :class="{ 'bg-gray-100 dark:bg-dark': selected === key }"
                @click="$emit('update:selected', key)"
            >
                <span class="flex items-center">
                    <Icon :source="icons.MobileAcceptMajor" :class="selected === key ? 'text-primary-600' : 'text-white dark:text-dark'" :style="type === 'colors' ? 'background-color:' + item.value + (selected === key ? ';color:white' : ';color:' + item.value) : ''"></Icon>
                    <span class="text-sm font-normal text-left truncate w-full ml-2" :style="styleTitle(item.value)">{{ item.title.substr(0, 20) }}</span>
                </span>
                <span
                    v-if="type === 'colors'"
                    class="text-xs text-gray-500 dark:text-dark-color font-normal py-0.5 px-1 rounded-sm ml-2"
                >
                    {{ item.value }}
                </span>
            </button>
        </ul>
        <div v-else class="block py-5 pt-0 m-auto text-sm text-center text-gray-400">Empty</div>
    </div>
</template>

<script lang="ts">
import { MobileAcceptMajor } from "@/assets/icons";
import { mapGetters } from 'vuex';
export default {
    name: 'Global',

    props: {
        global: {
            type: Object,
            default() {
                return {};
            }
        },
        selected: {
            type: String,
            default: ''
        }
    },
    emits: ['update:selected'],
    computed: {
        type() {
            return this.global?.type ?? '';
        },
        icons(){
            return {
                MobileAcceptMajor
            }
        },
        tooltip() {
            if (this.type === 'colors') {
                return 'Tooltip color';
            } else if (this.type === 'typography') {
                return 'Tooltip typo';
            }
            return '';
        },
        title() {
            if (this.type === 'colors') {
                return 'Global Colors';
            } else if (this.type === 'typography') {
                return 'Global typo';
            }
            return '';
        },
        ...mapGetters('settings', ['globalData']),
        valueType() {
            return this.globalData(this.type);
        },
        valueTypeFiltered() {
            let values = {};
            for (let k in this.valueType) {
                let d = this.valueType[k];
                if (
                    (typeof d.value === 'string' && d.value) ||
                    (typeof d.value === 'object' && Object.keys(d.value).length)
                ) {
                    values[k] = d;
                }
            }
            return values;
        }
    },
    methods: {
        styleTitle(value) {
            if (typeof value === 'object') {
                // eslint-disable-next-line no-unused-vars
                let { 'font-size': fontSize, 'line-height': lineHeight, ...val } = value;
                return val;
            }
        },

    }
};
</script>
