<template>
    <div class="inline-flex relative">
        <input :id="path ? path : null" v-model="enabled" type="checkbox" class="h-5 w-5 cursor-pointer opacity-0 absolute z-10 peer">
        <span class="w-5 h-5 border rounded flex justify-center items-center peer-checked:bg-interactive-600 peer-checked:border-transparent">
            <Icon :source="icons.TickSmallMinor" :size="'w-5'" :color="'text-white'" class="w-5 h-5 flex"></Icon>
        </span>
  </div>
</template>

<script lang="ts">
    import { TickSmallMinor } from "@/assets/icons";
    export default {
        name: 'ToggleField',
        components: {
            
        },
        props: {
            // eslint-disable-next-line vue/require-default-prop
            data: [String, Boolean, Number],
            path: {
                type: String,
                default: ''
            },
            field: {
                type: Object,
                default(){
                    return {};
                }
            }
        },
        emits: ['update:data'],
        data(){
            return {
                enabled: false,
                isWatch: true,
                isEmit: false,
                icons: {
                    TickSmallMinor
                }
            };
        },
        computed: {
            options(){
                return this.field?.options ?? {};
            },
            optionValues(){
                return this.options.values ?? {};
            },
            currentValue(){
                return this.optionValues[this.enabled ? 'on' : 'off'];
            },
            text(){
                return this.currentValue?.label ?? 'OFF';
            },
            value(){
                return this.currentValue?.value ?? false;
            }
        },
        watch: {
            data: {
                immediate: true,
                handler: function(val){
                    if(this.isWatch){
                        this.isEmit = false;

                        this.enabled = this.getEnable(val);
                    }
                this.isWatch = true;

                }
            },
            value: {
                immediate: true,
                handler: function(val){
                    if(this.isEmit){
                        this.isWatch = false;
                        this.$emit('update:data', val);
                    }
                    this.isEmit = true;
                }
            }

        },
        methods: {
            getEnable(value){
                let result = this.options?.default ?? false;
                for(let k in this.optionValues){
                    if(this.optionValues[k].value === value){
                        result = k === 'on';
                    }
                }
                return result;
            }
        }
    };
</script>
<style>
    .shadow-toggle{
        box-shadow: inset 0 1px 2px rgb(0 0 0 / 12%), inset 0 0 2px rgb(0 0 0 / 15%);
    }
    .shadow-switch {
        box-shadow: -1px 1px 5px rgb(0 0 0 / 20%);
    }
    .text-tiny{
        font-size: 0.65rem;
        line-height: 2;
    }
</style>