<template>
    <component :is="as" :class="`${textColor} ${textAlignment} ${textFontWeight} ${textTruncate} ${textSize} line-clamp-${lineClamp}`" :id="id">
        <slot></slot>
    </component>
</template>

<script lang="ts">
    export default {
        props: {
            alignment: {
                type: String,
                default: 'start',
                // 'start' | 'center' | 'end' | 'justify'
            },
            as: {
                type: String,
                default: 'p',
            },
            color: {
                type: String,
                default: 'subdued',
                // 'success' | 'critical' | 'warning' | 'subdued' | 'text-inverse'
            },
            fontWeight: {
                type: String,
                default: '',
                // 'normal' | 'medium' | 'semibold' | 'bold'
            },
            id: {
                type: String,
                default: '',
            },
            truncate: {
                type: Boolean,
                default: false,
            },
            size: {
                type: String,
                default: 'text-base',
            },
            // tiny,small,medium,large,extralarge
            lineClamp: {
                type: String,
                default: '2',
            }
        },
        computed: {
            textAlignment(){
                switch (this.alignment) {
                    case 'start':
                        return 'text-left';
                        break;
                    case 'center':
                        return 'text-center';
                        break;
                    case 'end':
                        return 'text-right';
                        break;
                    case 'justify':
                        return 'text-justify';
                        break;
                    default:
                        break;
                }
            },

            textFontWeight(){
                if(this.fontWeight !== ''){
                    return `font-${this.fontWeight}`
                }
                if(this.as === 'h1' || this.as === 'h2' || this.as === 'h3' || this.as === 'h4' || this.as === 'h5' || this.as === 'h6'){
                    return 'font-medium';
                }else{
                    return 'font-normal';
                }
            },

            textTruncate(){
                if(this.truncate == true){
                    return 'truncate';
                }
            },

            textSize(){
                switch (this.size) {
                    case 'tiny':
                        return 'text-xs';
                        break;
                    case 'small':
                        return 'text-sm';
                        break;
                    case 'medium':
                        return 'text-base';
                        break;
                    case 'large':
                        return 'text-lg';
                        break;
                    case 'extralarge':
                        return 'text-xl';
                        break;
                    default:
                        break;
                }
            },

            textColor(){
                switch (this.color) {
                    case 'primary':
                       return 'text-primary-600';
                        break;
                    case 'success':
                       return 'text-green-700';
                        break;
                    case 'critical':
                       return 'text-danger-600';
                        break;
                    case 'warning':
                       return 'text-yellow-800';
                        break;
                    case 'subdued':
                       return 'text-gray-600 dark:text-dark-color';
                        break;
                    case 'text-inverse':
                       return 'text-interactive-600';
                        break;
                    default:
                       return 'text-gray-600 dark:text-dark-color';
                        break;
                }
            }
        }
    }
</script>